module.exports = "<hr>\n<p>title: 第23章\ndate: 2017-07-18 03:59:59\nwords: 3567</p>\n<h2 id=\"index-23\">index: 23</h2>\n<!-- deleteAbove -->\n<p>   干娘我不想再回到那里我不想再见到他..我要走我要走到一个他找不到的地方...眼泪浸湿了萧淑的衣衫..</p>\n<p>  好..乖女儿..你走吧..干娘支持你..萧淑看着怀里嘤嘤哭泣的爱儿说道..</p>\n<p>  收拾好行装带了点盘缠爱儿告别了干爹干娘就离开了福来客栈只身上路了..天大地大她不知道要去哪里..但是她要走..一定要远远的离开</p>\n<p>  王府书房</p>\n<p>  天..刚才侍卫说看见爱儿从福来客栈出来..枫说..</p>\n<p>  倐的慕容临天紧闭的眼睛突然睁开..这几天他一直不眠不休..连朝也不上只靠酒来麻痹自己..下巴上已经长出了胡茬看起来很憔悴..可是这个消息就像把他这一具行尸走安上了跳动的心..</p>\n<p>  他什么也没说就冲了出去..驾~~~~黑色的骏马飞驰出去..黝黑的皮毛在阳光下闪着光天！</p>\n<p>  枫见叫不住慕容临天..赶紧叫上暗夜铁骑军去追...毕竟几天没休息的慕容临天还真是让他有些担心..</p>\n<p>  爱儿一个人走在城外的官道上心里一直空落落的..真的要走了..怕是这一辈子也见不到他了吧爱儿苦笑着..</p>\n<p>  嗖！</p>\n<p>  什么声音？转过头来一看！一把明晃晃的剑正向自己刺来...爱儿赶快往旁边一闪撞在一棵树上.索躲过了黑衣人的攻击..你是谁！</p>\n<p>  为什么要杀我！爱儿问要刺杀她的黑衣人...</p>\n<p>  受人钱财替人消灾！</p>\n<p>  说完手上寒光闪闪的飞镖向爱儿打来...爱儿微微一偏飞镖扎扎实实的扎在爱儿身后的树上...</p>\n<p>  抽出腰间的小红鞭..只是好久没用武功了动作都有些生疏...一剑一鞭之间果然爱儿敌不过黑衣人被一脚踢到在地...糟了他正在向爱儿逼近..手上的宝剑耀着闪闪寒光...</p>\n<p>  去死吧！</p>\n<p>  黑衣人大喝一声剑向爱儿刺来爱儿闭上眼睛只等剑刺穿自己的心脏...可是.....她感觉自己落入一个温暖的怀抱..这个怀抱她再熟悉不过了..</p>\n<p>  睁开眼睛..果然！</p>\n<p>  慕容临天来救她了！依旧是那么冷酷的脸庞..可是增添了几分憔悴..是你？</p>\n<p>  爱儿说道视线久久离不开..</p>\n<p>  闭嘴！慕容临天把她放下然后和黑衣人打斗起来..突然四周出现了一大群黑衣人！</p>\n<p>  少说也有百来个！若是以前的慕容临天这些人绝对不是他的对手！可是..现在的慕容临天可是精神十分欠佳！</p>\n<p>  逐渐..慕容临天落了下风..</p>\n<p>  见机一个黑衣人拿刀像爱儿砍去</p>\n<p>  爱儿小心！</p>\n<p>  说着慕容临天扑了过去挡在爱儿前面！</p>\n<p>  撕！他胸前的衣服被砍碎一条触目惊心的血痕汩汩淌着血..一脚踢开下刀之人拉起爱儿的手走！</p>\n<p>  他们一路跑黑衣人却紧追不舍..一路上慕容临天又受了好几刀..不知流了多少血..</p>\n<p>  呀！</p>\n<p>  他们刹住脚步..悬崖！怎么办前无去路后有追兵...慕容临天抓紧爱儿的手满目柔情的看着爱儿嘴角扬起一个美丽的弧度...敢吗？</p>\n<p>  他问道..</p>\n<p>  敢！爱儿很坚决的答道..</p>\n<p>  两人就这样对视着..纵身一跃</p>\n<p>  第四十九章昨夜星辰昨夜风（一）</p>\n<p>  老大他们跳下去了怎么办！</p>\n<p>  一个黑衣人问道..</p>\n<p>  回去复命相信这是他想要的结果..走！</p>\n<p>  说完一群黑衣人撤离了这里..</p>\n<p>  悬崖下</p>\n<p>  唔好痛哦爱儿嘟嚷着揉揉自己的小..睁开眼睛感觉全身骨头都快散架了..湿淋淋的从小河里走上岸...</p>\n<p>  回想刚才在快掉到地面的时候慕容临天紧紧抓住自己的手另一只手使劲攀在树枝上再从树上掉入河中才幸免于难..要不然可真不知道自己现在死得多难看..爱儿打了个哆嗦...</p>\n<p>  对了慕容临天呢？</p>\n<p>  怎么没看到他？爱儿环视着四周大声呐喊慕容临天你在哪儿在哪儿在哪儿在哪儿可是只听到山谷的回音..</p>\n<p>  糟了！</p>\n<p>  他中了那么多刀流那么多血不会掉河里被水冲走了吧....！这样想着爱儿的心里越是不安....</p>\n<p>  她沿着小河一路向下游寻找慕容临天——慕容临天你在哪里可是并没有发现慕容临天的踪迹...天啦那是什么！</p>\n<p>  爱儿好像有了什么发现..她跑到小河边那里的一块石头上挂着一块黑色的布..上面还有血....是的！</p>\n<p>  这是慕容临天衣服上的..他不会被那些人抓走了吧....</p>\n<p>  慕容临天..爱儿想到了千万种不好的可能心里不由的撕痛起来..她无助的蹲下来抱住双腿埋下头嘤嘤的哭泣..慕容临天你不要死我不要你死...呜呜...</p>\n<p>  半晌</p>\n<p>  哭什么呢？</p>\n<p>  一直大手搭在爱儿微微颤抖的肩膀上..这个声音是.....</p>\n<p>  慕容临天！</p>\n<p>  爱儿激动地站起来一把抱住慕容临天..太好了你没死呜呜我就知道你不会死的...呜呜</p>\n<p>  慕容临天惊愕的看着在自己怀中哭得一把鼻涕一把眼泪还往自己身上擦的爱儿..嘴角扬起了一个满足的微笑..她怎么哭也哭的这么可爱..不觉用手轻轻拍拍爱儿的背..</p>\n<p>  突然爱儿抬起头来怒目着他..你没死刚才怎么不回答我！</p>\n<p>  你跑哪去了！故意看我笑话是不是？你这个大坏蛋王八蛋！你怎么不去死！</p>\n<p>  ！边说边用粉拳不停地砸着慕容临天的胸口...</p>\n<p>  呃..慕容临天吃痛的哼了一声..</p>\n<p>  你怎么了？</p>\n<p>  看出了慕容临天的不对劲爱儿突然神经紧绷起来...轻轻扒开慕容临天胸前的衣服......天啦！</p>\n<p>  怎么流了这么多血...看着他胸前的伤口爱儿惊呼道...而慕容临天那双深邃的眸子正在一点一点的迷蒙..</p>\n<p>  慕容临天！</p>\n<p>  慕容临天你怎么了！你醒醒！那魁梧的身躯砰一声倒地漆黑的眼眸也缓缓的闭上了..</p>\n<p>  不行这样他会死的..脱掉慕容临天的上衣身上裸的伤口像一条条鲜红的蜈蚣攀在他那健硕的肌上..血流不止爱儿脱下自己的外衣撕成好几条包扎住慕容临天的伤口还好身上还有一颗百草丸可止血立即给他服下...</p>\n<p>  待一切忙完已经近黄昏了爱儿到附近拾来一些木柴再生起一堆火把慕容临天和自己淋湿的外衣放在火边烤干...</p>\n<p>  咕咕咕.......肚子里一阵叫唤...再看看慕容临天..他手上拿着什么？</p>\n<p>  爱儿好奇的扒开他的手指..野果！原来..原来他白天醒来就是为了找这个...笨蛋！</p>\n<p>  受了伤还要乱跑...她嘟起小嘴责备道可眼里全是晶莹的感动..</p>\n<p>  已经过去这么久了他还没醒过来..爱儿握起慕容临天的大手轻轻的在自己脸颊磨蹭..他的手好冰...寒秋的夜晚总是凉的..不行他受了这么重的伤不能再感染风寒了..</p>\n<p>  爱儿将慕容临天更加拖近火堆处然后趴在他的胸膛上用体温给他取暖...轻轻用手指勾画着他那如刀削般的脸部轮廓..回忆着自己受伤的时候他好像也这么做过..爱儿不咯咯的笑出声来..和他在一起真的好开心..爱儿现在不想再想其他的把一切恩怨情仇统统抛到一边去..只想好好享受现在只属于他们俩的美好时光...</p>\n<hr>\n<p>  亲们有没有觉得这章很温情呢？</p>\n<p>  呵呵前面虐了那么久现在稍微缓缓加深一下感情嘛..下章继续温情哦...小甜一下...</p>\n<p>  第五十章昨夜星辰昨夜风（二）</p>\n<p>  对上慕容临天的那张帅脸爱儿深情的着他粗粗的眉毛的鼻子还有那xing感的嘴唇...你真的好傻...你为什么要来救我还害的自己受了那么多伤....爱儿笑靥如花..情不自的在他的额头上印下一个浓浓的吻...一路向下到了鼻子..最后是那张无比魅惑的嘴唇...唇瓣冰凉凉的有一种特殊的触感..好久才不舍的离开那令人流连忘返的唇...</p>\n<p>  看着慕容临天健硕的胸膛爱儿不着..可碰到那鲜红的伤口爱儿的心却是无止境的揪痛着比自己受伤还要痛..你知道吗？</p>\n<p>  我宁愿受伤的是我这样..我也不用这么痛..</p>\n<p>  渐渐的慕容临天的身体有了些温度..爱儿还沉浸在无边无际的爱里她仿佛要把自己这么多天来的痛苦全都出来...</p>\n<p>  这几天我真的好想你..好想好想...想念你的怀抱你的温度你的味道..每天晚上没有你在我身边我都会睡不着..无论在哪里脑海里总是有你的身影你说的每一句话...好想就这样一直躺在你的怀里..不离开..想到要永远见不到你我的心就好痛痛得都快死了..可当我看到你为了救我而出现的那一刻我好高兴！</p>\n<p>  ..我真的不知道自己为什么会这样..或许这就是爱吧....说到这里..慕容临天的眼皮动了一下却没有睁开...爱儿更加抱紧了他..我爱你...我真的真的好爱你..我以前说的话都是骗你的..你快醒过来不要离开我..慕容临天...</p>\n<p>  叫我天...</p>\n<p>  天...咦不对！</p>\n<p>　</p>\n";

