module.exports = "<hr>\n<p>title: 第8章\ndate: 2017-07-18 03:59:58\nwords: 3555</p>\n<h2 id=\"index-8\">index: 8</h2>\n<!-- deleteAbove -->\n<p>   果然..如水冷笑着点点头....嗯如儿明白..事实上如水又怎么会不明白他在说什么...</p>\n<p>  好了你去休息吧...</p>\n<p>  干爹干娘如儿先告退了如水福了身走了....</p>\n<p>  来到爱儿房前...轻轻推开门看着熟睡的爱儿如水眼里满是心疼...多可怜的人..多可怜...轻轻道：爱儿对不起.....我今后一定会好好保护你....说完看了她可爱的脸蛋一眼转身不舍的离开了....</p>\n<p>  十一岁对于如水..是个最最重大的转折..从那天起她不能再像个孩子她的心智从那天起已经屡炼成熟已经蜕变....</p>\n<p>  苍天...你可知世间有多少可怜人等着你的垂怜？</p>\n<p>  如水姑娘..如水姑娘..</p>\n<p>  呃？清脆的声音把如水拉回现实...你是？</p>\n<p>  哦奴婢名叫小可王爷吩咐奴婢来伺候你...小丫鬟长得眉清目秀虽算不上美但让人看起来觉得很舒服....</p>\n<p>  小可你的名字真好听..如水微微一笑...</p>\n<p>  呵呵奴婢的名字怎么跟如水姑娘比呢？</p>\n<p>  嗯你先下去吧我想休息休息..如水有些睡眼朦胧..</p>\n<p>  那好吧等晚饭做好了奴婢再来叫如水姑娘起床...说罢退了出去..</p>\n<p>  第十六章众里寻她千百度，蓦然回首（八）</p>\n<p>  次日天大亮..盛夏的烈日照耀着世间的万物...当然也包括那个小小的人儿..</p>\n<p>  热好热爱儿喃喃着...苍白的脸庞乌羽扇般的睫毛因阳光的映照在脸上投下一排阴影.与苍白的脸色形成强烈的对比...手脚被捆绑着已经勒出了道道淤青..还好昨天在挣扎下塞住嘴的布包掉在了地上要不然就算不热死也得闷死...</p>\n<p>  一天一夜滴水未尽的她已经嘴唇干裂感觉身上的力气在一点一滴流失...</p>\n<p>  这一切的一切全部出现在如水的眼里同样也出现在他的眼里....</p>\n<p>  爱儿..如水正欲喊出口..</p>\n<p>  你已经看到她了本王的承诺也兑现了你该回去了..冰冷的语气丝毫没得商量..</p>\n<p>  留恋的再看最后一眼向男子福了福身..王爷如水先行告退..</p>\n<p>  他看着地上那个小小的身影似乎...不他不可能有怜悯之心谁叫他是易天齐的干女儿活该她承受这一切...</p>\n<p>  来人把她带到麒麟殿！</p>\n<p>  一声令下..爱儿的苦由此始...</p>\n<p>  ..........................................................................</p>\n<p>  躺在麒麟殿的软榻上爱儿不住的喃喃：水...水...</p>\n<p>  顿时感到唇瓣湿润了..是水！</p>\n<p>  爱儿贪婪的大口着这甘甜感觉这是生命之源...</p>\n<p>  喝饱了水力气稍稍恢复了点尽力睁开眼睛..</p>\n<p>  咦？</p>\n<p>  眼前好大一张脸...这是...冥王！而他的唇正在自己的唇上！难道刚才那些水都是从他嘴里渡过来的....</p>\n<p>  想到这爱儿一把推开男子趴在床边干呕起来......然后用力擦着自己的嘴唇想把刚才他吻过的地方全部擦掉...愤愤的瞪着眼前这个可恨的男人..爱儿忍不住大骂：你无耻！</p>\n<p>  男子的舌扫过嘴唇仿佛在留恋刚才的美好...本王无耻？是谁刚才要喝水是谁对着本王的唇猛吸？</p>\n<p>  其实他自己也没想到她的吻会这么甜让他如此留恋...该死他居然起了反应...</p>\n<p>  爱儿脸上青一阵红一阵但更多的是愤怒..你！</p>\n<p>  ！我要杀了你！！说罢爱儿挥拳向他砸去..可是却被他轻易地抓住..</p>\n<p>  握着爱儿的粉拳轻轻一拉便狠狠锢在怀里..敢骂本王？</p>\n<p>  我会让你后悔！话音落下翻身把爱儿压在身下</p>\n<p>  嚓啦..爱儿的外衣被他撕得粉碎..露出那的香肩和美丽的锁骨...男子狠狠的啃咬一番随即落下个个紫色的痕迹..</p>\n<p>  放开我！</p>\n<p>  ！爱儿手脚不停挥动着在男子身上捶打着身子不停的扭动想要逃脱男人的锢可是在他看来这像是一种变相的迎合..在她的扭动碰撞下他身下的男体征慢慢胀大...胀痛难忍抵着她的....</p>\n<p>  虽然爱儿还未及笄但她清楚的明白这意味着什么...她拼命地挣扎起来啪一巴掌甩在男人的左脸....</p>\n<p>  你个jian婢居然敢打本王！</p>\n<p>  果然爱儿很成功的激怒了他！</p>\n<p>  他起身一巴掌把爱儿打得摔倒在地..脸上五个火辣辣的手指印触目惊心嘴角渗出了丝丝血液...爱儿忍着不哼出声她绝不会向他屈服..</p>\n<p>  男人解开自己的衣服欺身上前一把扯掉粉红色的肚兜一片春色显露无疑...</p>\n<p>  你混蛋！</p>\n<p>  ！爱儿咬牙切齿着欲伸出手打他却被他一把抓住..</p>\n<p>  怎么？还想打本王一巴掌？</p>\n<p>  男人眼睛里的火焰快把爱儿烧死了..然爱儿还是无畏的瞪着他的眼睛里面写着坚韧不屈..男人彻底火了！</p>\n<p>  jian人不要用这种眼神看本王！他几乎是怒吼道..</p>\n<p>  我要看你管不着！</p>\n<p>  ！爱儿不服气道...可是在冥王面前这样得不到好下场....</p>\n<p>  一声撕心裂肺的惨叫...他硬生生折断了爱儿的皓腕...</p>\n<p>  爱儿痛得冷汗直冒使劲咬住下嘴唇不让自己再喊出声...</p>\n<p>  逆本王的意没有好下场！</p>\n<p>  男人说着分开爱儿的腿尽根而没..</p>\n<p>  又是一声惨叫痛..好痛...她的唇瓣因疼痛咬出了血..乌羽扇的睫毛上沾满了晶莹的泪珠儿..但强忍着不让它们从眼眶中掉落...</p>\n<p>  男人使劲着一下又一下毫不留情的冲击对爱儿来说只是更深的折磨..身下汩汩鲜血流出染红了一片....</p>\n<p>  许久男人满足的站起身...好不得意...爱儿忍痛睁开早就湿润的眼睛瞪着那个可恨的男人..本来得意着的男人顿时火冒三丈..</p>\n<p>  叫你不要用这种眼神看本王！</p>\n<p>  疯狂的咆哮..不知为什么看到那个女人这样的眼神总能让他火大...</p>\n<p>  咔嚓...</p>\n<p>  嗯...爱儿闷哼一声她已经没有力气叫出来...断骨的痛..骨扎进里的痛她能感到自己体内的血疯狂的乱窜着...霎时间血气上涌..哇..一口鲜血从爱儿口中迸出..缓缓的她闭上了那美丽的眼睛...</p>\n<p>  不知道为何..看到地上晕过去的爱儿.头发散乱的披在地上几缕散落在额前身上片片淤青两腿间已逐渐凝固的鲜血还有两只被他折断的手这一切的一切都是那么的触目惊心...他竟有一丝不忍..不他慕容临天不可能有怜悯之心..这一切都是她自找的！</p>\n<p>  拿起被撕烂衣服把爱儿包裹起来..</p>\n<p>  来人把她关到监牢去！</p>\n<p>  冰冷的命令..只为了封锁自己的心..</p>\n<p>  苍天...你可否垂怜这个可怜的人儿？</p>\n<p>  第十七章众里寻她千百度，蓦然回首（九）</p>\n<p>  如水在月华殿里不停地踱着步..心神不宁..不知道慕容临天会怎么对付爱儿？</p>\n<p>  天下人都知道京城有个冥王极其残暴冷血..一想到这里不攥紧了自己发抖的手..</p>\n<p>  如水姑娘..小可急急忙忙冲进来</p>\n<p>  小可爱儿现在怎么样了？</p>\n<p>  打听到消息了吗？一进来如水就立即上前抓住小可的手询问着紧张的心情统统写在脸上和行动中...</p>\n<p>  奴婢刚才听说王爷把爱儿姑娘打入天牢了而且好像王爷说这句话的时候很生气..这下肯定糟了！</p>\n<p>  小可理解如水的心急把刚刚打听到的一五一十的说了出来..</p>\n<p>  听了小可的话如水更是揪心糟了？</p>\n<p>  为什么糟了？打入天牢会怎么样？握住小可的手抖得更加厉害...</p>\n<p>  看见如水如此紧张小可都不忍心告诉她下文..会..会..可是支支吾吾的态度让如水愈加不安..</p>\n<p>  不行我要求王爷放过爱儿..说罢向外急急忙忙跑去..</p>\n<p>  如水姑娘...不要..见阻止不了如水小可也只好跟了上去...</p>\n<p>  书房慕容临天正在批阅奏折当朝皇帝还太小朝政暂由他担待..一旁冯枫还是一脸微笑好像天生就是这副死表情..只是眼睛里有些闪烁欲言又止..</p>\n<p>  枫你有什么想跟我说的？</p>\n<p>  只是这一点点表情都逃不过慕容临天的眼睛..</p>\n<p>  呵真是什么都瞒不过你枫笑着把手环抱在胸前..我想跟你说说那个女人的事..</p>\n<p>  哪个女人？</p>\n<p>  天沉思了一下：牢里那个？她有什么可说的？要长相没长相要身材没身材</p>\n<p>  哦？</p>\n<p>  枫玩味似的看了看他露出一丝戏谑的神情要长相没长相要身材没身材你干嘛把她吃了？</p>\n<p>  本以为天不会有什么反应可却出乎他的意料他居然在那深不见底的眼眸里看到一丝留恋..这可大大的提起了他的兴趣..吃了人家可是要负责的哦..她应该是第一次吧？</p>\n<p>　</p>\n";

