module.exports = "<hr>\n<p>title: 第28章\ndate: 2017-07-18 03:59:59\nwords: 3506</p>\n<h2 id=\"index-28\">index: 28</h2>\n<!-- deleteAbove -->\n<p>如今让她心死即使她不会恨那个jian人但是也绝对不会把事情说出来...顺便还可以看看她痛苦的样子哈哈...如雪大笑着掩藏不住的狠毒让梅凝打了一个寒战...</p>\n<p>  接下来该实行第二步了..梅凝扶我回胜雪居然后去通知王爷说我腹痛不止恐防孩子出事务必把他找来胜雪居...如雪的眼神里闪烁着诈..</p>\n<p>  是！梅凝答道..</p>\n<p>  叫人把易爱儿找来胜雪居就说我找她有事..如雪继续吩咐道..</p>\n<p>  梅凝明白..嘴角露出一丝狰狞的笑容..</p>\n<p>..............................................................................</p>\n<p>  王爷....如雪躺在脸色苍白额头上大汗淋漓...不断痛苦的申吟着...好痛..王爷..好痛....</p>\n<p>  一旁寒帮如雪把着脉...慕容临天坐在床边面无表情的看着如雪...内心里说不出的混乱甚至有些希望这个孩子能流掉...</p>\n<p>  王爷如雪姑娘有些动了胎气...寒向慕容临天说道...</p>\n<p>  此时爱儿已经来到胜雪居门外...梅凝看见了向如雪使了个眼色...</p>\n<p>  如雪立刻会意....</p>\n<p>  那这孩子保不保得住？慕容临天急切的问道...</p>\n<p>  王爷只要开几副安胎药服用就无大碍了...寒回答..</p>\n<p>  如雪见势一把抓住慕容临天的手臂顺势靠在慕容临天怀里..王爷如雪好怕......王爷好痛.....哭得梨花带雨令人好生怜惜...</p>\n<p>  这一切都落入了爱儿的眼里...慕容临天的着急..还有如雪那暧昧的动作...心里已是痛成一团..如雪你叫我来就是要我看他如何对你好吗？慕容临天..终究你还是在乎她的...不自觉的一滴泪顺着脸颊滴下来..转身向外跑去...</p>\n<p>  看着窝在自己怀里的如雪慕容临天皱了皱眉一把甩开她..你好好休息吧..天知道他有多么失望..刚才自己之所以那么着急是想听到孩子保不住的消息可是孩子却还是保住了拳头不拧紧...</p>\n<p>  第六十章 昨夜星辰昨夜风（十二）</p>\n<p>几天后典医阁</p>\n<p>  爱儿你的孩子很健康..寒给爱儿把完脉笑着说...</p>\n<p>  爱儿收回手呆呆的注视着桌上的酒杯...</p>\n<p>  对了慕容临天知道你怀孕了吗？寒关切的问道..</p>\n<p>  我还没有告诉他..爱儿说..端起桌上的酒杯一饮而尽口中顿时感到辛辣无比呛得直咳好辣.....</p>\n<p>  爱儿..寒立刻夺过她手里的酒杯..孕妇不要喝酒..随着放下酒杯..你是不是和慕容临天闹矛盾了？</p>\n<p>  爱儿没有说话只是苦笑了一下..今天就别说我和他了..寒哥哥...她顿了顿..你觉得如水姐姐怎么样？</p>\n<p>  寒一怔..半晌没说出话来...</p>\n<p>  如水姐姐她是我见过的最善良的女人..爱儿看出了寒的尴尬笑着把玩着酒杯..她从小到大就一直很照顾我像我的亲姐姐一样..每当我做错事的时候她总是先站出来跟爹说是她干的..记得有一次我打烂了爹最爱的古董花瓶..被爹重罚关到柴房里一天不准吃饭..结果晚上的时候她就偷偷溜进来给我送吃的可是被爹发现了就也被关进来和我一起受罚她却没半点怨言..如水姐姐甚至还帮我挡过刀结果差点送命..就是那次遇上了你救了她一命..这么多年来她受了什么委屈从来不会跟我说都是一个人默默的承受为的就是不希望我也陪她难过..有好多次我都看到如水姐姐一个人在偷偷的哭可是哭过之后却又是一副什么都没发生的样子...于是我便在心里发誓她永远是我的如水姐姐是我最亲的人...</p>\n<p>  听了爱儿的话寒不有些敬佩这个女子...</p>\n<p>  你知道吗？爱儿看着寒说..从她见你的第一面起就喜欢上你了..</p>\n<p>  什么？寒有些不敢置信可是脸上不自觉的有些泛红...</p>\n<p>  或许这就是所谓的一见钟情吧....爱儿笑着倒了一杯酒....</p>\n<p>  或许...寒轻轻的说道...</p>\n<p>  寒哥哥..爱儿站起来看着寒..我可不可以拜托你给如水姐姐一个机会..</p>\n<p>  这...寒有些惶恐..</p>\n<p>  爱儿只希望寒哥哥你能敞开心扉..只要和如水姐姐接触其实你就会发现她真的是比爱儿好一千一万倍的女子..而且是个一心只为你的女人...</p>\n<p>  看着爱儿那真挚的恳求的脸..寒动容了...好我答应你....</p>\n<p>  听了这话爱儿笑得合不拢嘴..寒哥哥谢谢你！遂拿起桌上的酒杯..爱儿敬你一杯！</p>\n<p>  寒拿过酒杯一口饮尽...</p>\n<p>  寒哥哥..爱儿的眼神忽然黯淡下来..其实不瞒你说我和天这么久的确有些不愉快...闻言：借酒消愁..爱儿今日只希望能醉一回..只要一回就可以了...</p>\n<p>  寒心疼的看着爱儿的脸..好就一回..今日我们不醉不归...</p>\n<p>  嗯..爱儿点点头拿起酒壶倒起酒来..一杯接一杯两人痛饮着...爱儿很少喝酒只几杯两颊便泛着醉人的红色摇摇欲坠..醉倒在桌上...剩下寒一人独酌直到酒温了一壶又一壶..渐渐地寒亦不胜酒力醉倒去...</p>\n<p>  没想到这个麟太医还挺能喝的...一个鬼鬼祟祟的身影潜入典医阁内...</p>\n<p>  第六十一章 昨夜星辰昨夜风（十三）</p>\n<p>喂你发现没有爱儿姑娘这几天和王爷好像闹不和呢！小奴说..</p>\n<p>  是王爷都好久没去麒麟殿了而爱儿姑娘就经常往典医阁跑..月莉说道..</p>\n<p>  对我看那个麟太医长得也挺帅的你说爱儿姑娘是不是和他有什么话还未说完名叫小奴的丫鬟就被一掌震开撞到墙上吐血而亡....一张冷冰冰的脸伟岸的身躯迸发着阵阵寒气...你们刚才在说什么！</p>\n<p>  月莉赶紧跪下浑身不住的发抖...王爷饶命..奴婢没说什么...</p>\n<p>  哼！不屑的瞥了跪着的丫鬟一眼就甩袖而去....半路遇上了一袭白衣的如雪...</p>\n<p>  王爷万福..如雪欠了欠身..</p>\n<p>  让开！慕容临天的语气里没有一丝温度..</p>\n<p>  王爷如雪正准备去典医阁拿药不知王爷可否同行？如雪微微笑着..心里不住的得意这样的好戏她怎么能错过...</p>\n<p>  慕容临天没有说话径直朝典医阁走去眼神中有着怒火..如雪紧随其后眼神里掩饰不住的阴狠...</p>\n<p>典医阁</p>\n<p>  寒睁开眼睛一丝不安在心中...他平常可是千杯难醉可刚刚喝了几壶就醉成这样里面绝对有鬼..瞥了一眼身旁...爱儿！他不大呼出声..</p>\n<p>  酒力像是过去了爱儿揉揉昏昏沉沉的脑袋...睁开眼睛...奇怪她怎么在？往旁边一看..寒哥哥！身下的被子里两人皆是裸的....</p>\n<p>  我们爱儿害怕的看着寒蜷缩到墙角..眼泪不争气的落下来...天啦她做了什么！她对不起天对不起如水姐姐...</p>\n<p>  爱儿...寒不忍的看着落泪的爱儿..你别怕我们什么都没做..这肯定是别人的计！</p>\n<p>  真的吗？爱儿抬起哭花的脸..还好她是清白的...</p>\n<p>  砰！来人一把推开门被阳光拉长的影子阴森森的笼罩了整个典医阁...这一看慕容临天脖子上的血管都要爆炸了...她竟和另一个男人赤身的在！眼神里的怒火显而易见拳头捏的咔咔作响....</p>\n<p>  爱儿看见了慕容临天..顿时一惊..抓起身旁的衣服赶快套在身上冲到慕容临天面前..着急的解释..天不是这样的...</p>\n<p>  哼你果然是个水杨花的女人...本王当初确实没看错你！本王？他用本王..</p>\n<p>  不是的..不是你想的那样...爱儿一把抓住慕容临天的手仿佛又回到上次恳求慕容临天相信自己的时候....</p>\n<p>  慕容临天一把甩开她的手那是怎样？语气冷得让人窒息...</p>\n<p>  我们昨天就是喝酒后来我喝醉了...爱儿希望解释却越解释越不清楚...</p>\n<p>  喝醉了然后就了是吧！慕容临天几乎怒吼着...</p>\n<p>  不是的真的不是..天..你听我解释..爱儿几乎哀求道..</p>\n<p>  有什么好解释的！慕容临天甩手而去...一旁的如雪眼神里净是得意...</p>\n<p>  天爱儿扑上去从后面抱住慕容临天..求求你听我解释好不好！</p>\n<p>  慕容临天猛地一用力爱儿一个趔趄倒在地上...慕容临天怒视着爱儿..怎么麟寒没满足你吗！jian人！</p>\n<p>  你怎么可以这么说！我没有！爱儿不服道...</p>\n<p>  哼！</p>\n<p>　</p>\n";

