module.exports = "<hr>\n<p>title: 第30章\ndate: 2017-07-18 03:59:59\nwords: 3510</p>\n<h2 id=\"index-30\">index: 30</h2>\n<!-- deleteAbove -->\n<p>枫一把把梅凝推进了书房中梅凝揉揉被捏红的手腕一抬头对上慕容临天嗜血的双眸...</p>\n<p>  慕容临天正在批阅奏折又心烦意乱突然闯进来的人让他很不爽...出去！！他几乎怒吼道..</p>\n<p>  天我只是占用你一点时间枫走上前来...</p>\n<p>  慕容临天强压住自己的怒火有话快说！！</p>\n<p>  说！枫对着梅凝说道...</p>\n<p>  梅凝低着头不敢看慕容临天的眼睛..这些事都是柳如雪主谋的...</p>\n<p>  什么是柳如雪主谋的！慕容临天的耐心快用完了..</p>\n<p>  天你要是想知道真相就给我冷静一点！枫制止着要爆发的慕容临天..</p>\n<p>  是如雪要我把迷心散放在典医阁的酒里面趁他们被迷晕后我就..就把他们脱光放到..知道爱儿怀孕如雪又叫我贿赂狱卒放打胎药在他们的饭菜里..还有...如雪肚子里的孩子根本不是王爷你的..梅凝一直颤抖着..连声音都在抖...</p>\n<p>  什么！慕容临天像被晴天霹雳击中一般...那爱儿</p>\n<p>  她是无辜的！枫吼道..出手给他一拳..希望打醒这个混蛋..</p>\n<p>  为什么...为什么我每次都要冤枉她...慕容临天任由枫打着..心中却不住的揪紧..嘴角流出了一丝鲜血他却浑然不觉更恨不得一掌打死自己..</p>\n<p>  爱儿...他无法再等待直接像牢房奔去...为什么我这么混蛋！一次次的伤害她...他不狠狠骂着自己...</p>\n<p>  爱情若是缺失信任那么这条路必定坎坷..无以复加..以至于结果如何亦未知..</p>\n<p>  牢房</p>\n<p>  爱儿！爱儿！慕容临天冲进去一把抱住爱儿...一贯冷漠如他只是碰到了她就再也无法平静..</p>\n<p>  爱儿什么话也没说甚至连眼皮都没有抬一下...</p>\n<p>  爱儿..慕容临天看着她苍白的小脸..心痛的都快死了...下巴摩挲着她的额头舍不得放开...</p>\n<p>  寒知趣的离开只留下他们两个..</p>\n<p>  一个心痛又内疚的人..一个心死的..</p>\n<p>  只是慕容临天不知道一旦心死就只是行尸走..任凭他再如何弥补或许也是无济于事..</p>\n<p>  就这样静静的..一个人垂着头..而另一个紧紧的抱着她..</p>\n<p>  对不起..对不起..慕容临天在心中千万次的忏悔可是不知道这份迟到的悔悟还能否唤醒爱儿死去的心..</p>\n<p>  你放开我！爱儿终于开口了..而却为什么她的心还会痛...</p>\n<p>  我不会放开的..永远不放...虽是霸道的占有却有着无尽的温柔..</p>\n<p>  该感动吗？感动是什么？她已经不知道了...一把挣开慕容临天的怀抱..滚！</p>\n<p>  一个字冻结了多少温情？就是这样意味着她要抛弃种种过往...</p>\n<p>  慕容临天失神着一个趔趄倒在地上..他的身上失去了往日的光环..在爱儿的面前他只是一个再普通不过的人..</p>\n<p>  我不滚..我这辈子再也不会放开你..坚定的语气甚至夹杂着些悲伤..</p>\n<p>  你这是什么意思？你听清楚我跟你已经没有关系！冰冷的话语..有甚只是伤人再自伤罢了...</p>\n<p>  没有关系..慕容临天一恸...不顾一切的抱住爱儿..我们有...爱儿你是我最爱的人..我慕容临天这辈子只爱你...你的肚子里还有我们的孩子不是吗..</p>\n<p>  孩子？他不过是你口中的野种..无情的话参杂着女人无尽的心寒..忽然血气上涌...眼睛慢慢的闭上...</p>\n<p>  爱儿！慕容临天横抱起她...我不会让你有事的..爱儿相信我..我会用剩下的生命加倍保护你...补偿你...</p>\n<p>  自古情关难过...慕容临天是如此...爱儿亦是...</p>\n<p>第六十五章 昨夜星辰昨夜风（十七）</p>\n<p>麒麟殿</p>\n<p>  麟太医爱儿她怎么样了？慕容临天表面冷静可心里却是像着火一样着急..</p>\n<p>  寒瞥了他一眼..想起前两天他对爱儿做的就真想狠狠揍他一顿...有些急火攻心加上怀了孕身体会变得比较虚弱..得开几副补药好好调理..</p>\n<p>  多谢..慕容临天不再多问只是静静的看着的爱儿...</p>\n<p>  慕容临天居然会跟别人说谢谢？寒真是难以置信..或许他真的是很爱爱儿吧..以至于为她改变了..不想打扰他们自觉的退了出去..</p>\n<p>  爱儿...对不起...慕容临天摸着她的小脸..苍白的小脸上写尽无限风霜与惆怅让慕容临天心痛的无以复加...</p>\n<p>  爱儿...你醒过来好不好？慕容临天的语气像水一样温柔完全没有了平时的那种戾气..我错了...我该相信你的...你别这样不理我好吗..你打我我心里都好过些...</p>\n<p>  爱儿...慕容临天将她的手放在自己的胸口..记得吗？我说过我会把你放在这里..你一直都在...现在我要给它加上一把锁我要把你一辈子都锁在这里...</p>\n<p>  爱儿乌羽扇般的睫毛微微扇动一下渐渐有些湿润..一滴冰冷的泪珠从眼角流出顺着脸颊直滴落到发丛里..</p>\n<p>  有太多爱与恨的纠缠..这样的爱情是没人可以主宰的..从相爱的那一刻起就注定了要陷入万劫不复..</p>\n<p>  慕容临天轻吻上爱儿眼角的泪痕..还有那陆续不涌出的泪..苦涩的她的泪是苦涩的...一点一滴慕容临天都吸进自己的嘴里..他愿意承担她所有的苦..只要她能原谅自己...</p>\n<p>  忽然间一只冰冷的小手抚上了慕容临天的脸..轻轻的抚平那皱起的眉头..像是感觉到了这样的温柔..爱儿睁开那早已泛滥的双眼...对上慕容临天的眸子...就这样四目相对着...</p>\n<p>  慕容临天抓住抚着自己脸颊的手轻轻一带爱儿便坠入他温暖的胸膛..</p>\n<p>  爱儿吾爱</p>\n<p>天吾爱</p>\n<p>  不知道我们可以这样爱多久..如果一定要有一个期限我希望是永远...</p>\n<p>  啪嗒..一滴泪滴落到地上毫不留情的溅开绚烂的水花...</p>\n<p>  不知道是不是真爱总要经过痛苦的考验或许是只有这样真爱才会浮现..</p>\n<p>  泪有点咸有点甜</p>\n<p>你的胸膛吻着我的侧脸</p>\n<p>回头看踏过的雪慢慢融化成草原</p>\n<p>而我就像你没有一秒曾后悔</p>\n<p>爱那么绵那么粘</p>\n<p>管命运设定要谁离别</p>\n<p>海岸线越让人流连总是美得越蜿蜒</p>\n<p>我们太倔强连天都不忍再反对</p>\n<p>深情一眼挚爱万年几度轮回恋恋不灭</p>\n<p>把岁月铺成红毯见证我们的极限</p>\n<p>心疼一句珍藏万年誓言就该比永远更远</p>\n<p>要不是沧海桑田真爱怎么会浮现</p>\n<p>泪有点咸有点甜</p>\n<p>你的胸膛吻着我的侧脸</p>\n<p>回头看踏过的雪慢慢融化成草原</p>\n<p>而我就像你没有一秒曾后悔</p>\n<p>爱那么绵那么粘</p>\n<p>管命运设定要谁离别</p>\n<p>海岸线越让人流连总是美得越蜿蜒</p>\n<p>我们太倔强连天都不忍再反对</p>\n<p>深情一眼挚爱万年几度轮回恋恋不灭</p>\n<p>把岁月铺成红毯见证我们的极限</p>\n<p>心疼一句珍藏万年誓言就该比永远更远</p>\n<p>要不是沧海桑田真爱怎么会浮现</p>\n<p>  把岁月铺成红毯见证我们的极限</p>\n<p>心疼一句珍藏万年誓言就该比永远更远</p>\n<p>要不是沧海桑田真爱怎么会浮现</p>\n<p>再度过些风冷雨春暖在眼前</p>\n<p>  不知还要经历多少风雨..亦不知结果如何..但是现在一定要勇敢的爱下去...</p>\n<hr>\n<p>  歌词摘自SHE的《一眼万年》看这章的时候放这首歌当背景音乐效果更好哦...太感人了...</p>\n<p>  第六十六章 昨夜星辰昨夜风（十八）</p>\n<p>温情是片刻的磨难的不断的这一对真注定要遭受生死的浩劫而始终贯穿其中的是那亘古不变的爱...</p>\n<p>  胜雪居</p>\n<p>  真是气死我了！那个慕容临天居然会把那个jian人放出来还对她那么好！难道他们已经查处事情的真相了？如雪咬着牙手一挥把桌上的东西全部打到地上..噼里啪啦砸个粉碎...正在这时候..啪一声门被慕容临天一掌击碎...一身黑袍依旧是一副冰冷的模样全身散发着王者风范..冯枫站在一旁脸上还是那种一脸无害的笑容..</p>\n<p>  镇定..要镇定..如雪心中默念道.用手轻轻抚了抚耳际的碎发的走进慕容临天..王爷怎么这么大火..说着正欲摸慕容临天的胸口却被他一把抓住..</p>\n<p>  王爷你弄的如雪好疼..如雪娇嗔道..手被甩开..</p>\n<p>  本王的身体你没有资格碰！</p>\n<p>　</p>\n";

