module.exports = "<hr>\n<p>title: 第21章\ndate: 2017-07-18 03:59:58\nwords: 3571</p>\n<h2 id=\"index-21\">index: 21</h2>\n<!-- deleteAbove -->\n<p>她真的笑了！好美的笑容..慕容临天希望这样的笑容永远不会从她脸上消失...</p>\n<p>  他慢慢走到爱儿的身边从袖子里拿出一个精美的锦盒递到爱儿面前..这是什么？爱儿看着眼前的盒子问道..</p>\n<p>  打开看看就知道了..说着慕容临天又把盒子往前递了递..</p>\n<p>  爱儿打开盒子..蝴蝶簪！她不可置信的看着慕容临天..</p>\n<p>  慕容临天嘴角微微上扬从锦盒里拿出蝴蝶簪插在爱儿的发髻上...这是送给你的礼物..</p>\n<p>  可是它不属于我..它属于你的王妃..爱儿冷冷道..</p>\n<p>  本王觉得没有人比你更适合它..蝴蝶簪插好一上一下两只古铜色的蝴蝶呈结伴翩飞之态翅膀嵌以五彩宝石底部坠以流苏映衬着爱儿绝美的容颜..</p>\n<p>  你好美慕容临天赞叹着..一把搂住爱儿的纤纤细腰..低头掳获着爱儿的红唇..这个吻吻得好深好深弥漫着他的浓浓情深就像要把爱儿用力的融进他的身体好永不分离..爱儿迎合着好想就此时间停住让千年的繁华直止于此..</p>\n<p>  蓝天之下没有黑暗没有阴谋没有刀光剑影没有波谲云诡..只有一男一女以彼此的深情诉说着爱...</p>\n<p>  第四十四章 此情可待成追忆，只是当时已惘然（十六）</p>\n<p>那晚室内一片浮靡的旖旎...没有喧嚣只有相爱的两人看着对方的眼睛那么那么深情...</p>\n<p>  待停歇之时..慕容临天将爱儿拥在怀里..像呵护着宝贝一般宠溺着..</p>\n<p>  爱儿你爱我吗？</p>\n<p>  爱儿一怔...他居然用‘我’..她好想说爱好爱好爱..但是她知道他们俩是不可能的...于是她背叛了自己的心..</p>\n<p>  不爱..</p>\n<p>  慕容临天的心狠狠的抽痛着..搂着爱儿的手加大了力度..爱儿可是我爱你..好爱好爱....</p>\n<p>如果我们不是仇人如果我没有误会过你你会爱我吗？</p>\n<p>  慕容临天紧张的看着爱儿的眼睛他的眼底满是脆弱..看着如此脆弱的慕容临天爱儿真的有一刻的不忍..慕容临天你为什么要逼我..</p>\n<p>  我要知道哪怕是假的好不好？慕容临天几乎是乞求的眼神..</p>\n<p>  爱儿不敢去看他的眼睛这只会让她不忍她不能给他任何希望有希望就会有失望...</p>\n<p>她握紧拳头低下头..不爱！不爱！不爱！</p>\n<p>  她疯了她一定是疯了！她正在想方设法的摧残他的心...摧残一个她最爱的人..同时自己也落得伤痕累累..</p>\n<p>  爱儿你好残忍..她的话像锋利的匕首狠狠刺着慕容临天的心一刀又一刀..直到鲜血淋漓..</p>\n<p>  慕容临天用力托着爱儿的下巴仰起她的脸就算你不爱我你也是我的！一辈子都是我的！说完一把掀开被子整理好衣物走下床就这样消失夜幕之中.</p>\n<p>  望着慕容临天远去的背影一颗滚烫的泪珠顺着爱儿的脸颊滴落..啪嗒一声溅开了无数水花</p>\n<p>  慕容临天..任谁也不能阻止我的心..我真的好爱好爱你..可是我也好恨好恨你..为什么上天要这么捉弄我们..爱儿忍不住失声痛哭起来..</p>\n<p>  不过你放心很快我就会离开这里你便可以忘记我永远忘记我.</p>\n<p>  空旷的麒麟殿里只剩下爱儿嘤嘤哭泣的声音..书房中慕容临天抱着酒坛子大口大口灌着酒..他的心好痛..痛得都快要停止呼吸了好想醉醉了就什么都不知道亦不用愁</p>\n<p>  接连好几日慕容临天都不曾回麒麟殿..每到夜晚爱儿就会贪恋起他那温暖的怀抱..还有那种独独属于慕容临天的味道..不过她也知道每天等她睡着的时候慕容临天都会偷偷的到麒麟殿来默默的注视着熟睡中的爱儿久久舍不得离去..</p>\n<p>  每到此时爱儿多么想睁开眼睛一把扑入他的怀里告诉她她有多么的爱他多么想他..她真的沦陷了..深深的陷进去了...再也无法自拔..</p>\n<p>  可是苍天并不垂怜..他对慕容临天的爱有多少..恨就有多少..它们像两股恶势力拉扯着爱儿的心她无时无刻不被自己折磨..</p>\n<p>  爱儿用手托着下巴眼神空洞洞的望着前方</p>\n<p>  爱儿姑娘小乐看着发呆的爱儿道这几天为什么王爷都不来麒麟殿..</p>\n<p>  呃..爱儿感到心里某个柔软的地方被触碰了..他很忙...</p>\n<p>  再忙也不能几天不来看爱儿姑娘你..小乐气嘟嘟的噘着嘴..你这么想他他都不来...</p>\n<p>  小乐！爱儿看着口不择言的小乐我哪有想他？</p>\n<p>  小乐不服气道怎么没有？你每天都呆呆的望着麒麟殿大门的方向不就是希望有一天王爷来嘛...</p>\n<p>  我....爱儿不知道如何辩驳..她连这个小丫头的辩不赢看来她真是无可救药了..</p>\n<p>  算了不说这个了..我去找枫哥哥...</p>\n<p>第四十五章 此情可待成追忆，只是当时已惘然（十七）</p>\n<p>枫哥哥...爱儿看着在练武房练剑的枫莞尔一笑..</p>\n<p>  爱儿..枫有点激动要知道他好久没有好好和爱儿聊一聊了真惦记这个小丫头...</p>\n<p>  他们坐在一旁的石凳上...</p>\n<p>  你这个小丫头这么久才想到你枫哥哥！他敲了一下爱儿的头..</p>\n<p>  前一阵子比较忙嘛..爱儿揉揉被枫敲疼的脑袋不满道..</p>\n<p>  忙什么？和天话一出口立刻打住他这几天看慕容临天老是坐在书房里喝闷酒也不回麒麟殿就知道他们出什么问题了可是话已经出口了..</p>\n<p>  果然爱儿那双明亮的眸子黯淡了..</p>\n<p>  枫哥哥爱儿弱弱的说道不管我做什么事你是不是都会支持我？</p>\n<p>  枫看着伤感的爱儿当然我一定会支持你..</p>\n<p>  谢谢你枫哥哥..爱儿看着枫请你帮我转告寒哥哥和如水姐姐...爱儿永远都会记住他们的..她顿了顿还有请你转告慕容临天叫他开心的活着..</p>\n<p>  枫一脸不解的看着爱儿你自己跟他说比较好..</p>\n<p>  我这不是见不到他嘛..她为自己编了个理由..枫哥哥那我就先回去了..</p>\n<p>  爱儿你变了很多..枫感叹道..</p>\n<p>  我？没有..爱儿笑着刮了一下枫的鼻子我永远都是你最爱撒娇的妹妹！呵呵..说完转身离开了..看着爱儿离开的背影枫的眼里满是心疼..你放心吧无论你作出什么决定我都会支持你...</p>\n<p>  夜深了...慕容临天依旧偷偷潜回麒麟殿看着熟睡的爱儿..他微微的笑了..如果能一直这样看着她真的就足够了...爱儿你知道吗？我真的好爱你...</p>\n<p>  好久慕容临天在爱儿的头上轻轻落下一个吻...留恋不舍的走了...</p>\n<p>  看着慕容临天远去的背影不争气的眼泪再次落了下来..恐怕是最后一次看到他的背影了..天对不起...我真的很爱你..但我们的爱是不单纯的恨让我不得不离开你...我们之间有太多的不舍太多的痛明明爱却还要互相伤害我不想再向你的心上狠狠刺去所有的痛都由我来承担...我走了..希望你快乐永远地忘记我....</p>\n<p>王爷！不好了！小乐冲进慕容临天的书房..本来慕容临天想大喝一声把来人拉下去杖责二十..可是看到是爱儿的贴身丫鬟他的心不安着..出了什么事？</p>\n<p>  王爷爱儿姑娘她..她不见了！</p>\n<p>  什么！慕容临天的瞳孔顿时收紧..</p>\n<p>  飞奔到麒麟殿桌上的蝴蝶簪和信夺去了他的全部注意力</p>\n<p>  锦瑟无端五十弦一弦一柱思华年。</p>\n<p>庄生晓梦迷蝴蝶望帝托杜鹃。</p>\n<p>沧海月明珠有泪蓝田日暖玉生烟。</p>\n<p>此情可待成追忆只是当时已惘然。</p>\n<p>  五十六个大字刺痛了他的心...</p>\n<p>  人有悲欢离合月有阴晴圆缺此时古难全.....月终究会残缺人也是如此..</p>\n<p>庄周梦为蝴蝶庄周之幸也..</p>\n<p>  爱儿的话一遍又一遍的在他脑海回响..该死他咬咬牙...</p>\n<p>  来人！他怒吼道给我全城搜查！一定要把易爱儿给我抓回来！嗜血的双眸泛着血红色..要是找不到你们全部给我提头来见！</p>\n<p>  是！侍卫们战战兢兢着..连大气儿都不敢出..</p>\n<p>  易爱儿你好大的胆子居然敢走！本王说过你是我的！这一辈子都是！</p>\n<p>第四十六章 此情可待成追忆，只是当时已惘然（十八）</p>\n<p>爱儿漫无目的地走在京都街上...又是这里..爱儿暗自思忖着..好像什么事都是从这里开始..她苦笑着看着茫茫人海..天大地大居然没有她的容身之处..</p>\n<p>  就这样一直一直走...</p>\n<p>  将近黄昏残阳如血一般挂在西边拉长了爱儿孤独的影子..爱儿走进福来客栈..她对这里印象很深很深..记得她亲眼看到干爹干娘死在慕容临天手下就是在这里..</p>\n<p>  这位姑娘吃饭呢还是住店呢？</p>\n<p>　</p>\n";

