module.exports = "<hr>\n<p>title: 第22章\ndate: 2017-07-18 03:59:59\nwords: 3982</p>\n<h2 id=\"index-22\">index: 22</h2>\n<!-- deleteAbove -->\n<p>店小二笑脸盈盈的走过来顺手把抹布搭在肩上..</p>\n<p>  爱儿知道慕容临天肯定会一家一家店搜的她不能住店只有连夜赶路才行..吃饭..</p>\n<p>  好嘞..小二引着爱儿来到窗边的一张桌子旁用抹布抹抹干净..姑娘请坐..</p>\n<p>  嗯..爱儿点点头便坐下了..</p>\n<p>  请问姑娘要吃点什么呢？店小二问道..</p>\n<p>  随便上个小菜就行了再来一碗白饭..爱儿在王府只是个丫鬟哪里有钱？只有一个月的月俸..她也不能拿慕容临天的钱..身上连一两银子都不够..唉..这次出逃实在是太狼狈了..</p>\n<p>  姑娘不点一点别的？本店招牌桂花糕姑娘要不要试试？小二问..</p>\n<p>  不用了我没有那么多钱..爱儿有些沮丧..</p>\n<p>  姑娘初到本店就当是我送姑娘的不要钱...小二像是个好心人..</p>\n<p>  可是这怎么好意思呢？爱儿看着店小二说..</p>\n<p>  没什么不好意思的姑娘请稍等饭菜很快就到..说完小二就去招呼别的客人了..</p>\n<p>很快爱儿这桌的菜就送了过来..这桂花糕还真是名不虚传还没送到就能闻到桂花的香味..爱儿尝了一口..果然是清新爽口回味不绝和以前干娘坐的好像...可是..</p>\n<p>  爱儿突然眼前一黑..</p>\n<p>这是哪里..爱儿醒了过来用手支撑着身子站起来..巡视四周一个人也没有..我怎么会在这里？爱儿回忆起刚才不是在客栈吃饭吗..后来她就什么也不记得了...难道这是黑店！爱儿想到这里顿时感到一丝危险正在慢慢靠近自己...有人来了！</p>\n<p>  听外面杂乱的脚步声还不止一个人！难不成他们想怎么样.....脚步声越来越近爱儿慢慢往后退着..可是后面是一堵墙！来了！来了！爱儿紧紧盯着门紧张得连呼吸都快停止了...吱呀<del>~</del>~~一声..门开了...</p>\n<p>  爱儿不敢置信的看着门外的人....他们是....是....</p>\n<p>  干爹干娘？爱儿惊讶的叫了出来...天啦...不会是见鬼了吧....</p>\n<p>  诶乖女儿！易天齐看见了爱儿老泪纵横一把上来抱住爱儿着她的头..爱儿..干爹想死你了..</p>\n<p>  真的是真的！！爱儿看着眼前抱着自己的人..他的两鬓是斑白的他还是这么的和蔼可亲更何况这么真实的触感...干爹爱儿高兴得哇哇大哭起来..干爹爱儿还以为你死了..呜呜..干爹干娘..爱儿好想你们..易天齐抓住爱儿的肩膀仔细看了看心疼的蹙起了眉..乖女儿你都瘦了..被慕容临天抓去真是苦了你了..</p>\n<p>  不苦..爱儿一点也不苦..爱儿用手抹去泪水..能再看见干爹干娘爱儿一点都不苦..可是说到慕容临天爱儿的心还是微微刺痛了一下..</p>\n<p>  萧淑在一旁没有说话只是默默地看着重逢的两父子微笑但眼眶的湿润爱儿是看得到的..上前也给了萧淑一个拥抱..干娘..爱儿好想念你做的桂花糕..</p>\n<p>  爱儿..今后干娘再做给你吃手爱怜的抚抚爱儿的脸..</p>\n<p>  对了！干爹干娘！你们怎么会在这儿？爱儿满脸的疑问..那天在客栈外我明明看到还有我又怎么会这儿？</p>\n<p>  易天齐拉着爱儿的手坐下..乖女儿听干爹慢慢给你道来..事情还要从那天说起...</p>\n<p>第四十七章 此情可待成追忆，只是当时已惘然（十九）</p>\n<p>那日我和你干娘的确受了很重的伤..可是我们并没有死..我们知道你和如水被抓进了王府本想晚上偷偷潜进王府把你们救出来..可惜天不遂人愿不仅没把你们救出来反而被王府的人发现了..慕容临天便带人追杀我们追杀了好几日..</p>\n<p>  好几日？难道是他们出去的那几天？可是枫哥哥说是去扫清叛党..是哪几日呢？爱儿问道..</p>\n<p>  你被抓的二十几天后..易天齐回答道..</p>\n<p>  什么！爱儿一震...</p>\n<p>  易天齐接着说幸好我和你干娘命大躲过了他的追杀..我们觉得最危险的地方就是最安全的地方..于是我们又回到福来客栈来用钱买下了这里做个幕后老板..刚才店小二看到了你就通知了我们又怕你不肯配合才把你迷晕送进来的..</p>\n<p>  原来是这样..爱儿若有所思道..</p>\n<p>  对了爱儿..你是怎么出来的？易天齐看着爱儿问..</p>\n<p>  我..爱儿的心狠狠的痛着..因为对慕容临天爱儿只想逃避..我偷偷逃出来的..</p>\n<p>  那你会回去吗？易天齐的表情变得严肃..</p>\n<p>  不会..我不会再回去了..那个地方像个牢笼..她绝对不要再回去..</p>\n<p>  不行！你一定要回去！回去杀了他！易天齐像变了个人似的..</p>\n<p>  爱儿不解的看着脸色突变的易天齐..为什么？干爹..</p>\n<p>  爱儿这件事本来干爹不应该告诉你的但是现在已经到了非说不可的地步了..易天齐突然站起来手背在背后..他的样子像是很气愤...</p>\n<p>  什么事？爱儿有不好的预感她能感觉到这件事好像很严重..</p>\n<p>  当年..你的爹娘根本就不是为山贼所杀..杀他们的..正是慕容临天！易天齐背在背后的手狠狠的握紧关节处咔咔作响...</p>\n<p>  什么不可能！她不相信慕容临天会这么做..</p>\n<p>  怎么不可能！易天齐转过来生气的看着爱儿..</p>\n<p>  他没有动机...爱儿肯定的说..</p>\n<p>  他没有动机可是他爹有动机！易天齐大吼着脖子上青筋暴起..</p>\n<p>  到底怎么回事？干爹你快告诉我！爱儿慌了她不相信慕容临天会这么做可是她也不相信干爹会骗自己..</p>\n<p>  当年四王爷和是旧识他们从小青梅竹马长大后他发誓要娶为妻可是已经心有所属他喜欢的是你爹便嫁给了你爹..后来王爷怀恨在心就杀光了你们全家只有你和如水幸免于难..虽然不是慕容临天谋划的可是我亲眼看到把刀插入你爹娘心脏的人就是他！！</p>\n<p>  扑通一声爱儿跌到了地上...不会的不会的她边摇头边自言自语着...</p>\n<p>  爱儿！易天齐也蹲了下来...你不要再执迷不悟了！这就是事实！事实就是慕容临天亲手杀了你爹娘！他是你的大仇人！！</p>\n<p>  不是的！她不停摇着头..不是的！干爹你是在骗我对不对..爱儿眼里噙着泪花..</p>\n<p>  干爹没骗你！他伸手抓住爱儿的肩膀..爱儿你喜欢他是不是？</p>\n<p>  爱儿没有说话滚烫的泪珠顺着脸颊一颗颗滑落下来..</p>\n<p>  你和他是绝对不可能的！！易天齐使劲摇着爱儿你清醒一点！他是你的仇人！仇人！</p>\n<p>  不是的不是的不是的！！！！！爱儿尖叫一声推开易天齐跑了出去...</p>\n<p>  不可能的...不可能的...爱儿疯狂的跑着..好像这样就能挥去现实..晶莹的泪珠随着爱儿的跑动像断了线的珠子洒了一地..已经是寒秋之夜了..爱儿穿着薄薄的单衣夜晚的寒风一股股窜进她的衣领里..爱儿不知跑了多远她只想这样一直跑着或许可以跑到天的尽头让她忘却这一切...</p>\n<p>  ！脚下绊到了一块大石头..爱儿狠狠的栽在地上..膝盖磕出了淋漓的鲜血染红了纯白的衣裙额外刺眼...鞋子早已经磨破了地上的碎石子深深的嵌进爱儿的脚底然而她却浑然不觉..她慢慢坐起来紧紧抱着双腿...</p>\n<p>  慕容临天...每到夜晚她总会想起慕容临天温暖的怀抱躲在那里面好安全好舒服...真的让她一生一世都不想离开..她好想他..</p>\n<p>  可是..慕容临天亲手杀了你爹娘！他是你的大仇人！！易天齐的话一遍又一遍的在她耳畔回响..她紧紧捂住耳朵..不要说了！不要说了！不要再说了！！她使劲摇着头不停的摇着...</p>\n<p>  慕容临天我爱你..恨你..爱你..恨你...她疯了！她一定是疯了！！</p>\n<p>  爱儿你是喜欢慕容临天的你是爱他的.....爱儿眼神空洞的看着前方黑漆漆的道路....对我爱他...</p>\n<p>  不！另一个声音从爱儿的心里蹦出来...爱儿你是恨他的！他杀了你爹娘还要杀你干爹娘...他这样折磨你..你应该恨他！！爱儿听我说杀了他！杀了他！</p>\n<p>杀了他杀了他爱儿眼神空洞的可怕...她一遍一遍重复着..杀了他杀了他</p>\n<p>  她站起来一步一步地往回走....的夜..落寞的身影..幽怨的眼神..爱儿脚下的血一直流淌...星星点点的血迹一直延伸到很远很远</p>\n<hr>\n<p>  唉...爱儿真的快被自己逼疯了...</p>\n<p>第四十八章 此情可待成追忆，只是当时已惘然（二十）</p>\n<p>回到客栈的房间爱儿满身血渍头发零乱的披着..眼神还是一样的空洞..</p>\n<p>  爱儿..你怎么弄成这个样子...易天齐和萧淑一直在房里等她等了整晚..看着满身血迹的爱儿他们赶快迎上来..</p>\n<p>  爱儿干娘带你去梳洗梳洗..说完领了爱儿进了内屋..</p>\n<p>  热水淋在伤口上爱儿像根本感觉不到痛一般..她只是静静的坐着..爱儿..萧淑轻轻地呼唤她..</p>\n<p>  干娘..爱儿终于有反应了..</p>\n<p>  爱儿有什么是跟干娘说吧..别憋在心里..萧淑摸摸她的头...</p>\n<p>  干娘..我真的好爱好爱他..可是我又好恨他..我该怎么办..爱儿的眼泪开始无助的往下滴..什么时候她也变得这么爱哭了..我不想去想他可是偏偏一直想着他..我到底该怎么办..爱儿又激动起来..双手不停的锤着自己的膝盖..才凝结的伤口又血流不止起来.染红了浴桶里的水..</p>\n<p>  萧淑赶快制止爱儿..抓住她的双手让她的头靠在自己的怀里..傻孩子你为什么要折磨自己呢？</p>\n<p>　</p>\n";

