module.exports = "<hr>\n<p>title: 第16章\ndate: 2017-07-18 03:59:58\nwords: 3644</p>\n<h2 id=\"index-16\">index: 16</h2>\n<!-- deleteAbove -->\n<p>如雪笑着拉着爱儿的手坐下天天要你给我送饭真是辛苦你了..</p>\n<p>  爱儿满脸笑意这有什么累的要不是你我现在还在厨房洗碗呢那才累..呵呵快吃吧凉了就不好吃了..</p>\n<p>  好你渴了吧？喝茶吧..如雪把茶递到爱儿面前..想起那茶的青草香味爱儿就觉得无比清新..</p>\n<p>  爱儿今天怎么那么高兴就看你一直笑呵呵的..梅凝问道..</p>\n<p>  爱儿又忍不住咯咯笑了两声梅凝如雪你们知道吗我今天及笄（象征成年）呢..说完指了指头上的蝴蝶发簪两只蝴蝶一上一下呈翩飞之态配着爱儿姣好的容颜简直出彩极了...刚才如水姐姐帮我弄的..</p>\n<p>  呀爱儿及笄之礼怎可如此简陋？梅凝惊呼道..</p>\n<p>  爱儿苦笑了一下那又有如何呢？爹娘已经不在了就连干爹干娘也离我而去....爱儿顿了一下..反正只是一个仪式而已没关系的...</p>\n<p>  如雪握住爱儿的手忘记不开心的今天要开心点哦..</p>\n<p>  嗯爱儿点点头...</p>\n<p>  半晌哦对了爱儿今天晚上王爷要宴请宾客他叫我嘱咐你晚上去侍旁..如雪说道你就当这晚宴是为了庆祝你及笄吧..</p>\n<p>  呵呵好！爱儿笑着谢谢你如雪..</p>\n<p>  晚宴</p>\n<p>  爱儿可是忙得不可开交又要帮忙上菜又要侍旁倒酒...</p>\n<p>  周管家枫问道人都到齐了吗？</p>\n<p>  冯统领还有如雪夫人没到..周管家回答要不小人去请她..</p>\n<p>  嗯枫点点头..周管家正准备走出去..</p>\n<p>  不好了！不好了！梅凝冲进来扑跪在慕容临天脚下不好了王爷！如雪夫人出事了！</p>\n<p>  什么！慕容临天眉头拧了起来...</p>\n<p>  胜雪居</p>\n<p>  麟太医雪儿怎么回事？慕容临天问道..</p>\n<p>  回王爷据下官诊断如雪夫人是中毒了。寒答道..</p>\n<p>  中毒？慕容临天看着跪在地上的梅凝梅凝雪儿今天吃了什么？从实招来！</p>\n<p>  梅凝不敢抬头看慕容临天的眼睛她知道此时此刻一定是冷到极点..回王爷今天夫人和往常一样就是一日三餐然后吃了点芋头糕其他的什么也没吃..</p>\n<p>  王爷麟寒开口了如雪夫人中的是‘十日噬命’。</p>\n<p>  什么是十日噬命？慕容临天问道语气还是那么的冷漠..</p>\n<p>  十日噬命顾名思义连服十日就会死于非命..是一种慢毒药但到了十日就会毒发身亡神仙也救不了。不过如雪夫人应该只服食了八日今日吃了芋头糕芋头遇水发胀所以把毒先逼了出来只要服药好好调理即可无碍..</p>\n<p>  八日？今天易爱儿不是刚好送饭到胜雪居的第八日吗？周管家小声说着..</p>\n<p>  慕容临天冲上来抓住周管家的领口你说什么！</p>\n<p>  周管家吓得战战兢兢回..回王爷从..从易爱儿开始送饭到胜雪居起到今日刚好第八日..</p>\n<p>  慕容临天怔住了..奴..奴婢给如雪夫人送饭..他回忆着那日见她从胜雪居出来时的情景为什么她的声音那么颤抖？难道真的是她？</p>\n<p>  王..王爷不可能是爱儿的如雪费力的说气如游丝爱...爱儿是..如雪的好朋友她..她绝对不会害我的....慕容临天走到床边抓住如雪的手..雪儿别说了放心不管凶手是谁本王一定会查出来...继而眼神一冷把她带过来！</p>\n<p>  不久爱儿就到了胜雪居如雪夫人..你怎么样？爱儿心疼的看着如雪苍白的脸..</p>\n<p>  闭嘴！慕容临天大喝道..爱儿被吓着不敢看他遂低下头...</p>\n<p>  这..这是..慕容临天不敢置信的看着爱儿头上的发簪一把扯下..你的发簪从哪里来的！说！此时的慕容临天像一只发现猎物的豹子..</p>\n<p>第三十三章 此情可待成追忆，只是当时已惘然（五）</p>\n<p>这..这是..慕容临天不敢置信的看着爱儿头上的发簪一把扯下..你的发簪从哪里来的！说！此时的慕容临天像一只发怒的豹子..</p>\n<p>  爱儿从来没看到他这么生气难道这簪子有什么渊源？不行不能把如水姐姐供出来..回..回王爷这簪子是奴婢捡的..因为今天奴婢刚及笄所以...话还没说完..所以想当王妃是吗！</p>\n<p>  爱儿吓得不敢置信的看着慕容临天猛摇着头辩解着不是的！我怎么会想当王妃呢不是的..</p>\n<p>  那你还敢撒谎！慕容临天吼道这支簪子是本王的王妃才能戴的一直放在如雪那儿保管你怎么会捡到！</p>\n<p>  我....我..爱儿不知道怎么说...</p>\n<p>  王爷这种十日噬命只有下官的典医阁才有爱儿怎么会有呢？寒赶忙替爱儿解围..</p>\n<p>  爱儿？慕容临天心里的火苗噌蹭往上冒叫得这么亲密莫非麟太医和她有什么见不得光的关系！</p>\n<p>  麟寒赶紧跪下双手抱拳..请王爷明鉴下官和爱儿之间绝无苟合希望王爷不要侮辱下官的清誉更不要侮辱了爱儿姑娘的清白..</p>\n<p>  什么清白她本来就是个水杨花的女人！想起爱儿窝在枫怀里那一幕慕容临天就要发狂恨不得把爱儿碎尸万段！而且他好像又想起了什么她不是每天都往你的典医阁跑吗就算不是你给她的她自己也可以偷！来人！给我搜她的房间！</p>\n<p>  是！一些侍卫听命..</p>\n<p>  很快...王爷在易爱儿床下我们发现了一包白色粉末！接着领头的侍卫将粉末呈了上来..</p>\n<p>  这..麟寒不敢相信的看着这包粉末...</p>\n<p>  慕容临天的嘴角向上扬起只是冷峻异常看来这包就是十日噬命..易爱儿你还有什么话说！</p>\n<p>  爱儿的眼眶湿润了却强忍着不让眼泪流下来..不是的王爷不是的真的不是我干的...王爷！！你相信我！王爷相信我！上前抓住慕容临天的衣袖..王爷你相信我！不知为什么她觉得只要此刻慕容临天能相信她只要慕容临天相信她就是死也愿意了..可是..</p>\n<p>  慕容临天用力甩开她的手爱儿不支地跌在地上...接下来...本王..不-相-信！啪..爱儿觉得身体里的某个地方好像碎了..碎成了几百万片几千万片..好痛好痛...</p>\n<p>  突然是我干的是我干的是我干的无数个声音在爱儿脑海叽叽喳喳的吵着快把她逼疯了....眼前一道绿光闪过..对是我干的！这一切都是我干的！爱儿站起来大声说道..可是没人知道她此刻她已经不是她自己了...这一切都是我策划的..我先设计陷害梅凝借王爷的手除掉她然后故作求情添油加醋本以为阴谋得逞可是后来柳如雪出现了救了她我便怀恨在心得知她是王爷最宠爱的小妾我就设法和她搞好关系结果她居然让我去给她送饭真是天助我也我每天进出典医阁取得十日噬命放进她的饭菜里现在已经得八日离成功之日不远了今天中午我便趁机偷得蝴蝶簪假言及笄戴着它没想到她居然福大命大没死......爱儿咬牙切齿着...慕容临天掐住她的脖子说！你为什么要这么做！哈哈...哈哈哈...爱儿大笑起来因为我要当王妃！！哈哈...一阵阵大笑让人为之惊悚...啪！爱儿被一巴掌打倒在地...汩汩鲜血从嘴角流出..还有那一滴未流出的泪从红肿的脸颊滑落滴落在血泊之中...渐渐地爱儿晕了过去..慕容临天看着自己打她的手..心里有个地方在抽痛...</p>\n<p>  来人！慕容临天大叫着把这个女人关进大牢！</p>\n<p>  爱儿被侍卫们拖走了剩下那一摊鲜血....慕容临天不敢看他的心乱了...尽管爱儿最后一句话真的很打动他的心可是她居然是这样的女人！嫉妒心如此之重为人如此狠毒！怎能容忍！</p>\n<p>  ...唉....苍天垂怜....苍天该如何垂怜？</p>\n<p>第三十四章 此情可待成追忆，只是当时已惘然（六）</p>\n<p>夜深了慕容临天独自坐在书房中...前几天的一幕幕像放电影般在他脑海回放那时的爱儿是多么的可爱让他忍不住的想去保护..可是为什么爱儿居然是这样狠毒的女人？难道她与我欢好只是为了得到王妃的位置？慕容临天想到这里握紧了拳头关节咔咔作响如果这样是可忍孰不可忍！或许爱儿本就是如此只是平常掩饰的好罢了..</p>\n<p>  唉...他叹了口气..不想再触碰..</p>\n<p>  伸手到墙上的画像后轻轻一按..咔~~~~里面居然有个暗格！暗格里放着一个精美的楠木盒子通体暗红色盒盖上的桃花浑然天成似有大气而不失娇美..打开木盒.......</p>\n<p>  慕容临天看着盒里的东西嘴角露出了难得的笑容....</p>\n<p>  典医阁</p>\n<p>  寒大口大口灌着酒可是他又如何忘却得了爱儿那张稚嫩而天真的脸庞？他不相信爱儿会做那样的事..爱儿不会是那样的人...</p>\n<p>  一杯接一杯..渴望一醉解千愁谁料借酒消愁愁更愁呢....</p>\n<p>  冰冷的大牢里..爱儿蜷缩在墙角受刑犯人的哀嚎一股股的血腥味无一不充斥着她的神经..为什么..为什么..爱儿目光呆滞的看着前方..他不相信我...他不相信我....她的要求真的不高..哪怕全天下都不相信她只要慕容临天相信就够了..她真的就满足了...可是...慕容临天用力甩开她的手本王不-相-信！</p>\n<p>　</p>\n";

