module.exports = "<hr>\n<p>title: 第5章\ndate: 2017-07-18 03:59:58\nwords: 3628</p>\n<h2 id=\"index-5\">index: 5</h2>\n<!-- deleteAbove -->\n<p>前面传来寒冰凉的声音..</p>\n<p>  如水一把松开爱儿双手捏着她的肩膀像发现珍宝一样看着爱儿不敢相信寒的话...</p>\n<p>  爱儿他真的没对你如水紧张的问...</p>\n<p>  嗯寒哥哥是个好人！爱儿笑道...</p>\n<p>  寒哥哥？不知为什么听到爱儿这样叫他如水心里有种莫名的失落感..</p>\n<p>  对他说我可以叫他寒哥哥而且我们是好朋友！爱儿继续笑道..</p>\n<p>  朋友...可是他刚才还拒绝了我跟他做朋友...如水这样想心里不知觉的有些隐隐作痛..但是不管怎样看到爱儿原封不动的站在她面前才是最重要的..</p>\n<p>  爱儿你没事就好..如水的眼睛又湿润了不过这次更多的是感动和高兴..</p>\n<p>  给你寒不知什么时候走了过来递给如水一张药方你的伤还需要多调理这药每天服用一剂补身体的。</p>\n<p>  接过寒的药方如水感到心里暖暖的原来他也可以很温柔..不自觉地抿着小嘴笑了..</p>\n<p>  这时候....他们又来了！寒自言自语道...</p>\n<p>  他们？谁？喂爱儿还没说完寒的脚尖一点飞身上屋顶随即不见了踪影...如水不有些许的失落..这一别不知何时才能再相见...</p>\n<p>  不一会儿一群蒙面人冲进庙来把爱儿和如水吓了一跳....</p>\n<p>  你们刚才有没有看到一位银发男子？带头的蒙面人厉声质问着...手中的刀在阳光下闪着寒光..</p>\n<p>  爱儿抓着如水颤抖的双手似在安抚..这群人肯定不怀好意想伤害寒哥哥她想。那鬼灵精的眼轱辘一转伸手指着相反的方向他刚刚往那边跑了！</p>\n<p>  追！带头人一声令下所有蒙面人立刻不见了踪影...</p>\n<p>  呵呵真是一群白痴这话居然都信..爱儿啧啧着..</p>\n<p>  寒他不会有事吧？如水紧张得紧紧攥着爱儿的手...</p>\n<p>  爱儿似乎看出了些端倪戏谑着寒哥哥不会有事的有事的我看是你！</p>\n<p>  我？如水不解..</p>\n<p>  爱儿凑到她耳朵旁边你喜欢他对不对？</p>\n<p>  唰一下如水的脸都红到脖子根了..爱儿你说什么呢</p>\n<p>  哟哟哟害羞羞了脸好红哦！！爱儿继续调侃着笑得像盛开的桃花..</p>\n<p>  讨厌如水娇嗔着用手摸着发烫的脸不好意思的露出个娇羞的笑...</p>\n<p>  第九章 众里寻她千百度，蓦然回首（一）</p>\n<p>如水姐姐我们快到客栈了你再坚持一下下..如果伤口痛就跟我说我背你走..爱儿扶着如水慢慢往客栈方向走着走着....</p>\n<p>  没关系我能走.伤口隐隐作痛着但如水咬着牙不让疼痛表现出来..</p>\n<p>  此时京都街</p>\n<p>  嘶一阵马嘶叫声几匹骏马飞驰而过像一阵呼啸的风让人来不及看清马背上的人..后面紧跟着一群骑着铁甲战马的侍卫说是侍卫实则看起来更像杀手.暗红色的上衣左手整齐划一的拿着弯刀令人不寒而栗街上的人赶忙四散逃窜生怕被飞驰的骏马踩的粉碎.</p>\n<p>  很快侍卫们包围了福来客栈。一位白衣男子从马上一跃而下不紧不慢走进客栈里面带着微笑与这严肃的气氛形成了鲜明的对比...</p>\n<p>  掌柜哪敢怠慢赶快亲自出来招呼：这位大爷不知是哪位大人物大驾光临本小店？</p>\n<p>  你往外面看看就知道了相信你应该有所闻。白衣男子道。</p>\n<p>  掌柜转过头看到这暗红色的侍卫装醒目的弯刀还有那铁甲战马暗..暗夜铁骑军！声音都变得结结巴巴难道是冥王！掌柜不敢相信..</p>\n<p>  不错正是！白衣男子笑道..</p>\n<p>  那阁下莫非是冥王的贴身统领冯枫统领？掌柜瞪大着眼睛...</p>\n<p>  在下正是！</p>\n<p>  鄙人不知是冥王驾到有失远迎请冥王恕罪掌柜战战兢兢的跪下就怕一个不小心脑袋搬家...</p>\n<p>  呵呵掌柜的你不用这么紧张冥王又不会滥杀无辜枫扶起跪着的掌柜还是扬起那笑容只是殊不知在这种情况下笑比不笑更令人心寒..</p>\n<p>  不..不知冥王和冯统领今日来有..有何贵干？</p>\n<p>  也没什么就是来找个人请问掌柜的可有一个叫易天齐的人住在这儿？</p>\n<p>  掌柜完全不敢怠慢拿出一册登记簿卖命的翻着..易天齐有找到了！掌柜格外兴奋在天字号上房来我带您去..</p>\n<p>  有劳掌柜了..</p>\n<p>  不客气不客气..此时掌柜悬着的一颗心终于放了下来..</p>\n<p>  来到楼上枫一把推开门易天齐好久不见了！依旧是笑容满面..</p>\n<p>  掌柜赶快开溜看着场面今天肯定没好事发生了...</p>\n<p>  里面坐着一个两鬓有些许斑白的中年男子双目细长虽看起来有些仙风道骨但眼角却流露出一丝jian佞..旁边还有一位人到中年却风韵犹存的女人..</p>\n<p>  原来是冯统领怎么？找老夫有何事？易天齐用手捋着胡子...</p>\n<p>  要找你的不是我而是话还未说完..</p>\n<p>  是本王一个冷到极点的声音响起</p>\n<hr>\n<p>偶的亲们天天（慕容临天）终于登场了！！</p>\n<p>接下来会发生什么事呢？</p>\n<p>  第十章 众里寻她千百度，蓦然回首（二）</p>\n<p>什么？是他？易天齐的眼中闪过一丝惊慌...</p>\n<p>  原来是冥王大驾光临又恢复了原来的平静..</p>\n<p>  易天齐你真厉害光听声音就知道是本王..不知何时一袭黑袍的男子出现在门口房内顿时压抑非常仿佛连空气都凝固了...</p>\n<p>  冥王的称号早就名扬四海谁人不知谁人不晓？</p>\n<p>  说得好听有什么用.语气冻结了四周的一切..</p>\n<p>  这么说来冥王今日是找老夫算帐的咯？易天齐邪佞的眼光扫在男子脸上..</p>\n<p>  易天齐你死之前终于聪明了一回。男子嘴角扯起一丝戏谑的笑...</p>\n<p>  难道老夫有愚钝的时候吗？易天齐显然有些愤懑..</p>\n<p>  五年前的今天你做了这辈子最蠢的事从那时起你就已经蠢钝如猪了..哈哈哈哈男子的笑声像是把把利剑向易天齐射来空气中布满了血腥的味道....</p>\n<p>  废话少说！话音刚落易天齐便向男子直奔而去寒冰掌的寒力使其所到之处均结起层层白霜...突然！</p>\n<p>  易天齐你的本事就止于此吗？男子居然轻易接住了十成威力的寒冰掌...烈焰神掌的掌力让易天齐的掌心感到被火焰灼烧般的疼痛钻心的痛...可是他不能收掌这时收掌不仅会被对方的掌力所伤还会自伤其身命堪虞....</p>\n<p>  老爷我来帮你！说罢桌旁的女人立即起身用尽全力将内力注入易天齐的体内以扺挡烈焰神掌的威力...</p>\n<p>  哼不自量力！男子加强掌力夫妻二人的内脏都被震伤双双吐血倒地</p>\n<p>  走！易天齐大喝一声拉起女人的手飞身向外欲逃..</p>\n<p>  想走？男子的眼神微微眯起让人感觉危险异常..</p>\n<p>  夫妻二人的轻功哪赶得上冥王？刚出客栈男子便一跃而出落在他们面前..</p>\n<p>  让本王亲自送你们去见阎王！说罢脸上露出嗜血的笑容伸出双手掐住二人的脖子咔嚓一声</p>\n<p>  干爹！干娘！围观的人群中响起刺耳的声音</p>\n<p>  爱儿冲出人群扑倒在死去二人的尸体旁..</p>\n<p>  干爹干娘你们醒醒你们不要丢下爱儿你们不能不要爱儿爱儿以后会乖乖听话的爱儿哭着喊叫着泪如雨下</p>\n<p>  周围的百姓都为之动容了但没人敢说什么...只是有的会偷偷抹泪..</p>\n<p>  男子抓住爱儿的衣领一把把她提起来你是他们的干女儿？</p>\n<p>  你为什么要杀我干爹干娘！！我要杀了你帮他们报仇！！爱儿恶狠狠瞪着男子手脚不停向男子攻击可伤不到男子半分..</p>\n<p>  冥王狠狠把爱儿丢在地上抬起手掌...</p>\n<p>  ！爱儿吃痛的叫了一声抬头看见那正向自己劈来的掌紧紧闭上眼睛</p>\n<p>  不要！如水不顾伤口冲出来跪在男子脚下...</p>\n<p>  求求你不要杀她！</p>\n<p>  如水姐姐不要求他！爱儿依旧瞪着男子..</p>\n<p>  你也是易天齐的干女儿？男子的声音没有一丝温度..</p>\n<p>  我话音未落</p>\n<p>  她不是！她只是我的朋友。我要杀要剐随便你但不准你伤害她！</p>\n<p>  男子收回掌俯身直视着爱儿快喷火的眼睛...本王不会让你死只会让你生不如死！声音冷到不能再冷透着一丝诡异..</p>\n<p>  你爱儿恨得咬牙切齿..</p>\n<p>  带她们俩走！</p>\n<p>  说罢男子翻身上马飞奔而去......</p>\n<p>第十一章 众里寻她千百度，蓦然回首（三）</p>\n<p>冥王府</p>\n<p>  王爷人带回来了侍卫单膝跪地双手抱拳道..</p>\n<p>  冥王用手一甩背后的黑袍安安然坐在大厅最前方的虎皮凳上动作一气呵成..</p>\n<p>  带上来！</p>\n<p>　</p>\n";

