module.exports = "<hr>\n<p>title: 第20章\ndate: 2017-07-18 03:59:58\nwords: 3516</p>\n<h2 id=\"index-20\">index: 20</h2>\n<!-- deleteAbove -->\n<p>爱儿有些吓到了..</p>\n<p>  嘘慕容临天做了个嘘的动作..继而一把把爱儿搂在怀里..睡觉..</p>\n<p>  爱儿不敢相信慕容临天居然还会有这样孩子气的举动..她这次不想反抗..因为她已经没有多少和他相处的时间..不可置否..他的怀抱真的好温暖好舒服..爱儿把头埋在慕容临天的胸膛里..贪婪的呼吸着这专属于他的男气味..</p>\n<p>  爱儿真的好怕好怕..好怕自己会贪恋他的怀抱好怕自己会恋上他的味道..然而她无法说服自己..她绝对不可以爱上他..这之中有太多太多的无可奈何..</p>\n<p>天大亮..待爱儿醒来的时候身边的位置已经空了但是还是有他的味道..</p>\n<p>  爱儿姑娘你醒了..一个小丫鬟走了进来..</p>\n<p>  你是？爱儿看着眼前这个长得水灵灵的小丫鬟问道..</p>\n<p>  回爱儿姑娘奴婢小乐是王爷吩咐奴婢来服侍爱儿姑娘的..小乐福了福身..</p>\n<p>  小乐王爷现在在哪？爱儿接着问道..</p>\n<p>  爱儿姑娘王爷正在大厅审问此次下毒事件的元凶..说是要把她们全部赐死..小乐回答..</p>\n<p>  什么？爱儿惊讶的睁大了眼睛..马上下床朝大厅跑去..</p>\n<p>  爱儿姑娘等等小乐..爱儿姑娘你的身子还很虚弱别跑那么快..小乐在后边边追边喊..</p>\n<p>大厅</p>\n<p>  这次的下毒事件本王已经彻查清楚了不是爱儿干的而是另有其人栽赃嫁祸..慕容临天仔仔铿锵的说...</p>\n<p>  什么？另有其人..谁这么大胆子..</p>\n<p>  就是..</p>\n<p>  下人们议论纷纷..</p>\n<p>  王爷是我干的..如水站了出来跪在地上..所有的事都是如水干的如水甘愿受死..</p>\n<p>  就你一个人吗？说着慕容临天瞟了一眼如雪..你妹妹呢！</p>\n<p>  如雪一怔..不敢置信的看着慕容临天没想到他连这都知道了...</p>\n<p>  王爷如水没有妹妹..如水继续说道势要包庇如雪..</p>\n<p>  哼！慕容临天拍桌而起..大胆！你以为本王什么都不知道吗？你包庇也没有用！本王绝不允许王府里再有这样的人搬弄是非祸害他人！来人把秋如水拖下去斩！</p>\n<p>  一声令下几名侍卫便涌上前来拖起地上的如水往外走..</p>\n<p>  住手！爱儿终于赶及了..走上前来跪在慕容临天脚下..</p>\n<p>  奴婢希望王爷不要追究这次的事..</p>\n<p>  什么？不追究？慕容临天心疼的看着爱儿惨白的脸...轻轻扶起爱儿上次你也说不追究结果如何？</p>\n<p>  王爷爱儿看着慕容临天一双明亮的眸子里蕴藏着坚毅..上次爱儿只是不想看到有人死去但这次不一样..</p>\n<p>  有什么不一样..慕容临天问道..</p>\n<p>  这次爱儿根本没有怪过任何人..爱儿坚定的说道..</p>\n<p>  你不怪她们？可是她们差点害死你！说到这里如水已经是泪流满面..对不起爱儿..真的对不起...</p>\n<p>第四十二章 此情可待成追忆，只是当时已惘然（十四）</p>\n<p>如水亦听说了爱儿在牢里自杀的事..她真的很后悔很自责..爱儿我对不起你..</p>\n<p>  是的爱儿肯定的说爱儿没有怪过她们..既然爱儿不怪她们恳请王爷放过她们...</p>\n<p>  如果本王坚持要把她们正法呢？慕容临天不敢置信的看着爱儿她太善良了..我绝对不能让王府再有任何威胁她的份子...</p>\n<p>  那就请王爷将爱儿一起正法！爱儿没有一丝犹豫..</p>\n<p>  什么！慕容临天一震...周围所有的人也是以一种不敢相信的眼光看着爱儿..</p>\n<p>  爱儿你为什么..慕容临天问道语气里满是不解..</p>\n<p>  王爷如水姐姐是爱儿最好的朋友也是最亲的人即使这次她有错但她也只是无可奈何爱儿不怨..爱儿不想失去如水姐姐而站在如水姐姐的角度她一定也不想失去她的亲妹妹若是这样那爱儿则与她们生死与共...爱儿不知道自己在慕容临天的心中有多少分量她也不知道自己的死对慕容临天算不算威胁她只能一搏..</p>\n<p>  生死与共？好重的四个字...爱儿你有没有为你自己想过？你有没有为我想过？慕容临天爱怜的看着爱儿...手不自觉的抚上她苍白的脸..爱儿你不可以这么善良..</p>\n<p>  王爷爱儿轻轻别开脸躲开他的手..爱儿并不是善良只是通过这些事懂得了生活这就是生活..</p>\n<p>  慕容临天僵在空气中的手尴尬无比..把手放在爱儿瘦弱的肩膀上爱儿生活不是一味的原谅一些不该原谅的人...说话间眼神柔情似水的看着爱儿..</p>\n<p>  一旁的如雪此时已经气得直咬牙..慕容临天从来没有这么温柔的看她即使在别人看来他对自己甚是宠爱可是只有她自己知道这不过是表面的浮华罢了..慕容临天看她的眼神向来是冰冷的不含一丝温度..而且在慕容临天看来她居然是不可原谅的人！想到这里不满眼愤恨的瞪着爱儿..易爱儿你到底用了什么妖术让王爷如此对你..我绝对绝对不会放过你！狠狠的指甲嵌入掌心...</p>\n<p>  爱儿还是下定决心请王爷成全..</p>\n<p>  唉...慕容临天叹了一口气..爱儿你终究还是这样傻..你知不知道这样的你让我很心痛...无奈的挥挥手..放了她..</p>\n<p>  爱儿本王可以答应你不杀她们也可以把秋如水继续留在月华殿但是..狠狠地看着如雪和梅凝..剩下的两个本王要把她们赶出王府！</p>\n<p>  什么！如雪不自觉的站不住脚还好梅凝扶住了她..她看着慕容临天他看自己的眼神里居然充满了厌恶...不行不甘我不甘心！</p>\n<p>  慕容临天不再看她扶着爱儿转身离开了大厅...不久大厅的人们纷纷散去...剩下如水如雪和梅凝三人...</p>\n<p>  妹妹..如水走过来想安慰安慰如雪..</p>\n<p>  闭嘴！我没有你这样的姐姐！如雪大吼着怒目着如水..连自己的亲妹妹都保护不了你当的什么姐姐！</p>\n<p>  如雪..眼泪从如水的眼眶里一滴滴流出来伸出手想抓住如雪流血的手..却被如雪打开..</p>\n<p>  你高兴啦！你的宝贝爱儿现在得宠啦你今后在王府的日子就好过了！今后没人来害你的爱儿啦！你高兴了吧！！如雪近乎歇斯底里...</p>\n<p>  不是的..如雪..不是这样的..没人知道如水此刻的心有多痛..她只是看着如雪愤怒的眼神..任泪水肆意的流落..</p>\n<p>  什么不是！我告诉你我不会原谅你我这一辈子都不会原谅你！我恨你我恨你！如雪抓狂般的跑了出去</p>\n<p>  偌大的大厅里只剩下如水一个人形单影只的背影还有那数不尽的惆怅道不尽的辛酸...</p>\n<p>  第四十三章 此情可待成追忆，只是当时已惘然（十五）</p>\n<p>看着坐在院中发呆的爱儿..</p>\n<p>  在想什么？他坐在爱儿旁边看着出神的爱儿问道..</p>\n<p>  没什么...我想爹娘了...爱儿淡淡的说眼底流露出一丝悲伤...</p>\n<p>  慕容临天爱怜的拿起她的手手腕上的浅色疤痕刺痛了他的眼睛..把手放在自己的胸口逝者已矣从今以后我会把你放在这里</p>\n<p>  爱儿满眼泪花的看着他的确有些感动..不对应该是很大的感动让她的心情久久不能平静..可是已经晚了..太晚太晚...把手抽回来不用了...</p>\n<p>  慕容临天看着抽回去的手胸口隐隐作痛...</p>\n<p>  对了爱儿赶紧岔开话题..我娘以前经常给我讲故事我好想听故事..继而看了一眼慕容临天..你给我讲故事好不好？</p>\n<p>  讲故事？慕容临天本想拒绝可是看到爱儿充满期待的眼神他动容了...</p>\n<p>  好..他顿了顿以前庄子喜欢白天睡觉他常常梦到自己变成一只栩栩然的蝴蝶可是一觉醒来之后发现自己还是自己并没有变成蝴蝶他觉得很奇怪不知道是自己变成了蝴蝶还是蝴蝶变成了自己..</p>\n<p>  爱儿认真的听着..庄周梦蝶的故事的确很令人遐想..说着看到几只色彩斑斓的蝴蝶飞舞在花丛中...那么快活无忧无虑...饶有兴致的说道..庄周梦为蝴蝶庄周之幸也；蝴蝶梦为庄周蝴蝶之不幸也。说完笑着看那群翩飞的蝴蝶站起身在花丛中翩然起舞..一身素衣的爱儿肌若凝脂气若幽兰手如柔荑颜如舜华及腰的长发似墨玉一般..秋波流转明眸皓齿乌羽扇般的长睫毛扑闪扑闪..周围的蝴蝶纷纷围过来在爱儿的周围纷飞绵绵飞梭自赏乐在其中..爱儿就像九天的仙女和周围的蝴蝶融为一体那么的风华绝代不可亵玩...</p>\n<p>  慕容临天从来不知道爱儿跳舞的时候会是这么的美..他简直看呆了他很确定他已经沦陷了....爱儿你是我的你一辈子都会是我的...</p>\n<p>  一曲终了爱儿站在原地看着周围美丽的蝴蝶她笑了！</p>\n<p>　</p>\n";

