module.exports = "<hr>\n<p>title: 第37章\ndate: 2017-07-18 04:00:00\nwords: 3511</p>\n<h2 id=\"index-37\">index: 37</h2>\n<!-- deleteAbove -->\n<p>最终梦娘还是把方法告诉了爱儿..有办法只要把你孩子身上的毒逼到你的眼睛上..只是这样....</p>\n<p>  我的眼睛就会瞎对不对？爱儿弱弱的问..</p>\n<p>  对..梦娘不必瞒她..所以你一定要想清楚...</p>\n<p>  我已经想清楚了就这样办吧...爱儿毫不犹豫的站起来对着梦娘微微一笑...</p>\n<p>  她居然还在笑？梦娘不敢相信..你真的不怕吗？或许你一辈子都看不见了！</p>\n<p>  怕...爱儿很诚实..但我更怕我的孩子没有了...没有眼睛我还可以用耳朵去听右手去触摸用心去看...这样我就很满足了..说着低下头去看看自己的肚子..孩子你会不会嫌你们的娘看不见呢？</p>\n<p>  他不会的！梦娘坚定地说..有这样的娘他一定会感到自豪..</p>\n<p>  是吗？咯咯..爱儿银铃般的笑声感染了梦娘...那什么时候准备解毒？</p>\n<p>  明天早上..你可以准备一下..梦娘说..</p>\n<p>  爱儿莞尔一笑她又走到桃花林欣赏这里的满园春色..这很有可能是最后一次爱儿要把它们全部刻在心里..还有你...天...我一定会记住你的样子..看着远方仿佛他就在不远处看着自己..</p>\n<p>  第二天</p>\n<p>  爱儿你准备好了吗？梦娘问道..</p>\n<p>  爱儿点点头..闭上了眼睛...</p>\n<p>  梦娘排开一版银针朝着爱儿的位扎了下去..她坐在爱儿背后运功..然后把内力输入她体内...爱儿只感到一股温热在自己体内所到之处倍感轻松..温热逐渐上升到了喉咙口..梦娘一用力..呕一大口黑紫色的毒血吐了出来触目惊心...</p>\n<p>  爱儿你体内的毒已经逼出来了现在是你的孩子你要忍住会很痛..说着继续运功..一股温热从爱儿的小腹一直上升到了头部..近了..近了..爱儿能够感觉到这些毒越来越靠近自己的眼睛..突然猛烈的刺痛朝爱儿的眼睛袭来..唔...爱儿极力忍着不让自己叫出声...快到临界..爱儿再也忍受不住这种刺痛晕了过去...</p>\n<p>  梦娘收功用毛巾擦擦爱儿额头的汗珠心中一阵难过...</p>\n<p>  好久爱儿终于醒了过来第一反应摸摸自己的小腹...还好孩子还在..呼..她吁了一口气露出了会心的微笑...</p>\n<p>  也就只有你还笑得出来了..梦娘端着一碗药走了进来坐在床边..</p>\n<p>  梦娘..谢谢你..爱儿谢道..</p>\n<p>  快吃药吧..梦娘用勺子舀起一勺药放在嘴边吹了吹喂给爱儿吃..</p>\n<p>  唔好苦..爱儿皱眉别过头去..</p>\n<p>  痛你都不怕还怕苦？梦娘还真是搞不懂她..为了你的孩子好赶快吃药！</p>\n<p>  哦..听到了孩子爱儿没办法只得喝了..她想象着现在是慕容临天在给她喂药他正柔情的看着自己...咯咯...不自觉地笑了出来..</p>\n<p>  真不知你在想些什么..梦娘还真是没话说了..看着她眼睛上缠的厚厚的纱布梦娘一阵揪心..多么美的眼睛就这么毁了..唉...</p>\n<p>第六章 从别后，忆相逢，几回梦魂与君同（六）</p>\n<p>几天后拆下了包着爱儿眼睛的纱布...缓缓地爱儿睁开眼睛...她在梦娘的搀扶下走到门边..</p>\n<p>  咯咯..她笑了一下..眼前如她所料的一片漆黑不过..有你陪我娘不怕..她笑着摸了摸自己日渐隆起的肚子...</p>\n<p>  唉梦娘叹了口气..虽然她觉得爱儿很傻但是当娘的不都是这样吗？为了自己的孩子在所不惜..</p>\n<p>  梦娘..爱儿轻唤道..今后我想在这里落脚把孩子生下来等孩子长大我要带他去找他爹..不知可否方便？</p>\n<p>  可以你就住在这里吧我一个人也挺闷的..有个小家伙还挺热闹..梦娘笑了..</p>\n<p>  梦娘你真是个好人..等孩子生下来不如就认你做干娘吧你说怎么样？爱儿提议..</p>\n<p>  好好！梦娘可高兴了..当不了亲娘当干娘也不错！</p>\n<p>  爱儿一听一个主意在心中敲定..梦娘你有没有心上人？</p>\n<p>  当然没有了！我每天都在这山谷里哪有什么心上人！梦娘肯定的说..可怜我都十六了估计今后都嫁不出去了..梦娘撇撇嘴满脸无奈..</p>\n<p>  那我给你介绍一个怎么样？爱儿又当起了媒婆..我有个干哥哥叫做冯枫长得十分俊朗为人风趣温柔体贴可谓是男人中的精品！只不过比我家慕容临天差了那么一点点而已..嘿嘿..爱儿傻笑着..</p>\n<p>  去去去！我才不信呢哪有这样的男人..梦娘说着赶紧转身进了厨房小脸通红..</p>\n<p>  八个月后</p>\n<p>  好痛爱儿正被阵痛折磨着..</p>\n<p>  爱儿用力孩子就快出来了！一、二、三用力！</p>\n<p>  爱儿满身都被汗浸湿小脸惨白...</p>\n<p>  突然...</p>\n<p>  哇一声清脆的啼哭声响彻整个木屋..</p>\n<p>  爱儿恭喜你是个男孩长得好帅！梦娘笑呵呵的说..把孩子放到爱儿的枕边..</p>\n<p>  爱儿用手摸摸孩子的脸..想必跟天是一个模子刻出来的..可是..又一阵痛楚袭来...</p>\n<p>  不是吧！还有！梦娘又忙的不亦乐乎几经奋战终于又一个女婴出世了..</p>\n<p>  哇爱儿你好厉害！龙凤胎哎！！梦娘笑得嘴都合不拢了..她和你长得简直一模一样！尤其是那双眼睛说到这里梦娘意识到说错话了..对不起爱儿..</p>\n<p>  没关系你要跟我说我才知道嘛..让我抱抱我的女儿..爱儿微微一笑..</p>\n<p>  摸着怀中两张胖嘟嘟的小脸爱儿高兴极了..可是难免有些苦涩她看不到自己的孩子长什么样子不过听梦娘的描述男孩就跟慕容临天一样女孩就和我一样..呵呵..她笑出声来...</p>\n<p>  梦娘用毛巾帮她擦着脸上的汗...生出两个这么可爱健康的孩子那些痛值了..爱儿你说给他们取什么名字好呢？</p>\n<p>  爱儿沉思了一会儿..他们都是我和慕容临天的结晶是我们爱的延续..不如男孩叫小天女孩叫小爱怎么样？</p>\n<p>  好！好可爱的名字..梦娘笑得像个孩子一样..</p>\n<p>  现实中</p>\n<p>  事情就是这样了...爱儿终于说完了..</p>\n<p>  爱儿...慕容临天不一把抱住她..这么说他们两个是我慕容临天的孩子？</p>\n<p>  当然了..爱儿回答..</p>\n<p>  怪不得..怪不得一见到他们两个就有一种莫名的好感..感动的看着爱儿紧紧搂住她我有孩子了..爱儿你太伟大了！可是你的眼睛慕容临天微微一震...</p>\n<p>  爱儿感到了他的异常...莞尔一笑..我的眼睛不重要只要能和你在一起我就满足了..</p>\n<p>  爱儿我一定会治好你的眼睛的！慕容临天信誓旦旦的说道..</p>\n<p>  嗯爱儿点点头虽然她知道是治不好的但是她不想打破慕容临天的希望..赶快岔开话题...手慢慢的摸上慕容临天的胸口..不知道我还住不住在这里...</p>\n<p>  当然住在这里了..我给它上了一把大锁你永远都不可能从这里跑掉...慕容临天一把抓住那只在自己胸口游离的手...嘴角勾起一抹邪邪的笑...凑到爱儿耳边..你在我吗？</p>\n<p>  我哪有！爱儿犟嘴道..不服气的嘟起的小嘴...</p>\n<p>  没有吗？看着爱儿的动作甜美的唇生育过后更加丰盈的柔软...这一切的一切都勾起了他最原始的冲动...爱儿为了你我已经欲很久了..现在..他的声音变得有些沙哑..你是不是该喂饱我？</p>\n<p>  爱儿虽然看不见他的眼神..但可以感觉到那炙热的渴望像火一样焚烧着他们俩...</p>\n<p>  大白天..爱儿娇嗔道..粉拳轻砸他的胸膛..</p>\n<p>  抓住那只调皮的手无妨说着一把抱起爱儿朝床边走去...</p>\n<p>第七章 从别后，忆相逢，几回梦魂与君同（七）</p>\n<p>慕容临天把爱儿压在身下温暖的大手不停的在爱儿身上所到之处皆引起娇躯的一阵阵轻颤...</p>\n<p>  爱儿你还是和以前一样敏感...慕容临天戏谑道..</p>\n<p>  爱儿的手慢慢攀上慕容临天宽敞的背指尖轻柔的画着圈圈朱唇轻启...</p>\n<p>  嗯..爱儿你这是在玩火..慕容临天闷哼一声...飞速解开爱儿的衣裙露出里面淡粉色的肚兜...手附上爱儿的丰盈或轻或重的着...</p>\n<p>  嗯~~爱儿忍不住的溢出两句申吟..却更加强了身上男人的..正在慕容临天欲解开爱儿肚兜带子的时候</p>\n<p>  娘！叔叔你们在干什么？两个小家伙异口同声的说道..</p>\n<p>  ..爱儿赶紧抓起被子包裹住自己的身体...脸唰一声变得绯红...</p>\n<p>  慕容临天又好气又好笑的看着他们小破孩一来就破坏他们的好事！</p>\n<p>　</p>\n";

