module.exports = "<hr>\n<p>title: 第34章\ndate: 2017-07-18 04:00:00\nwords: 3514</p>\n<h2 id=\"index-34\">index: 34</h2>\n<!-- deleteAbove -->\n<p>进去扶起易天齐向牢外跑去...如雪也跟着逃跑的机会她自然不会放过...</p>\n<p>  看出了爱儿脸上的泪痕以及哭红的眼睛...想必她已经成功下毒了..毕竟她还要留着肚里的孩子...想到这里易天齐的眼神里流露出不易被察觉的得意...</p>\n<p>  来人！易天齐逃跑了！快追！他们的身影未逃过王府侍卫的眼睛...</p>\n<p>  该死！易天齐暗骂道..很快成百上千的王府侍卫举着火把追击易天齐一行人...</p>\n<p>  一路跑一路追..不知不觉天空已经泛起鱼肚白...</p>\n<p>  逼到山巅..而后是深不见底的万丈悬崖....无路可退爱儿出马挡在易天齐前面...侍卫包围了返回的路...十位暗夜铁骑军骑着铁甲战马..左手整齐划一的拿着弯刀寒气逼人..</p>\n<p>  突然众人让出一条道来一袭黑袍的慕容临天不经意中透出天神一般的气质刚毅的眉宇间满是神圣不可侵犯..冯枫依旧是白衣款款只是脸上再也没有了那种笑容...如水在寒的怀抱里早已是泣不成声...寒的蓝色眸子里传送出一种哀鸣..</p>\n<p>  一切都是那么的沧桑那么的阴森令人不寒而栗...</p>\n<p>  爱儿你不惜杀了我就是为了要救他？慕容临天的眼神里有些心痛...</p>\n<p>  是！爱儿毫不犹豫...干爹待我如亲女儿我决不能见死不救...</p>\n<p>  那我呢？慕容临天把手放在胸口..你可为我想过？</p>\n<p>  你的心里不是有另一个女人吗！爱儿吼道..你该死！</p>\n<p>  狠狠揪住心中的痛楚..慕容临天不拧起了眉..爱儿..你终究还是恨我..</p>\n<p>  是！爱儿说的很坚决不留给他半点幻想的余地...只有她知道说这些违心的话她该要忍住多大的痛..不妨告诉你！我昨晚给你吃的毒药我也吃了！不过只有一颗解药..所以你绝对活不了！哈哈爱儿仰天长笑着..引起无数哀鸣...只是这笑里包含了太多的苦涩..</p>\n<p>  干爹把解药给我我要当着他的面吃下去！爱儿对易天齐说道..</p>\n<p>  易天齐嘴角上扬着..这样慕容临天定会痛苦而死..从衣兜里拿出一粒绿色药丸递给爱儿...</p>\n<p>  接过药丸爱儿冷笑了一下漠然的看着慕容临天..看到没有这是世上绝无仅有的一颗解药只要我吃下去你就一定会死！我就是要看着你痛苦而死的模样！哈哈说罢放进口中...</p>\n<p>  慕容临天你看到没有...如雪无法相信的看着眼前的爱儿..笑着..这就是你最爱的女人！到最后又怎么样？还不是为了自己不顾你的死活！他根本就是对你只有恨！</p>\n<p>  爱儿这真的是你想要的？慕容临天神色痛苦的看着爱儿...你爱过我吗？</p>\n<p>  周围所有人都看着慕容临天..他们从来没有见过如此脆弱的王爷..仿佛只要爱儿说没有他便会完全崩溃...</p>\n<p>  爱儿你怎么会变成这样？枫忍不住出口了..</p>\n<p>  爱儿...寒更是不知道该说什么..</p>\n<p>  爱儿的心里深深的撕扯着..对不起枫哥哥寒哥哥...这是我唯一的选择...</p>\n<p>  冷笑着走到慕容临天面前对上他的黑色眼眸..重重的压上他的唇...突然..出手点住他的..继而深情的与他的舌口中的解药慢慢由舌送进慕容临天的嘴里...</p>\n<p>  爱儿你这是干什么？慕容临天想问..无奈被点后动弹不得也说不出话来...</p>\n<p>  爱儿狠狠啃咬着慕容临天的嘴唇勾住他的舌..这个吻深而狠尽情诉说着无尽的悲伤...仿佛时间也为他们停止在这一刻...没有黑暗没有阴谋没有刀光剑影没有波谲云诡..只有一男一女以彼此的深情诉说着爱...</p>\n<p>  爱一旦蒙上恨的阴影就不再单纯..因为恨他们互相折磨..而却这只是一个令人不耻的阴谋...当真相揭开必定会伤得一败涂地...这样的爱从一开始就注定陷入万劫不复..结果如何亦未知..或许只能把这份爱保留..待洗尽铅华再来重恋...</p>\n<p>  离开慕容临天的唇爱儿笑了笑得那么的干净那么的开心...她终于完成了她所能为慕容临天做的最后一件事...</p>\n<p>  爱儿你想干什么？慕容临天的眸子里满是深深的忧虑...</p>\n<p>  爱儿一步步向后退着..轻启红唇..我欲与君相知长命无绝衰。</p>\n<p>  爱儿爱儿你不要做傻事！慕容临天的眼神急迫的向她传递..</p>\n<p>  山无陵江水为竭冬雷震震夏雨雪。爱儿莞尔一笑...</p>\n<p>  爱儿求求你不要..不要...慕容临天的眼神已经化为乞求..他好恨现在的自己..</p>\n<p>  天地合乃敢与君绝！爱儿笑靥如花在寒风中墨玉般的发丝随风扬起..肤如凝脂宛若画中的仙女...</p>\n<p>  爱儿！不可以...不可以！慕容临天快要抓狂....</p>\n<p>  又走到崖边...看着眼前的众人...爱儿笑得无比灿烂像怒放的桃花...</p>\n<p>  你说过你会等我..就算知道我不会回来你也会一生一世的等下去..边说边往易天齐旁边走..小王爷你要信守诺言！我一定会回来的...</p>\n<p>  说完牢牢抓住易天齐的手臂爹娘爱儿给你们报仇！易天齐反应到不妙可是无法挣脱爱儿的手..突然她的身子向后倒去..带走无尽的仇恨..</p>\n<p>  一袭白色的衣裙像九天下凡的仙女在云海中慢慢下落....寒冬的风凛冽的刮着扬起爱儿的长发干净的没有一丝杂质的眼眸缓缓闭上享受着生命的尽头...</p>\n<p>  爱儿！！枫如水寒异口同声的呐喊着...在崖边向下看去..已然失去了爱儿的身影..只是袅袅轻烟环绕在山谷中还有连绵不绝的回声...</p>\n<p>  天空中下起了鹅毛大雪霎时间装点了一切...银装素裹覆盖着整个大地...</p>\n<p>  慕容临天使尽全力解开道..痛心疾首的看着爱儿跳下去的地方...他看见爱儿在他的眼前纯净的眸子一眨一眨..伸出一只手..好想能够抓住爱儿...只是刚刚触碰到..便消逝得无影无踪...</p>\n<p>  爱儿...他无力的轻唤....呕出一口鲜血洒在这的大地之上像怒放的血梅...</p>\n<p>  刹那间满山的桃花竞相争放！大片的桃花灿烂了山间点缀出粉妆玉砌的世界....</p>\n<p>  上邪！我欲与君相知长命无绝衰...慕容临天如此爱儿亦如此...</p>\n<hr>\n<p>  呜呜<del>~~爱儿把口中的解药通过吻送给了慕容临天...她这是用生命在爱慕容临天</del>~~~最后为了报仇不得不抓着易天齐一起跳崖太悲壮了！！大家肯定说爱儿傻其实不是她不跳也会毒发而死而且是死在慕容临天面前她无法这么残忍所以她选择杀身成仁...这是她唯一的选择...一句小王爷包含了太多情太多爱太多纠葛太多不舍..这段上天注定的姻缘必定要遭受各种苦难以至于生死离别..或许只有当一切都不顾的时候奇迹会发生...寒冬时下着漫天大雪桃花居然会开..如桃花的爱儿相信也会如此...</p>\n<p>  注意！这不是大结局！！！</p>\n<p>  第一章 从别后，忆相逢，几回梦魂与君同（一）</p>\n<p>一年后</p>\n<p>  天枫走过来拍了下慕容临天的肩膀..又想爱儿了？</p>\n<p>  慕容临天看着香巾用手轻轻中央的桃花...</p>\n<p>  已经一年了爱儿你什么时候回来呢？慕容临天微微一笑：我很想你..</p>\n<p>  天...爱儿不会回来了...一年前他们去山谷中寻找几天几夜看到了易天齐摔得粉碎的尸体可是没有看到爱儿的..可能爱儿已经被野兽叼走了或是...尸骨无存...</p>\n<p>  她会回来这是她对我的承诺...慕容临天轻轻的说眼睛却始终没离开过香巾上的桃花...</p>\n<p>  天...枫不知该如何劝他默默的离开了...</p>\n<p>  两年后</p>\n<p>  慕容临天坐在麒麟殿外的小院子里独酌...旁边的位置上还摆了一个酒杯...</p>\n<p>  两年前他曾和爱儿坐在这里赏月...记得当时...</p>\n<p>  今晚的月亮好圆..</p>\n<p>  可是今天的月圆又能代表什么？</p>\n<p>  人有悲欢离合月有阴晴圆缺此事古难全...</p>\n<p>  月终究会残缺人也是如此..</p>\n<p>  慕容临天抬头看看天上的玉盘...爱儿今天月圆了可是人什么时候才能团圆呢？举起酒杯轻轻碰了碰身边座位上的酒杯...干杯！爱儿...脸上浮现出一抹温柔的笑...</p>\n<p>  三年后</p>\n<p>  爱儿今年的冬天特别冷..慕容临天走进麒麟殿中的温泉里...闭上眼睛爱儿的身影再次在脑海浮现...</p>\n<p>  嗯！</p>\n<p>　</p>\n";

