module.exports = "<hr>\n<p>title: 第4章\ndate: 2017-07-18 03:59:58\nwords: 3576</p>\n<h2 id=\"index-4\">index: 4</h2>\n<!-- deleteAbove -->\n<p>   好人？寒一怔..伤感又从眼底流露出掩盖不住....</p>\n<p>  你怎么了？</p>\n<p>  爱儿关切的看着他原来他还有这么脆弱的一面..</p>\n<p>  你为什么宁愿牺牲自己的救她？</p>\n<p>  寒反问爱儿..</p>\n<p>  因为如水姐姐是好人她对我好我当然要对她好而且她是因为我才会受伤的如果不治好她我会内疚一辈子的！</p>\n<p>  爱儿坚定的说那眼神却比这话语更为打动人...</p>\n<p>  寒不由的悲怆起来你有个好姐姐她也有个好妹妹可是...</p>\n<p>  他嘴角动了动最终把下面的话埋进肚子里...</p>\n<p>  你怎么了？</p>\n<p>  他摇摇头..</p>\n<p>  如果你有什么不开心的事可以跟我说我可以帮你分担呢！</p>\n<p>  爱儿嘴角扬起一个好看的弧度那笑容像冬日的暖阳悄悄融化了寒的心...</p>\n<p>  寒回忆起那不堪的过去</p>\n<p>  第七章取次花丛懒回顾，半缘修道半缘君（七）</p>\n<p>  十年前麟府</p>\n<p>  三少爷你回来了？</p>\n<p>  老爷在书房等你呢说是找你有事。丫鬟道...</p>\n<p>  知道了你先下去吧。</p>\n<p>  寒道..</p>\n<p>  书房内</p>\n<p>  爹你找我？</p>\n<p>  寒儿最近涣关有外敌侵扰你可知道？</p>\n<p>  爹孩儿略有所闻。</p>\n<p>  今日上朝皇上下旨要我麟家带兵消除边关之患我们麟家三代为将深受皇上信赖皇恩不可不报而且事关黎民百姓我们更要重视才行。</p>\n<p>  爹你放心吧我和大哥二哥一定会尽力帮助爹扫清边关祸患不负圣恩。</p>\n<p>  寒儿你从小机智聪敏相信你一定不会让我失望的。至于你的两个哥哥唉</p>\n<p>  爹不用担心大哥二哥只是还未成熟等他们成熟了相信也是我朝的两员猛将。</p>\n<p>  希望如此</p>\n<p>  房门外</p>\n<p>  麟冰（寒的大哥）：二弟爹如此器重三弟莫不是要把今后的家产都交给他？</p>\n<p>  麟雪（寒的二哥）：大哥岂止是家产怕是此仗一胜三弟就立了大功从此之后家里可就没有我们两兄弟的立足之地了。</p>\n<p>  麟冰：哼麟寒说我不成熟我就成熟给你看看这就叫你不仁我不义！</p>\n<p>  三个月后</p>\n<p>  麟威（寒的爹）亲率三万大军直奔涣关外与胡虏决一死战..</p>\n<p>  麟寒率一万大军麟冰、麟雪各率五千精兵等候听令</p>\n<p>  激战过后麟威大军损失惨重....</p>\n<p>  麟将军：命人快马传书叫三位麟副将军所领军队务必在三日内到达前线支援火速</p>\n<p>  严副将：遵命！</p>\n<p>  距援军宿营地十里处</p>\n<p>  ！传令兵倒地身亡..</p>\n<p>  援军军营</p>\n<p>  麟雪：大哥爹果然支持不住了请我们去支援！</p>\n<p>  麟冰：这是我预料当中的事。寒喝了我们给他送去的酒没有？</p>\n<p>  麟雪：大哥他已经喝了现在你打算怎么办？</p>\n<p>  麟冰：怎么办？</p>\n<p>  哼当然说完用手在脖子上比了个杀的手势。</p>\n<p>  一个时辰后</p>\n<p>  麟寒慢慢睁开眼睛摇了摇昏昏沉沉的头..</p>\n<p>  冰：三弟你醒了。</p>\n<p>  寒：大哥二哥你们怎么在这儿？说完欲起身舒展筋骨</p>\n<p>  寒：怎么回事？</p>\n<p>  我怎么被绑着？</p>\n<p>  冰：三弟刚才爹命人送信来了要我们在三日之内赶去支援。</p>\n<p>  寒：真的？那赶快给我松绑我们得出发了不然就来不及了！</p>\n<p>  雪：可惜我们不打算给你松绑。</p>\n<p>  而且我们会当作没有收到过这封信。</p>\n<p>  寒：大哥二哥你们什么意思？</p>\n<p>  你们想违反军令？</p>\n<p>  雪：违反军令？哈哈这封信可只有我们三人知道。</p>\n<p>  寒：爹平常待你们可不薄你们怎么能见死不救？</p>\n<p>  冰：麟寒你闭嘴！</p>\n<p>  爹要怪就只能怪他偏心你要怪就只能怪你自己太聪明！</p>\n<p>  寒咬牙切齿：你们..你们！</p>\n<p>  ！</p>\n<p>  冰：哼受死吧！</p>\n<p>  说罢抽出长剑一剑刺穿寒的身体鲜红的血溅了一地</p>\n<p>  冰：来人！</p>\n<p>  麟寒欺君卖国已被我斩杀把他抬去乱葬岗！</p>\n<p>  属下：麟将军怎么会</p>\n<p>  雪：混账你是怀疑我和我大哥咯！</p>\n<p>  属下：属下不敢！属下遵命！</p>\n<p>  战场上</p>\n<p>  麟威大军所剩无几胡虏大军亦损失惨重</p>\n<p>  将军我军..我军全面溃败了</p>\n<p>  怎么会！</p>\n<p>  寒儿的援军怎么还没来？</p>\n<p>  属下不知！将军我们会护送你出此地留得青山在不怕没柴烧！</p>\n<p>  哼！我麟家三代为将皆是忠君为国的猛将今天我若弃甲而逃有何颜面见皇上和麟家列祖列宗！</p>\n<p>  将军好吧我们誓死跟随将军要生一起生要死一起死！</p>\n<p>  嘶~~一阵马嚎声...</p>\n<p>  胡虏（敌方首领）：麟威你们已经被包围了快投降吧！</p>\n<p>  今日我就是战死沙场也不会向你们投降！</p>\n<p>  麟威你之所以有今日要多谢你的两个宝贝儿子要不是他们把你们作战指挥图交给我们我们又怎么会赢得如此轻松？</p>\n<p>  哈哈哈哈</p>\n<p>  什么？麟威不敢相信..</p>\n<p>  而且他们果然遵守诺言没有带兵支援你最能干的儿子恐怕也..嘿嘿命丧黄泉了...今日告诉你这些就是让你能死个明白！</p>\n<p>  寒儿..我的寒儿..麟威悲痛欲绝...胡虏无论如何我不会投降我死也是为国而死！</p>\n<p>  我死也死得无愧于天地！！</p>\n<p>  说罢麟威带领剩下的百余人与敌军展开一段惨烈的厮杀抛头颅洒热血浩浩汤汤人神共泣一代名将大成于此风萧萧兮易水寒壮士一去兮不复返</p>\n<p>  现实中</p>\n<p>  就这样我爹殉国了</p>\n<p>  麟寒一脸悲伤....</p>\n<p>  从那以后我再也不相信任何人朋友又怎么样？</p>\n<p>  连亲兄弟都可以自相残杀这世上还有什么人可以相信！寒攥紧拳头关节发出咔咔作响的声音....</p>\n<p>  爱儿低头沉思着他居然有着这么伤痛的过去..</p>\n<p>  那后来你的两个哥哥怎么样了？</p>\n<p>  爱儿小心翼翼的问..</p>\n<p>  死了！五年前我潜进麟府亲手解决了他们为爹报仇了..哈哈哈哈寒大笑着笑声令人不寒而栗...</p>\n<p>  其实杀了他们你一点也不开心对吗？</p>\n<p>  爱儿问道...</p>\n<p>  寒怔住了...不语...</p>\n<p>  虽然他们罪大恶极但他们毕竟是你的哥哥是你最亲的人你其实不想杀他们但心中的仇恨迫使你杀了他们如此纠结的心相信你很难过爱儿说道流露出些许忧伤...</p>\n<p>  寒不知道为什么有种被她说中了的感觉但不想承认..他们要杀我还害死了爹我怎么会不想杀他们？</p>\n<p>  你的想法未免太可笑了！</p>\n<p>  因为人皆有恻隐之心爱儿一本正经道因为你的心中还有爱..</p>\n<p>  爱..寒直勾勾的看着爱儿看着眼前这个并未成熟的女孩她说的每句话都是那么的有理深深烙在他的心底他似乎对这个女孩有了极大的兴趣...</p>\n<p>  爱儿被他盯得心里发毛不得不换了个话题你不是说他们杀了你吗？</p>\n<p>  为什么你没死？</p>\n<p>  你很希望我死吗？寒的嘴角扬起个美丽的弧度戏谑着因为他们对着我的心脏扎了下去可是那之后我才知道我的心脏长在右边</p>\n<p>  长在右边？</p>\n<p>  爱儿疑惑着可相信他的话看吧老天也是有爱的所以他才救了你一命..爱儿笑了...</p>\n<p>  寒看着爱儿她的笑容为何让我如此让我痴迷？</p>\n<p>  ！爱儿突然大叫起来..</p>\n<p>  你叫什么！寒显然被吓到了..</p>\n<p>  如水姐姐还在破庙呢万一那些人追来又把她抓走怎么办？</p>\n<p>  爱儿担心极了..</p>\n<p>  不用担心我们现在就回去..寒没发现他的语气里竟有了些许的关切...</p>\n<p>  第八章取次花丛懒回顾，半缘修道半缘君（八）</p>\n<p>  被寒抱着在天空中飞舞还真是让爱儿有种说不出的感觉...</p>\n<p>  你叫麟寒对吧？</p>\n<p>  我听如水姐姐像是这么叫你的..爱儿看着寒..</p>\n<p>  嗯你可以叫我话还没说完....</p>\n<p>  寒哥哥我们现在是朋友对不对？</p>\n<p>  爱儿笑着问...</p>\n<p>  嗯。本来他想说她可以叫他寒的..但不管怎么样一声寒哥哥也让他心情大好居然答应和她做朋友他真是越来越不了解自己了....</p>\n<p>  真好！</p>\n<p>  ！爱儿高兴极了眼睛都眯成了一条缝儿我就喜欢和好人做朋友！</p>\n<p>  好人？</p>\n<p>  呵这十年来第一次有人说他是好人你说好人就是好人吧..</p>\n<p>  到了下来吧。</p>\n<p>  寒轻轻放下爱儿..</p>\n<p>  如水姐姐！！听到这熟悉的声音如水立马转过头来爱儿一下子扑进她怀里像个撒娇的孩子...</p>\n<p>  爱儿..如水哽咽着对不起都是我...</p>\n<p>  如水姐姐你别哭爱儿喜欢看到你笑的样子...爱儿更加抱紧了如水..</p>\n<p>  你放心吧我没把她怎么样。</p>\n<p>　</p>\n";

