module.exports = "<hr>\n<p>title: 第36章\ndate: 2017-07-18 04:00:00\nwords: 3545</p>\n<h2 id=\"index-36\">index: 36</h2>\n<!-- deleteAbove -->\n<p>   此情无计可消除才下眉头却上心头。</p>\n<p>  娘<del>~娘</del>~~两声童稚的呼唤唤醒了沉醉在相思中的女人...</p>\n<p>  小天小爱..你们回来了...女人依旧背着门快去洗手要吃饭了..</p>\n<p>  好！</p>\n<p>  两个小家伙异口同声的答道..</p>\n<p>  好一句‘此情无计可消除才下眉头却上心头’..慕容临天磁的声音响起...</p>\n<p>  女人怔住了..手中的绣品掉在了地上...</p>\n<p>  她不敢相信..这个声音是....</p>\n<p>  慕容临天不解的看着女人的背影总觉得她好像爱儿...</p>\n<p>  姑娘你....话还没说完...</p>\n<p>  还有一首...女人说道...上邪！</p>\n<p>  我欲与君相知长命无绝衰。山无陵江水为竭冬雷震震夏雨雪。天地合乃敢与君绝！</p>\n<p>  女人慢慢转过身来..</p>\n<p>  慕容临天的瞳孔瞬间收紧五年来的爱忧愁思念在此刻统统决堤...</p>\n<p>  你说过你会等我..就算知道我不会回来你也会一生一世的等下去..女人的眼中满是泪花晶莹的泪珠顺着脸颊滑落下来....</p>\n<p>  慕容临天不顾一切的冲上去把女人紧紧的拥在怀中像是要把她融进自己的身体..我做到了...爱儿.....</p>\n<p>  滚烫的泪打湿了慕容临天的胸膛...天爱儿轻轻的唤道...真的是他！</p>\n<p>  这不是幻觉...五年来不知有多少次在梦中和他相遇可现在是真的！</p>\n<p>  真的！虽然看不见但是他的怀抱他的味道她一辈子也不会忘...</p>\n<p>  爱儿..爱儿...慕容临天一次又一次的呼唤她的名字他要永远把她留在身边不给她任何离开的机会.....</p>\n<p>  轻轻抬起爱儿挂满泪珠的脸吻去她脸上的泪痕..接着朝着那两片朱唇狠狠的吻下去...尽情的啃咬...爱儿无声的迎合着...五年了..整整五年了...多少爱？</p>\n<p>  多少愁？只道相思无尽处...</p>\n<p>  慕容临天放肆的蹂躏着爱儿的唇瓣舌长驱直入的挑起她的丁香小舌...尽情她口中的甘甜..他快疯了..被无穷无尽的思念逼疯了..当一切再次拥有的时候他只有尽情着....</p>\n<p>  小的时候相遇经过了五年..他们相逢..却被仇恨蒙蔽了双眼互相折磨..只落得满心伤痕...又是五年上天让他们再次重逢当一切糜恨都消散净尽再打开当年的那份炙热的爱恋一切都会变得不一样了..有道是..第一次是偶然第二次是必然第三次则是命中注定...</p>\n<p>  整个房间都被他们的激情点燃被他们的爱融化...</p>\n<p>  第四章从别后，忆相逢，几回梦魂与君同（四）</p>\n<p>  爱儿...爱儿....慕容临天被热泪充斥了眼眸..五年...终于让他等到了...轻轻抓住爱儿的肩膀看着这张让他日夜思念得抓狂的脸...虽不施粉黛却显得那么的清秀...如今的爱儿早已褪去原来的稚气浑身上下散发出一种成熟的气质...</p>\n<p>  爱儿你更有女人味了....慕容临天的嘴角勾起一抹邪邪的微笑...</p>\n<p>  是吗？</p>\n<p>  爱儿莞尔一笑清澈的眸子扑闪扑闪...手摸索着抚上慕容临天的脸...</p>\n<p>  爱儿你的眼睛慕容临天看着爱儿的动作想起小天和小爱的话...不由的心中揪紧...</p>\n<p>  没事只是看不见了...爱儿平静的说像涟漪都没有的湖面..</p>\n<p>  爱儿...慕容临天心疼的看着那双美丽的眼睛虽然还是那样清澈眼神却是分散的..不轻吻她的眼睛...爱儿这到底是怎么回事？</p>\n<p>  爱儿带着他慢慢坐下这要从五年前我摔下山崖时说起...</p>\n<p>  五年前</p>\n<p>  慕容临天我们来生再见...爱儿心中默念着闭上了眼睛..晶莹的泪水顺着眼角落下...突然..</p>\n<p>  姑娘...一个农家女打扮的女子使用轻功腾空跃起一把接住从空中落下的爱儿...这个女人长得虽不及爱儿那样风华绝代但也让人赏心悦目..</p>\n<p>  嗵！</p>\n<p>  同时旁边一声重物落地的声音...看着地上摔得惨不忍睹的易天齐爱儿别过脸胃中一阵翻滚干呕着...</p>\n<p>  他是谁？</p>\n<p>  女子问道...</p>\n<p>  好不容易爱儿停止了呕吐..罪人..她吐出两个字..</p>\n<p>  女子沉思了一下估计死的不是什么好人..你呢？</p>\n<p>  叫什么名字？</p>\n<p>  我叫易爱儿..爱儿说道..谢谢姑娘相救..爱儿感激不尽..不知怎样才能从这里上去？</p>\n<p>  你要上去？女子问道..突然抓过爱儿的手腕...</p>\n<p>  姑娘你爱儿不解..</p>\n<p>  你怀孕了..女子说道..而且中毒了...</p>\n<p>  你怎么知道？</p>\n<p>  难道姑娘是大夫？爱儿问道..</p>\n<p>  算是吧..你就是上去了也没用很快就会毒发身亡..女子皱眉说道..</p>\n<p>  我知道...爱儿低下头眸子里满是悲恸..可是我一定要上去..</p>\n<p>  怎么..女子好笑似的看着爱儿你就算自己不怕死也要为你肚子里的孩子着想吧！</p>\n<p>  可..爱儿的眼眶湿润了..这毒已经没有解药了..我难逃一死..</p>\n<p>  如果我说我可以救你呢？</p>\n<p>  女子的嘴角向上扬起..</p>\n<p>  真的？爱儿猛地抬起头来眼里满是激动..你真的可以帮我解毒？</p>\n<p>  不是很确定但有一些把握..女子说..这样吧你先跟我回桃花园吧..我好准备..</p>\n<p>  姑娘！</p>\n<p>  太谢谢你了！爱儿激动地又哭又笑..紧紧握住女子的手..</p>\n<p>  好了你也别叫我姑娘了我叫云梦娘你就叫我梦娘就好..我就叫你爱儿吧..云梦娘笑着说..</p>\n<p>  嗯..爱儿点点头...太好了这样我就可以回去见天了..爱儿心中一阵窃喜...</p>\n<p>  跟着云梦娘她们穿过一大片竹林..继而..好美...爱儿看着眼前盛开的朵朵桃花不由自主的赞叹道..</p>\n<p>  这不是冬天吗？</p>\n<p>  为什么会开花？爱儿好奇的问云梦娘..大眼睛一眨一眨..</p>\n<p>  看着爱儿清澈的眼眸云梦娘若有所思..因为这里四季如春..到底为什么会这样我也不知道...</p>\n<p>  真的吗？</p>\n<p>  爱儿笑靥如花..好神奇的地方好美的桃花..她轻抚着每一棵桃树目不转睛的盯着这里的桃花看..</p>\n<p>  穿梭在桃花林中的爱儿就像仙子一般她的一颦一笑令云梦娘赞叹不已..只可惜这样美的女子终究是天妒红颜...看吧多看看..或许你今后都看不到了...</p>\n<p>  唉...云梦娘叹了一口气...走吧我们回家...</p>\n<p>  爱儿赞同的点点头...走进桃花林深处有几座小木屋虽不豪华却让人感到无比的温馨..看见云梦娘回来几个周围的邻居从小木屋出来..手里提着篮子里面放着一些吃的..</p>\n<p>  一位大娘立刻上来抓住云梦娘的手热泪盈眶梦娘谢谢你治好了我孙子的病！</p>\n<p>  太谢谢你了...说着把手中的篮子塞给云梦娘这是一点吃的你就收下吧..</p>\n<p>  这怎么行呢？</p>\n<p>  梦娘赶紧把篮子又递了回去..治病本来就是我该做的..</p>\n<p>  我们真不知怎么感谢你拜托你就收下吧不然我们会不安心的..大娘继续劝道..</p>\n<p>  几经推让梦娘最终还是勉为其难收下了..</p>\n<p>  这位姑娘是？</p>\n<p>  大娘看着梦娘身旁的爱儿...</p>\n<p>  我叫爱儿大娘你好..爱儿笑着自我介绍..</p>\n<p>  好一个生的水灵的姑娘..大娘不夸赞起爱儿的倾城姿貌..</p>\n<p>  大娘过奖了..爱儿有些不好意思..大娘不知梦娘...</p>\n<p>  哦梦娘可是个好人！</p>\n<p>  她医术高明而且给我们治病从不要钱..我们这里的每个人都受过她的恩惠呢！</p>\n<p>  大娘滔滔不绝的夸道..</p>\n<p>  好了大娘别夸我了我会骄傲的..呵呵我们回去吧..梦娘说着带爱儿进了一间小木屋..</p>\n<p>  第五章从别后，忆相逢，几回梦魂与君同（五）</p>\n<p>  爱儿我要跟你说一件事..梦娘的神色有些异常..你要做好心理准备..</p>\n<p>  什么事？</p>\n<p>  爱儿能感觉到这件事和她有关而且很严重..</p>\n<p>  你的毒可以解..但是..梦娘顿了一下...</p>\n<p>  但是什么？</p>\n<p>  爱儿的心狠狠的揪了起来...</p>\n<p>  你孩子的毒不能解...梦娘低下了头..</p>\n<p>  什么....爱儿瘫坐在地上...那我的孩子....会死？</p>\n<p>  .....</p>\n<p>  梦娘点点头...爱儿摸摸自己有些凸起的小腹爱怜的看着仿佛能够看到他在一天天长大....不可以..这是我和慕容临天的孩子他不可以死.....</p>\n<p>  梦娘你告诉我是不是真的不能保住孩子的命？</p>\n<p>  爱儿期待的看着梦娘眼眶已经泛红但还是忍住不让眼泪滴下来...</p>\n<p>  看着爱儿的眼神..梦娘真的不忍心说出来...如此坚强的女子...但她到底能不能受得了？</p>\n<p>　</p>\n";

