module.exports = "<hr>\n<p>title: 第12章\ndate: 2017-07-18 03:59:58\nwords: 3698</p>\n<h2 id=\"index-12\">index: 12</h2>\n<!-- deleteAbove -->\n<p>爱儿那气鼓鼓的样子着实把寒逗笑了..</p>\n<p>  咦！寒哥哥你笑了耶！你笑起来好好看哦！爱儿看到寒脸上难得的笑高兴极了！</p>\n<p>  笑？我笑了？寒觉得不可思议..从十年前的那次事后他就再没有真心笑过..也没有人可以让他笑...这也算是他心中的脆弱之处吧...深蓝的瞳中闪过一丝惊慌..不想让别人看到他的脆弱尤其是已经看到一次的爱儿..</p>\n<p>  好了不说这个了让我看看你的伤..赶忙岔开话题关上手中的医书走到爱儿面前小心翼翼地拆开爱儿胡乱包的布..几道触目惊心的伤口出现在眼前..手心的血痂旁有大片泛红相信已经被感染手背呈红肿状大概是被烫伤的...捏住爱儿手腕的那只手似乎有些许的颤抖..她的手断过...</p>\n<p>  寒不敢相信的看着眼前的爱儿蓝色的眸子里满是动容..她到底出了什么事..怎么会受这么多的伤..不过他不想问爱儿他知道这只会勾起她伤心的回忆..用药水小心清理伤口尽量不弄疼爱儿再用纱布包好..</p>\n<p>  记住伤口不要沾水每天到我这来换药相信三天就能好得连疤都不留..虽是这样说可是为什么心会痛..</p>\n<p>  第二十四章 众里寻她千百度，蓦然回首（十六）</p>\n<p>伤口不能沾水？爱儿有些着急可是我还要洗碗呢..</p>\n<p>  洗碗？你还嫌自己的伤口感染得不够厉害是吧..寒说道显然有些责怪..</p>\n<p>  如果我不干活就没有饭吃..爱儿深深地低下头.</p>\n<p>  为什么？难道你就是易天齐的干女儿？</p>\n<p>  爱儿没有说话只是轻轻点点头..</p>\n<p>  寒很讶异..他是听说过慕容临天把易天齐的干女儿抓了回来当丫鬟可没想到就是爱儿..以慕容临天的格他绝不会让仇人好过..想起爱儿被折断过的手腕寒不倒吸了一口气..</p>\n<p>  看来他是错怪爱儿了她也是身不由己不有些懊悔..</p>\n<p>  爱儿我去跟周管家说一声让他这几天别给你安排活儿等伤好了再说..满眼的宠溺语气是那么的温柔可他自己却没发现..</p>\n<p>  看着寒温柔的模样真是迷人..怪不得如水姐姐对他一见钟情呢..哎对了要是如水姐姐知道寒在王府那她肯定高兴死了！明天就带寒哥哥去见如水姐姐..就这么说定了！</p>\n<p>谢谢你寒哥哥..爱儿今天可真是像捡到宝一样高兴..寒哥哥我今天可以在你这里吃晚饭吗？想起那一碗稀饭两个硬邦邦的馒头心里就发麻..</p>\n<p>  可以..寒其实也巴不得不知为什么他希望爱儿能一直留在他身边..</p>\n<p>  晚饭时间到了</p>\n<p>  哇寒哥哥你这里的饭菜好丰盛哦..好香..爱儿看着满桌的菜口水都快掉下来了..</p>\n<p>  我这里的饭菜比较清淡不知你吃不吃得惯..看着爱儿的表情寒没由来的感到很满足..夹了一点菜放到爱儿碗里..快吃吧口水都快掉碗里了..</p>\n<p>  爱儿狼吞虎咽着完全不顾形象像饿鬼投胎似的..好吃真好吃！边吃边赞不绝口..饭粒儿都跑到脸上了慢点吃..寒笑呵呵的看着爱儿帮她把饭粒从脸上捡下来..</p>\n<p>  又看到寒的笑容寒哥哥你真的应该多笑你笑起来好好看！边说还不忘边大口嚼着饭菜..</p>\n<p>  笑..寒真的不敢相信..一天之内这个爱儿居然有本事让他在不知不觉中笑两次..为什么会这样？就这样一种不知名的感觉在心里渐渐生根、发芽..</p>\n<p>  看来你真的饿了慢点吃..寒道..</p>\n<p>  嗯好饿我这么久都没好好吃过一餐饭..爱儿说道..突然一顿不过还是有饭吃的只是菜没这么好吃呵呵..她不想让寒知道这些天她是怎么过的..</p>\n<p>  晚饭过后夜晚的月亮总是那么明亮..</p>\n<p>  寒哥哥我要回房了..爱儿笑着说心里却在苦笑那算是房间吗？不过不能让寒哥哥知道..</p>\n<p>  爱儿我送你吧..寒道..</p>\n<p>  不用了寒哥哥我自己回去就行了万一用人找你治病就不好了..说罢笑着跑开了不给寒开口的机会..</p>\n<p>  回到破旧不堪的柴房爱儿慢慢躺在稻草堆里辗转反侧间幻想着明天见到如水姐姐和寒哥哥自己可以帮他们牵牵线..咯咯笑声像银铃般清澈..甜甜的进入了梦乡..只是她不知道脸上的一抹笑容牵动了某个人的心..</p>\n<p>  窗外....月光照耀着寒的银发像倾泻而下的瀑布...深蓝的眸子里融着这破旧的柴房稻草堆还有——她...她为什么还能笑得那么开心？宛如含苞欲放的桃花那么娇嫩欲滴..</p>\n<p>  怪不得她不让我送她回来...爱儿你为什么这么傻？当然这句话只能在心里说...</p>\n<p>  注视着里面小小的人儿寒久久不愿离去就想这样看着她一辈子直到地老天荒..</p>\n<p>  第二十五章 众里寻她千百度，蓦然回首（十七）</p>\n<p>你饿了吗？</p>\n<p>  不用害怕这里是四王爷行宫我是小王爷我们可以做朋友吗？</p>\n<p>  呵呵那我们现在开始就是朋友了我叫下人给你准备了点吃的快吃吧明天我带你去找你父母。</p>\n<p>  我..我..呃我只是觉得你很可爱..</p>\n<p>..............................................................................</p>\n<p>小王爷...爱儿呓语道..</p>\n<p>  天大亮.....爱儿揉揉惺忪的睡眼...呃天亮了..</p>\n<p>  刚才的梦境爱儿是无比的熟悉从五年前的那次偶遇后不知多少个日日夜夜爱儿都作着同样的梦他的话一直在她脑海回响着..爱儿还是那么怀念小王爷那个温柔善良的小王爷...</p>\n<p>  咦？爱儿奇怪的看着身上披着的衣服这好像是寒的..难道寒哥哥昨晚来过？爱儿自言自语着..那他不是知道了这些..唉爱儿轻叹一口气希望他不要太担心..</p>\n<p>  抱着那带有甘草香味的衣服爱儿来到了典医阁..</p>\n<p>  寒哥哥！不知为什么看到寒就会觉得有安全感像看到枫哥哥一样..双手把衣服送上谢谢你的衣服..</p>\n<p>  寒接过衣服把刚熬好的桂圆薏米粥递给爱儿吃早饭吧..</p>\n<p>  他从下人那儿了解到王府里的人都故意对爱儿不好不让她吃早饭和午饭连晚饭也要先把王府一天的碗洗完才可以吃而且只有一碗粥和两个过夜馒头..真不知她是怎么挺过来的..好歹她以前也是个千金小姐..看着爱儿瘦了一圈的身影寒的眼里有的只是疼惜..</p>\n<p>  接过香味四溢的粥爱儿三下五除二就搞定了..擦擦与尤未尽的嘴寒哥哥我带你去一个地方..说完拉着寒的手往外跑去..</p>\n<p>  来到月华殿...</p>\n<p>  如水姐姐！爱儿一看到坐在桌边发呆的人就冲了上去紧紧抱住她..如水还没反应过来来人是谁不过只有爱儿一个人会叫她如水姐姐那种久违的亲切一下子唤醒了如水....</p>\n<p>  爱儿你怎么来了..看着眼前抱着自己的人如水感动得热泪盈眶..好多天没见你都瘦了..爱儿你过得好吗？</p>\n<p>  我过得很好如水姐姐你放心吧..爱儿笑着在她怀里撒娇..</p>\n<p>  如水爱怜的看着爱儿她怎么可能过得好..自己又如何能放心？爱儿你真傻..</p>\n<p>  哦对了如水姐姐我今天带了一个人来..说话间这才想起被自己晾在一边的寒...朝着门外喊道寒哥哥进来吧..</p>\n<p>  很快一头银发的男子出现在如水面前绝美的面容让如水难以忘怀...是你...寒如水不敢相信自己的眼睛..</p>\n<p>  哎哟叫得那么亲密干嘛啦...爱儿在如水耳旁打趣道..嘿嘿这招果然有效如水的脸一下子就红了起来..</p>\n<p>  爱儿你说什么呢..我哪有..如水赶快掩饰自己的害羞...</p>\n<p>  你没有？那刚才‘寒’是谁叫的？</p>\n<p>  我...我..如水真的没话说了..这个爱儿真的是让人哭笑不得..</p>\n<p>  爱儿溜到寒的身边寒哥哥你看我如水姐姐长得是倾国倾城国色天香沉鱼落雁闭月羞花（叽里咕噜把所有能想到形容人美的词全用上了）而你呢长得也是祸国殃民的..刚好凑一对儿！寒和如水都无语了...尤其是寒凭什么到我这了就是祸国殃民..贬义词褒用也不是这么带杀吧..</p>\n<p>  爱儿接着说我如水姐那可是琴棋书画无一不精女红也是好得不得了而你呢医术精湛赛华佗你们俩可真是天作之合...</p>\n<p>  寒明白了感情这小丫头在帮他们俩牵线搭桥呢...深蓝的眸子一下降到冰点...</p>\n<p>  爱儿！！寒和如水几乎是同时出声...</p>\n<p>  哇你们俩也太有默契了吧..爱儿见形势不妙..三十六计走为上计我溜......</p>\n<p>  咯咯房外传来爱儿一阵又一阵的笑声...房内寒和如水一脸黑线...四目相对时尴尬异常两人的想法皆有不同..</p>\n<p>  如水：我今天打扮得好不好看千万不要失礼于人....</p>\n<p>  寒：爱儿这个小丫头看我回到典医阁怎么整你....</p>\n<p>  房外的爱儿踱着步呵呵不知道他们两个在里面怎么样了..今天真是开心如果枫哥哥和他也在就好了...</p>\n<p>  想到枫爱儿自然高兴可是想到他（小王爷）爱儿的双颊却泛起了红晕不知道他现在在哪儿还记得我吗？</p>\n<p>　</p>\n";

