module.exports = "<hr>\n<p>title: 第40章\ndate: 2017-07-18 04:00:00\nwords: 3568</p>\n<h2 id=\"index-40\">index: 40</h2>\n<!-- deleteAbove -->\n<p>在哪里！快给我滚出来！如雪咆哮道..</p>\n<p>  如雪你不就是想威胁他吗你放了小天小爱我过来..爱儿没有办法只有这样做先救出孩子要紧...</p>\n<p>  好那你快过来..如雪笑得很邪恶...</p>\n<p>  爱儿姑娘..小乐抓住她的手不让她过去...</p>\n<p>  小乐放心没事的..松开她的手爱儿伸手摸索着前方走得几步便被如雪一把拽过来掐住了脖子...不过她倒也放了小天小爱..那两个小家伙只是累赘利用爱儿相信才能更好的威胁慕容临天...</p>\n<p>  很快慕容临天一行人就赶了过来...</p>\n<p>  柳如雪你放了爱儿！慕容临天吼道...只要是碰到了爱儿的事他就无法冷静...</p>\n<p>  你有什么资格跟我大呼小叫！如雪也不示弱你最爱的女人在我手里你最好给我恭敬一点不然...掐住爱儿脖子的手一用力..唔...爱儿闷哼一声...</p>\n<p>  柳如雪你放手！你要是敢对爱儿怎么样我不会放过你！慕容临天的瞳孔猛一收紧..</p>\n<p>  怎么怕了？那好你只要答应我的条件我就放了她！如雪狞笑着眼底满是得意..</p>\n<p>  你说什么条件！慕容临天怒目着她..</p>\n<p>  很简单一命换一命如雪不屑的看着他..要她活着你就自杀！</p>\n<p>  不可以！爱儿着急的说道..天不要答应她！</p>\n<p>  嚓~~如雪从袖子里抽出一把雪亮的匕首比着爱儿的脖子..闭嘴！这里没你说话的份！继而瞪着慕容临天怎么样！慕容临天你答不答应！</p>\n<p>  天不要！不要答应！爱儿不管怎样都不能让慕容临天代自己去死..</p>\n<p>  叫你闭嘴！如雪恶狠狠的看着爱儿...</p>\n<p>  如雪...如水早已泪流满面..求求你放过爱儿好不好？</p>\n<p>  哼！如雪根本不屑看她...</p>\n<p>  如雪你有什么要怪的就怪我一个人吧！不要伤害无辜你杀我..你杀我！说着如水向她慢慢走去...</p>\n<p>  你不要过来！如雪对她大吼..我们的帐等会儿再慢慢算！先算这一笔！慕容临天枉我这么爱你可是你居然狠心得把我赶出王府还想杀我！都是因为这个女人！如果不是她我一定会成为你的王妃！既然你不仁那么我也不义！我是绝对不会让你们这么开心的在一起的！</p>\n<p>  柳如雪你简直丧心病狂！慕容临天骂道...如果不是爱儿在她手里真想一掌毙了她！</p>\n<p>  你骂！你骂得越多她就死得越快！如雪威胁道...</p>\n<p>  你慕容临天狠狠攥紧了拳头...</p>\n<p>  废话少说你答不答应！如雪步步紧逼...看他一直不答复手中的匕首轻轻一划爱儿的脖子上便多了一条血印...</p>\n<p>  爱儿！慕容临天的心揪痛着...柳如雪你放手！我...答应你...他咬牙切齿着恨不得把她碎尸万段居然敢伤害爱儿...</p>\n<p>  好！如雪得意极了把匕首扔在慕容临天脚下...</p>\n<p>  天不要！爱儿的眼眶泛红着着急的不得了...如雪我求你你杀了我！你杀了我！</p>\n<p>  杀了你？我没那个兴趣...如雪的嘴角邪恶的上扬着...看着你痛苦我会比杀了你更开心！</p>\n<p>  你！爱儿的拳头攥着指甲深陷入掌心...</p>\n<p>  爱儿..慕容临天看着她说道..如果我的死能换你一命我愿意！</p>\n<p>  不可以！爱儿打断他天你不要死！我好不容易等到和你团聚我不要你死！小天小爱他们都很需要你！五年前既然我没让你死今天我一样不要你死！爱儿心急如焚她真的不知道到底该怎么办..</p>\n<p>  爱儿五年前你为了我跳崖这五年来我每天都备受折磨你知道吗？这比死还要痛苦！今天我不会让旧戏重演的爱儿...慕容临天饱含深情的说道...两行热泪留下...</p>\n<p>  你们够了没有！如雪看不下去了吼道..罗嗦什么！快动手！</p>\n<p>  慕容临天弯捡起地上的匕首在耀眼的阳光下匕首闪闪发光隐隐约约透着些寒凉...</p>\n<p>  天不可以！枫劝道...</p>\n<p>  是爱儿需要你！你不可以死！寒抓住慕容临天的手臂..</p>\n<p>  如雪你放了爱儿好不好...如雪...如水苦苦哀求...</p>\n<p>  如雪我求你杀了我！爱儿不能让慕容临天死..</p>\n<p>  切~~~如雪根本无视眼前的一切...易爱儿我就是要他死要他死在你面前！是你害死他的！哈哈我要你痛苦！痛苦一辈子！慕容临天还不动手！</p>\n<p>  深情的看了爱儿最后一眼看她沾满泪珠的小脸看她墨玉般的长发看她美丽的眼睛...慕容临天微笑着抬起手臂闪光的匕首直直的插进他的胸膛艳阳映照着他的侧脸镶着一道迷人的金边像是描绘他那刚峻的脸部轮廓</p>\n<p>  霎时间鲜血像水柱一样喷出染红了半边天..几滴溅到了爱儿苍白的小脸上看起来那么的格格不入...</p>\n<p>  天！寒和枫异口同声的喊道</p>\n<p>  爹！小天和小爱大喊着想冲上去却被小乐和小可拉住..</p>\n<p>  爱儿呆住了她能感觉到溅在自己脸上的滚烫..是血..血！是慕容临天的血！！</p>\n<p>  疯了！她疯了！！她疯一般的撞开自己身后的如雪..向正前方跑去她知道慕容临天一定在那里！</p>\n<p>  猛地扑倒在地毫不留情的磕破了膝盖她匍匐着..鲜血淋淋的膝盖在地上留下了一条长长的血迹像是朱砂红泼在地上那么刺眼...</p>\n<p>  天天你在哪里？她无力的到处摸索擦破了手心的皮肤也不自知..她要找到慕容临天一定要！</p>\n<p>  爱儿...慕容临天支撑起身体努力抓住她孤独的小手拽到自己怀里...</p>\n<p>  血...好多血...爱儿吓得迷失了神志..她感觉到慕容临天全身都是血它们正一点一点吞噬他的生命...墨玉般的长发被风吹起漫天散乱的扬着数不尽的凄惨...沾着鲜血的小脸苍白到了极点白到几乎透明浓密的睫毛在脸上投下一片阴影...心好痛...好像正被人无情的插了一刀又一刀直至血模糊也不停止..</p>\n<p>  爱..爱儿...慕容临天紧紧抱着她..心疼的看着她折磨自己..太..好了你..你终于又到了我的身边...</p>\n<p>  别说了..别说了...爱儿无法忍受这种生不如死的痛她的心都撕裂了...一滴滴黑色的血泪从那双清澈的眸子里流出像一条长河...</p>\n<p>  爱儿..慕容临天吃力的说憋着最后一口气...死..生..契..阔与..子..成..说.说着伸出沾满血的大手..</p>\n<p>  爱儿的眼泪又变得晶莹冲刷掉了刚才黑色的血泪...她伸出手紧紧的抓住慕容临天伸过来的手永远不想放开...执子之手与子偕老..天..你不要离开我..眼泪打湿了爱儿的脸更加撕扯着爱儿的心...</p>\n<p>  好..我不离开你..我还要执子之手..与子偕老..慕容临天艰难的笑了一滴热泪夺眶而出..</p>\n<p>  看着眼前生死离别的场景..所有的人都忍不住落下泪来..</p>\n<p>  慕容临天和爱儿的手紧紧握在一起仿佛宣示着永不分离...爱儿用另一只手擦掉慕容临天的泪...天..我是你的..我永远都是你的..我要嫁给你我下辈子还要做你的新娘...</p>\n<p>  第十一章 执子之手，与子偕老（三）</p>\n<p>看着爱儿抹掉自己的眼泪慕容临天欣喜若狂..爱儿..你的眼睛好了？你看得见了？</p>\n<p>  听慕容临天这么说爱儿才反应过来..真的..我看得见了..天..我看得见了！</p>\n<p>  慕容临天伸手抚上爱儿的脸深情的看着她美丽的眸子看那里面只属于自己的倒影..爱儿..做我的新娘好不好？</p>\n<p>  好..好..爱儿的声音简直有些颤抖...她一把抓住慕容临天的手在自己的脸颊摩挲..</p>\n<p>  行了！慕容临天你要装到什么时候！戏演完了散场吧..如雪笑着说..</p>\n<p>  戏？什么戏？周围的人一脸茫然...爱儿也是..</p>\n<p>  慕容临天抽出胸口的匕首在爱儿面前晃了晃..这把匕首是可以伸缩的..所以...</p>\n<p>  所以你骗我！爱儿气愤的用粉拳向慕容临天的胸膛砸去..小嘴生气的嘟着...你给我解释清楚唔看着面前突然放大的脸爱儿的脸唰一下红到了脖子根...</p>\n<p>  可以跟你解释不过我们先洞房花烛再说吧！慕容临天邪邪的笑道抱起爱儿往麒麟殿方向去了...</p>\n<p>  你卑鄙！唔爱儿始终斗不过慕容临天的柔情攻势只得乖乖束手就擒了...</p>\n<p>  还好小乐小可反应快急忙捂着小天小爱的眼睛...</p>\n<p>  这怎么回事？怎么刚才悲剧一下子变喜剧了？枫不解...</p>\n<p>  让我来给你们解释吧..如雪说着走过来挽住如水的手姐姐..</p>\n<p>  听见这一声姐姐如水感觉什么都值了...</p>\n<p>  情景倒退</p>\n<p>  如雪看到慕容临天一家人团聚心中好生不甘易容混进王府...哼！</p>\n<p>　</p>\n";

