module.exports = "<hr>\n<p>title: 第3章\ndate: 2017-07-18 03:59:58\nwords: 3602</p>\n<h2 id=\"index-3\">index: 3</h2>\n<!-- deleteAbove -->\n<p>   爱儿抬起头来我说我愿意一颗晶莹的泪珠从眼角滑落...</p>\n<p>  很好...嘴角向上扬起满是得意...</p>\n<p>  现在你可以救我姐姐了吧！</p>\n<p>  当然</p>\n<p>  男子走近如水从那宽大的袖子中拿出一个精致的小瓶子倒出一粒蓝色药丸给如水服下..</p>\n<p>  你先去集市上买些吃的回来她很虚弱...男子说道...</p>\n<p>  爱儿嗯了一声跑了出去...</p>\n<p>  男子抱起如水绕到佛像后面再轻轻放下..接着解开女子腰间的束带..</p>\n<p>  你你要干什么如水无力的说道还欲伸手抓开男子的手可是完全没有力气..</p>\n<p>  别乱动相信我语气很冷可是她竟然有一种温暖的感觉..</p>\n<p>  很快如水的身上只剩下白色的肚兜一大片已经被染成红色十分刺眼...</p>\n<p>  伸手到如水的后欲扯开肚兜的带子..</p>\n<p>  不要如水的脸上泛出微微的红色</p>\n<p>  相信我..轻轻一扯肚兜滑落..露出胸前的..还有左胸上那道狰狞的伤口..而这时如水的两颊通红含羞的少女...</p>\n<p>  第五章取次花丛懒回顾，半缘修道半缘君（五）</p>\n<p>  面对面颊绯红的少女还有那女子曼妙的同体男子竟不生一丝邪念...</p>\n<p>  处理好伤口涂上自制的药膏男子从自己的上撕下一条包扎好如水的伤口再轻轻地把轻纱似的衣服重新穿在她身上..</p>\n<p>  在这一短暂的过程中如水一直目不转睛看着他他那俊俏的脸庞细长的双眉的鼻梁薄而xing感的嘴唇还有额前垂下来的两三缕银丝无一不让她痴迷连伤口处的疼痛都感受不到。</p>\n<p>  而最让人意乱情迷的是那一双蓝色的眼眸像是一种毒药让如水深陷其中不能自拔...</p>\n<p>  看够了吗？</p>\n<p>  男子冷笑道..</p>\n<p>  我..我没有看你..如水反应过来吞吞吐吐的掩饰却反而更加暴露了自己的心...</p>\n<p>  是吗？</p>\n<p>  男子嘴角向上翘起一个好看的弧度用手捏住如水尖削的下巴如水的心噗通噗通的跳着不过很可惜世上爱慕我的人千千万万却没人能打动我的心...就算有也轮-不-到-你！</p>\n<p>  轮不到你四个字故意说得很重重得像是要压碎如水的心..</p>\n<p>  她甚至可以听见自己的心慢慢被撕裂的声音还有那撕心裂肺的痛比匕首插进来还要痛千万倍..</p>\n<p>  为什么..为什么只是这么短的时间我就可以爱你爱到死去活来...如水默默地问自己...可是就算在心里问了千万遍也根本得不到一个答案...</p>\n<p>  如水强忍住心里的痛你放心吧得不到你的爱我也会祝福你..说完滚烫的泪早已在眼眶里挣扎却倔强的不让它们流出来...</p>\n<p>  男子怔了一下很微弱的稍纵即逝...</p>\n<p>  不要爱上我爱上我的人都没有好下场....男子的眼中闪过一丝痛楚...</p>\n<p>  是恐吓吗？</p>\n<p>  不他的语气明显比刚才有了一点不易察觉的温度..</p>\n<p>  那么是关心？</p>\n<p>  难道他的过去曾经有什么不堪回首的痛？</p>\n<p>  你以前是不是有过让你难过的事？</p>\n<p>  如水关切的眼光望着男子的背影.</p>\n<p>  男子的心像是被什么刺痛了..闭嘴！</p>\n<p>  不要跟我提以前！语气十分的冰冷而且带着浓厚的怒火与仇恨...</p>\n<p>  如水不敢再多问什么并不是因为她害怕而是她不想再提起让男子伤心的过去...</p>\n<p>  那个...请问如水小心翼翼的你叫什么名字？</p>\n<p>  说完她的心仿佛都提到了嗓子眼儿..</p>\n<p>  麟寒...男子依旧冷漠..</p>\n<p>  麟寒？</p>\n<p>  真好听....如水微笑着反复在心里默念他的名字像是找到了一种温暖..我叫秋如水很高兴能和你做朋友...</p>\n<p>  你叫什么与我有什么关系？</p>\n<p>  寒冷眼道而且我从来不和别人做朋友..朋友？呵对我来说没有一丝利用价值...</p>\n<p>  如水的心仿佛又沉到了谷底她觉得一点也不了解这个时而温柔时而冷漠的男人当然冷漠几乎占了全部...</p>\n<p>  ........................................................................................</p>\n<p>  YE场景：</p>\n<p>  记者甲：请问麟寒先生你的过去到底发生了什么样的事情呢？</p>\n<p>  麟寒：闭嘴！闭嘴！（怒吼着）</p>\n<p>  记者甲的头发吓竖三尺高..</p>\n<p>  记者乙：请问麟寒先生你的择偶标准是什么呢？</p>\n<p>  麟寒嘴角上扬用手捏住记者乙那似有似无的下巴一双蓝瞳注视着记者乙的豆屎眼发出迷人的光芒...</p>\n<p>  记者乙快速奔逃妈妈咪！</p>\n<p>  ！这人是同志！！</p>\n<p>  麟寒一脸黑线....</p>\n<p>  记者丙：请问麟寒先生除去童年你这位男二居然比男一先出场对此你有什么看法呢？</p>\n<p>  此时感到背后阴凉一片记者丙毛骨悚然..</p>\n<p>  慕容临天闪亮登场！</p>\n<p>  ！！！</p>\n<p>  慕容临天一把揪住记者丙的脖子：老哥你采访错人了吧！</p>\n<p>  我是男一居然后登场应该我才有看法吧！！</p>\n<p>  一把扔掉记者丙揪住妍嬭的衣领：妍嬭婲婔别人都是男一先出场和女一缠绵了一段了男二才出来你怎么能让男二先出呢？</p>\n<p>  我居然还没见影子！！！（咆哮着）</p>\n<p>  麟寒：还有我本来应该是美女左拥右抱的你干嘛要给我美好的人生旅途上安排一段悲惨的过去！</p>\n<p>  ！今天我要好好教训教训你！！（继续咆哮着）</p>\n<p>  于是妍嬭很惨的被戏中两大帅哥一起K了一顿...</p>\n<p>  妍嬭鼻青脸肿的爬起来...艰难的说欲知后事如何请支持《### 命中注定的王妃》倒！</p>\n<p>  偶的亲们（估计目前还没有亲）这是妍嬭的第一部作品而且是最近才开始看小说滴写得不好的地方请大家多多见谅同时也希望得到大家的鼓励！</p>\n<p>  来吧用你们手中的鲜花砸死我吧！！！</p>\n<p>  第六章取次花丛懒回顾，半缘修道半缘君（六）</p>\n<p>  如水姐姐！</p>\n<p>  爱儿看到此时此刻已经能坐起来的如水激动不已连手中的东西都忘了放下就直奔过去他把你治好了对不对？</p>\n<p>  嗯！不知为什么看到爱儿那稚嫩而纯真的脸庞如水的心情似乎好了不少..</p>\n<p>  伤口还疼吗？</p>\n<p>  爱儿关切的问道像个懵懂的小孩。</p>\n<p>  不疼了呵呵！如水笑道...</p>\n<p>  太好了！</p>\n<p>  爱儿脸上露出灿烂的笑容笑声仿佛比银铃还要清脆..</p>\n<p>  如水姐姐给你吃香喷喷的包子还热着呢！</p>\n<p>  爱儿递给如水一袋包子拿着另一袋转身走向寒..</p>\n<p>  给你谢谢你救了我姐姐..爱儿真诚的说道..</p>\n<p>  寒却露出不屑的眼神..一袋包子就想打发我？</p>\n<p>  现在该是你兑现诺言的时候了！说罢一把抱起爱儿脚尖轻轻一点人就不见了踪影</p>\n<p>  麟寒！</p>\n<p>  你放了爱儿！求求你不要伤害她她还是个孩子如水泪流满面大声恳求可是又有什么用？</p>\n<p>  如水望着那残旧的佛像双手合十默默念到求佛祖保佑</p>\n<p>  啪泪珠像断了线的珠子不停的落到地上溅开了无数朵梅花....</p>\n<p>  虽然破庙离镇上不远但寒的速度着实让爱儿吃了一惊不到半柱香的时间他们就到了一家客栈。</p>\n<p>  寒把爱儿重重地扔在榻上痛得爱儿那弯月般的眉毛都皱变了形..不等爱儿抱怨寒已经压了上来雨点般的吻不停落在爱儿的脸上唇上..寒解掉自己的上衣露出结实的胸肌和腹肌爱儿不敢睁开眼睛只感觉自己仿佛被什么紧紧包围透不过气来...寒粗暴的撕开爱儿的外衣很快粉红色的肚兜和如雪般的便展露人前引得寒更有了一种.....于是从爱儿的颈部起到腹部落满了大大小小的烙印身上也是青一块紫一块她紧闭着眼睛重重的咬着下嘴唇任他狠狠的..唇瓣已经渗出了丝丝血液令人心生怜悯....</p>\n<p>  许久爱儿身上已是斑痕累累..疼得她头上直冒冷汗...</p>\n<p>  轻轻的一声不要更像是从鼻子里哼出来的..寒停了下来抬头看看爱儿的脸长长的睫毛上沾满了晶莹的泪珠嘴角咬出的血已经流到了下巴那楚楚可怜的模样......寒的蓝色眸子里居然会有一丝动容...他顿时起身背对着榻上的人儿...</p>\n<p>  怎么会？</p>\n<p>  刚才那种感觉是什么？为什么我会有种想要保护她的冲动？</p>\n<p>  不不不..他摇着头一定是我多心了...不会的....</p>\n<p>  爱儿感到自己身上像是无比轻松慢慢睁开眼睛寒站在窗前背对着自己不由的松了一口气...这时才感到自己全身火烧般的痛爱儿起身靠在床边倾城的面容因疼痛拧在一起可也却成了另种美...</p>\n<p>  谢谢你没有对我....爱儿轻声说...</p>\n<p>  呃？</p>\n<p>  寒反应过来又换起那副邪恶的皮囊转过身来...可是看到伤痕累累的爱儿尤其是那坚定的眼神那纯净得没有一丝杂质的眸子他竟说不出一句嘲笑的话...</p>\n<p>  谢谢你治好我姐姐爱儿微笑着其实你不坏你是好人对不对？</p>\n<p>　</p>\n";

