module.exports = "<hr>\n<p>title: 第1章\ndate: 2017-07-18 03:59:57\nwords: 3509</p>\n<h2 id=\"index-1\">index: 1</h2>\n<!-- deleteAbove -->\n<p>  第一章 同居长干里，两小无嫌猜（一）</p>\n<p>夜已深明月当空凉如水。</p>\n<p>玉锦河荡起微微涟漪揉碎了明月..河岸旁的柳絮纷飞被风挂出留~留~的声音像是在召唤什么。大山原是一片死一般的沉寂然而</p>\n<p>小姐~小姐~你在哪儿 ~~ 爱儿 ~ 我的爱儿 ~  你在哪儿？快应娘一声！爱儿妹妹~~我是姐姐你听到了吗？</p>\n<p>这一阵阵呐喊打破了夜的宁静在大山回响</p>\n<p>  玉锦河畔古色古香的梦亭里一个十岁的小女孩趴在石桌上睡去..脸颊残留的泪痕在明月的照耀下发出一闪一闪的光芒长长的浓密的睫毛上沾有晶莹的泪珠儿..乌黑的长发像一泻而下的瀑布一直垂到腰间随风微微飘动着一袭粉色的长裙及地领口和袖口绣着盛放的桃花是那么的可人..</p>\n<p>  月色中一个身着金衣的身影路过..小王爷很晚了快回去吧不然丽妃娘娘要着急的。</p>\n<p>  知道了把她也带走吧一个小女孩独自在这儿真可怜。</p>\n<p>  是小王爷</p>\n<p>  四王爷行宫..</p>\n<p>  娘孩儿回来了！一个稚嫩而又略微成熟的声音..</p>\n<p>  天儿一道倩丽的身影..下次不许这么晚回来了你还小。</p>\n<p>  娘孩儿不小了孩儿已经十五岁了是男子汉大丈夫了今天孩儿还救了一个小姑娘呢她独自睡着在亭中定是同家人走散了孩儿不忍把她继续留在亭中于是把她带回来了。</p>\n<p>  是小王爷可有同情心了呢！随行的家奴插嘴道。</p>\n<p>  天儿我们是出来巡游的在这里呆不了几天我们赶快帮她找到家人送她回去吧。</p>\n<p>  是娘亲孩儿会帮助她的。</p>\n<p>  后院的偏房内一个娇小的身影坐在桌旁。门开了女孩的脸上露出一丝惊恐的神色水汪汪的大眼睛里泛着水花却强忍着不让它们掉出来..</p>\n<p>  你饿了吗？一句轻柔的话语顿时让女孩心里安稳不少。。。。</p>\n<p>  嗯...女孩嘴里发出十分轻微的声音像游丝一般。。</p>\n<p>  他仿佛看出了她眼里的惊恐微笑着说不用害怕这里是四王爷行宫我是小王爷我们可以做朋友吗？</p>\n<p>  嗯.女孩点点头..露出一丝羞怯的微笑..</p>\n<p>  呵呵那我们现在开始就是朋友了我叫下人给你准备了点吃的快吃吧明天我带你去找你父母。男孩一本正经道...</p>\n<p>第二章 同居长干里，两小无嫌猜（二）</p>\n<p>真的？女孩抬起头来一双晶莹的眸子看着他扑闪扑闪的像天上最亮的星星不是比天上的星星还要美..的脸颊带着些许的羞怯像含苞欲放的桃花与一袭粉色长裙交相呼应宛若画中人..</p>\n<p>  他震慑住了多美的眼睛多美的人儿..就这一眼他竟有了要保护她的冲动。</p>\n<p>  围坐在圆桌旁烛焰跳跃着她大口大口地吞咽显然是饿坏了。他在一旁静静地看着看女孩那狼吞虎咽的可爱动作不笑了..</p>\n<p>  伸手抚上女孩额前的头发竟有种幸福的感觉在全身流窜这是什么样的感觉？为什么会如此奇妙让他如此迷恋。女孩停下手中的动作抬头望着他而他正凝视着她那眼神是那么的深邃是那么的柔情柔的仿佛要流出水来..</p>\n<p>  时间悄悄地溜走..</p>\n<p>  小王爷..女孩弱弱道..</p>\n<p>  呃..男孩仿佛突然从梦中醒过来看见自己抚在女孩额前的手顿时收回不满脸通红..</p>\n<p>  小王爷你怎么了？你的脸好红。女孩关切的问道。</p>\n<p>  我..没什么..</p>\n<p>  小王爷你是不是生病了？你的脸怎么这么烫？</p>\n<p>  我..我..呃我只是觉得你很可爱..</p>\n<p>  女孩惊讶得更加睁大了眼睛像流萤..她轻轻别过头去不知不觉脸颊染上两抹红晕..</p>\n<p>  就这样两人对持了很久谁也不好意思打破这个悄然情动的局面..</p>\n<p>  嗯哼！沉默被丽妃娘娘的一声轻哼打破了..</p>\n<p>  娘！孩儿来给这个女孩送点吃的。。</p>\n<p>  嗯娘知道了很晚了你快回房休息吧人家姑娘家也要休息呢..</p>\n<p>  是娘亲孩儿去休息了您也早点歇息吧..</p>\n<p>  说罢走出房门..</p>\n<p>  丽妃娘娘我本无心打扰只是我与爹娘走失小王爷好心带我来这儿给你们造成麻烦我感到很抱歉..眼睛扑闪扑闪令人见怜..</p>\n<p>  嗯是个谈吐文雅的孩子本宫很喜欢你你叫什么名字？</p>\n<p>  回娘娘我叫易爱儿..</p>\n<p>  爱儿？真是个可爱的名字..你的爹娘为什么会给你取这个名字呢？</p>\n<p>  因为爹娘希望我成为一个善良的女子要心中有爱爱自己爱身边的所有人和事故取名为爱儿..</p>\n<p>  丽妃微笑着点点头或许你的父母还希望你今后遇到的那个人能深深爱着你能给你幸福的生活吧...对吗？</p>\n<p>  女孩红着脸悄悄别过头..</p>\n<p>  第三章 同居长干里，两小无嫌猜（三）</p>\n<p>天大亮四王爷行宫..</p>\n<p>  阳光照射进窗户映在榻上人儿的脸庞浓密的睫毛在脸上留下了一片阴影.</p>\n<p>  女孩慢慢睁开眼睛轻轻地起身伸了个懒腰惬意极了..</p>\n<p>  穿好鞋走下榻嘎吱一声门开了..一个金衣少年走进来..呵呵你醒了。男孩脸上洋溢着笑容嗯女孩轻声答道也报以一个灿烂的笑容在阳光的照耀下眼睛一眨一眨浓密的睫毛一上一下就像天上的仙子..男孩看得呆住了多美的女孩儿...</p>\n<p>  小王爷..怎么又和昨晚一样呆了？</p>\n<p>  呃男孩反应过来该死居然第二次在她面前发呆了脸不知不觉泛起红来。</p>\n<p>  走男孩拉起女孩的手那么柔软我带你去一个地方！说罢便拉着女孩往外跑..</p>\n<p>  灿烂的阳光下两个小人儿快乐的笑着跑着像两只翩飞的蝴蝶是那么的快乐..</p>\n<p>  玉锦河畔两抹小小的人影并排躺着柳条低垂随风飘荡着。。女孩闭着眼睛享受着阳光和从河面上吹来的风一袭粉衣在葱绿的草地上像一朵娇美的花领口的桃花映衬着女孩的脸颊..睁开眼唔男孩的眼波柔得要流出水来他正深情的盯着自己呢..心里噗通噗通的跳着..而他虽是一脸稚气的脸庞但那仿佛刀削的轮廓刚正的眉毛那么浓高高的鼻子还有那不厚不薄的唇瓣真是英气逼人..怕是年长一点的女生看到这幅面容也会被迷得神魂颠倒芳心暗许吧..</p>\n<p>  小王爷谢谢你昨天把我带回来我想把这个送给你.说着从腰间拿出一条香巾中心的位置绣了一朵盛开的桃花娇艳欲滴..</p>\n<p>  谢谢男孩接过香巾右手大拇指着上面的桃花嘴角流露出一丝喜悦..</p>\n<p>  我想问.男孩温柔地说道你话还未说完便重重地倒下..</p>\n<p>  小王爷小...女孩的声音也愈加地弱眼皮变沉不自觉倒下了..</p>\n<p>  旁边草丛中一个高大的身影..一声冰冷的命令..</p>\n<p>  把人带走</p>\n<p>  许久...</p>\n<p>  该死头怎么这么晕..男孩用手支撑着身体慢慢爬起来睁开眼还是在河边你..咦？人呢？那个女孩儿..低头可旁边空无一人..奇怪那个女孩刚才明明在自己身边的怎么一转眼不不是一转眼男孩看着如血的残阳身后被拉出了一条长长的影子...已经晕了那么久了身边的人什么时候被带走的他都浑然不知然那个女孩的名字他居然都没来得及问...</p>\n<p>  ..............................................................................</p>\n<p>  到底是谁带走了爱儿？一个巨大的阴谋即将上演....</p>\n<p>  第一章 取次花丛懒回顾，半缘修道半缘君（一）</p>\n<p>五年后</p>\n<p>  京都街</p>\n<p>  哇如水姐姐你看你看这个簪子好漂亮！还有这条丝帕看那个怎么都这么好看！哇~~如水姐姐看那边好多人！！我们过去看看！！</p>\n<p>  爱儿一个似出水芙蓉般的女人说道一个半月髻挽在头上左侧的两只玉簪衬着女人姣好的面容你呀可真是越来越调皮了都十五岁的女孩子家了还上蹦下跳的干娘看到又要说你了。</p>\n<p>  如水姐姐哪还要干娘在你不是已经在说我了嘛..说着撅起那而水嘟嘟的小嘴如水姐姐好不容易到京都来玩干爹干娘又不在旁边你就别扫我的兴嘛以前在江川我可都憋屈着呢...那可怜巴巴的模样大眼睛眨巴眨巴的还真是让人无法拒绝...</p>\n<p>  哎你这个小妮子还真是拿你没办法记住我们天黑前要回客栈哦..</p>\n<p>  知道了我就知道如水姐姐你对爱儿最好了！！说罢又一蹦一跳的向那人群中跑去...而秋如水无奈地摇摇头只有跟着..</p>\n<p>  如水姐姐快看是套圈圈呢！</p>\n<p>　</p>\n";

