module.exports = "<hr>\n<p>title: 第25章\ndate: 2017-07-18 03:59:59\nwords: 3584</p>\n<h2 id=\"index-25\">index: 25</h2>\n<!-- deleteAbove -->\n<p>慕容临天的警觉还是比较好的立即觉察到有人来了....大煞风景的人.....</p>\n<p>  听他这么反应爱儿赶快睁开眼睛窜到慕容临天身后像个做了坏事的小孩...</p>\n<p>  看眼前...冯枫和十位暗夜铁骑军尴尬的看着眼前的一幕...来的真不是时候..慕容临天心里默念着脸上流露出冰冷的表情...</p>\n<p>  那个嘿嘿...枫一脸无害的笑着...你们两个继续...我们什么都没看到..我们只是路过..嘿嘿..路过...转身看自己背后的暗夜铁骑军..撤！唰一声十一个煞风景的人就消失了..谁知道他们躲哪儿偷窥呢！</p>\n<p>  慕容临天不满的看着人消失的地方...算你们识相！他心里默念着...转身看躲在自己身后的爱儿..她正可怜巴巴的盯着自己纤细的手指不停的打着转转...我们本来就没做什么嘛...她小声嘀咕着..</p>\n<p>  那个...慕容临天有些吞吐...</p>\n<p>  什么？爱儿问道..</p>\n<p>  不要把我晕倒的事说出去...有点儿丢面子..慕容临天嘴里这么说脸上居然有一丝不好意思....他也会有害羞的时候！</p>\n<p>  说完他紧紧牵住爱儿的手爱儿脸上洋溢着幸福....冯枫！你们不该出现的时候偏偏出现该出现的时候还不出来了是吧！</p>\n<p>  嘿嘿出来了出来了..带领暗夜铁骑军的枫还是那副死表情..那我们回去吧..走！就这样一群人浩浩荡荡的回到了王府...</p>\n<p>  听说王爷带着爱儿回来了王府所有人都在大门口等候...</p>\n<p>  爱儿！一看到爱儿的身影如水激动地泪流满面冲上去一把抱住爱儿..慕容临天对这有些反感由始至终他对那件事还是很介怀的..不皱了皱眉...爱儿倒是很高兴毕竟又见到了如水姐姐..几天不见你都瘦了一圈了..如水心疼的看着爱儿..</p>\n<p>  是爱儿..寒也走出人群..</p>\n<p>  没关系爱儿莞尔一笑..能再见到你们爱儿觉得很高兴..</p>\n<p>  一旁的慕容临天不高兴了她居然忽视他！！冷着脸横抱起爱儿..有什么事等会儿再说！在众人的诧异下把爱儿抱进了王府....</p>\n<p>第五十三章 昨夜星辰昨夜风（五）</p>\n<p>天你要抱我去哪里？这么多人看着呢！快放我下来..爱儿嘟着嘴小腿调皮的一弹一弹..</p>\n<p>  带你去个好地方..慕容临天的嘴角邪邪的上扬着..</p>\n<p>  来到麒麟殿..慕容临天轻轻按下墙上的一个黑死按钮咔~~~一声一道暗门打开了！</p>\n<p>  爱儿看得是一愣一愣的..她在麒麟殿呆了那么久居然没发现这是个开关...被慕容临天抱着走进去...</p>\n<p>  哇！！爱儿不赞叹着...好美....室中间一个巨大的水池想必是温泉..水池四周用大理石砌成水中冒出的水雾配以低暗的光线别具一番格调..水池周围栽种的有桃树因温泉的原因这里面四季如春桃花灿烂的开放着芬芳使爱儿陶醉了..</p>\n<p>  喜欢吗？慕容临天问怀中的人儿..</p>\n<p>  嗯！我最喜欢的就是桃花了！还有温泉！好棒哦！咯咯..爱儿欢呼着..这就是你说的好地方吧！</p>\n<p>  对..不知道为什么爱儿总觉得他脸上有着一丝不怀好意..</p>\n<p>  可你身上有伤不能泡水..爱儿看着他道...</p>\n<p>  这里的水我事先叫人放好了治伤疗养的药材不会对我的伤有伤害的..慕容临天说道..</p>\n<p>  那你还挺会享受的嘛！爱儿笑着说..</p>\n<p>  那我们来玩好不好？慕容临天的表情有些邪恶...</p>\n<p>  玩什么？还没说完爱儿就被慕容临天毫不留情的扔到温泉里..</p>\n<p>  咳咳..她呛了一口水..怒目着慕容临天..却发现慕容临天的眼里闪烁着一种异样的光芒...低下头一看...天啦！身上素白的纱裙一泡水之后变得极其透明...紧贴着爱儿的衬出那姣好的曲线...</p>\n<p>  你..你..你不要过来！爱儿一边指着正向自己走来的慕容临天一边往后退着...他的眼睛里像是一种猎豹发现了美味的讯号...</p>\n<p>  想跑！慕容临天一把抓住爱儿把她像小猫一样拎了过来....</p>\n<p>  接着...室内水雾缭绕掩盖了那令人面红耳赤的一幕...（这个就不详细说了亲们自己想象吧..）</p>\n<p>  只是..这里仅仅是阴谋的开始而不是结束..暴风雨之前永远是宁静的...</p>\n<hr>\n<p>  到底是怎样的阴谋呢？下面会慢慢提到的哦！亲们不要着急到时候会爆出一个惊天内幕！！！爱儿和慕容临天又会怎么样呢？........像鬼一样飘走............</p>\n<p>第五十四章 昨夜星辰昨夜风（六）</p>\n<p>月华殿</p>\n<p>  如水正坐在桌前和小可聊着今天看到爱儿完好无损的回来了别提她有多高兴了..</p>\n<p>  如水姑娘你看你都快笑的合不拢嘴了..小可笑道..</p>\n<p>  那当然爱儿回来了！呵呵...如水依旧笑脸盈盈..</p>\n<p>  苦了如水姑娘你前几天日日以泪洗面..为爱儿姑娘担心..今天你终于又露出笑脸了..小可高兴的说..</p>\n<p>  只要爱儿能安全回来我什么都无所谓..小可..小可..看着突然睡去的小可如水轻轻呼唤道...可是为什么头会昏昏沉沉的？很快如水也支持不住晕了过去....一个黑影窜进屋来掳了如水而去....</p>\n<p>揉揉昏昏沉沉的头如水慢慢睁开眼睛...这是哪里？只见四周是一片死气沉沉的树林呼啸的风吹动着枯枝像一只只张牙舞爪的手...如水战栗着...</p>\n<p>  如儿一个十分熟悉的声音也只有他会这样叫自己...</p>\n<p>  干爹...如水弱弱的喊道..</p>\n<p>  黑衣人撕下自己的面罩..没错掳走如水的正是易天齐....</p>\n<p>  不知干爹找如水来有什么事？如水低着头问..</p>\n<p>  如儿还记得我五年前跟你说的话吗？易天齐冷冷的问..</p>\n<p>  是如儿记得不该说的话不该做的事就不要说不要做..如水回答道..</p>\n<p>  很好果然不愧为我易天齐的乖女儿..易天齐扶起地上的如水..如儿那你要记住尤其是这段时间绝对不可以说一个不该说的字..继而换为威胁的语气..不然你就永远见不到你的亲妹妹了..眼角的一丝诈此时体现得淋漓尽致..</p>\n<p>  什么？如雪在他那儿...如水恨得直咬牙..是干爹如儿铭记于心..</p>\n<p>  易天齐的表情又温和下来像是个慈父般..好我的乖女儿...你放心你的妹妹很好事成之后干爹会说服她原谅你的..</p>\n<p>  轻轻点了如水的睡...</p>\n<p>  老爷你果然是神机妙算故意让人给慕容临天报信告诉他爱儿的行踪再派出一帮死士去追杀他们..若是慕容临天就这么死了那我们也就无了后顾之忧若没死也可以不费吹灰之力就让爱儿心甘情愿回到慕容临天的身边..好执行我们的计划..这招真是高！萧淑从树林中走了出来表情狰狞...</p>\n<p>  易天齐的嘴角向上扬着..如雪该是你出场的时候了..</p>\n<p>  是！如雪一袭白色的衣裙在夜晚显得格外显眼..</p>\n<p>第二天一大早起来如水还是在月华殿里...</p>\n<p>  她寻思着..到底易天齐要干什么？</p>\n<p>  麒麟殿</p>\n<p>  爱儿一只手挽着慕容临天的脖子一条腿还架在他的腰上...呼呼的睡得挺香...</p>\n<p>  慕容临天笑呵呵的看着她这种古怪的睡姿像是恨不得挂在他身上一样...一只手爱怜的着爱儿光滑的脸..看来这小家伙昨晚真是累着了...想起昨晚自己不知要了她多少次..只知道每次都把满满的爱射入她的体内..没办法谁叫这小家伙这么呢.....</p>\n<p>  情不自的又吻上那甜美的小嘴..</p>\n<p>  唔爱儿像是醒了一睁开眼睛就看到面前放大的脸...自己的腿从他身上滑下来不经意间碰到了慕容临天正慢慢活跃之处..</p>\n<p>  一大早的干嘛呢..爱儿不满得别开脸..</p>\n<p>  慕容临天闷哼一声用手托起爱儿的小下巴转过她的脸..怎么火气这么大？难道昨晚为夫还没满足你吗？</p>\n<p>  你爱儿气得小嘴嘟了起来..被子慢慢向下滑了一截..</p>\n<p>  看着爱儿不小心泄漏的慕容临天的喉结动了一下...而这小妮子还不知死活的用溺死人的眸子盯着他眨巴眨巴的....</p>\n<p>  一个翻身把爱儿压在身下..</p>\n<p>  干嘛？大白天的...爱儿说着用粉拳砸着他的胸膛..</p>\n<p>  无妨还没说完就开始对爱儿上下其嘴了...突然...</p>\n<p>  王爷有人在大厅找你...</p>\n<p>  又坏了他的好事！</p>\n<p>　</p>\n";

