module.exports = "<hr>\n<p>title: 第13章\ndate: 2017-07-18 03:59:58\nwords: 3574</p>\n<h2 id=\"index-13\">index: 13</h2>\n<!-- deleteAbove -->\n<p>   很快房门吱呀一声打开了寒从里面走了出来..</p>\n<p>  怎么这么快就出来了？</p>\n<p>  爱儿问道..</p>\n<p>  寒看着爱儿的眼神天啦怎么变得跟慕容临天一样冷..好像不妙..爱儿心里想..快跑！</p>\n<p>  往哪儿溜？寒一把抓住想要逃跑的爱儿</p>\n<p>  典医阁内</p>\n<pre><code class=\"lang-~~~~~！\">\n  ！！！一阵阵惨叫传出.....（由于过程过于所以...本人就不写了...）\n\n  *******************************************************************************\n\n  本章属于稍微轻松一下的部分下章继续小虐....不过我觉得我写虐殇好像不太会写哎..写得一点都不悲伤...呜呜~~我哭去了\n</code></pre>\n<p>  第二十六章众里寻她千百度，蓦然回首（十八）</p>\n<p>  哼这个可恶的寒居然换药时下手那么狠气死我了！</p>\n<p>  爱儿这样想着往月华殿走...我得问问如水姐姐刚才他们俩都聊了些什么呵呵！</p>\n<p>  ！爱儿自言自语道...</p>\n<p>  从水池旁经过..天啦她怎么在这儿！</p>\n<p>  爱儿转身想走..</p>\n<p>  你好大的胆子！见到梅凝夫人居然不行礼！</p>\n<p>  小月（梅凝的贴身丫鬟）叫嚣着..这下是逃不掉了真是冤家路窄..转过身来向梅凝福了福身...奴婢向梅凝夫人请安如果夫人没什么事的话奴婢先行告退..</p>\n<p>  站住！</p>\n<p>  这次开口的是梅凝..本夫人说没事了吗？</p>\n<p>  果然见到她就没好事...你是准备去月华殿吧..梅凝不屑道..奴婢...爱儿还没说完听说王爷让那个和你一起带回来的女人住在月华殿了本夫人还没见过她呢刚好和你一起去..走吧说罢一扭一摆的向月华殿走去..她到底想干什么？</p>\n<p>  爱儿不安着不行不可以让她伤害如水姐姐...</p>\n<p>  如水姑娘在吗？</p>\n<p>  ..小月问道..</p>\n<p>  如水从里屋出来看着眼前不认识的女人还有爱儿..爱儿这位是？</p>\n<p>  如水姐姐这是梅凝夫人..爱儿说..</p>\n<p>  大胆既然如水姑娘是王爷请来的那便是主子你一个奴婢怎可直呼‘姐姐’？</p>\n<p>  梅凝怒斥道..</p>\n<p>  如水看梅凝的样子应该不是善类..恭敬的说梅凝夫人爱儿一直是这样叫我的请不要介意您请坐..</p>\n<p>  梅凝坐了下来如水姑娘这话就不对了。</p>\n<p>  本夫人知道你们关系好不过这是王府..俗话说没有规矩不成方圆主子就是主子怎可和下人称姐道妹？</p>\n<p>  如若如此那主子和下人有何两样？听了这话如水气极了但是她不可以出口驳斥梅凝不然后果不堪设想..</p>\n<p>  易爱儿你给本夫人跪下！</p>\n<p>  梅凝看如水没反应对着爱儿大呼道..</p>\n<p>  呃？爱儿一惊什么！</p>\n<p>  梅凝一拍桌子起身骂道你这个jian婢叫你跪下没听到吗？</p>\n<p>  梅凝夫人是不是爱儿做错什么事惹您生气了？请您大人不计小人过饶了她吧..如水恳求道..</p>\n<p>  如水姑娘你不用说了今天本夫人一定要教训这个jian人给这王府里树立权威！</p>\n<p>  不然这些奴婢们越来越大胆！</p>\n<p>  听到没有夫人叫你跪下！小月说道真是狗仗人势..</p>\n<p>  如水还欲求情..爱儿不想让她为难..扑通一声跪下..敢问夫人奴婢所犯何事？</p>\n<p>  你所犯何事？你犯的事就多了！梅凝冷笑着你对主子不敬不顾尊卑妄想王爷在王爷面前胡言乱语搬弄是非中伤他人！</p>\n<p>  哼真是最毒妇人心..我没有！爱儿反驳道..</p>\n<p>  难道本夫人还会冤枉你一个丫鬟不成！</p>\n<p>  请夫人拿出证据！</p>\n<p>  好你要证据我给你证据！那天本夫人只是对你小惩大诫一番第二天王爷就来紫凝阁警告我不要对下人滥用私刑这不是你在王爷面前挑拨是非是什么！</p>\n<p>  若不是你王爷他又怎么会帮你说话你个小狐狸精害人不浅！说罢啪一巴掌劈头盖面的打来.</p>\n<p>  .</p>\n<p>  爱儿苦笑着用手擦去嘴角的血..夫人你没听过吗？</p>\n<p>  欲加之罪何患无辞..她知道慕容临天那个人就是死了也不可能为了她跟自己的小妾过不去..</p>\n<p>  爱儿...如水心疼的看着爱儿脸上的手指印扑通一声跪在梅凝脚旁梅凝夫人我相信爱儿不会的求夫人开恩放了爱儿吧..梅凝一脚踹开如水你要是再替她求情本夫人连你一起罚！</p>\n<p>  ..如水吃痛地捂住隐隐作痛的胸口..说不出话来..</p>\n<p>  见状爱儿大怒你要打就打我不准你对我如水姐姐动手！</p>\n<p>  你..你居然敢对本夫人大呼小叫！小月给我掌她的嘴！说完小月便左右开弓每一巴掌都是响声震天..可想力道之大..</p>\n<p>  很快爱儿的脸就红肿得不像样了嘴角的鲜血滴到衣领上晕染开像怒放的血梅..但还是倔强地不允许眼泪在梅凝面前掉出来！</p>\n<p>  如水哭着扑过去抓住梅凝的脚夫人求求你放过爱儿..求求你..泪眼滂沱却更惹梅凝厌恶..如水姑娘你不会也这样在王爷面前梨花带雨的博取王爷的同情吧？</p>\n<p>  梅凝夫人如水没有如水真的没有求求梅凝夫人发发慈悲吧...</p>\n<p>  梅凝讨厌如水现在的模样你们这些狐狸精就只知道男人以为我不知道吗！</p>\n<p>  小月拿鞭子来！</p>\n<p>  是！夫人..说话间一根皮鞭已经到了梅凝手里对着如水狠狠地挥了下去..</p>\n<p>  惨叫声响彻了整个月华殿..</p>\n<p>  爱儿！</p>\n<p>  爱儿你怎么样了...爱儿...如水痛哭着..原来鞭子快打下来时爱儿扑过来挡在如水前面替如水挡了一鞭..背上的衣服裂开皮开绽血汩汩流出来像不平静的溪流不多会儿血流如注身上地上到处都是..梅凝还不解气鞭子像雨点一样继续落在爱儿身上..</p>\n<p>  爱儿忍着不叫出来她不能示弱！</p>\n<p>  很快爱儿的背上一片狼藉原本细腻的裂开了一道又一道的口子..那么的触目惊心！</p>\n<p>  打够了梅凝扔下带血的鞭子表情狰狞记住再敢王爷下次要的是你的命！</p>\n<p>  说罢甩手走了出去小月在后面对她们哼了一声也跟着走了出去..</p>\n<p>  爱儿爱儿....如水哭得不成样子你怎么样了爱儿我去找寒救你你等我..你一定要坚持住！</p>\n<p>  ...</p>\n<p>  爱儿强挤出一个笑容然后晕了过去......</p>\n<hr>\n<p>  哈哈今天四更哦！</p>\n<p>  哈哈！就当对前几天更的慢的补偿吧..亲们看吧你们的支持是我的动力！</p>\n<p>  ！！strong!!</p>\n<p>  第二十七章众里寻她千百度，蓦然回首（十九）</p>\n<p>  唔爱儿艰难的睁开眼睛..这是哪里？</p>\n<p>  她只记得被梅凝抽的半死不活后来她就什么都不知道了...</p>\n<p>  如水从外面端了一碗乌漆抹黑的药边吹边走进来想必是刚煎好的..</p>\n<p>  爱儿你醒了！</p>\n<p>  如水兴奋极了..把药搁在床头的木柜上转身坐在床边用手绢给爱儿擦擦额头的汗..</p>\n<p>  如水姐姐这是哪儿？</p>\n<p>  爱儿问道声音有气无力的..</p>\n<p>  如水笑着回答这是典医阁..你知道吗你已经昏迷三天了我们都快担心死了..用手背探探爱儿的额头还好已经退烧了..快喝药吧寒说这药等你一醒就要赶快喝..如水端起那碗乌漆抹黑的药一大股难闻的味道就充斥着爱儿的鼻子..</p>\n<p>  好难闻爱儿不偏过头我不要喝..</p>\n<p>  生病不喝药怎么行！</p>\n<p>  一个熟悉的声音响起..只见话音刚落一位天神般的男子走进来魁梧的身躯刚毅帅气的面容美到浑然天成...</p>\n<p>  爱儿听到这熟悉不过的声音顿时慌了神抓起被子挡住自己的脸像是一只受惊的小猫...见状慕容临天挥挥手示意如水出去..自己走到床前爱儿觉得房间里的空气压抑了不少..紧张得连心跳都要停止了..</p>\n<p>  怎么？</p>\n<p>  这么不愿意看到本王吗？慕容临天问道..</p>\n<p>  爱儿不出声她心里是想说不愿意的但她怕说了以后慕容临天又对她做出什么事来..</p>\n<p>  见她一直不说话慕容临天一把拉开她的被子爱儿蜷缩在床脚瑟瑟发抖眼睛不敢看他..真是的就出去几天她怎么变得这么苍白憔悴..爱儿吓坏了她知道慕容临天扯开被子后肯定对她是一通乱吼于是闭着眼睛等待着暴风雨的来临..</p>\n<p>  一直用被子这么捂着会捂出病来的..慕容临天平静的说道..</p>\n<p>  什么？</p>\n<p>  爱儿顿时睁大眼睛..我耳朵没听错吧..她悻悻的转过头来看着眼前这个高大的男人..你说什么？</p>\n<p>  本王的话从不重复第二次..尤其是关心你的话..当然这句没有说出来..</p>\n<p>  你为什么不骂我打我凶我？</p>\n<p>  爱儿咽了咽口水低下头璀璨的目光暗淡起来..毕竟我让你的女人生气了..</p>\n<p>  慕容临天苦笑原来我真的在她心里树立了这么个不好的形象..也罢也罢..本王警告过她不要滥用私刑所以这次的事跟你无关。</p>\n<p>　</p>\n";

