module.exports = "<hr>\n<p>title: 第24章\ndate: 2017-07-18 03:59:59\nwords: 3517</p>\n<h2 id=\"index-24\">index: 24</h2>\n<!-- deleteAbove -->\n<p>爱儿猛然睁开眼睛看着慕容临天张着漆黑的双眸正玩味的看着自己...爱儿一把推开他你什么时候醒的！</p>\n<p>  慕容临天还是那副戏谑的死样子..这个嘛具体时间我也不知道..</p>\n<p>  那..那你刚才..听到了多少？爱儿嘟着小嘴有些害羞的问..惨了这次丢脸丢大了...</p>\n<p>  慕容临天仿佛就是喜欢看她这种害羞的小模样..更加火上浇油地凑到爱儿耳边以一种极xing感的声音说道..该听到的..都听到了...说完还在爱儿的耳边轻轻呼了一口气..</p>\n<p>爱儿的脸唰一下红得像苹果一样...妈妈咪..</p>\n<p>  尴尬的用力推开慕容临天..却不小心碰到了他的伤口..慕容临天吃痛一声..你怎么了！是不是碰到了伤口？对不起对不起...猛一抬头对上慕容临天的脸..两人的唇之间只差一厘米...慕容临天的喉结动了一下而爱儿的心扑通扑通的跳着..脸更红了..</p>\n<p>  那个....今天天气真好..爱儿想扯开话题...轻轻别过脸..</p>\n<p>  闭嘴！慕容临天用手捏住爱儿的下巴把脸又转了过来..</p>\n<p>  为什么老叫我闭嘴..唔.爱儿不满的嘟嚷着..继而天冰凉的唇直接压了上来不让她再有说话的机会...</p>\n<p>  慕容临天一只手揽住爱儿的纤纤细腰..在她的唇上尽情啃咬着..继而转换为轻柔的..时狠时柔的吻点燃了爱儿心中的微微张开嘴慕容临天的舌长驱直入着爱儿的丁香小舌..渐渐的爱儿沉迷在慕容临天深情的吻里不能自拔...</p>\n<p>  慕容临天的另一只手不停地在爱儿身上..最后停在胸前的处..柔软的触感令他..忽重忽轻的引起爱儿一阵又一阵的颤栗..嗯～不小心溢出口的申吟让爱儿的脸顿时又红了起来...慕容临天只是邪邪的笑了一下手伸到爱儿的后欲解开带子..不要..爱儿抓住他的手..你身上还有伤呢..</p>\n<p>  你在关心我？慕容临天看着爱儿的脸问道..</p>\n<p>  才..才不是呢！我只是觉得你死了我难帮你收尸而已..爱儿偏过头随便编了一个理由..</p>\n<p>  哦~~原来是这样..慕容临天戏谑的看着爱儿羞红的脸真是太可爱了...低头啃咬起爱儿美味的锁骨来...要是不说实话我会继续惩罚你哦...说话间嘴的目标已经到了胸前...</p>\n<p>  好啦好啦我承认我是在关心你啦..爱儿气呼呼的说顺便翻了个白眼..</p>\n<p>  这还差不多..俯身向爱儿压去..</p>\n<p>  你还想干什么？爱儿脸红的看着他..</p>\n<p>  嘘睡觉..说完把爱儿拥在怀里..躺了下来..</p>\n<hr>\n<p>  亲们有没有觉得这一章很浪漫呢？嘻嘻后面的稍微有点H...不过相信对这一章的温情有帮助啦....不知道亲们对这种美满的章节看够了没有呢？什么？没有？</p>\n<p>好吧..妍嬭就再多码几章大家喜欢看的...因为后面就又要开始虐了总得多给他们俩多一点相处时间对不对？嘻嘻.......：）</p>\n<p>  第五十一章 昨夜星辰昨夜风（三）</p>\n<p>爱儿..耳畔响起慕容临天极具磁的声音..</p>\n<p>  嗯？爱儿转过头来以一个极舒服的姿势窝在慕容临天怀里..</p>\n<p>  你爱我吗？看着怀中的人儿他不由自主的问道..</p>\n<p>  爱..这次爱儿没有犹豫也没有欺骗他..</p>\n<p>  为什么爱我呢？听到这话他竟高兴的合不拢嘴..</p>\n<p>  不知道..爱儿贪婪的闻着专属于他的男气味但我就是爱你..</p>\n<p>  也是爱一个人根本不需要什么理由..慕容临天这样想到心情大好..</p>\n<p>  那你呢？她抬起头看着慕容临天的帅脸..恨不得咬上一口...</p>\n<p>  我不爱你他假装严肃的说...爱儿的小嘴气呼呼的嘟了起来....他用食指轻轻从那嘟起的樱桃小嘴上滑过...才怪！</p>\n<p>  讨厌~~~爱儿娇嗔道..哈哈引来的是慕容临天的一阵爽朗大笑..</p>\n<p>  他笑了！他居然笑了！爱儿欣喜的看着他小脸笑靥如花..他笑了他好像是第一次在我面前这么爽朗的笑..是真心的笑...</p>\n<p>  你笑起来好好听哦！今后要多笑哦！！！爱儿激动极了...</p>\n<p>  慕容临天没说什么只是呆呆的看着怀中的人..</p>\n<p>  你怎么了？爱儿看着他问..</p>\n<p>  呃？他突然反应过来..呃没什么..我只是觉得..你很可爱...</p>\n<p>  咦..这话怎么这么耳熟？爱儿寻思着然后不可思议的看着慕容临天..小王爷！她想到了他...</p>\n<p>  干嘛这样看着我？你是不是觉得本王无比的英俊潇洒呢？慕容临天玩味的说...</p>\n<p>  爱儿的脸上挂着三条黑线....送了他一个卫生眼...她想什么呢！他怎么可能是那个温柔又平易近人的小王爷！对！你帅呆了！爱儿不敢跟他说小王爷要让他知道自己心里还想着另外一个男人还不被他给扒皮抽筋...</p>\n<p>  慕容临天也觉得刚才的话好像当年自己跟那个女孩说的一模一样连场景都一样！都是自己先看呆了..不过他不能让爱儿知道..</p>\n<p>  沉默了许久...</p>\n<p>  慕容临天..爱儿开口了..</p>\n<p>  叫我天..慕容临天刮了刮她的小鼻子纠正道..</p>\n<p>  天如果有一天我离开了你你会怎么办？爱儿问道..眼底流露出一丝悲伤..</p>\n<p>  慕容临天天怔了一下..他真的不敢再想失去她的感觉..不更加抱紧了她..不要离开我..爱儿..</p>\n<p>  爱儿感受到了慕容临天加重了力道..她知道他在害怕..我是说如果我不得已离开了你..</p>\n<p>  那我会一直等你一直等到你回来为止..慕容临天坚定的说..</p>\n<p>  如果知道我不会回来呢？</p>\n<p>那我也会等一生一世永永远远的等下去</p>\n<p>  爱儿感动的看着他眼里湿润了心里更是波涛汹涌....</p>\n<p>  不过我一定不会让这种如果发生的我要把你永远留在我身边..慕容临天恳切的说道在爱儿的额头上轻轻印下一个吻...</p>\n<p>  嗯！爱儿知道他是爱惨自己了..自己又何尝不是呢？得夫如此妻复何求？</p>\n<p>  我也是..说完狠狠吻上他的唇</p>\n<p>  得妻如此夫复何求..慕容临天在心里默念着...</p>\n<p>如果知道我不会回来呢？</p>\n<p>那我也会等一生一世永永远远的等下去</p>\n<hr>\n<p>  哇咔咔....妍嬭自己都被感动了....太浪漫了....（作花痴状）...</p>\n<p>今天就更到这儿啦....老爸喜欢跟我抢电脑无语</p>\n<p>  第五十二章 昨夜星辰昨夜风（四）</p>\n<p>冯统领我们已经在这儿找了这么久了都没找到王爷和易姑娘的踪迹..一名暗夜铁骑军向枫报告...</p>\n<p>  枫若有所思的盯着云雾缭绕的万丈悬崖..下去找！</p>\n<p>  是！十名铁骑军抱拳领命...</p>\n<p>天喝点水吧..爱儿用树叶从小河里舀了一瓢甘甜的水递到慕容临天嘴边..</p>\n<p>嘴里喝着水眼睛却是一直目不转睛的盯着爱儿..看她吹弹可破的标致的小脸..红润的樱桃小嘴..不喉结动了一下..</p>\n<p>  好甜..他赞叹道..可是没有你甜...</p>\n<p>  爱儿的脸颊一下子泛起了红晕..咯咯笑出声来..这一切看在慕容临天眼里他真是无比的感谢上天..感谢他赐了个爱儿给自己...</p>\n<p>  爱儿..我饿了...慕容临天说道..</p>\n<p>  那我去摘一些水果给你吃..爱儿根本没听出来饿的含义...</p>\n<p>  我不想吃水果..慕容临天一把抓住转身欲走的爱儿嘴凑到她的耳边..感觉下腹一阵燥热..声音也变得低哑..我想吃你...</p>\n<p>  什么？爱儿唰的一下脸红了个透...不行你的伤还没好呢..</p>\n<p>  无妨说着xing感的唇压了上来...</p>\n<p>  那伤口又裂开怎么办..</p>\n<p>  无妨....还是这句..</p>\n<p>  可..唔爱儿还想说什么却被慕容临天堵得有口难言...爱儿心里那个火大白了慕容临天一眼..男人果然都是思考的动物自己眼前这个尤其是！</p>\n<p>  慕容临天的大手不安分的在爱儿身上着..所到之处皆引起一阵骚热...</p>\n<p>  可是</p>\n<p>  谁！</p>\n<p>　</p>\n";

