module.exports = "<hr>\n<p>title: 第14章\ndate: 2017-07-18 03:59:58\nwords: 3544</p>\n<h2 id=\"index-14\">index: 14</h2>\n<!-- deleteAbove -->\n<p>说得那么平静但在爱儿的心中却是波涛汹涌..他真的警告过梅凝..爱儿那双干净的眸子里顿时泛起了浪花..</p>\n<p>  慕容临天坐在床边端起药碗用舀了一勺放在嘴边小心翼翼地吹了吹递到爱儿嘴边喝药乖~说完才发现自己的语气是那么的温柔简直不像他..奇怪的是刚才拒绝喝药的爱儿居然鬼使神差的把药喝了进去...眼睛一直盯着眼前这个曾经那么残暴对自己的男人..这真的是同一个人吗..难道他良心发现了？她想象不出来也不敢想象..</p>\n<p>  很快一碗药喝完了而爱儿居然还不知道苦..慕容临天站起身你好好休息吧本王还有事。说罢正欲离去..</p>\n<p>  你为什么对我这么好？爱儿问道..</p>\n<p>  他顿了一下因为本王一向赏罚分明..说完走了出去...真的是因为这样吗？只有他自己的心清楚..麟太医慕容临天叫道..</p>\n<p>  王爷有何吩咐？寒双手抱拳敬道..</p>\n<p>  用王府里最好的药给她调理..</p>\n<p>  寒的眼神闪烁一下抬起头来只是看到他远去的背影...这个自然..他自言自语道..</p>\n<p>  爱儿摸摸刚才慕容临天坐的地方.还留有他的味道..好闻的男人气息..</p>\n<p>  爱儿见慕容临天出去了如水马上进来了..喝药了吗？</p>\n<p>  爱儿点点头如水看到床头的空碗不可思议他喂你的？嗯爱儿答道..</p>\n<p>  如水若有所思的笑了笑你知道吗他和冯统领刚回来听到你受伤的消息就马上赶过来了..</p>\n<p>  是吗？爱儿看着床边可他始终是我的仇人..</p>\n<p>  第二十八章 众里寻她千百度，蓦然回首（二十）</p>\n<p>大厅</p>\n<p>  枫..召集王府所有人。慕容临天一脸寒冰..</p>\n<p>  是！枫道...</p>\n<p>  半柱香后</p>\n<p>  冯枫开始。天命令道..</p>\n<p>  枫向天双手抱拳是王爷。</p>\n<p>  今天王爷找大家来是要重整我王府的规矩俗话说没有规矩不成方圆王爷希望王府里的每个人都循规蹈矩可是偏偏有人逆命而为居然对下人滥用私刑！说到这里顿了一下看向梅凝梅凝夫人按照王府的规矩你说滥用私刑者该当何罪？</p>\n<p>  梅凝手心直冒冷汗腿在发抖还好旁边的小月扶着她..半天说不出话来..</p>\n<p>  枫笑着看着她梅凝夫人不知道吗？那好本统领来告诉大家按照规矩滥用私刑者放置王府后山！</p>\n<p>  梅凝全身都是冷汗这下站不住脚了扑出来跪在地上小月也跟着跪下..</p>\n<p>  王爷梅凝知错了求王爷开恩..边说边狠狠的磕头很快额头上就磕出了血..</p>\n<p>  慕容临天站起来走到梅凝面前伸手捏住梅凝的下巴眼睛直勾勾的盯着她深不见底的黑色眼眸中满是血腥..现在知道错了？</p>\n<p>  王爷梅凝这次真的知道错了下次再也不敢了王爷开恩..哭得暴雨梨花楚楚可怜任人看了都会不忍..可是慕容临天根本不为所动嘴角勾起一抹嗜血的笑容..晚了！</p>\n<p>  这两个字硬生生的把梅凝像是千刀万剐一般...王爷求你开恩就算看在我们多年的夫妻情分上求求你...</p>\n<p>  夫妻情分？你没资格...慕容临天看也不看梅凝一眼冷冷道..带下去！</p>\n<p>  很快几个侍卫上来拖起地上的梅凝往外走..王爷！王爷！开恩王爷！梅凝苦苦恳求..</p>\n<p>  唉真可怜到后山还有命嘛肯定是被野兽咬得尸骨无存..真惨</p>\n<p>  谁叫她惹怒了王爷呢这是活该看她平常还仗势欺人！围观的下人们悄悄议论道..</p>\n<p>  不行这样太残忍了..爱儿想随即不顾伤痛走出人群..住手！</p>\n<p>  慕容临天的眉毛微皱..转过头来是你？</p>\n<p>  爱儿立刻跪下可是不小心扯痛了伤口她忍住不让自己叫出来可是苍白的脸色和额头的汗滴背叛了她...</p>\n<p>  求王爷开恩饶了梅凝夫人..她艰难的说道..</p>\n<p>  什么？慕容临天不解的看着她为什么替她求情？本王是在帮你。</p>\n<p>  谢谢王爷厚爱奴婢很感激..奴婢固然恨她可是她的罪并不致死更何况扔到后山太残忍了奴婢不希望王府里出现这么残忍的事希望王爷高抬贵手放过梅凝夫人..爱儿依旧求着情..</p>\n<p>  慕容临天看着爱儿他真是越来越看不懂这个女人了为什么要替仇人求情？他只想用眼神把她看透...</p>\n<p>  易爱儿不用你在这里假惺惺！你这个jian人！梅凝骂道指甲深深掐进手心..</p>\n<p>  慕容临天厌恶的看着梅凝那种疯妇形象又看看爱儿她的脸越来越苍白了..心中就气不打一处来..</p>\n<p>  带下去！他大吼道..</p>\n<p>  是！众人皆吓出一身冷汗..此时..</p>\n<p>  住手！众人哗然..是谁这么大胆子！只见一个女子走了进来..一袭白衣胜雪柳叶眉杏仁眼樱桃小嘴一个飞云髻挽在头上像翩翩下凡的仙女...</p>\n<p>  好美爱儿默赞道..</p>\n<p>  王爷..女子走过来怎么如雪才去白云观参经拜佛不久王府就出了这么大的事呢？柳如雪真是像雪一般纯净令人赏心悦目..</p>\n<p>  雪儿你回来了。慕容临天好像平静了许多..如雪走到慕容临天身边用手轻轻他的胸口王爷消消气火大伤身..</p>\n<p>  慕容临天抓住她的手目光柔和了很多还是雪儿深得我心..</p>\n<p>  如雪莞尔一笑王爷梅凝姐姐的事我已经知道了这位姑娘说得对说着转过来看了看爱儿爱儿也向她回敬了一个微笑..放置后山实在太残忍了更何况梅凝姐姐和王爷好歹三年夫妻所以...慕容临天摸摸她的脸那雪儿认为本王该如何惩罚她呢？</p>\n<p>  如雪觉得生命诚可贵希望王爷饶姐姐一命正好如雪身边缺一个知心姐妹不如就让梅凝姐姐搬到胜雪居和如雪同住转过头来看看梅凝不知姐姐是否嫌弃妹妹的胜雪居简陋？</p>\n<p>  不嫌不嫌谢如雪姑娘求情..梅凝满眼感激..</p>\n<p>  如雪笑着点点头那王爷觉得这样如何？既然雪儿这样说本王哪有不同意之理呢？说罢一手把如雪搂进怀中..雪儿刚回来肯定累了吧本王陪你到胜雪居可好？</p>\n<p>  如雪谢王爷..说完两人向胜雪居走去厅里的下人也纷纷散去了..如水过来扶起爱儿看着爱儿苍白的脸傻丫头哪有你这样善良的..</p>\n<p>  爱儿看着一脸担心的如水如水姐姐我没事。刚才那个如雪姑娘真的是好贴心看样子慕容临天那个家伙都被她收服了呢听她说话真的能让人欣悦..</p>\n<p>  是这么温婉的姑娘真是令人喜爱..</p>\n<p>  可是为什么看到慕容临天抓住如雪的手时我的心会不舒服....爱儿不懂亦不想去懂..</p>\n<p>第二十九章 此情可待成追忆，只是当时已惘然（一）</p>\n<p>胜雪居</p>\n<p>  王爷如雪去白云观这么久你有没有想我呢？如雪坐在慕容临天身旁轻轻把头靠在他宽厚的肩膀上..</p>\n<p>  本王怎么会不想雪儿呢？一把把如雪搂在怀里.你可是最深得本王心的女人..</p>\n<p>  如雪听到慕容临天的话笑靥如花用筷子夹了一块黄灿灿的菊花糕递到他嘴边王爷...慕容临天张开嘴咬下筷子上的菊花糕品尝式的嚼了嚼..嗯好吃..本王记得菊花糕是雪儿最爱吃的吧..不过这里好像只有一块已经在本王嘴里了雪儿怎么办呢？</p>\n<p>  如雪笑了笑装作很遗憾的样子..那就是雪儿无福消受这次的菊花糕咯..真可惜..唔还没等如雪说完慕容临天的嘴已经压了上来..侵略式的长驱直入继而转化为温柔的挑起如雪的丁香小舌反复纠缠很快如雪感到浑身无力瘫软在慕容临天的怀里...他的一只大手在如雪身上游离扯开她的腰带抱起她放在....的幕帘放下却掩盖不住室内的旖旎...一阵阵令人面红耳赤的申吟从胜雪居传出~~~~</p>\n<p>  典医阁</p>\n<p>  爱儿无力的躺在全身都在痛痛得都快麻木了..但是最不舒服的却是心..为什么她会有这么奇怪的感觉？这种感觉到底是什么？爱儿闭上眼不再去想..</p>\n<p>  爱儿..枫从门外走进来依旧笑脸吟吟似乎不管有什么烦恼只要看到他的笑容就会消散净尽..</p>\n<p>  枫哥哥..爱儿笑着坐起身靠在靠垫上..你来看我了我好想你哦...说着又撒起娇来好像在枫面前她永远可以是个小孩..</p>\n<p>  你这小丫头伤还没好又开始撒娇了真是头痛..他说的可是实话真不知拿爱儿怎么办..或许他这辈子可能就真栽她手上了..</p>\n<p>  哦对了枫哥哥你们这次出去是办什么事？爱儿问道眼睛扑闪扑闪的像是会说话一样..</p>\n<p>  这个嘛..不能告诉你..枫的眼神有些闪烁..</p>\n<p>  哼小气！不理你了！爱儿装作生气的样子转过头不看他..她知道这样枫就一定会告诉她的..果然..</p>\n<p>  好啦好啦告诉你就是啦别生气了..枫无奈地摇摇头..我们是去抓一些企图叛乱的人..</p>\n<p>  叛乱！</p>\n<p>　</p>\n";

