module.exports = "<hr>\n<p>title: 第6章\ndate: 2017-07-18 03:59:58\nwords: 3799</p>\n<h2 id=\"index-6\">index: 6</h2>\n<!-- deleteAbove -->\n<p>声音不含一丝温度..</p>\n<p>  两名侍卫分别压着爱儿和如水来到大厅</p>\n<p>  跪下！侍卫恶狠狠道..</p>\n<p>  爱儿一把甩开侍卫抓住她的手凭什么要我跪他！我恨不得杀了他！心中的愤怒像火一样喷出来..</p>\n<p>  你们退下.依旧是那冷冰冰的话语..</p>\n<p>  是王爷。放开如水两个侍卫一起退了出去..</p>\n<p>  爱儿看着冥王不可置否他身上散发出来的王者气质是怎样也磨灭不了的像是天生如此刚刚没仔细看他那脸庞线条如刀割的一般刚毅黑而浓的眉毛深不见底的黑色眼眸连长黑的睫毛也淹没其中巍峨挺立的鼻梁xing感的嘴唇没有丝毫的阴柔之气...无一不让人痴迷其中..可是现在在爱儿看来这一切的一切都像是对她的挑衅！</p>\n<p>  你为什么杀我干爹干娘？你说！爱儿对厅上的人咆哮着..</p>\n<p>  本王做的事应该没必要向你这个黄毛小丫头交代原因吧..嘴角向上挑了挑..</p>\n<p>  那你把我抓到这里来干什么？你不怕我杀了你吗？爱儿气愤极了..</p>\n<p>  父债女还没听过吗？而且本王不认为你有这个能耐杀得了我..脸上不屑的笑意彻底激怒了爱儿..</p>\n<p>  哼！我现在就为我干爹干娘报仇！说罢向冥王冲去...</p>\n<p>  两旁的侍卫欲上前阻挡..你们都不准出手！冥王命令道..</p>\n<p>  爱儿挥起一拳向男子胸前袭去可男子连眼睛都不眨一下蔑视般的笑着就这点能耐？跟棉花没什么区别..</p>\n<p>  哈哈..嗯咳咳一旁的冯枫忍不住笑出声来可周围的人都没笑顿时感觉有些尴尬..</p>\n<p>  哼好戏还再后面呢！爱儿说着一脚向男子踢去却被男子的双腿紧紧夹住抽不出来..爱儿气不过左右开弓准备打他个措不及手但刚出手就被男子用一只手钳制住不得动弹..</p>\n<p>  就这三脚猫的功夫还想杀我？另一只手捏住爱儿光滑的下巴微微用力..</p>\n<p>  你爱儿的下巴被他捏得生痛...干爹要她从小学武按理说她的功夫差不到哪儿去可是他却这么轻易的让她无法还手爱儿盯着眼前的人不敢想他的武功到底有多高..</p>\n<p>  从今天开始你就是本王的贴身奴婢也是本王府最低的奴婢本王会代你干爹好好‘照顾’你的..哈哈哈哈..照顾两个字故意说的特别重一听便知其意..</p>\n<p>  你卑鄙！爱儿咬牙切齿道感觉捏住下巴的手又加重了力道疼得眉毛都拧了起来..</p>\n<p>  你要怪就怪你是易天齐的干女儿！我会让你知道本王绝不会只是卑鄙..手上再次加重力气..</p>\n<p>  唔...爱儿痛得申吟出来头上冷汗直冒...</p>\n<p>  请你放了爱儿..如水焦急的出声道..</p>\n<p>  冥王抬起那深不见底的眸子瞟了如水一眼仅仅只是一眼而已..重重推开爱儿..</p>\n<p>  ..爱儿吃痛的叫道..</p>\n<p>  带她们下去冰冷的话语深深烙在爱儿心中同时标上了恨的印记..</p>\n<p>第十二章 众里寻她千百度，蓦然回首（四）</p>\n<p>你叫易爱儿是吧？</p>\n<p>  爱儿盯着眼前这个中年男人你是谁？</p>\n<p>  我姓周是这个王府的管家这里的下人奴婢都归我管。</p>\n<p>  他奶奶的感情这该死的冥王还真叫我当一个奴婢？爱儿心里气愤着..</p>\n<p>  听说你是易天齐的干女儿对吧？周管家若有所思着怪不得王爷让你当他的贴身丫鬟看来你今后是不会有好日子过了。周管家的眼光忽然变得凌厉起来大家听好！渐渐地周围的下人丫鬟都聚拢过来他指着爱儿她叫易爱儿是易天齐的干女儿。</p>\n<p>  爱儿不解低头思索着有必要向大家介绍她吗？</p>\n<p>  爱儿..如水扯扯她的袖子你看..</p>\n<p>  爱儿抬起沉思的头咦？为什么大家都这么看我？</p>\n<p>  只见所有的下人丫鬟都像仇视她一般死死瞪着她...瞪得爱儿毛骨悚然....</p>\n<p>  你..你们为什么这么看我？爱儿不解..</p>\n<p>  可是没人回答她好啦好啦大家都干活去吧！周管家一句话大伙纷纷散去...</p>\n<p>  周管家..一个下人走进来王爷叫我带如水姑娘去..</p>\n<p>  他干嘛带走我如水姐姐？他想干嘛？爱儿挡在如水前面张开双手护着她..</p>\n<p>  王爷要做什么还轮不到你这个奴婢来管！周管家严厉呵斥道..</p>\n<p>  不行！你们要针对就针对我一个人我如水姐是无辜的！可是她自己又何尝不无辜呢？</p>\n<p>  来人！给我抓住她！周管家命令着接着冲进来一群下人左右前后把爱儿包围起来虽然爱儿会武功可就凭她一人又如何同这么多人对抗？几招下来便被人抓住双臂放肆不了....</p>\n<p>  带走..先前那个下人应了一声如水姑娘请！</p>\n<p>  如水无奈只有跟着走可前路是福是祸？还是未知之数...</p>\n<p>  如水姐姐！如...唔唔..大喊大叫的爱儿被周管家用布包塞住了嘴..</p>\n<p>  把她绑起来关进柴房！周管家道今天不给你点教训你就不知道王府的规矩！</p>\n<p>  是！下人们把爱儿拖进柴房里....</p>\n<p>  洪天殿</p>\n<p>  王爷如水姑娘带到。下人道..</p>\n<p>  你先退下.冥王连眼皮都没抬一下语气冰冷..</p>\n<p>  王爷不知你找如水来有何事？如水的语气虽然很平缓但冥冥中也可以听出一丝不满之气...</p>\n<p>  你不是易天齐的干女儿？男子问道似有一丝怀疑..</p>\n<p>  我...如水本来想说是可想起在客栈外爱儿已经大声说她不是目的就是要冥王别伤害她如果现在说是岂不是浪费了爱儿的一片苦心..我不是！我姓秋不姓易..</p>\n<p>  秋如水男子像是斟酌着这个名字...既然是干女儿不一定要姓易..这个算不上什么证据..</p>\n<p>  如水心里慌了一下但很快镇定下来王爷我确实不是易天齐的干女儿如果王爷不信我说再多也是徒劳既然王爷心里已经有了一个答案又何必再问？</p>\n<p>  男子站起身走近如水如水低下头心里砰砰跳着他想干什么？</p>\n<p>  轻轻捏起如水那尖削的下巴让她抬起头来力道跟刚才对爱儿的完全不同..</p>\n<p>  好一张利嘴..嘴角戏谑着..美人说的话本王怎么会不信？</p>\n<p>  既然这样王爷我可以退下了吗？如水还要干活..</p>\n<p>  来人带如水姑娘去月华殿..男子收回捏住她下巴的手..</p>\n<p>  王爷你这是什么意思？如水疑惑的看着冥王道..</p>\n<p>  既然你跟易天齐没什么关系本王当然会好好待你今后你就住在月华殿..虽然这么说但声音依旧冰冷..</p>\n<p>  那爱儿....</p>\n<p>  她？哼易天齐的干女儿本王当然要亲自‘招呼’她了...嘴角扯起一抹深不可测的笑...</p>\n<p>第十三章 众里寻她千百度，蓦然回首（五）</p>\n<p>如水姑娘月华殿到了..下人恭敬的说..</p>\n<p>  谢谢你如水回道..想到爱儿..我可不可以去看看爱儿？</p>\n<p>  如水姑娘这个小人我也做不了主而且你刚搬来月华殿王爷赐了些日常所需的衣服珠钗给你先让我们帮你整理整理你再看看缺些什么我们好添置..看爱儿姑娘的事还是等明天吧...看王爷对如水还不算差下人自然也不敢怠慢....</p>\n<p>  你说的也有道理那..好吧麻烦你们了。如水微笑道..</p>\n<p>  一点也不麻烦如水姑娘的事就是我们的事小人名叫杜泽人人都叫我小杜今后如水姑娘有什么事就叫小杜去做就行了..</p>\n<p>  小杜谢谢你了..如水道...</p>\n<p>  等他们把房间整理干净如水坐在桌旁沉思着...</p>\n<p>  其实这个王爷还挺细心的如果不是我心里已经有了寒的话或许可以考虑考虑...其实对于易天齐夫妇的死如水似乎没那么伤心或者可以说一点也不伤心...当年夫人对我多好像亲生女儿一样可是从那天起一切都不一样了</p>\n<p>  五年前易府</p>\n<p>  爱儿你醒了？</p>\n<p>  爱儿缓缓睁开眼睛摇了摇昏昏沉沉的头...如水姐姐！没错坐在床前的正是如水...</p>\n<p>  嗯爱儿你终于醒了你睡了好久了..</p>\n<p>  如水姐姐这是哪里？我怎么会在这里？爹娘呢？爱儿扑闪着大眼睛...</p>\n<p>  听到爱儿提起爹娘如水面露悲伤之色啜泣着：老爷和夫人他们他们死了...</p>\n<p>  什么！爱儿只觉得大脑嗡一声不可能的爹娘怎么会死呢？</p>\n<p>  你前几天走失了老爷夫人到处找你前天在路上发现你被山贼抢走了他们就去追结果..被山贼给...说到这里如水哽咽了一下...碰巧二老爷经过就带家丁和山贼苦战很久这才把你救回来</p>\n<p>  不可能的爹娘怎么会死？他们不会丢下爱儿不管的....爹..娘...爱儿放声大哭着..</p>\n<p>  如水一把抱住爱儿眼泪顺着脸颊滑下轻轻拍着爱儿的背安慰着..爱儿别哭老爷夫人不在了还有我...如水姐姐会保护你的...</p>\n<p>  这时易天齐夫妇走进房内...</p>\n<p>  爱儿对不起如果二叔早点经过那里你爹娘就不会死都是二叔不好...易天齐强忍着但可以明显看出那一圈眼眶通红....</p>\n<p>  二叔....不关你的事不是你的错谢谢你救了爱儿如果不是你爱儿肯定也活不成...爱儿满脸泪水看起来楚楚可怜...</p>\n<p>  爱儿你真是个善解人意的好孩子....易天齐抱着爱儿从今以后你就是我的干女儿了我会把你当成自己的亲生女儿好吗？</p>\n<p>　</p>\n";

