module.exports = "<hr>\n<p>title: 第9章\ndate: 2017-07-18 03:59:58\nwords: 3685</p>\n<h2 id=\"index-9\">index: 9</h2>\n<!-- deleteAbove -->\n<p>说得正经实际上心里在偷笑慕容临天看你怎么回答.</p>\n<p>  好我就负责把她送给你怎么样？用的是我因为只有他们两人时完全就像兄弟一样不分彼此..</p>\n<p>  我可不要..他敢要嘛那可是天的仇人之女虽然只是干女儿..</p>\n<p>  门外响起一阵谈话声..</p>\n<p>  侍卫大哥你让我见见王爷吧..如水来到门外却被守门的侍卫挡住..</p>\n<p>  王爷和冯统领正在里面商议国事任何人都不能打扰..</p>\n<p>  怎么办如水急得像热锅上的蚂蚁..侍卫大哥求你了我有要紧事要见王爷求求你了..说着正欲跪下求情..</p>\n<p>  让她进来！声音虽不狠厉但其寒冷程度也着实让如水吃了一惊..</p>\n<p>  走进房里面压抑非常如水不敢抬起头观察周围的情况只是扑通一声跪下王爷求求你放了爱儿吧..她还未及笄只是个孩子而且易天齐已死王爷又为何还不解恨要如此折磨她呢..求王爷高抬贵手放了爱儿吧..如水虽这么说但心里还是战战兢兢的..</p>\n<p>  慕容临天好像很是喜欢看这样的好戏嘴角一直挂着笑只是这笑比什么都要可怕罢了..</p>\n<p>  谁告诉你易天齐死了？慕容临天此语一出让如水惊讶不已..</p>\n<p>  那天在客栈外我和爱儿明明看到王爷你..如水不敢相信</p>\n<p>  看到本王掐死了他们是吗？只是易天齐如此狡猾怎会如此轻易为本王所杀？死的只是他找的替身而已..眼里多了些许的怒火..令人不敢直视..所以易爱儿本王绝对会让她生不如死！生不如死四个字彻底浇凉了如水的心..美人说了半天本王还真是有点想她了不知道本王的狱卒有没有好好招呼她呢？枫统领随本王去看看..说罢黑袍一挥往天牢走去..</p>\n<p>  此时如水已完全瘫坐在地上爱儿....对不起我救不了你....一滴滴热泪掉下来...</p>\n<p>  给你看眼前一块素白的手帕..慢慢抬起头一袭白衣的男子正对着自己微笑..</p>\n<p>  冯统领..如水哭得更厉害了..</p>\n<p>  他凑到她耳边..王爷心里的恨有多深你们是不会明白的不要给他折磨易爱儿的理由..说完像风一样飘然而去..</p>\n<p>  如水姑娘..小可跑进来扶起地上的如水你没事吧如水姑娘..如水摇摇头.不要给他折磨易爱儿的理由..枫的这句话一直在她脑海回响..难道我今天来给爱儿求情是给了他折磨爱儿的理由？</p>\n<p>  第十八章 众里寻她千百度，蓦然回首（十）</p>\n<p>慕容临天和冯枫来到天牢看着趴在地上的那个小小身影她还没有醒过来凌乱的发丝像纷飞的柳絮身上薄薄的单衣有着被撕烂的痕迹枫看着慕容临天表情有些许复杂..转身向狱卒道把门打开！</p>\n<p>  是！狱卒不敢怠慢..</p>\n<p>  枫欲进去察看爱儿的情况可慕容临天却是站在原地不动..怎么？枫转过来看他不进去看看？</p>\n<p>  本王没有关心她的兴趣..说罢甩袖走了..</p>\n<p>  呵慕容临天你是真没兴趣还是..枫这样想道..走了进去</p>\n<p>  看着地上的人枫有种说不出的感觉：听如水姑娘说她还没及笄。枫这样想到。脸上的手指印依旧还是那么清晰左半边脸已经肿了起来不过不难看出伤痕下那张清秀的脸..咦？她的手为什么是这个样子？身旁耷拉着的手让枫有所怀疑伸手一摸..天这样对一个女孩也太残忍了..</p>\n<p>  许是感觉到手上的痛楚爱儿申吟出来：痛...好痛...这一声声申吟勾起了枫的不忍..一把横抱起爱儿向牢外走去..</p>\n<p>  冯统领这恐怕不妥..狱卒上前劝阻道..</p>\n<p>  王爷怪罪下来本统领一人承担..枫不想跟他们多废话让开！</p>\n<p>  是是是...狱卒颤抖着站开了...</p>\n<p>  爱儿模模糊糊中似乎看到一个一身白衣的男子抱着自己是那么温柔让她感到很放心很安全...仅一眼又晕了过去..</p>\n<hr>\n<p>似乎过了很久爱儿睁开沉重的眼皮..这是哪里？不像是天牢..白色的缎被白色的丝绸帘白色的床单..好像一切都是白色的..</p>\n<p>  你醒了？枫的声音很温柔..</p>\n<p>  他是谁？全身也是白色的..还长得..挺俊的..当然爱儿只是心里想不会说出来..这里是天堂吗？你是天使吗？</p>\n<p>  哈哈...听到爱儿的这一番话枫忍不住大笑起来你还真像个孩子..</p>\n<p>  不知为什么听到他笑自己爱儿总觉得有点愤愤嘟起小嘴我才不是孩子呢！话一出口却又有些伤感是自己都已经被..怎么还会是孩子似乎看到爱儿眼里的悲伤枫立刻打趣道：别嘟了像只小猪一样小猪妹..说完还哄哄两声学猪叫..</p>\n<p>  你爱儿气急了想出拳揍他刚一动动手刺骨的痛就阵阵袭来..好痛.....枫见此情景赶忙上前别乱动你的手脱臼了大夫刚帮你接好。</p>\n<p>  想到那时恐怖的情景爱儿还在后怕一双明亮的眼睛黯淡下来..不知道为什么看到她这样枫觉得有什么东西牵动了自己的心..爱儿....</p>\n<p>  你怎么知道我的名字？爱儿不解..我听如水姑娘说的她很担心你..枫如实相告..</p>\n<p>  如水姐姐？她现在好不好？她被慕容临天的人带走了！他会怎么对付她....爱儿这样想着蜷缩着身子瑟瑟发抖让人有些心疼..</p>\n<p>  你放心她没事的她现在在月华殿枫安慰道..</p>\n<p>  对了你是谁？你怎么救我出来的？爱儿睁着水灵灵的大眼睛问道..好美的眼睛让人迷醉..</p>\n<p>  我叫冯枫是慕容临天手下的统领枫笑道..你敢直呼那个王八蛋的名字看来你和他关系很好嘛..爱儿露出一种不屑的表情他的人不得不防...可是这点小心思怎么逃得过枫的眼睛？呵呵的确我和他在私底下是好兄弟..那你还敢叫他王八蛋呢比我面子还大..你放心吧我可是好人哦..戏谑的刮刮她的小鼻子..好人又不是写在脸上的！我怎么知道你不是把我拐骗出来的...哼一声头扭向一边小嘴噘得更高了....拐骗你？枫真不敢相信自己听到的..你看我哪里像人贩子！小猪妹！听到他又叫自己小猪妹爱儿很不高兴眼骨碌一转我可以认你当哥哥吗？为什么？枫不解..这样的话你就是猪哥哥！哈哈...爱儿笑起来..你个小鬼头！枫轻轻敲了一下爱儿的头...不过她笑了..受到这样的伤害她居然还能笑出来枫真是对这个妹妹越来越感兴趣了..可是他始终是天的仇人之女天不会这么轻易放过她的枫看了一眼小孩子一般的爱儿心头涌出无数的怜惜与同情..不知道明天又会怎样..这个小小的人儿在这里只会受到深深的伤..</p>\n<p>  谢谢你枫哥哥..爱儿忽然平静的说道..</p>\n<p>  为什么？枫感到很奇怪..</p>\n<p>  今天是我进王府以来最高兴的一天你是这里除了如水姐姐以外对我最好的人..爱儿的眼神很真诚反而让这个平常嘻嘻哈哈的枫有些不习惯了..</p>\n<p>  唉爱儿叹了口气..</p>\n<p>  在想什么？枫温柔的问..</p>\n<p>  不知道明天还会不会这样开心..爱儿耷拉起脑袋..如果你见到如水姐姐千万别告诉她我现在的情况不然我怕她会伤心..枫看着她耷拉的头有些宠溺的摸摸..你怎么不担心担心你自己呢？</p>\n<p>  我？我清楚那个王爷不会放过我的只要如水姐姐不知道能过得开心就行了..枫看着她眼中有些不忍..她的眼睛却突然亮起来不过我也不是好欺负的我才不会任那个王爷欺负我呢所以我不怕！</p>\n<p>  枫怔住了...她是如此柔弱但却如此坚强..她不担心自己而是心系他人..这样的女子...为何上天要这样对她？</p>\n<p>  第十九章 众里寻她千百度，蓦然回首（十一）</p>\n<p>在冯枫房间里疗伤的这几天爱儿像个孩子一般开心只要枫一回来爱儿就会像小妹妹一般揪着枫的袖子眨巴眨巴大眼睛装出一副楚楚可怜的样子要枫陪她玩儿..这样的撒娇枫可受不了唉..只要爱儿一这样枫还真是拿她没办法..谁叫他认了这么个古灵精怪的妹妹？也罢也罢...看说话间爱儿又冲了上来...</p>\n<p>  枫哥哥今天回来的好晚哦都不陪我玩...又是那副让人难以抵挡的表情...枫你怎么就栽在这个女人手上了...</p>\n<p>  好爱儿今天王府里事情多所以就...他不敢告诉她慕容临天要枫把她带回去..他也不忍..</p>\n<p>  枫哥哥爱儿扑进他的怀里你带我出去玩好不好？</p>\n<p>  看着爱儿可爱的表情枫用手宠溺的摸摸她的头不行你呀给我好好养伤伤养好再说..</p>\n<p>  他们不知这一切全落入了门口那个人眼里...</p>\n<p>  呵本王的奴婢还真是厉害慕容临天环抱双手靠在门框上脸上的表情说不出的冷酷居然区区几天时间就把本王身边的枫统领走了..易爱儿你还真是无所不能..</p>\n<p>  看到那张冷峻的脸爱儿不害怕起来更加抓紧了枫而这一细节自然没能逃过天的眼睛..深不见底的黑色眼眸散发着阵阵寒气..</p>\n<p>  天不是你想的那样你误会我们了..枫解释道..</p>\n<p>  是不是我想的那样又有什么关系？</p>\n<p>　</p>\n";

