module.exports = "<hr>\n<p>title: 第35章\ndate: 2017-07-18 04:00:00\nwords: 3521</p>\n<h2 id=\"index-35\">index: 35</h2>\n<!-- deleteAbove -->\n<p>我最喜欢的就是桃花了！还有温泉！好棒哦！咯咯..</p>\n<p>  这就是你说的好地方吧！</p>\n<p>  玩什么？</p>\n<p>  你..你..你不要过来！</p>\n<p>  爱儿慕容临天的嘴角微微上扬着..你真可爱...</p>\n<p>  看着温泉周围盛放的桃花就像爱儿陪伴在身边一样..爱儿这里的桃花一年四季都在开放好像等你回来一起看看...</p>\n<p>  四年后</p>\n<p>  慕容临天坐在书房中桌上铺着白色的宣纸...墨已经磨好了..稍稍蘸了点墨汁</p>\n<p>  彩袖殷勤捧玉钟。当年拚却醉颜红。</p>\n<p>  舞低杨柳楼心月歌尽桃花扇影风。</p>\n<p>  从别后忆相逢。几回魂梦与君同。</p>\n<p>  今宵剩把银红照犹恐相逢是梦中。</p>\n<p>  拿起剪下的一枝桃花小心翼翼的欣赏着..爱儿我很想你...可惜我们只能在梦中重逢..你该回来了..爱儿</p>\n<hr>\n<p>  本章节主要写了慕容临天这四年来对爱儿的思念虽然没有什么波澜起伏..但是平平淡淡未尝不是一种美....</p>\n<p>第二章 从别后，忆相逢，几回梦魂与君同（二）</p>\n<p>五年后</p>\n<p>  天已经一切都准备好了相信这次一定可以将叛党清除干净！枫高兴地对慕容临天说道...</p>\n<p>  好今晚行动！慕容临天的眸子里满是寒冷...</p>\n<p>  晚上</p>\n<p>  万将军所带领的一群人穿着黑色的盔甲从官道上向京城逼近..这场叛乱他们已经谋划了很多年自认为天衣无缝..今晚就是开始他们要夜袭皇宫..殊不知这一切都在慕容临天的掌控之中...黑暗中无数双雪亮的眼睛正在暗处观察他们的一举一动...</p>\n<p>  已经有人做好内应只待万将军一到来就打开宫门...在这之前必须想办法解决掉他们！</p>\n<p>  铛铛铛..万将军敲了三声门...这是他们只间的暗号..一个侍卫模样的人立刻打开了宫门在这千钧一发的时刻！忽然不远处亮起了千万只火把把叛党团团围住...慕容临天的双眼散发着嗜血的红天神一般的气质完全不可侵犯</p>\n<p>  不好！万将军大喊一声而此时侍卫们已经杀了过来...一阵阵的刀光剑影喧嚣了整座皇城！不一会儿到处都充斥着血腥味令人作呕...地上尸横遍野惨绝人寰...万将军所带的上千偷袭士兵已经死的死伤的伤！眼看着自己的计划败露无疑为今之计只有先逃命再说！撤！他大喊道剩下的士兵帮他做着掩护跨上先前准备好的骏马..</p>\n<p>驾！一道黑影渐渐消失在黑夜之中...</p>\n<p>  不行！绝不能让他逃了！慕容临天看着他消失的方向不由分说的上马奔驰而去...天小心！枫无法分身只好嘱咐道...</p>\n<p>  漫漫长夜..月亮亦躲进云里...宽敞的官道上两匹骏马一前一后的奔驰着只不过其间洋溢的满是杀气而已...</p>\n<p>  该死！万将军骂道...整整一个晚上他居然一直追着自己不放这样下去难保自己的马会不会吃不消...而慕容临天的嘴角勾起一抹邪恶的笑..令人心颤...</p>\n<p>  驾！万将军借势转进一条小道说不能这样还能逃走...慕容临天自是不甘示弱驾！</p>\n<p>  天际已经渐渐明亮起来...可是万将军却是不知去向...慕容临天跳下马来巡视着四周...这里是个山谷颇有种世外桃源的美感...只是不知道是在哪里...大片的竹林苍翠欲滴若是进来了的确很难找到出口之处...</p>\n<p>  慕容临天牵着马大步走着也不知道这里有没有人住..想到让那个人跑了心中难免气愤眉毛不由得拧了起来...</p>\n<p>  可是...好美....</p>\n<p>  原来这里穿过竹林是一大片桃花林！正值暖春所有的桃花都竞相开放好不热闹...慕容临天的眉头渐渐舒展开来...小心翼翼的摘下一朵桃花放在手心观赏...的花瓣娇艳无比..可却又有些羞涩...令人好生怜惜...看到桃花慕容临天不露出会心的微笑..爱儿最爱的就是桃花了不知道什么时候爱儿才会回来？一定要带她来看看这里的桃花...</p>\n<p>  咯咯妹妹你来追我！一个小男孩边跑边笑着看看后面的小女孩...</p>\n<p>  哥哥你跑慢点...我跑不动了...后面的小女孩气喘吁吁的说...最后索停了下来一双粉唇噘得老高..哥哥就知道欺负我我不玩了！</p>\n<p>  见妹妹耍起了小子男孩跑到她身边摸摸她的脑袋..好啦妹妹别生气哥哥背好不好？</p>\n<p>  好呀好呀！一听哥哥要背她高兴得直拍手一骨碌爬上了男孩的背..肥嘟嘟的小手环住男孩的脖子..</p>\n<p>  抓好了！飞咯！说着男孩向前跑起来还不时的转圈圈...</p>\n<p>  哎哟！不小心撞上了一个人...轻轻放下妹妹两个小家伙一齐盯着慕容临天看感觉很亲切...</p>\n<p>  慕容临天被他们看得不自在蹲下来微笑的看着他们...他不知道为什么总觉得对这两个小孩很有好感..小男孩和他一样有着一对深不见底的黑色眼眸而且就这样盯着他的眼睛看一点也不害怕...而那个小女孩正紧紧拽住男孩的手臂躲在他身后..那双美眸清澈无比..这种感觉似曾相识...</p>\n<p>  你叫什么名字？慕容临天拍拍小男孩的肩膀..</p>\n<p>  我叫小天..小男孩答道..指着身后的小女孩她是我妹妹叫小爱..</p>\n<p>  小天小爱..好可爱的名字..慕容临天轻轻的抱住他们..你们住在这里吗？爹娘呢？</p>\n<p>  小天说对我们就住在桃花园我们和娘还有干娘住在这里听娘说我们的爹在京城当大官所有不能来看我们..</p>\n<p>  原来是这样..慕容临天若有所思的说道..</p>\n<p>  对了叔叔你是不是迷路了？小天问..</p>\n<p>  你怎么知道？慕容临天很好奇..</p>\n<p>  因为几乎没有人到这里来而且这里的地形很复杂很容易迷路的..小天解释道..这样吧叔叔你跟我们回家好不好？</p>\n<p>  回家？</p>\n<p>  对娘和干娘一定会好好招待叔叔你的！小天一本正经道..</p>\n<p>  对对！我们的两个娘都是好好的人！尤其是娘！咯咯小爱插嘴道..</p>\n<p>  哦？慕容临天有些感兴趣是怎样的女子才能生出如此可爱的孩子..那你们跟叔叔说说你们的娘怎么样？</p>\n<p>  好！小天和小爱异口同声的回答...</p>\n<p>  第三章 从别后，忆相逢，几回梦魂与君同（三）</p>\n<p>我们的娘是个特别好的人她每次都很热心的帮助邻居大娘照顾周围所有的人。娘什么都会她会做好吃的给我们会武功会跳舞会刺绣..她最喜欢桃花了！所以每次都会在手绢上绣上桃花..</p>\n<p>  桃花？慕容临天一怔不由自主的又想到爱儿..</p>\n<p>  她对我们有的时候很严格因为娘说我们的爹是个好厉害的人！还说等我们长大了带我们去找爹..小爱抢着说..</p>\n<p>  我们的干娘对我们很好很宠爱我们每次只要我们做错事他都会护着我和妹妹不让娘惩罚我们..而且她是个神医哦！！从小我和小爱的病都是她治的..不过可惜..小天的神色有些异常...</p>\n<p>  可惜什么呢？慕容临天一手抱一个温柔的说道..</p>\n<p>  可惜她治不好娘的眼睛...小天有些伤心..</p>\n<p>  哦？你们娘的眼睛怎么了吗？慕容临天继续问..</p>\n<p>  从我们出生起娘的眼睛就看不见...小爱也伤心起来..</p>\n<p>  看不见...慕容临天不有些可惜这样的女子上天居然要这样对她..</p>\n<p>  但是娘的眼睛好美！一点都看不出来她看不见呢！小爱接着说...</p>\n<p>  看着可爱的小爱她的眼睛真的好像一个人...慕容临天对他们的娘真是越来越期待了..不知是怎样的一位女子..</p>\n<p>不知道小天和小爱去哪儿玩了真叫人担心..云梦娘说道手上还切着一卷白菜..</p>\n<p>  他们快五岁了不用担心..而且这桃花园没外人来...一个如天仙般的女子说道..的脸上有着一对清澈的眸子一头墨玉般的青丝像瀑布一般一泻而下..几缕漫不经心的垂在额前..</p>\n<p>  好好好我这是皇帝不急太监急行了吧..还真没见过你这么当娘的一点都不担心儿女..云梦娘撇撇嘴..</p>\n<p>  女人微微一笑摸索着回到房中坐下...摸起桌上还未完成的绣品这里的一针一线包含了太多情..五年了..不知道他过得怎么样..不朱唇轻启：</p>\n<p>  红藕香残玉簟秋。轻解罗裳独上兰舟。</p>\n<p>  云中谁寄锦书来？雁字回时月满西楼。</p>\n<p>  花自飘零水自流一种相思两处闲愁。</p>\n<p>　</p>\n";

