module.exports = "<h1 id=\"-\">命中注定的王妃</h1>\n<p><img src=\"" + __webpack_require__("FgWf") + "\" alt=\"命中注定的王妃\"></p>\n<h4 id=\"-\">简介</h4>\n<p>同居长干里，两小无嫌猜—— 幼时因缘初遇，寄情香巾..</p>\n<p>取次花丛懒回顾，半缘修道半缘君—— 五年后，再次相逢，却深陷巨大阴谋..</p>\n<p>众里寻她千百度，蓦然回首—— 恨遮挡了他的眼睛，对她百般折磨..</p>\n<p>此情可待成追忆，只是当时已惘然—— 恨蒙蔽了她的内心，不知爱入骨髓..</p>\n<p>昨夜星辰昨夜风—— 真相揭晓，他和她皆身中剧毒.. 黑暗背后的狰狞面目摧残了一对生死恋人..</p>\n<p>我欲与君相知，长命无绝衰—— 生死边缘，诀别一吻..</p>\n<p>只是凄——令苍天为之哭泣..</p>\n<p>只是美——让桃花灿烂山间..</p>\n<p>从别后，忆相逢，几回梦魂与君同—— 是是非非，爱恨缠绵，亦或是天上人间..</p>\n<p>执子之手，与子偕老—— 只是因为命中注定...</p>\n";

