module.exports = "<hr>\n<p>title: 第17章\ndate: 2017-07-18 03:59:58\nwords: 3736</p>\n<h2 id=\"index-17\">index: 17</h2>\n<!-- deleteAbove -->\n<p>不-相-信本王不-相-信！慕容临天的话一遍又一遍在她耳边回响...爱儿捂着自己的耳朵发疯般的摇着自己的头..不要说了！不要再说了！歇斯底里的尖叫无助的呐喊......不争气的眼泪像断了线的珠子..一颗又一颗滴落在地上...等她重新冷静下来缓缓睁开眼睛出现在眼前的..如水姐姐？</p>\n<p>................................................................................</p>\n<p>现在最冷静的要属枫了...他静静的坐在桌旁思考着刚才发生的所有不放过任何一个细节希望能找出一丝蛛丝马迹..因为他好像发现了什么..可是又好像没有一丝头绪..真是头痛..夜晚格外的宁静尤其与刚才的慌乱相比..枫走出房间在院子里借月色踱着步...</p>\n<p>  你是谁？在干什么？枫发现了一名小丫鬟半夜三更不知鬼鬼祟祟的在干嘛..</p>\n<p>  看见了来人丫鬟赶紧跪下.回冯统领奴婢小兰负责王府的除草工作今天白天奴婢肚子痛就..就忘了除草被周管家发现了吩咐我现在把草除干净...</p>\n<p>  好了你起来吧..枫继续散着步..忽然把草除干净...草！他好像发现了什么重要的地方....转身向典医阁跑去....</p>\n<hr>\n<p>  哇哇哇！！这一章通过多个场景设置了好多悬念哦！！亲们有没有看糊涂呢？呵呵没关系真相很快为你解答..不过爱儿这次又进了大牢陷害她的人当然不会那么轻易的放过她咯飘走<del>~</del>~~~还有草到底是怎么回事呢？关注下一章吧！！！</p>\n<p>第三十五章 此情可待成追忆，只是当时已惘然（七）</p>\n<p>次日胜雪居</p>\n<p>  如雪！梅凝笑着从门外进来顺带捎上了门..我已经跟狱卒们安排好了..看那个jian人这次还有没有命活！哈哈哈哈~~~~</p>\n<p>  如雪虚弱的躺在但表情仍不乏阴狠与得意..小声点别让别人听到..</p>\n<p>  梅凝会意的点点头做了个嘘的动作..如雪我以前真是小看你了没想到为了除掉那个jian人你居然连那么毒的毒药都敢吃..梅凝真是佩服..她接着说道那个jian人万万想不到我们把锁魂草混在你每天给她沏的茶里那股青草香就是她的噩梦！说着梅凝坐到如雪的床边不过如雪有几件事我怎么也没想通..十日噬命你怎么得来的？还有那只发簪怎么到她头上去了？</p>\n<p>  如雪用手支撑着身子坐起来..因为她..说着打了个响指..</p>\n<p>  一位黄衣女子从珠帘后走出来脸上还挂着泪痕...</p>\n<p>  秋如水！梅凝惊呼...你..你...你怎么会在这里...？</p>\n<p>  这就是这次事件的大功臣..如雪笑着说..</p>\n<p>  她？梅凝不敢相信她和那个jian人关系那么好怎么会帮我们？</p>\n<p>  关系好又怎么样？如雪不屑的看着如水比得上血缘关系吗！</p>\n<p>  什么！！梅凝彻底惊到了..</p>\n<p>  如雪拍着梅凝的肩如雪、如水..你不觉得这两个名字如出一辙吗？</p>\n<p>  梅凝简直不敢相信自己的耳朵..难道你们是......</p>\n<p>  没错！这次开口的是如水...我是她亲姐姐....</p>\n<p>  天啦！梅凝惊呼得站起来这么说来你</p>\n<p>  是！是我！如水发狠的看着梅凝和如雪是我进典医阁偷了十日噬命是我把剩下的所谓‘证据’放进爱儿的房间是我给爱儿进行及笄之礼亲手把那该死的蝴蝶簪插在她的头上！是我在昨晚用锁魂草控制她说出所谓的‘真相’！是我！是我！都是我干的！如水激动极了泪眼滂沱地冲出胜雪居奔向自己的月华殿....</p>\n<p>  远处枫站在转角的墙后..如水？她怎么从胜雪居哭着跑出来？而且......昨晚她不在场......枫若有所思的盯着如水远去的背影.....</p>\n<p>  梅凝看着如雪她不会说出去吧！</p>\n<p>  当然不会因为如雪的眼神里迸射出一丝仇恨..这是她欠我的！</p>\n<p>  梅凝看着眼前的如雪感觉恐怖极了...</p>\n<p>  如雪笑了不过再也不是平常那种让人赏心悦目的笑..笑得甚至有些狰狞梅凝还记得那晚我跟你说的吗？你之所以败给爱儿是因为你一切都浮在表面真正的坏人是看不出来的要坏得坏在骨子里..明白吗？</p>\n<p>  梅凝颤抖着点点头...</p>\n<p>  这次我要看那个爱儿怎么死！死字咬得特别重..如雪攥着拳头...关节都泛着淡青色...</p>\n<p>  月华殿</p>\n<p>  如水扑在嘤嘤哭着...一旁的小可心疼的看着哭泣的如水心想：如水姑娘一定是在为爱儿姑娘难过...也没说什么转身走出月华殿...</p>\n<p>  许久如水终于停止了哭泣抬起哭花的脸蛋无力地斜靠在床框上...回忆起那些曾经.......</p>\n<hr>\n<p>  亲们终于知道怎么回事了吧............我相信你们肯定在为爱儿抱不平吧！我也是呢！！！</p>\n<p>  如水真是聪明一世糊涂一时哦....可把我们心爱的爱儿害惨了......呜呜........到底是什么让如水这样做？看下章就知道了.............</p>\n<p>  第三十六章 此情可待成追忆，只是当时已惘然（八）</p>\n<p>十二年前秋家堡</p>\n<p>  爹爹爹爹两个可爱的小家伙看到秋桐轩（如水和如雪的爹）回来了亲热地叫着一把扑进他的怀里...秋桐轩喜笑颜开一手抱一个两个小家伙像是约好一般同时在他们爹爹的脸上啄了一口...</p>\n<p>  哎我的宝贝儿女儿们.....看着眼前两张粉嘟嘟的脸蛋儿秋桐轩好不得意..</p>\n<p>  你们两个小捣蛋快下来让你们爹爹休息休息..云黎（如水和如雪的娘）从里屋走出来手里拿着一块毛巾帮秋桐轩擦着汗...</p>\n<p>  如水和如雪噘噘小嘴不情愿的从爹爹身上下来但还是围在秋桐轩的左右撒着娇..</p>\n<p>  爹爹你明天带我们出去玩好不好...如水拽着秋桐轩的衣袖不停的摇着..</p>\n<p>  你们那个小捣蛋想玩什么呢？他摸摸两个女儿稚嫩的脸..</p>\n<p>  我要去看庙会！如水说..</p>\n<p>  我要买冰糖葫芦吃！这次是如雪...</p>\n<p>  好好好...秋桐轩满口答应..</p>\n<p>  云黎无奈的看着这父女三人摇摇头都是你把她们都宠坏了！</p>\n<p>  次日</p>\n<p>  在喧闹而繁华的大街上人山人海...</p>\n<p>  卖冰糖葫芦喽！小贩的叫卖声吸引了如水和如雪的注意..</p>\n<p>  妹妹我们去买冰糖葫芦吃好不好？如水问道..</p>\n<p>  好好！！如雪高兴极了拍手表示同意...于是趁爹娘不注意两姐妹穿过密密麻麻的人群来到小摊前面..</p>\n<p>  买好了冰糖葫芦她们又走进人群里准备去找爹娘可是突然感觉有人在摸自己的头..刚转过头来就眼前一黑</p>\n<p>  再次醒来..</p>\n<p>  姐姐我们在哪儿？如雪问着如水..</p>\n<p>  妹妹我也不知道我们在哪儿但是肯定是坏人抓走了我们..如水说道...</p>\n<p>  坏人..如雪很害怕紧紧抱住如水那我们怎么办？姐姐我怕....</p>\n<p>  不用怕..如水摸着妹妹的头我们要想办法逃出去.....</p>\n<p>  如水看了看周围乌漆抹黑的不过前方的墙角旁边有些光线射进来...如水放开如雪沿着光线走过去..呀！原来这里有个洞！刚好够一个人穿过不过被杂草挡住了..妹妹快来！她呼唤着如雪..我们把这些草拔掉就可以逃走了..</p>\n<p>  嗯！说干就干两人用力的拔着草小手被草划出了小口子也咬牙坚持着..不久草被拔光了一个洞清晰的呈现在两人眼前..妹妹我先爬出去你跟着我..说着就从洞往外爬..可等她刚爬出去门忽然被打开了一个瞎了一只的男人看见这一幕马上冲上来抱住如雪你们居然敢跑！见如雪没办法跑出来如水便逃走了妹妹姐姐找人来救你！临走时留下一句话...</p>\n<p>  她不知他们已经到了离家乡很远的地方一个她们十分陌生的地方...</p>\n<p>  如水在街头徘徊了整整一天肚子饿得咕咕直叫...头发散乱的披在眼前脏兮兮的像个乞丐没人愿意理她..正在她最无助的时候..</p>\n<p>  小姑娘你怎么一个人在这儿？如水抬起头一张慈祥的脸出现在她面前...</p>\n<p>  我被坏人骗到这里来..还有妹妹..呜~~~说着呜呜哭了起来...</p>\n<p>  哎哟正可怜女人说道于是把如水带了回去...</p>\n<p>  女人对如水很好如水便夫人夫人的称呼她..她还有个女儿叫爱儿如水就把她当成了自己的亲妹妹..后来如水告知了妹妹的情况夫人便带她去找..可是那里已经空空如也如雪不见了....</p>\n<p>  现实中</p>\n<p>  如水叹着气..直到前几天看到如雪觉得非常熟悉果然她就是自己的亲妹妹...但她对自己说的第一句话却是..你为什么不来救我？</p>\n<p>　</p>\n";

