module.exports = "<hr>\n<p>title: 第10章\ndate: 2017-07-18 03:59:58\nwords: 3637</p>\n<h2 id=\"index-10\">index: 10</h2>\n<!-- deleteAbove -->\n<p>我看她的伤好得差不多了该回王府了别忘了易爱儿你可是本王的贴身奴婢！冰冷的语气充斥着霸气..</p>\n<p>  天可不可以在等几天？枫请求道..可是</p>\n<p>  不可以本王的命令你敢反抗？枫知道这已经不容商量看来他是真的生气了..如果违反他的命令只会害得爱儿更惨..</p>\n<p>  看出了枫脸上的愁容爱儿决定不让他为难..好奴婢跟王爷回去..语气里像是背负了很多..说完对枫扬起一个甜甜的笑..枫哥哥不用担心..</p>\n<p>  不知为什么听她叫他枫哥哥叫得那么亲密慕容临天心里竟有一丝怒火..控制不住地冲上前一把抓住爱儿的手往外拽疼得爱儿的眼泪不住的往下掉但她的眼神里却是满溢的坚强她看到枫的眼里全是心疼忍痛回敬他一个微笑让他安心只是她却忽略了自己身边的那个男人..他的眼神里又是什么...</p>\n<p>..............................................................................回到麒麟殿一把把爱儿扔在榻上爱儿吃痛的忍住快要溢出口的叫喊..大骂道王八蛋你不会温柔点吗！揉揉自己摔痛的小...</p>\n<p>  哼本王从不知道什么是温柔更不用说对你这个水杨花的女人！眼底的怒火更是藏不住..</p>\n<p>  水杨花？爱儿气急了对我就是水杨花怎么了？我就算全天下所有的男人也不会你！</p>\n<p>  哼是指冯枫？火气这么大怎么？这几天他没满足你吗？不仅是怒火甚至还有一丝其他的因素..说罢一下压了上去..</p>\n<p>  你骂我我随便你但我不准你侮辱我枫哥哥！爱儿大喊道..眼睛里满是痛恨.</p>\n<p>  没想到身下的女人居然在想别的男人..他决不允许！！还有那眼神她该有多恨我？她怎么可以用这样的眼神看我...你很恨我吗？天问道..</p>\n<p>  是！我恨你我恨死你了！我恨不得杀了你！爱儿像是疯了一般..</p>\n<p>  虽然明明知道答案是这样但从她嘴里出来为什么还会心痛...好那我就只有让你更恨我！说完一个挺身进入了她的身体...像是要穿过她的身体一般每一次都是那么用力..爱儿使劲咬着下嘴唇双手紧紧拽着床单.默默承受着晶莹的泪珠从眼角滑下...</p>\n<p>  看到她的痛苦天好想擦掉她的眼泪但是他做不到他是不会对任何人有感情的他绝对不允许！</p>\n<p>  倐的从她身上爬起来整理好衣物头也不回的迈出了麒麟殿向紫凝阁走去重重推开门房内一位女子迎上来紫色的衣裙的皮肤细长的蛾眉丹凤眼红红的薄唇眉心的花钿呈梅花型好不妖艳..</p>\n<p>  王爷你终于来了凝儿好想你..女子不由分说直接钻进天的怀里声音嗲得溺死人..</p>\n<p>  而慕容临天则一句话也没说撕拉一声撕开女人的衣服用手附上那柔软用力着..梅凝娇喘着那声音使天手上的动作更加放肆直接把女子压在榻上..</p>\n<p>  房外夕阳似血房内一片旖旎..</p>\n<p>  见慕容临天走出房间爱儿马上爬起来整理好自己身上的衣服跑出去却撞到一个人身上..</p>\n<p>  周管家..爱儿神色慌张的福了福身..</p>\n<p>  易爱儿前几天你受伤了我就没给你安排活儿现在你回来了是奴婢就该做奴婢该做的事！跟我来！说罢带着爱儿来到厨房..今天你必须把这些碗洗完否则没饭吃..</p>\n<p>  看着成堆的碗爱儿无可奈何只有蹲来一个一个的洗..</p>\n<p>  不知过了多久碗才洗完而此时天已经全黑了..易爱儿你洗完了？周管家进来问道..</p>\n<p>  嗯周管家我的房间在哪里？爱儿看着周管家..</p>\n<p>  后面柴房爱儿上次就是被关在那里顿时充满了恐惧..怎么？不愿意？周管家不屑的看着爱儿..</p>\n<p>  不不很满意..总之能有的住就不错了..给你的晚饭..说着周管家把手上端着的一碗稀饭两个馒头递给爱儿..看见爱儿的表情周管家不蹙眉该满足了毕竟王爷看你不顺眼能有的吃就不错了..是她易爱儿此时此刻还能奢望什么呢？拿着稀饭狼吞虎咽起来累了一天肚子早就饿得咕咕叫了。</p>\n<p>  又回到这个柴房里面除了一堆稻草什么也没有窗户是破的要是冬天一定会冻着..爱儿低头叹着气什么时候才能拜托这样的生活？或许永远也无法摆脱了吧..</p>\n<p>  今晚月明星稀是谁在叹息？</p>\n<p>第二十章 众里寻她千百度，蓦然回首（十二）</p>\n<p>天空刚刚泛起鱼肚白星星月亮还不愿褪去的时候...</p>\n<p>  柴房中一抹小小的身影形单影只怜</p>\n<p>  爱儿还在熟睡中长长的睫毛被晨风吹拂像是振翅欲飞的蝴蝶..咚咚..什么声音？现在她只想好好睡一觉..易爱儿快起来！一声大吼让爱儿从梦中惊醒。揉揉朦胧的睡眼..周管家？这么早有什么事吗？用手支撑着身体坐起来..</p>\n<p>  早？周管家像是很生气已经寅时了快去伺候王爷起床更衣！</p>\n<p>  为什么我要伺候他..心中忿忿不平..唉那个王八蛋干嘛要我当他的贴身奴婢不知道他安得什么心...当然这些爱儿只能在心里想..</p>\n<p>  紫凝阁</p>\n<p>  推门进去里面的场景让爱儿的脸唰的一下红到脖子根..梅凝正的趴在慕容临天胸前小手好不停逗弄着..而慕容临天则是着躺在手还不忘捏着梅凝的柔软...房间内到处是欢好过后浮靡的味道..爱儿尴尬的欲关门退出房间却被慕容临天一口叫住..</p>\n<p>  想去哪儿？过来替本王更衣.</p>\n<p>  什么？这种情况还叫我过去...爱儿这样想着不把天的十八代祖宗亲候了一遍..王..王爷这样不好吧..爱儿依旧背对着他们小手食指不停打着转....</p>\n<p>  有什么不好的再说了这种场面你也不会陌生吧..玩味的话让爱儿那本就红通通的脸更加赤色了..还有那魅惑的眼神虽然爱儿背对着看不到但她可以感受到背后有一双炯炯的眸子正看着她...该死的慕容临天..她在心里骂道..</p>\n<p>  看爱儿半天矗在那儿不动天似乎有些不高兴..水杨花的女人这时候给我装单纯..怎么还不过来！你要让本王等多久！</p>\n<p>  无奈爱儿慢悠悠的转过身走了过去拿起衣服给他穿上...她不敢抬起头怕对上他那炽热的目光..看着爱儿脸颊的两抹红晕还有眼里的羞涩慕容临天的心里竟然漾起一丝爱怜..他根本不知道他现在看爱儿的目光有多么的温柔..柔的都快流出水来..</p>\n<p>  梅凝看着天看爱儿的眼神他对自己从未有过这样的眼神那么温柔...不觉心生妒忌..眼前这个女子非除不可..</p>\n<p>  天抬起手想要爱儿白嫩的脸庞却被爱儿害羞的躲开王爷该上朝了..半举在空中的手有些尴尬只有放下来..端杏仁莲子粥给梅凝..说罢拂袖而去..</p>\n<p>  看到他走了爱儿总算松了一口气..要知道慕容临天那家伙在太压抑了...转过身看着不着寸缕的梅凝虽然都是女人但这样真空相见还是很别扭..</p>\n<p>  奴婢给您去端杏仁莲子粥..说罢借机退出房间...</p>\n<p>  低头快步向前走着突然撞入一个温暖的怀抱...枫哥哥！爱儿高兴极了可是很快又摆出一副可怜的小模样..枫哥哥我好想你...</p>\n<p>  呵呵才一天没见就这么想说着用手刮着爱儿的小鼻子..</p>\n<p>  对好想呢...爱儿撒娇道..把头埋在枫怀里蹭了又蹭..</p>\n<p>  呵呵爱儿你这是准备去哪儿呢？枫看着爱儿问道..</p>\n<p>  哦差点忘了慕容临天叫我端杏仁莲子粥给那个叫什么梅凝的..我正准备去呢..爱儿抬起头来..</p>\n<p>  爱儿你是说那个紫梅凝？冯枫问道..我也不知道我是听慕容临天叫她梅凝..看来昨天天是在她那儿那你快去吧..</p>\n<p>  嗯枫哥哥你没事的时候都要来看我哦..爱儿天真的笑着..转身跑了看来她已经把枫当作亲人了..</p>\n<p>第二十一章 众里寻她千百度，蓦然回首（十三）</p>\n<p>端了杏仁莲子粥回来枫已经不知去向不过爱儿倒很满足毕竟看到了枫哥哥心里暖洋洋的...推开门轻轻把一碗香喷喷的粥放在桌上..梅凝夫人杏仁莲子粥来了..</p>\n<p>  好香~肚子咕咕叫着爱儿这才想起来自己还没吃早饭..此时梅凝已梳洗完毕一个小丫鬟站在她的旁边想必是她的贴身丫鬟吧..</p>\n<p>  听到爱儿的声音梅凝心里的妒忌开始肆无忌惮起来虽然脸上还是十分平静..笑着坐在桌旁用勺子缓缓搅拌着那碗香味四溢的粥爱儿则悄悄咽着口水..</p>\n<p>  你叫什么名字？梅凝问道语气里净是酸味..</p>\n<p>  回夫人奴婢易爱儿..爱儿回答道..</p>\n<p>  梅凝舀起一勺粥递到嘴边突然眉毛一皱表情狰狞起来顺手把一碗粥向爱儿扔去..滚烫的粥洒在爱儿手上..梅凝又是一巴掌扇过去爱儿顿时跌倒在地碗的碎片扎入爱儿的手心...</p>\n<p>  --爱儿痛得眼泪都掉了出来鲜血汩汩从手心流出手背则是被粥烫得通红..她的半边脸被打得都肿了起来可想这一巴掌之重..</p>\n<p>  jian人粥这么烫你想烫死我吗！</p>\n<p>　</p>\n";

