module.exports = "<hr>\n<p>title: 第29章\ndate: 2017-07-18 03:59:59\nwords: 3519</p>\n<h2 id=\"index-29\">index: 29</h2>\n<!-- deleteAbove -->\n<p>这时候跟我装什么纯洁..来人！把这一对jian夫yin妇给我关进大牢！说罢头也不回的走了...</p>\n<p>  爱儿无力的坐在地上...眼泪不争气的流着..一直流着..寂静的典医阁偶尔会溢出一声似有似无的啜泣...</p>\n<p>第六十二章 昨夜星辰昨夜风（十四）</p>\n<p>大牢</p>\n<p>  爱儿爱儿....寒轻轻呼唤着目光呆滞的爱儿..可是她却毫无反应...</p>\n<p>  爱儿你不要这样好不好...看着这样的她寒的心里隐隐作痛..他真该死为什么要喝醉呢！</p>\n<p>  爱儿依旧是一动不动的看着前方就像是失去灵魂的躯壳...上次也是在这里爱儿经历了生与死的考验..难道这次又是一样吗？</p>\n<p>  他不相信我..不管是以前还是现在他都不愿意相信我..</p>\n<p>  哐铛牢房的锁打开了..慕容临天走了进来脸上仍是一脸冰冷..如雪紧紧的抓住他的手臂不停的往他怀里蹭看起来暧昧异常...</p>\n<p>  怎么样坐牢的滋味很好吧？慕容临天讽刺道...</p>\n<p>  你来干什么..爱儿看见如雪的动作火冒三丈..</p>\n<p>  本王当然是来看你们这对jian夫yin妇的咯！慕容临天面无表情可是从他那嗜血的眼神中可以看出他的怒火..</p>\n<p>  你闭嘴！爱儿猛地站起来恶狠狠的瞪着慕容临天..你侮辱我就算了但是不要侮辱我寒哥哥！</p>\n<p>  听到爱儿的话慕容临天顿时青筋暴起她居然还护着这个男人！寒哥哥叫得多亲密！</p>\n<p>  哟..如雪开始煽风点火了..爱儿姑娘这个时候还有心情护住自己的情郎真是情浓..看来爱儿姑娘的心里根本没有王爷嘛..说完又往慕容临天身上蹭了蹭...</p>\n<p>  爱儿此时已经是一肚子火..啪！爱儿一巴掌打在如雪脸上大吼道..你说什么！</p>\n<p>  你如雪摸着脸上火辣辣的五指印正欲打回去不过..王爷如雪只是为你报不平....说着装作委屈的哭起来..</p>\n<p>  爱儿以前从来没有打过人难道今天她真的是为了这个寒而打如雪？慕容临天想到这里气不打一处来..啪..五个裸的手指印印在爱儿的左脸上...谁让你打雪儿的！万一她肚子里的孩子出什么事怎么办！</p>\n<p>  孩子？爱儿的眼眶湿润了..他果然是在乎他和如雪的孩子的..那我们的孩子呢？</p>\n<p>  慕容临天铁定要让爱儿难看..伸手抹掉如雪脸上的泪水当着爱儿的面直直的吻上了如雪的唇....</p>\n<p>  这个吻吻得深而狠..没有感情只有恨...</p>\n<p>  看到这一幕..爱儿无力的跌坐在地上心中已是伤痕累累血模糊揪心的痛楚一阵一阵向她袭来...</p>\n<p>  许久慕容临天才放开如雪的唇不屑的瞥了爱儿一眼..本王的痛也要让你知道！一旁的如雪得意极了脸上的狰狞藏都藏不住...</p>\n<p>  慕容临天！你怎么可以这样对爱儿！她肚子里还有你的孩子！寒冲上来揪住慕容临天的衣领咆哮着..</p>\n<p>  慕容临天一掌打去寒立即被震开很远撞在墙上口吐鲜血...</p>\n<p>  寒哥哥..爱儿哭着跑过去抱起地上的寒眼泪不住的落下来...</p>\n<p>  该死的她居然抱着那个男人！慕容临天的眼睛里简直可以喷出火来..她这种水杨花的女人谁知道她怀的是谁的野种！甩下这句话慕容临天怒气冲冲的离开了大牢...</p>\n<p>  她怀孕了！如雪紧随其后贝齿咬得咔咔作响....</p>\n<p>  野种？哈哈哈哈爱儿仰天长笑着笑得肝肠寸断撕心裂肺...笑声在牢房里不觉回响像是哀鸣...</p>\n<p>  爱儿的心彻彻底底的死了...因为他...</p>\n<p>  我真的可笑..我居然以为我和他会幸福....爱儿一把擦干眼泪...看着寒寒哥哥你的伤怎么样？</p>\n<p>  无碍..寒慢慢坐起来..怜惜的用手轻抚爱儿的脸...对不起如果我不喝醉就不会让她们的计得逞...</p>\n<p>  寒哥哥不是你的错..爱儿谁也不怨..要怪只能怪我自己太过天真..你疗伤吧..爱儿平静的说道..就像什么也没发生过...</p>\n<p>  爱儿...寒不知道该怎样安慰她...坚强的外表下有多痛恐怕只有她自己能知道...</p>\n<p>  爱儿静静的坐在墙角处摸着自己的小腹...苍白的脸上露出一丝苦涩的笑...</p>\n<p>  第六十三章 昨夜星辰昨夜风（十五）</p>\n<p>枫从外面办事回来一回到王府就听说了这件事...不用多想慕容临天这时候肯定是别人说什么他都不会听的于是他便直奔大牢去探望爱儿...</p>\n<p>  走到大牢门口..</p>\n<p>  狱卒大哥等会给里面的那个易爱儿送饭的时候记得把这个放进去..梅凝说着从腰间拿出一包粉末..</p>\n<p>  这是？狱卒不解..</p>\n<p>  打胎药..梅凝说..</p>\n<p>  这可不行这话还没说完梅凝就塞了一大包银子到狱卒的手里..狱卒大哥帮个忙吧..</p>\n<p>  看着这么多银子就是他干一辈子也赚不到这么多！不两眼放光...是是是小人一定办妥...说完拿着银子和药走进了大牢...梅凝看着狱卒进去了便悄悄的离开...</p>\n<p>  等梅凝一走枫马上冲进大牢...</p>\n<p>  吃饭了吃饭了！狱卒吆喝着把两大碗白米饭放到爱儿所在的监牢..</p>\n<p>  爱儿吃饭吧..寒轻轻唤道..端起一碗饭准备递到爱儿面前..不对！他闻了闻...</p>\n<p>  这里面有打胎药...寒说道..</p>\n<p>  不愧是神医...枫笑着走进来..我还正准备提醒你们呢！爱儿呢？</p>\n<p>  寒无奈的王旁边一指..顺着寒手指的方向看去冰冷的墙角爱儿一言不发的坐着面无表情..枫叫人打开门走进去...心疼的看着这样的爱儿...</p>\n<p>  爱儿我是你枫哥哥...</p>\n<p>  枫哥哥...我知道..爱儿弱弱的说着...莞尔一笑..只是这笑却是那么的让人心碎...</p>\n<p>  枫轻轻的摸着爱儿的头..好爱儿..有委屈就哭出来吧...哭出来会好受些..</p>\n<p>  爱儿抬起头眼神黯淡的看着枫..</p>\n<p>  不爱儿不哭..爱儿说道脸上流露出的尽是坚强..她别无它法只能用坚硬的外表掩盖住那颗孱弱的心..</p>\n<p>  爱儿...这一句话让眼前两个男人的心碎了...</p>\n<p>  爱儿枫哥哥会还你清白的你放心吧...说完枫恋恋不舍的离开了...</p>\n<p>..............................................................................</p>\n<p>  紫梅凝...枫挡住梅凝的去路...</p>\n<p>  梅凝向他福了福身..不知冯统领找梅凝有何事呢？</p>\n<p>  哦..也没什么大事就是有些事情想向梅凝姑娘请教..枫礼貌的说道眼底却尽是鄙夷..</p>\n<p>  哦？梅凝不解的看着他..冯统领还有要向梅凝请教的？梅凝出身卑恐怕不能为冯统领指点迷津..</p>\n<p>  不..在下的问题只有梅凝姑娘能解答..枫说道..</p>\n<p>  但说无妨..</p>\n<p>  有什么是完全无色无味让人难以辨认出来的呢？枫笑着问..</p>\n<p>  ！梅凝心中一震却是故作镇静..这个嘛梅凝也不清楚..</p>\n<p>  是吗？那还有一个问题..不知梅凝姑娘的身家有多少呢？枫的嘴角邪邪的上扬着与他平常的笑容完全不同..</p>\n<p>  身家？不知冯统领问这个做什么？梅凝不懂..</p>\n<p>  没什么只是刚刚在牢房外在下碰巧看到梅凝姑娘塞了一大包白银给狱卒目的只是为了一包打胎药..在下猜想梅凝姑娘必定家财万贯！说着一把抓住梅凝的手腕..说！是不是柳如雪指使你陷害爱儿和麟太医的！</p>\n<p>  梅凝想挣脱岂知手被紧紧锢着..我不知道你在说什么！</p>\n<p>  还想狡辩？枫的手加大了力度..你帮助柳如雪是没有好处的！她那样心狠手辣的人将来总有一日会为了保住这些秘密而杀你灭口怎么这样你还是要追随她！</p>\n<p>  梅凝疼得眉头都拧了起来...不过她似乎有些动容...想到如雪的表情她的手段难免自己不会死在她手下..</p>\n<p>  可我凭什么帮助你！梅凝问道..</p>\n<p>  因为我可以保你的命你们的事情已经败露你迟早难逃一死若是你愿意说出真相我冯枫以命起誓绝对会保住你的命！枫严肃的说道..</p>\n<p>  从来没看见过这样的冯枫梅凝的确心动了...犹豫了一阵...好我说！</p>\n<p>  枫的脸上又露出了笑容好跟我来！说着拉着梅凝向慕容临天的书房走去...</p>\n<p>  第六十四章 昨夜星辰昨夜风（十六）</p>\n<p>书房</p>\n<p>  进去！</p>\n<p>　</p>\n";

