module.exports = "<hr>\n<p>title: 第15章\ndate: 2017-07-18 03:59:58\nwords: 3593</p>\n<h2 id=\"index-15\">index: 15</h2>\n<!-- deleteAbove -->\n<p>爱儿吓得睁大了眼睛真的？</p>\n<p>  嗯枫点点头..</p>\n<p>  爱儿低下头沉思了一会儿问出了她心中想问的那个问题那个...枫哥哥..如雪姑娘是什么人？</p>\n<p>  如雪姑娘？枫觉得很奇怪你问她干嘛呀？</p>\n<p>  那个我就随便问问..爱儿低下头手指头不停的搅来搅去..看到她的反常枫似乎知道了什么..</p>\n<p>  如雪姑娘原是青楼女子但她只卖艺不卖身精通各种弦类乐器尤其弹得一手古琴像是天籁之音而且她为人温柔善良又善解人意天就把她娶了回来王府每个人都很喜欢她现在是王府最得宠的小妾..说完后才发现爱儿贼眉鼠眼的盯着他..你看什么呢..</p>\n<p>  你说慕容临天和她是在青楼认识的？那你是他的统领加最好的朋友那你是不是也陪他进出青楼...爱儿诈的眼神让枫的心里发毛..</p>\n<p>  枫的脸上全部挂满了黑线.......这个..这个嘛嘿嘿你知道的嘛这..这是工作需要..豆大的汗滴从枫的头上滴下来...</p>\n<p>  见爱儿还是用这种眼神看着他..枫说再说了我又没怎么样嘛..哦对了我还有事我先走了..按现在的说法这叫落跑！</p>\n<p>  爱儿想着刚才枫的狼狈样咯咯的笑了出来...从枫的介绍相信如雪不会是梅凝那样的人只要她别再说我慕容临天一类的话就行...呼..爱儿吁了一口气..</p>\n<p>  门外</p>\n<p>  枫看着里面咯咯直笑的爱儿心里一紧..不能让她知道我们此行是为了抓住易天齐...真想爱儿能一直这样开心..该死的易天齐居然又让他们扑了个空..咔咔枫握紧了拳头</p>\n<p>第三十章 此情可待成追忆，只是当时已惘然（二）</p>\n<p>从那日之后几天相安无事的过去了...爱儿的伤在寒大名医的治疗下居然好得连疤都不留....</p>\n<p>  爱儿装束好后看着镜中的自己肤如凝脂般甚至比受伤之前还要光滑..心中暗喜不过伤好了也有个坏处..又要听那个周管家使唤来使唤去的还要伺候那个慕容临天...唉...和寒说了一声走出典医阁垂头丧气的向厨房走去...</p>\n<p>  爱儿姑娘..一个好熟悉的声音..转过头来眼前的人着实让爱儿一惊..梅..梅凝夫人..不会吧时间掐得这么准我伤刚好她又来！</p>\n<p>  爱儿姑娘说笑了梅凝现在哪是什么夫人我现在是如雪夫人的贴身丫鬟和你是一个级别的..今后叫我梅凝就行了...梅凝的语气像是完全换了一个人..</p>\n<p>  夫人..哦不梅凝你还是凶点跟我说话比较好你这样..我不习惯..爱儿悻悻的说..</p>\n<p>  不爱儿姑娘昨天如雪夫人跟我彻夜长谈让我明白了很多说着梅凝突然泪眼汪汪起来一把握住爱儿的手以前是我做错了爱儿姑娘你不计前嫌帮我求情我却还不领情对不起..爱儿姑娘你愿意原谅我吗？</p>\n<p>  看着眼前哭得梨花带雨的梅凝爱儿的心软了下来或许她是真的改变了..我原谅你快别哭了用手帮梅凝擦干泪水..只要你改了就行..</p>\n<p>  梅凝笑了爱儿姑娘你真善良..哦对了差点忘了正事儿如雪夫人找你..</p>\n<p>  找我？爱儿感到很奇怪..她只见过我一面为什么要见我..</p>\n<p>  走吧！梅凝不由分说拉着爱儿的手便走..</p>\n<p>  胜雪居</p>\n<p>  如雪爱儿姑娘来了..梅凝笑着把爱儿拉近房里..</p>\n<p>  叮叮当当珠帘被拨开发出阵阵乐音一位白衣女子徐徐走了出来笑靥如花..没错正是如雪...</p>\n<p>  爱儿姑娘来了如雪笑着说请坐..</p>\n<p>  不不如雪夫人爱儿不敢逾越..爱儿忙摇头道上次她可是因为一声如水姐姐被梅凝打个半死..她可不想再死一回..</p>\n<p>  听了这话如雪笑得更深了爱儿姑娘放心吧我如雪的朋友是不用理会这些的这里没外人..还有只有我们在的时候直接以名相呼就可以了不用叫我夫人..说话间已经拉着爱儿坐下..</p>\n<p>  爱儿只好却之不恭不知如雪找我来有什么事呢？</p>\n<p>  如雪倒了一杯茶给爱儿其实也没什么事上次我刚回来听到爱儿姑娘的一席话如雪为爱儿姑娘的胸襟深感佩服所以想好好和爱儿姑娘聊聊天..相信我能获益良多..</p>\n<p>  爱儿喝了一口茶感觉茶里有着青草一般的清新笑道如雪姑娘谬赞了爱儿只是一个小小的丫鬟说的懂得只是一些生活罢了哪能和如雪姑娘相提并论呢？先前就听人说如雪姑娘温柔善良善解人意今日一见果是名不虚传..</p>\n<p>  那些只是他们太过高赞了如雪不像爱儿姑娘好一个生活要知世间最难体味的就是生活..要谈生活何其容易呢..如雪笑着说..</p>\n<p>  爱儿能得如雪姑娘这一知己真是荣幸只是爱儿还有很多琐事还望下次能深谈一番..说完向如雪福了福身爱儿告退..</p>\n<p>  回到厨房碰到周管家周管家今天我要洗的碗在哪儿？爱儿问道..</p>\n<p>  周管家捋了捋胡子从今天起你不用洗碗了..</p>\n<p>  真的！爱儿听到后高兴的手舞足蹈起来..那我干什么？</p>\n<p>  如雪夫人特别吩咐过了你除了伺候王爷外每天负责给她送午餐就行了..周管家道..</p>\n<p>  真的？爱儿简直笑得合不拢嘴了如雪姑娘真是好人..</p>\n<p>  第三十一章 此情可待成追忆，只是当时已惘然（三）</p>\n<p>就这样每天中午给如雪送完午餐爱儿总会和如雪聊上半个时辰顺便品尝那青草味的茶..然后无事的爱儿就到如水那儿去串门晚上有时回到寒的典医阁去蹭饭..谁叫他那里的饭菜好吃呢！总的来说这几天爱儿的小日子过得真不错！不过她为什么总觉得心里有一块儿是空荡荡的？却不知少了什么..</p>\n<p>  枫我最近总觉得心里空荡荡的可是不知道少了什么..慕容临天一边批阅奏折一边说..</p>\n<p>  枫转转眼骨碌是吗？我也是最近老陪着你忙好几天没见我的爱儿了还怪想这个小丫头的...</p>\n<p>  慕容临天一怔感觉心中那个空空的地方被触碰了..</p>\n<p>  感觉到慕容临天的不对劲枫心里暗自偷笑着准备接着添油加醋..哎这个小丫头平时一见我总是缠着我撒娇‘枫哥哥枫哥哥’的叫个不停真是够烦的可这见不到了却又想你说我怎么这么奇怪呢？不过人就是这样..人在你面前时就是不珍惜折磨来折磨去可是没看到呢心里又空荡荡的...唉..喂！你去哪儿！等枫叽里呱啦的说完慕容临天已经冲出门外..</p>\n<p>  刚好碰上从胜雪居出来的爱儿..</p>\n<p>  看着眼前这个高大的身影爱儿感觉心中的空荡一下子被填得满满的...</p>\n<p>  你在干什么？慕容临天问道..</p>\n<p>  奴..奴婢给如雪夫人送饭..爱儿回答道声音都有些颤抖..</p>\n<p>  慕容临天二话不说直接走上前来把爱儿打横抱起向麒麟居走去</p>\n<p>  一把把爱儿压在身下..此时的她肤如凝脂脸颊泛着红晕小嘴粉粉的嘟着重要的是那干净的眸子的只有他的倒影...</p>\n<p>  爱儿眼中的他如刀削般的轮廓浓黑的剑眉的鼻梁xing感的嘴唇还有那深不见底的眼眸里满是温柔..爱儿看得痴迷了痴迷得忘了反抗！待自己已经的时候感到皮肤的丝丝凉意低头一看.....</p>\n<p>  ！！！！爱儿尖叫着赶快用手挡住自己的重要部位...你这个大！！！！</p>\n<p>  慕容临天揉揉快被爱儿震聋的耳朵...然后抓开爱儿干扰他视线的双手锢在身体两侧...本王又不是没看过挡什么挡..</p>\n<p>  你爱儿脸红得像熟透的苹果..</p>\n<p>  你不说话的时候比较可爱..什么她没听错吧可爱？从慕容临天嘴里说出来她还真不信！正准备开口反驳.....结果......</p>\n<p>  唔唔唔嘴被堵得死死的什么东东堵的呢？嘿嘿.......</p>\n<p>  看着眼前突然放大的脸爱儿不停的动着反抗着可是全身上下都被箍的死死的毫无还手之力...</p>\n<p>  你的反抗等于是玩火慕容临天凑到爱儿耳边说道温热而酥麻的气息使得爱儿微微颤抖..感受到身下人儿的反应慕容临天邪邪地勾起嘴角.女人的身体永远不会说谎...再次吻上她的嘴时急时缓的着..爱儿感觉身上的力气一丝丝被抽走直到最后一丝理智也被抽走的时候她..彻底沉沦了浮靡的气息充满了整个房间两具裸的rou体在一起....</p>\n<p>  于是爱儿一天的行程有所改变本来应该给如水的那部分时间现在变成了慕容临天和她的双人秀....</p>\n<p>  唉....爱儿叹着气..她这个贴身丫鬟当得可真够贴身的</p>\n<p>第三十二章 此情可待成追忆，只是当时已惘然（四）</p>\n<p>可以行动了吧？</p>\n<p>  当然要做得不留下任何蛛丝马迹！</p>\n<p>  放心吧！</p>\n<p>................................................................................</p>\n<p>胜雪居</p>\n<p>  爱儿你来了！</p>\n<p>　</p>\n";

