module.exports = "<hr>\n<p>title: 第33章\ndate: 2017-07-18 04:00:00\nwords: 3736</p>\n<h2 id=\"index-33\">index: 33</h2>\n<!-- deleteAbove -->\n<p>听到这个消息菲雪和易天凌都很激动..天齐快告诉我们爱儿在哪儿？</p>\n<p>  爱儿她正在和四王爷那里...易天齐说道..</p>\n<p>  什么？四王爷！那太好了！菲雪高兴极了..易家跟四王爷是世交现在爱儿在四王爷那儿他们就放心了..</p>\n<p>  不过...四王爷行宫的人已经全部死于非命了！易天齐冷笑着眼睛里流露出前所未有的凶狠...</p>\n<p>  什么！是谁这么大胆敢杀四王爷！易天凌很气愤..</p>\n<p>  易天齐笑着站起来拔出自己的佩剑..浓烈的血腥味弥漫开来...剑上的鲜血还未完全凝固顺着剑只向下滴..</p>\n<p>  是你？易天凌不敢相信的看着眼前的弟弟...你为什么要这么做？</p>\n<p>  为什么？易天齐笑了...为什么你应该是最清楚不过了...是你害死他们的！说着继而看着菲雪....想当年我和菲雪情投意合可你却叫四王爷帮你和菲雪赐婚！因为你是大哥依照长兄先行嫁娶爹居然答应了！可是菲雪根本不爱你她是爱我的！</p>\n<p>  你错了！菲雪怒目着易天齐..我一直以来喜欢的就是你大哥易天凌不是你！这些只是你一厢情愿罢了！当年公公就是看出你心术不正思想偏激所以才用传统教条约束你让我和凌哥哥有终成眷属..凌哥哥知道你会不同意怕你闹出事才去请王爷赐婚毕竟王爷是有权的..可是没想到时至今日你居然死不悔改！还杀死了四王爷你真是无可救药！</p>\n<p>  菲雪...易天齐抓住她的双手满目深情..你为什么要这么说呢？你明明是爱我的..对不对..菲雪我这么爱你我们才是天生一对...</p>\n<p>  可是在菲雪看来这一切都是那么的恶心！一把甩开他的手不是！易天齐我再说最后一次！我不爱你！我从来都没有爱过你！以前没有以后也没有永远都不会有！</p>\n<p>  别说了！！易天齐快被逼疯抽出带血的剑手起剑落...</p>\n<p>  菲雪！！易天凌亲眼看着易天齐的剑贯穿菲雪的身体鲜红的血一股股从胸口喷出来在天空散开像一场玫瑰花的葬礼....他冲过去抱住菲雪的身体...大口大口的血从菲雪的嘴里吐出染红了易天凌的衣裳...</p>\n<p>  凌...凌哥哥...我快不行了...菲雪忍住口中的血腥...</p>\n<p>  不会的不会的！菲雪你不会有事的...快别说话了..好好保存体力..易天凌抓住菲雪冰冷的手他知道自己是在自欺欺人..</p>\n<p>  凌哥哥...听..听我说...菲雪憋住最后一口气...菲雪这一辈子...能..能和凌哥哥在一起..死..死而无憾...愿..来生..还能和你..做夫..夫..妻..最后一个字音落..苍白的手从易天凌的手中滑落下来...</p>\n<p>  菲雪！！易天凌大喊..两行热泪灼灼流出....</p>\n<p>  她...她死了...易天齐脸色苍白的看着自己滴血的剑...是我杀了菲雪....不！易天凌是你害死菲雪的！我要你偿命！说完拿着剑向易天凌刺去...易天凌起身招架..可是却不是易天齐的对手..几招便被打趴在地...</p>\n<p>  菲雪..菲雪..易天凌已经站不起来他努力的向前爬爬到菲雪身边..握住她的手..看着菲雪苍白的小脸..易天凌笑了...菲雪..我要这样牢牢抓住你..我们下辈子..还..还是夫妻..说着从腰间抽出一把匕首对着自己的心脏</p>\n<p>  <del>~</del><del>~~易府顿时乱成一团..惊叫着呐喊着...不敢再看这漫天血花飞舞的场面.....易天齐的双眼因愤怒成了鲜红..给我杀！一声令下血洗整个易府...鲜血溅到躲在椅子后的如水的脸上..</del>她因害怕惊叫出声...顿时引起易天齐的注意..血一滴滴顺着剑地下成了一条长长的细线向如水这边来...</p>\n<p>  如水惊坐在地上慢慢的向后退着....可是后面..是堵墙！看着易天齐的剑扬了起来她紧紧闭上双眼....</p>\n<p>  慢着！萧淑阻止了易天齐的动作...听说她和爱儿玩得很好得把她留着..</p>\n<p>  你的意思是？易天齐不解..</p>\n<p>  四王爷的儿子不是没死吗？我们好好抚养爱儿等她长大想办法让他们相爱然后告诉爱儿是那个小子杀了他爹娘好让他们痛苦一辈子！你觉得怎么样？萧淑脸上藏不住的狰狞..</p>\n<p>  易天齐沉思了一会儿..好！转身看着地上瑟瑟发抖的如水..记住今天你看到的一切绝不可以跟任何人提起！你懂了吗？眼神凌厉的像是可以杀死人..</p>\n<p>  懂...懂...如水颤抖着说...</p>\n<p>  现实</p>\n<p>  事情就是这样...如水的眼眶已经红透了..对不起爱儿我到现在才告诉你真相...</p>\n<p>  一旁的爱儿眼睛睁的像铜铃一样大...小脸苍白的没有一丝血色...怎..怎么会呢？如水姐姐..你骗我的对不对...</p>\n<p>  爱儿..我知道你很难接受..可是..事实就是这样....如水安慰道...</p>\n<p>  不等她安慰爱儿发狂的向书房奔去...不可以..怎么会这样...</p>\n<p>  慕容临天...你等我...你不要喝那碗粥....</p>\n<p>  爱一旦蒙上恨的因素就会遭遇万劫不复..从古至今都是如此..自一开始命中就已经注定..只是等知道的时候已晚</p>\n<p>  自古多情空余恨此恨绵绵无绝期...</p>\n<p>  第七十一章 我欲与君相知，长命无绝衰（五）</p>\n<p>啪一声门被爱儿推开....</p>\n<p>  慕容临天对于这突如其来的人有些惊讶..不过看起来却是一副平静..</p>\n<p>  爱儿气喘吁吁的走进来可在看到桌上那空空如也的碗时...她呆住了...揪心的痛铺天卷地的向她袭来..她不能自己的皱着眉晶莹的泪珠在眼眶里打转....</p>\n<p>  真的...晚了....</p>\n<p>  你来了？慕容临天温柔的说道..</p>\n<p>  爱儿没有说话她扶着墙吃力的走到慕容临天桌前...</p>\n<p>  一泓清泉似的眸子里满是复杂...让人不能看透..</p>\n<p>  就这样一直静静的看着彼此在对方的眼眸中看到只属于自己的倒影...</p>\n<p>  有毒....半晌爱儿的嘴里才迸出一句话...</p>\n<p>  我知道...慕容临天轻抚上她的脸深不见底的黑色眼眸里满载着爱怜...死在你手上我无憾...</p>\n<p>  爱儿痛苦的抬起乌羽扇般的睫毛..盈盈的水花痛诉着爱恨纠葛..啪嗒..啪嗒..酝酿已久的泪像断了线的珠子狠狠的砸着地面...誓要拍打掉一切的一切...你这个笨蛋！为什么要吃！爱儿歇斯底里着拍掉慕容临天的手..</p>\n<p>  爱儿..我...如果我死了你会快乐的话我愿意...慕容临天的眼睛里有着无穷无尽的爱..只是这句话没有说出来...</p>\n<p>  你根本就是个大坏蛋！爱儿的小脸被泪水铺花了...手用力的锤着慕容临天的胸膛...你只会让我难过！让我痛苦！我恨你！我恨你！我恨死你了！拳头像雨点一般落在慕容临天胸前..慕容临天不躲闪只是任由她心中的痛苦..</p>\n<p>  她真的好恨好恨....是她杀了他...是她杀了一个无辜的人....是她亲手杀了她最爱的人...是她亲手把自己送进万劫不复...</p>\n<p>  打累了...爱儿无力的向后退去...重重的撞在墙上的画卷上...咔<del>~</del>一声作响...暗格打开了...这是...一个暗红色的锦盒...上面刻满了桃花...</p>\n<p>  爱儿...不要看....慕容临天轻声道...他不想让爱儿知道他这么多年来心中的秘密...</p>\n<p>  难道这就是他心里的那个女人？爱儿的心揪痛着...或许是出于女人特有的敏感...她打开了...是含着一种怎样的心情打开？恨亦或是爱？</p>\n<p>  粉色的香巾静静的躺在锦盒里....正中央一朵娇艳欲滴的桃花狠狠刺痛了爱儿的眼睛...仿佛此刻是被毒药吞噬...</p>\n<p>  对不起....慕容临天低下头...</p>\n<p>  这是什么..爱儿孱弱的问...而心中却是心痛得无以复加...</p>\n<p>  是我永远的牵挂...慕容临天回答心中有些隐隐作痛...她终究还是知道了...</p>\n<p>那么是不是我死了她的心里也会有羁绊...</p>\n<p>  疯了！她疯了！</p>\n<p>  两颗的心就这样被无情的打破...</p>\n<p>  被蒙蔽的爱撕扯的心...黑暗的背后无比狰狞的真相摧残了一对生死恋人..</p>\n<p>  残忍的现实让人无法接受的纠合....只是因为身陷阴谋..只是因为恨毁了他们的一生...</p>\n<p>  慕容临天...爱儿抬起头黯淡的眼神对上慕容临天的眼...如果可以...我情愿从来不曾遇见你...这样我就不会如此爱你...</p>\n<p>  爱儿...慕容临天的手不觉揪住胸口的涌痛..对不起....</p>\n<p>  傻瓜...该说对不起的是我...如果我们两个里只能活一个的话....爱儿用手抹掉眼泪冲出了书房留下一室的悲怆也留下了无尽的遗憾...</p>\n<p>第七十二章 我欲与君相知，长命无绝衰（六）</p>\n<p>一路狂奔..爱儿想挥洒自己所有的痛眼中的晶莹随风飘洒了满地..像一条长长的线一直延伸到天边...</p>\n<p>  哐当..爱儿打开牢房的锁...干爹爱儿救你出去！</p>\n<p>　</p>\n";

