module.exports = "<hr>\n<p>title: 第41章\ndate: 2017-07-18 04:00:00\nwords: 3246</p>\n<h2 id=\"index-41\">index: 41</h2>\n<!-- deleteAbove -->\n<p>易爱儿慕容临天我要你们死！</p>\n<p>  当日晚和爱儿翻云覆雨之后...看着熟睡的爱儿慕容临天轻吻着她的额头..爱儿我一定要想办法治好你的眼睛就算要我死我也愿意..起身穿好衣服向月华殿走去...</p>\n<p>  天你终于来了..寒说道..</p>\n<p>  你示意我来是不是因为爱儿的眼睛？慕容临天着急的问..</p>\n<p>  是有一个办法可以治好她的眼睛...寒平静的说道..</p>\n<p>  什么？</p>\n<p>  她眼睛上的毒靠我们是无法逼出来的只有靠她自己..寒轻轻坐下..</p>\n<p>  你说清楚一点..天知道不是这么简单..</p>\n<p>  要撕心裂肺的痛促使她血气上涌直达眼睛流出血泪方可逼出此毒..寒喝了口水意味深长的说道..只是像爱儿那样坚强的人恐怕很难做到..</p>\n<p>  慕容临天别过头沉思着....</p>\n<p>  窗外如雪将这一切都清楚的听在耳里...</p>\n<p>  次日月华殿外如雪听到了如水和爱儿所说的所有...她怔住了..看来自己真的错怪了姐姐很多年...她当年真的找过我...如雪一咬牙转身向书房走去她知道这个时候慕容临天一定在那里果不其然</p>\n<p>  慕容临天..她推开门撕下人皮面具..</p>\n<p>  是你你来干什么？慕容临天看到她语气变得冷淡...</p>\n<p>  怎么？看到我很惊讶吗？或者是很讨厌？那你杀了我！她叫嚣着..</p>\n<p>  我不会杀你..慕容临天淡淡的说..</p>\n<p>  为什么？如雪的眼中闪过一丝异常..</p>\n<p>  因为伤害你等于伤害了我身边的所有人...你是秋如水的妹妹...慕容临天依旧很冷静...</p>\n<p>  你以为这样说我就会放过你吗？如雪说道..</p>\n<p>  那你就杀了我...慕容临天的语气不起一丝涟漪..</p>\n<p>  什么？如雪真的不明白她攥紧拳头...你到底有没有喜欢过我？</p>\n<p>  没有..慕容临天毫不犹豫..</p>\n<p>  那你当年为什么要把我赎出来还娶我！如雪咆哮着..</p>\n<p>  因为我很欣赏你我也认为你是个很好的女子..慕容临天并不隐瞒..</p>\n<p>  如雪使劲的咬着下嘴唇从袖子里抽出一把匕首比着慕容临天的脖子..你真的不怕死？</p>\n<p>  只要你不伤害爱儿..慕容临天冷冷的说闭上了眼睛...</p>\n<p>  如雪不得不承认她输了输得很惨...她输给慕容临天对爱儿的爱输在如水对自己的姐妹情上...这么多年来的算计居然都只是为了她自己的错买单...她苦笑着笑自己的愚蠢...</p>\n<p>  如雪收回匕首..我可以帮你治好爱儿的眼睛...</p>\n<p>  闻言慕容临天立刻睁开眼睛注视着如雪..怎么治？</p>\n<p>  如雪将匕首往墙上一插结果匕首居然缩了进去..慕容临天的眼底涌现出一丝惊异..</p>\n<p>  明天你只要配合我演戏就可以了...如雪说完离开了书房.....</p>\n<p>第十二章 执子之手，与子偕老（大结局）</p>\n<p>一年后桃花园</p>\n<p>相公..云梦娘大着个肚子从木屋里走出来..枫看到了赶紧冲上去扶住梦娘..梦娘小心点快回去躺着！孩子都七个月了别乱动...</p>\n<p>相公人家都在躺好久了你就让我出来透透气吧好不好嘛...梦娘撒着娇...</p>\n<p>好吧好吧..枫可受不了她的撒娇攻势...怎么女人怀了孕就变得这么奇怪呢？现在一天到晚跟自己撒娇的梦娘还真是小女人...</p>\n<p>  .................................................................................</p>\n<p>  如儿...寒脱下自己的外套披在如水的身上...这里风大小心着凉了..</p>\n<p>放心吧我不冷..如水笑着对寒说..轻轻靠在他的肩膀上...寒我觉得好幸福..我以前根本不敢相信我能跟你在一起上天待我不薄...</p>\n<p>如儿..其实我才应该说这句话...寒微笑着...</p>\n<p>  ................................................................................</p>\n<p>  麟嫣你要快点长大哦！等你长大我就娶你！小天抱着一岁大的嫣儿满脸温柔的笑容...</p>\n<p>哥哥...你觉得是我长得美呢还是嫣儿长得美？小爱问道..</p>\n<p>当然是嫣儿了！你那么肥！小天戏谑着说..</p>\n<p>吼！你重色轻妹！说着小爱伸出肥嘟嘟的小手去掐小天的脸...</p>\n<p>嫣儿！我们快跑女魔头来了！小天飞一般的抱着嫣儿到处躲避小爱的追杀...</p>\n<p>  ................................................................................</p>\n<p>  唉我的真命天子什么时候出现呢？如雪虐待着地上的狗尾巴草...看着他们都成双成对心里还真是不好受...</p>\n<p>不管了我闭上眼睛数三下第一眼看到谁我就嫁给谁！说做就做！闭上眼睛..</p>\n<p>一、二、三....</p>\n<p>这位小姐你在这儿干嘛呢？一个穿着奇怪但长相不赖的男人正盯着如雪...</p>\n<p>我要嫁给你！！如雪蹦进男子的怀里..</p>\n<p>？男子不敢相信21世纪的人都没这么开放呢！这古人也太Open了吧！！（一个不小心穿越过来的人...）</p>\n<p>  ................................................................................</p>\n<p>  坐在桃花林的尽头看着如血的残阳两人的影子被拉得很长很长...</p>\n<p>天能和你在一起我此生无憾..爱儿头上插着一朵娇艳欲滴的桃花可却比不上她的容颜的百分之一...</p>\n<p>把爱儿搂在怀里慕容临天的嘴角一直挂着迷人的微笑...紧紧握住爱儿的手那你愿意和我就这样一直到老吗？</p>\n<p>愿意..爱儿甜甜一笑...</p>\n<p>  有道是第一次是偶然第二次是必然第三次是命中注定..</p>\n<p>爱恨情仇生生死死是是非非...或许这些只是爱情路上的小小插曲真正刻骨铭心的爱定要经过千锤百炼...这一切乃是命中注定..</p>\n<p>  时光穿不断流转在从前</p>\n<p>刻骨的变迁不是遥远</p>\n<p>再有一万年深情也不变</p>\n<p>爱像烈火般蔓延</p>\n<p>记忆是条长线盘旋在天边</p>\n<p>沉浮中以为情深缘浅</p>\n<p>你再度出现我看见誓言</p>\n<p>承诺在水天之间</p>\n<p>回头看不曾走远</p>\n<p>依依目光此生不换</p>\n<p>要分散不习惯</p>\n<p>怎么算都太难</p>\n<p>分开之后更勇敢</p>\n<p>愿这爱世代相传</p>\n<p>喝不完水不让你如烟</p>\n<p>前尘再怀恋望剑如面</p>\n<p>挥舞的瞬间别再闭上眼</p>\n<p>错过惊世的依恋</p>\n<p>回头看不曾走远</p>\n<p>眷恋一人流连忘返</p>\n<p>多少汗够温暖你哭喊我呼唤</p>\n<p>听清耳边的呢喃</p>\n<p>别害怕风轻云淡</p>\n<p>回头看不曾走远</p>\n<p>依依目光此生不换</p>\n<p>要分散不习惯</p>\n<p>怎么算都太难</p>\n<p>分开之后更勇敢</p>\n<p>愿这爱世代相传...</p>\n<p>................................................................................</p>\n<p>  偶的亲们今天《### 命中注定的王妃》大结局了！（鼓掌！！！）不知大家还满意不？妍嬭很快就要上高中了这三年可能大家都会忘记我哦但是希望大家期待三年后妍嬭会为大家推出更加精彩的言情更加纠结的爱恋！妍嬭绝不食言！到时候看到妍嬭婲婔这个名字可别不知道是谁哈！</p>\n<p>感谢支持妍嬭文文的所有亲最后送大家一句话：</p>\n<p>这个世界有爱不一定会完美但是没有爱就万万不会完美...把真挚的爱送给所有人！</p>\n";

