module.exports = "<hr>\n<p>title: 第2章\ndate: 2017-07-18 03:59:57\nwords: 3599</p>\n<h2 id=\"index-2\">index: 2</h2>\n<!-- deleteAbove -->\n<p>我要玩老板来十个圈圈！老板递过十个圈圈堆出满脸的笑容似乎是不怀好意..</p>\n<p>  看我的！只见爱儿使出一招天外飞仙立马套中一个青花瓷的小酒杯再使出一招天女散花剩下的九个圈立即脱手而出在天空布成个九宫格状再一起落下同时套中了九样小玩意儿..</p>\n<p>  耶！！全中！！爱儿满脸的笑容像是快要满溢出来...</p>\n<p>  那个满脸堆笑的老板的脸上惊成了三个大大的○不敢相信眼前发生的一切..</p>\n<p>  老板再来十个圈圈！爱儿笑道..</p>\n<p>  什么？老板惊呼着瞪大了眼睛再来十个？这位小姑娘你别说笑了。</p>\n<p>  爱儿行了玩得差不多了就拿走刚才套中的东西吧。</p>\n<p>  .....那好吧爱儿似意犹未尽的样子俯身抱起地上的那些玩意儿正欲转身离开突然..</p>\n<p>  怎么？二位姑娘赢了这么多东西就想走？说着露出一副诈的表情。</p>\n<p>  老板想赖账？爱儿不悦道..</p>\n<p>  哼想走也要问问我豹大爷同不同意兄弟们上！刚落下话音几个手握木棍的打手便冲上前来惊起一阵惊慌围观的人们纷纷逃散..</p>\n<p>  如水姐姐你退后我来教训他们..爱儿冲上前一个横扫两位大汉猛的倒地见来势汹汹掏出腰上的小红鞭一抽剩下的几个大汉都被抽得哇哇大叫爱儿正得意着..</p>\n<p>  爱儿~~一转身糟糕那个诈的老板正用匕首抵着秋如水的脖子..</p>\n<p>  突然感觉脑袋后面很痛眼前一黑</p>\n<p>  第二章 取次花丛懒回顾，半缘修道半缘君（二）</p>\n<p>唔爱儿缓缓伸手摸了摸后脑勺..！好痛！猛然睁开眼四周黑漆漆的只有高处的墙壁上开了一个圆形的小洞一束阳光射进来能看见许多细微的浮尘在光线里飘荡...</p>\n<p>  这里是哪里？我怎么会在这儿？爱儿自言自语闭上眼回忆着....我和如水姐姐在京都街我在套圈圈然后老板赖账叫了打手如水姐姐被老板挟持我眼前一黑接着就什么都不知道了！如水姐姐！如水姐姐！你在哪儿？爱儿大声喊着可是没有人回应...</p>\n<p>  突然门开了猛烈的阳光射进来让爱儿睁不开眼过了好一会儿才适应这强光慢慢睁开眼睛咦？那是？怎么？小姑娘这么快就不认得我了？那诈的声音让爱儿顿时惊觉过来..是你！你这个黑心的老板！快说你把我如水姐姐藏哪儿去了？爱儿没好气儿地说。</p>\n<p>  放心吧她好着呢！这么个美人儿我当然不会对她怎么样了...嘿嘿嘿嘿....</p>\n<p>  收起你恶心的笑声我警告你赶快放了我们不然姑奶奶让你尝尝鞭子的滋味儿！说着伸手去掏腰上的小红鞭..咦？我的鞭子呢？王八蛋你把我鞭子藏哪儿去了？</p>\n<p>  哟哟哟这小妞还挺呛我喜欢！嘿嘿~~~那满脸的笑真是两个字欠揍！</p>\n<p>  死胖子你活得不耐烦了！快放了我们！爱儿咬牙切齿道...</p>\n<p>  兄弟们把她给我绑起来绑到我去今天我要和两位美人共度良宵..美人儿现在你没了鞭子看你有何能耐！</p>\n<p>  共度良宵？爱儿咬着牙咬得那贝齿咔咔作响..哼姑奶奶今天送你上黄泉！你跟牛头马面去共度良宵吧！！</p>\n<p>  看那两个大汉已经冲上前来爱儿赶快站起长得很猥亵的壮汉一拳过来被爱儿灵活地躲过..</p>\n<p>  喂你们看着点儿可别伤了美人的脸！豹二爷喊着..</p>\n<p>  爱儿右拳向壮汉眼睛攻去却被壮汉一下子抓住手腕一个反手爱儿便转过身子手被牢牢钳制住看着另一位大汉拿着粗绳过来了爱儿灵机一动抬起脚重重踩了下去..只听哇！一声身后的壮汉松了手抱着脚大喊大叫..撞到了拿绳子的大汉两人一起摔倒在地...</p>\n<p>  饭桶！都是饭桶！豹二爷气得直跺脚！</p>\n<p>  爱儿从大汉手中夺过绳子冲向豹二爷三下五除二就把那胖子五花大绑活像一个粽子..然后抽出胖子腰间的匕首抵住他的脖子银白的匕首发着寒光令胖子连大气都不敢出一口...</p>\n<p>  爱儿一拳揍向胖子的肚子..</p>\n<p>  哎哟姑奶奶饶命..胖子痛得直哼哼赶忙求饶..</p>\n<p>  死银虫！（又换外号了）快说如水姐姐在哪儿？爱儿死死瞪着他..</p>\n<p>  在在另一间柴房胖子哆嗦着不敢有所隐瞒...</p>\n<p>  快带我去找她告诉你死银虫你要是敢玩什么花样我手上的匕首可是不长眼的！爱儿恐吓着</p>\n<p>第三章 取次花丛懒回顾，半缘修道半缘君（三）</p>\n<p>爱儿挟持着胖子一步步退出门外在胖子的带领下左拐右拐到了一间茅草屋外..</p>\n<p>  爱儿一脚踢开门只见秋如水的手脚被捆绑着眼睛里充满了惊恐但顿时转变为惊喜...</p>\n<p>  死胖子叫人给她松绑！</p>\n<p>  是是你们这群笨蛋还不快松绑！！胖子对下人怒吼着...</p>\n<p>  下人们赶快给秋如水松了绑如水姐姐到我旁边来我保护你..</p>\n<p>  嗯！秋如水说不出的感动....</p>\n<p>  不一会儿她们挟持着胖子退到了一片竹林而那些下人也是步步紧逼爱儿一直瞪着他们丝毫不敢懈怠因此没注意到胖子脸上不经意流露出的诈...</p>\n<p>  突然不知何时身后出现了胖子的爪牙飞出一脚爱儿便向前扑倒在地继而爪牙抽出的匕首向地上的爱儿刺去...</p>\n<p>  爱儿吓得发了愣腿脚都不听使唤在原地丝毫也挪不动步..像一只待宰的羔羊....</p>\n<p>  爱儿！在这千钧一发的时刻秋如水奋然扑身过来挡在爱儿前面只听嚓一声匕首刺进了如水的胸膛...汩汩鲜血流出染红了如水的绿色长裙像是一朵盛开的血莲是那么的刺眼..</p>\n<p>  如水姐姐！！！爱儿大叫着刚才还好好的人儿因为她现在居然成了这样她怒目着瞪着周围的一切人像是要用眼神将他们凌迟...看着这样可怕的目光众人连动都不敢动爱儿抬起手将手中的匕首插入爪牙的肩膀那人顿时痛得惨叫连天..抽出匕首上面沾满了殷红的鲜血一滴一滴滴落到地面....</p>\n<p>  你们谁再过来这把匕首就不只是插进肩膀这么简单而是心脏！众人吓得不敢再吱声....</p>\n<p>  爱儿抱起地上的如水用尽全力将她带离这个地方...</p>\n<p>  爱儿带着如水来到一个破庙一路上并没有看到有人追来想必她们目前是安全的..</p>\n<p>  将如水放在干燥的稻草上如水的胸前已是一片血红.....爱儿的身上脸上沾染了许多血而那眸子却像万里无云的天空一样干净得不含一丝杂质晶莹的泪珠一颗颗滚落下来....</p>\n<p>  都是我！都是我害的！如水姐姐你为什么要这么做.....爱儿呜咽着...</p>\n<p>  不不是不是你的错如水的额头上豆大的汗珠滑落脸色惨白像一碰即碎的陶瓷娃娃艰难而缓慢的说声音像游丝一般因因为我比你大我我要保护你......</p>\n<p>  如水姐姐.....爱儿的眼泪像决堤的洪水倾泻而出..你怎么这么傻....你刚才为什么不逃走....</p>\n<p>  我我要保护你....同样的五个字同样虚弱的语气此时却显得那么掷地有声...</p>\n<p>  啪啪啪佛像后走出了一个一袭白衣的男子头发是迷人的银白色那蓝色的眼睛像是迷惑你心灵的魔法的鼻梁薄薄的的双手天只能说这人长得太妖孽了！！</p>\n<p>  好一对姐妹情深..嘴角带出一丝狡黠的笑..</p>\n<p>第四章 取次花丛懒回顾，半缘修道半缘君（四）</p>\n<p>你是谁？爱儿警觉起来..虽然这人长得这么这么祸国殃民但也不能就此断定他是好人..</p>\n<p>  姑娘不要这么紧张会长皱纹的哦~~语气中带着分明的戏谑..而且我是谁并不重要现在重要的是治好你姐姐的伤吧...</p>\n<p>  这么说你能治好我姐姐？</p>\n<p>  当然..不过..嘴角扯起一抹丝毫不在意的笑容...</p>\n<p>  求求你救救我姐姐不管你提什么条件我都答应..求求你..爱儿真切的恳求着...</p>\n<p>  你说不管我提什么条件你都答应对吗？那好要我救你姐姐你就把你最珍贵的东西给我..眼神似乎放出一种异样的光芒令人望而生畏...</p>\n<p>  是我的命吗？我愿意用我的命换姐姐一命..爱儿坚定的说..</p>\n<p>  命？我要你的命做什么？对于你们女子最珍贵的应该是..我说得对吗？嘴角的笑似乎有些邪佞..</p>\n<p>  什..什么？？你是要我爱儿睁大惊恐的双眼..</p>\n<p>  男子的手指轻轻捏住爱儿光滑的下巴向上扬起蓝色的眸子对视着爱儿那一尘不染的眸子似在嘲弄..</p>\n<p>  如水挣扎着抓住爱儿的右手脸色惨白吃力地摇摇头意在示意不要..</p>\n<p>  看着如水那痛苦的样子爱儿的眼睛晶莹了...</p>\n<p>  不要就算了..松开捏住下巴的手我不会逼你..眼神中闪过一丝不屑...转身离开....</p>\n<p>  我愿意爱儿的双手紧紧攥着拳头指甲深陷进里..</p>\n<p>  男子眼神中出现一丝复杂的情愫但很快便消失取而代之的是那令人痛恶的戏谑..你说什么？</p>\n<p>　</p>\n";

