module.exports = "<hr>\n<p>title: 第32章\ndate: 2017-07-18 03:59:59\nwords: 3650</p>\n<h2 id=\"index-32\">index: 32</h2>\n<!-- deleteAbove -->\n<p>明日为大家揭晓！一定要关注哦！！</p>\n<p>  第六十八章 我欲与君相知，长命无绝衰（二）</p>\n<p>爱儿像失了神一般眼神空洞的坐在易天齐身旁...</p>\n<p>  到底该怎么办？慕容临天宁愿答应和我诀别也不肯放了我干爹..我到底该怎么办？</p>\n<p>  爱儿....易天齐醒了过来...</p>\n<p>  干爹...爱儿赶快扶他起来..你怎么样了？有没有好一点..</p>\n<p>  乖女儿干爹好多了...易天齐看到了爱儿的愁容..爱儿你是不是有什么不开心的事？慕容临天欺负你？</p>\n<p>  干爹...听他提到慕容临天爱儿的眼泪不簌簌落下来...对不起爱儿没用爱儿救不了干爹....</p>\n<p>  傻孩子..易天齐伸手擦干她的眼泪..干爹不要紧倒是..你父母的仇...我希望你能报了那干爹就死而无憾了..</p>\n<p>  什么？要我杀了慕容临天...这怎么可以！</p>\n<p>  干爹我做不到...爱儿孱弱的说...</p>\n<p>  为什么？难道你真的那么爱慕容临天？他可是你的仇人！易天齐有些怒了...</p>\n<p>  爱儿不低下了头..</p>\n<p>  我说易爱儿..如雪插话道..你爱他可是他不爱你..不然又怎么会和我生孩子？说着摸了摸自己一天天隆起的肚子..</p>\n<p>  爱儿看着她的动作可是我还是下不了手...</p>\n<p>  呵！易爱儿你还真是见色忘祖...放着父母的仇不报现在他要杀你的干爹你也不救...就是因为你的一厢情愿...真是可笑..如雪接着讽刺...</p>\n<p>  我不是一厢情愿！天他是爱我的！干爹我一定要救可是我不会杀他...爱儿很坚定..</p>\n<p>  易爱儿..真不知该说你笨还是傻！他不爱你！他心里其实这么多年来一直有个别的女人！不然他怎么不立王妃？如雪道...</p>\n<p>  不可能的...爱儿摇着头..绝对不可能！</p>\n<p>  我骗你干嘛对我又没什么好处...如雪不屑道...现在你只要杀了慕容临天你就报了你爹娘的仇能救你干爹还能报复这个负心的男人！一箭三雕有何不好？</p>\n<p>  爱儿如雪说的有道理..你一定要为你爹娘报仇知道吗！易天齐激动地说道...</p>\n<p>  干爹...爱儿的脸上已经被泪铺满...我....</p>\n<p>  你想说下不了手对吧！易天齐猜到..</p>\n<p>  爱儿说不出话来...</p>\n<p>  把它吃下去...易天齐从衣兜里拿出一颗红色的药丸塞进爱儿嘴里...</p>\n<p>  咕咚一声就咽了下去...爱儿睁大朦胧的泪眼看着易天齐...这是什么？</p>\n<p>  爱儿你不要怪干爹刚才那颗是毒药！你今天一定要帮你爹娘报仇杀了慕容临天！</p>\n<p>  毒药？爱儿不敢相信..干爹居然会这么对她...易天齐又拿出一颗和刚才一模一样的药丸递给爱儿...把这颗放进给慕容临天吃的东西里...</p>\n<p>  拿着这颗小小的药丸爱儿的手居然在颤抖...吃了会怎么样...</p>\n<p>  刚吃不会有任何反应但是三日后会肠穿肚烂而死..易天齐回答..</p>\n<p>  有解药吗..</p>\n<p>  有不过只有一颗！只要等你完成了任务我就把解药给你...乖女儿你千万不要怪干爹..干爹只是想帮你爹娘报仇...而且..你也不想你肚子里的孩子有事吧！易天齐的语气更像是威胁..</p>\n<p>  孩子...我的肚子里还有他的孩子...我不可以让孩子死...</p>\n<p>  是不是我不杀慕容临天你就不会给我解药？爱儿问..</p>\n<p>  是！易天齐口气很坚定..你要想清楚..慕容临天是你的仇人死不足惜可你肚子里面的孩子是无辜的..还有你的命..干爹可是舍不得我的乖女儿死..所以今晚..就在今晚..</p>\n<p>  我知道了..爱儿弱弱的说道..手扶着墙缓缓站起来..颤颤巍巍的离开了大牢...</p>\n<p>  慕容临天...对不起....我必须这么做......为了我们的孩子.....</p>\n<p>第六十九章 我欲与君相知，长命无绝衰（三）</p>\n<p>月华殿</p>\n<p>  如水...寒走了进来..</p>\n<p>  寒..如水很兴奋..对不起不该叫你寒麟太医...如水轻轻福了福身..毕竟他喜欢的是爱儿不能这么没礼貌..</p>\n<p>  没关系叫我寒吧..寒笑着说..</p>\n<p>  这真的是麟寒吗？他居然说话这么温柔还对我笑...寒....如水竟看得失神不由自主的叫道...</p>\n<p>  如儿..寒亦道..只是话一出口两人皆羞红了脸...</p>\n<p>  他居然叫我如儿！怎么听都觉得暧昧...</p>\n<p>  两人就这样默不作声很久...</p>\n<p>  仔细看她真的好美...满头青丝挽起一个半月髻上下各插有一只白玉簪柳叶眉杏仁眼秀气的鼻子小巧的嘴..一身淡黄色的衣裙就像出水芙蓉般..还有那的皮肤姣好的身段...如果能</p>\n<p>  该死我在想些什么！寒在心里骂着自己..为什么看着眼前这个女人居然会有..！</p>\n<p>  寒..如水打断他的思想..你怎么了？</p>\n<p>  哦没什么...寒掩饰道..</p>\n<p>  如水莞尔一笑倒了一杯茶给他..今天怎么会想到来找如水呢？</p>\n<p>  听爱儿说如水姑娘是她见过的最善良的女人所以我还真想见识一下..寒笑道..</p>\n<p>  是吗..听到寒提起爱儿如水的心里有一丝酸涩..或许女人都会吃醋的吧..</p>\n<p>  她还说....接下来的话寒有些不好意思说...</p>\n<p>  说什么？如水疑惑的看着寒他现在的表情...是不好意思吗？</p>\n<p>  她说....寒还是无法说出口..手指不停的摆弄着桌上的茶杯...可以看出他的紧张...</p>\n<p>  不小心茶杯打翻滚烫的茶水溅到寒的手上..他闷哼了一声..</p>\n<p>  你怎么样？要不要紧！如水紧张的拿起他的手吹着..殊不知他们俩的手正紧紧握在一起...</p>\n<p>  寒的脸顿时红了个透...（他居然也会脸红！！）拼了！他在心里说...随即另一只手一把握住如水握住他的手..她说你爱我...</p>\n<p>  如水感觉到他的动作..抬起眼来正好对上他的蓝眸...他的眼睛真的好迷人..只一眼就让如水深深陷了进去...那你呢...如水摒住呼吸轻轻的问..眼神里满是期待...</p>\n<p>  寒没有说话..只是眼神变得柔情起来...嘴角勾起一抹好看的笑...朝着如水的唇压了上去...看着眼前突然放大的脸庞..如水受宠若惊...眼睛睁得大大的..不敢相信眼前发生的一切...</p>\n<p>  我爱你...xing感的声音在耳边响起...像一阵清风抚着如水的心..缓缓闭上眼睛任由自己沦陷在寒的温柔攻势里...缠绵过后...天黑了..天上的月亮和星星都黯淡无光...注定是个不平凡的夜晚...</p>\n<p>  如水睁开眼睛寒已经离开了..闻着床边寒留下的味道如水不微微一笑...</p>\n<p>  我爱你...</p>\n<p>  爱儿说你是她见过的最善良的女人...</p>\n<p>  他说过的话一遍一遍在耳畔回响...对了爱儿！如水想到...我必须把事情告诉她易天齐已经来到王府说不定很快就要实行下一步计划了...起身穿好衣服...</p>\n<p>端着一碗红豆粥...爱儿的手一直在颤抖...而颤抖得更厉害的是心...</p>\n<p>  一步一步...离慕容临天的书房越来越近了...每走一步爱儿的心都在揪痛..</p>\n<p>  她不知道这样做是不是对的..她只知道她必须这么做..不可以退缩..为了孩子...</p>\n<p>  慕容临天对不起你就当我自私为了孩子为了我们之间的仇恨为了干爹...我只能杀了你...</p>\n<p>  爱儿！如水叫住她..我要跟你说件事..</p>\n<p>  爱儿姑娘小乐帮你送吧..一旁的小乐说道...</p>\n<p>  盯着这碗致命的红豆粥爱儿有些犹豫...不过..嗯..说完把粥交到小乐手上..亲眼看着小乐的身影消失在夜色中...为什么.心好痛好痛？她多想叫住小乐..可是她放弃了..她无法把这碗粥亲自端给慕容临天..唉..叹了一口气....</p>\n<p>  如水已经来到了她的身边..爱儿听这件事你一定要有心理准备...</p>\n<p>  爱儿点了点头..相信没有什么是比听到慕容临天杀了他爹娘更让她心痛的了..可是她不知道..刚才的放弃..意味着什么...</p>\n<p>  意味着一切...</p>\n<p>  事情发生在五年前你失踪的时候...</p>\n<p>第七十章 我欲与君相知，长命无绝衰（四）</p>\n<p>五年前</p>\n<p>  老爷你说怎么办..菲雪（爱儿的娘）在易天凌（爱儿的爹）的怀里盈盈哭泣...</p>\n<p>  夫人别太伤心了..易天凌轻轻擦掉菲雪脸上的眼泪安慰道..爱儿一定会找到的..</p>\n<p>  哈哈易天齐笑着走进来萧淑紧跟其后..放心吧我已经找到爱儿了..</p>\n<p>  真的？</p>\n<p>　</p>\n";

