module.exports = "<hr>\n<p>title: 第11章\ndate: 2017-07-18 03:59:58\nwords: 3508</p>\n<h2 id=\"index-11\">index: 11</h2>\n<!-- deleteAbove -->\n<p>梅凝破口骂道完全不顾形象..当然她这只是找的一个折磨爱儿的理由而已..</p>\n<p>  爱儿气不过为什么慕容临天欺负我周管家欺负我这里每一个人都欺负我！为什么！她忍痛抬起头大声说道烫你自己不会吹！你根本就是找借口欺负我！</p>\n<p>  梅凝本以为她会向自己求饶可她居然对自己大喊大叫梅凝气急败坏想要好好教训这个不知好歹的丫鬟可还没等她动手身边的贴身丫鬟已经冲上去用力踩住爱儿的手手底的碎片毫不留情地刺穿手心..</p>\n<p>  锥心的痛袭来让爱儿无法忍受..</p>\n<p>  哼你这个jian人居然敢对梅凝夫人大呼小叫！丫鬟仗着有梅凝夫人有恃无恐着一下踩的比一下狠..行了小月梅凝嘴上虽是制止丫鬟但脸上的狰狞是掩盖不住的易爱儿本夫人今天就当给你一个小小的教训别以为自己有几分姿色就可以王爷让王爷爱上你封你做王妃要是今后再看到你王爷本夫人绝不饶你！滚！终于说出了她心里的真实想法..</p>\n<p>  听了这些爱儿苦笑着居然说她王爷真是天大的笑话..但对她们说这些又有谁信呢？爱儿用手肘支撑着身体慢慢爬起来晃晃悠悠的走了出去..</p>\n<p>  回到柴房爱儿拔出手心的碎片每拔一片都是钻心的痛..血和泪一滴滴滴落下来在地上交融像一朵盛开的血梅..</p>\n<p>  一直到晚上爱儿没再踏出柴房一部手上的痛脸上的痛心里的痛时时刻刻折磨着她已经一天没有吃东西了腹中的饥饿感让爱儿不得不面对面前那一堆碗筷..不洗完就没饭吃！为什么要这样难道丫鬟不是人吗..</p>\n<p>  真的世界上无奈的事太多太多了..手上的伤口已经结成血痂可是一碰到水就疼痛难耐.尽管如此还是会选择痛苦把手伸进水中洗净一个个碗..而这样做得到的照旧只是一碗粥几个干瘪的馒头..但是她易爱儿不可以死她死了如水姐姐怎么办？干爹干娘的仇谁报？枫哥哥可能也会难过..所以她要活着..</p>\n<p>  痛手真的很痛..可是更痛的却是心.......</p>\n<p>  第二十二章 众里寻她千百度，蓦然回首（十四）</p>\n<p>又是晨...从衣服上撕下一块布条裹在手上然后端起几样小糕点往麒麟殿走去..</p>\n<p>  推门进去发现慕容临天已经自行更好衣正坐在桌子旁等着她..</p>\n<p>  向他福了福身王爷奴婢给您送早餐来了...把东西放在桌子上转身想往外走可不想被慕容临天抓住手轻轻一拉爱儿便被他拉扯过去以一个极其暧昧的姿势坐在他的腿上..好痛...爱儿疼的皱了皱眉头把手收回来该死的手上还有伤呢..这下肯定又流血了..呜呜..</p>\n<p>  你的手怎么了？看到她异样的表情慕容临天这才发现她的手上包着东西...</p>\n<p>  没怎么洗碗时不小心打破碗被割的..爱儿不想说是那个梅凝夫人干的万一慕容临天跑去找她麻烦自己今后肯定没好日子过了..不对她摇摇头自己在想什么呢慕容临天怎么可能为了她去找梅凝麻烦？他要是知道说不定还帮梅凝对付我呢爱儿想到这里赞同的点点头还是不要说好..</p>\n<p>  慕容临天看着她刚才的一举一动完全懵了..又摇头又点头是什么意思？不过这样的她好可爱...他满足的笑着...</p>\n<p>  奇怪他怎么会有这种想法？......</p>\n<p>  瞬间转移话题..本王明天会出去办事你去找麟太医把伤治好..说着从怀里掏出一块令牌递给爱儿拿这个给麟太医就行..</p>\n<p>  爱儿不敢相信的看看慕容临天他干嘛突然对我这么好？良心发现？不可能他这种人怎么可能良心发现？你又有什么阴谋？爱儿疑惑的眼神上下打量着他..</p>\n<p>  什么？阴谋？真是好心当作驴肝肺！慕容临天气急败坏的把令牌扔在地上猛地站起来却忘了腿上还坐着爱儿.....爱儿还没反应过来一下摔倒在地..受伤的手按在地上~~痛呼出声来...</p>\n<p>  这一声痛呼刺痛了慕容临天的心好想扶她起来..但是碍于面子令牌不要就算了！说罢迈出门去..当然只有他自己知道不是因为生气而是他必须逃避不然他的心会让他背叛仇恨...</p>\n<p>  出了门往紫凝阁去梅凝像是还才睡醒睁开眼就看到王爷推门而入赶快起来迎接身上薄薄的中衣紫色的肚兜若隐若现..王爷~~声音嗲得溺死人...这么早就来紫凝阁啦凝儿还没准备好呢..媚人的桃花眼一眨一眨甚是勾人...说是没准备好可手臂已经攀上了男人的脖子..</p>\n<p>  看着眼前梅凝勾人的双眼不知为何慕容临天居然想到爱儿那双眼眸干净得没有一丝杂质..仿佛只要能在她的眸子里看到自己就是一种幸福..眼前的梅凝是那么的风情万种但在他看来却不及爱儿的万千分之一..她们这种人只是一味的讨好自己而已而爱儿对每个人都是真实的..</p>\n<p>  想到这里慕容临天的嘴角露出一丝满足的笑..难得看到慕容临天对她笑梅凝更是热情一双红唇凑了过去...</p>\n<p>  从美好的想象中回到现实迎上的却是梅凝妖艳的唇不觉很煞风景...在两人的唇快碰撞在一起时一把推开梅凝..梅凝不知这是怎么回事半侧着身子水袖轻轻拂过天的脸庞王爷...凝儿不依嘛~~~声音娇嫩至极不过还真让人吃不消鸡皮疙瘩都起来了...语气里似有埋怨但更像是一种变相的邀请...</p>\n<p>  慕容临天可不吃她这一套抓住梅凝的手紫梅凝不要认为本王宠你你就可以在王府为所欲为要知道你只不过是本王的玩物而已..要是下次再让我看到你对下人滥用死刑本王绝不饶你！说完狠狠甩开梅凝的手头也不回的离开...</p>\n<p>  其实昨天紫凝阁发生那一幕时他正在门外...那时候不说是因为被折磨的是爱儿因为是她就没必要计较...若是别人他定会好好教训这个仗势欺人的梅凝..可是今天她明明可以在我面前告梅凝一状的却没有这样做...她到底是个怎样的女人？</p>\n<p>  而此时梅凝一张绝美的面容已经扭曲了哼王爷居然为了一个jian人凶我！粉拳紧紧攥着指甲深陷紧里..易爱儿我要让你死无葬身之地！</p>\n<p>  阿嚏！爱儿打了个大大的喷嚏..谁在骂我？唉这里会骂我的人太多了管他是谁呢..不过看样子这个令牌不会有假爱儿捡起地上的令牌小心地放进怀里..这个要好好保管今后还要靠它逃出王府呢...</p>\n<p>第二十三章 众里寻她千百度，蓦然回首（十五）</p>\n<p>爱儿紧紧揣着怀里的令牌走在回厨房的路上咦？枫哥哥！他好像在这里等着我呢..</p>\n<p>  枫哥哥！爱儿满脸笑容的跑过去..你在等我吗？</p>\n<p>  枫还是那副微笑真怀疑他是不是面瘫..嗯爱儿明天我会和天出去办事要好几天才能回来呢...语气里好像有些不舍..</p>\n<p>  ？你也要去..我还以为只有他去呢..那我会想你的..爱儿的小脸那模样还真是可怜兮兮的...</p>\n<p>  好啦好啦你就会跟我撒娇..枫一脸无奈..只去几天而已更何况我们都出去了你就可以见你的如水姐姐啦好久没见她了吧..</p>\n<p>  嗯嗯嗯！爱儿想想也是高兴地猛点头还真是个小孩..</p>\n<p>  好了那你快回去吧我要走了哦..枫摸摸爱儿的头看着爱儿离去..</p>\n<p>  虽然有几天见不到枫哥哥不过也不用对着慕容临天那个魔鬼还可以去看看如水姐姐想想就开心..因为这件事爱儿一天都是笑脸盈盈的比吃了糖还甜..这不要干活了才想起来手上的伤还在火辣辣的疼呢..难怪又没擦药就随便包扎一下...嗯还是听他的话去找麟太医吧毕竟吃亏的是自己..</p>\n<p>  来到典医阁..哇这里好多书好多药..怪不得叫典医阁呢一位年轻男子背对着自己在翻阅医书..咦他的头发怎么是银色的？感觉他的背影好熟悉...</p>\n<p>  爱儿轻声问道：请问麟太医在吗？她绝不相信前面这个年轻男子会是太医在她心里太医都是又老又胖的才显得医术高明嘛..</p>\n<p>  我就是..男子回答道.奇怪他的声音怎么也这么熟悉..男子见她没反应便转过身来..看见那妖孽的面容深蓝的眼眸一切都是那么熟悉..爱儿不大叫出来寒哥哥！！</p>\n<p>  没错此人正是麟寒..看到爱儿他也吃了一惊当然只是在心里..是你？你怎么会在王府？</p>\n<p>  被那个王八蛋慕容临天抓来的..说到他爱儿就气不打一处来..</p>\n<p>  王八蛋慕容临天？寒被他的话惊到了..爱儿你敢叫他王八蛋？</p>\n<p>  那有什么不敢他本来就是嘛！爱儿噘噘嘴..</p>\n<p>  哈哈..寒又露出他一贯的戏谑面容估计普天之下只有你一人敢这么叫他..梦璃的蓝色眼眸或深或浅那你的手也是他弄的咯？看到爱儿包裹着的手寒问道..</p>\n<p>  这个？爱儿光顾着和寒聊天去了都忘了是来干嘛的..这个虽然不是他弄的但是！这个转折说得十分重和他也脱不了关系！都是他害的可以说是间接伤害！</p>\n<p>　</p>\n";

