module.exports = "<hr>\n<p>title: 第18章\ndate: 2017-07-18 03:59:58\nwords: 3558</p>\n<h2 id=\"index-18\">index: 18</h2>\n<!-- deleteAbove -->\n<p>我恨你！</p>\n<p>  从如雪的口里得知她当年是被人贩子卖到了青楼吃尽了苦头...所以当如雪叫如水帮她时她只有忍痛答应....</p>\n<p>  爱儿..一滴泪从脸颊滑落...对不起...</p>\n<hr>\n<p>这章写得好像有一些些苍白哦......亲们不会介意吧.............嘿嘿我就知道你们不会的你们最好的！！（他们有说吗？）...............</p>\n<p>  第三十七章 此情可待成追忆，只是当时已惘然（九）</p>\n<p>牢房</p>\n<p>  爱儿彻夜未眠一直蜷缩在墙角..昨晚如水来告知她真相的时候连她自己都不敢相信..不过她不怪如水姐姐毕竟她们有这么多年的感情如水也是被逼无奈...</p>\n<p>  但是她终于知道了什么叫最毒妇人心..如雪..那样不食人间烟火的女子在所有人心中那么完美的女人......</p>\n<p>  呵..爱儿苦笑了一声...........</p>\n<p>  哐铛！门锁被打开了..两个狱卒模样的人进来拖走了爱儿..爱儿也不叫喊默默的任由他们带走...</p>\n<p>  来到刑房这里好像各种各样的刑具应有尽有..爱儿看着周围五花八门的刑具没有反应只是又收回目光呆滞的看着前方的地面...</p>\n<p>  肥得流油的监狱长耀武扬威的坐在正前方手里拿着根鞭子..看着爱儿大喝道大胆犯人居然敢下毒谋害如雪夫人！本官今天要好好教训教训你！</p>\n<p>  一般到这个时候犯人都会哭着喊着饶命所以他习惯的等着爱儿的求饶..可是等了好几秒刑房却是鸦雀无声..监狱长奇怪的看着爱儿你怎么不求饶！</p>\n<p>  爱儿连看都不看他一眼我从来不会求饶要打就打吧！</p>\n<p>  哼！监狱长火冒三丈你这小妮子我看你的骨头能硬到什么时候！来人给我打！说完身后的两个狱卒就拿了鞭子上来啪～啪～啪～鞭子应声的落在爱儿身上像雨点般毫不留情..鞭子所到之处皆是皮开绽..很快爱儿的身上被打出了一条又一条的血印..爱儿咬着下嘴唇忍着被鞭打的痛楚努力不让自己叫出来....</p>\n<p>  一顿鞭打之后..爱儿有气无力的趴在地上全身上下火辣辣的痛...</p>\n<p>  监狱长走上前来俯视着地上的爱儿怎么样求我..求我我就稍微怜香惜玉一点儿..你也不用吃这么多的苦...</p>\n<p>  我..我不会..求..求你....她吃力的说..</p>\n<p>  好你敬酒不吃我们就吃罚酒！说完两个狱卒拿了块木板上来恐怖的是上面密密麻麻的全是细细的铁钉！狱卒抬起爱儿然后往上面一扔...</p>\n<p>  爱儿再也忍不住..那些钉子刺穿她的皮残忍至极...接着..一人按住她的肩膀一人按住她的腰.两人一齐向下用力..</p>\n<p>  更惨烈的叫声传来爱儿仿佛能听到冰冷的铁钉扎进里刺进骨里的声音.....她感觉一股股温热的液体从背上无数个孔里齐齐渗出..浓烈的血腥味弥漫在冰冷的空气中..闻者胃里不停的翻滚..爱儿感觉喉咙上涌出点点甜味..呕一大口鲜血吐在地上..最终她忍受不了撕心裂肺般的痛..晕了过去...</p>\n<p>  把钉板拔下来爱儿的背部已经血模糊惨不忍睹..连两个狱卒都不忍再看下去...</p>\n<p>  哼想不到这丫头的骨头这么硬！监狱长发话了带下去！</p>\n<p>  与此同时书房</p>\n<p>  慕容临天依旧批着奏折一晚未眠的他有些憔悴...这时枫冲了进来.天有新发现！</p>\n<p>  慕容临天瞥了他一眼什么？</p>\n<p>  枫已经习惯了他这种不火不热的态度..你看！说着拿出一个东西递到慕容临天面前..</p>\n<p>  这是什么？慕容临天不解的看着这一根相貌平平的草..</p>\n<p>  这叫锁魂草枫解释道服用者会被锁住魂魄一般被别人控制言行..</p>\n<p>  那又如何？你想说什么？慕容临天问道..</p>\n<p>  那天我看到爱儿眼睛瞬间变成了绿色不过只是一闪而过..你有没有注意到？枫说着..</p>\n<p>  慕容临天回想着..有那么一瞬好像的确如此..他点点头..</p>\n<p>  这就是被控制的征兆..枫坚定的说..如此说来爱儿后来所说的‘真相’全是假的！</p>\n<p>  慕容临天一怔想着那之前她一直求自己相信她为什么后来突然改口供认一切？的确事有蹊跷..</p>\n<hr>\n<p>妈妈咪...这一章我自己都觉得太残忍了....那个谁谁谁心脏不好的或者心理承受能力差一点的...最好备好硝酸甘油在旁边.....下章继续虐....不过是虐心.....呵呵虐恋情伤嘛不虐一虐怎么行呢？</p>\n<p>  第三十八章 此情可待成追忆，只是当时已惘然（十）</p>\n<p>可是这并不能完全证明毒不是爱儿下的..慕容临天说道..</p>\n<p>  当然不过我还有别的证据..枫笑道..据一个小丫鬟说在爱儿送午餐到胜雪居之前就看到爱儿插着那只蝴蝶簪也就是说如雪夫人和梅凝也应该看到了那时候她们为什么没反应？</p>\n<p>  慕容临天思考着这一切..还有什么疑点？</p>\n<p>  枫说那天晚上如水不在场..</p>\n<p>  可是秋如水和她关系那么好怎么会害她？慕容临天觉得奇怪..</p>\n<p>  天那天我无意中看到如水姑娘从胜雪居哭着跑出来就觉得奇怪于是便去查了如水的身世得知枫故意卖了个关子...</p>\n<p>  得知什么？慕容临天还是沉住气..如水姑娘和如雪夫人..是亲生姐妹！</p>\n<p>  什么！慕容临天不敢相信..</p>\n<p>  正如你所说如水姑娘不会害爱儿那么就是帮凶真正的主谋则是..柳如雪！</p>\n<p>  慕容临天听了这话沉思着..秋如水是不是也常进出典医阁？</p>\n<p>  是！枫肯定的说..</p>\n<p>  慕容临天好像想起了什么..</p>\n<p>  几天前</p>\n<p>  胜雪居..翻云覆雨过后的两人相拥着</p>\n<p>  雪儿你的房里有一种淡淡的青草味..真好闻是什么？慕容临天问道</p>\n<p>  如雪的眼神闪烁了一下..王爷是茶叶的余香...</p>\n<p>  哦原来如此..</p>\n<p>  慕容临天低头闻了闻锁魂草的味道..一震..和那日在胜雪居闻到的一模一样...看来真是如此！</p>\n<p>  牢房</p>\n<p>  爱儿浑浑噩噩的醒来全身上下没有一个地方不是彻骨的痛...身下的稻草已被背上的血沾染一大片恐怖的血腥味难闻得让她想吐...果然胃里开始翻滚起来血气上涌...呕又是一滩触目惊心的血空气中的血腥味顿时又浓了许多..一侧身摸到一块什么东西？碎玻璃..</p>\n<p>  活着还有什么意思呢？她苦笑着....</p>\n<p>  他不相信我..我最信任的如水姐姐也帮别人陷害我..王府里所有人现在肯定都在唾弃我..没有人相信我没有人在意我没有人帮助我当然不会有人来救我..不如....她举起手里的碎玻璃在微弱阳光的照耀下一闪一闪发着寒光..一咬牙....朝着自己的手腕使劲一划....</p>\n<p>  呵呵..看到一股股鲜血从手腕涌出爱儿有一种莫名的..她笑了..笑自己终于可以解脱离开这个让她没有半分留恋的世界..爹娘爱儿来找你们..还有..小王爷我们来生再见....只是这一句她已经没有力气说出口...</p>\n<p>  血请你流得慢一点..给我多一点时间....让我再想想我的小王爷......</p>\n<p>  你饿了吗？</p>\n<p>  不用害怕这里是四王爷行宫我是小王爷我们可以做朋友吗？</p>\n<p>  呵呵那我们现在开始就是朋友了我叫下人给你准备了点吃的快吃吧明天我带你去找你父母。</p>\n<p>  我..我..呃我只是觉得你很可爱..</p>\n<p>  儿时的场景在脑海里一遍又一遍不停回放着....只是....</p>\n<p>  生病不喝药怎么行！</p>\n<p>  一直用被子这么捂着会捂出病来的..</p>\n<p>  喝药乖~</p>\n<p>  你不说话的时候比较可爱..</p>\n<p>  为什么会有慕容临天的身影.....</p>\n<p>  一个硕大的监牢..</p>\n<p>  一个冰冷的房间..</p>\n<p>  一滩触目惊心的鲜血..</p>\n<p>  一抹小小的身影..</p>\n<p>  一切都是那么苍白..</p>\n<p>  白得几乎透明的爱儿面带着微笑闭上美丽的眼睛浓密的睫毛依旧在脸上投下阴影...是不是等血流光了..爱儿就会化作一朵不知名的桃花..亦或是..一粒微尘...</p>\n<p>  如果是...希望我下辈子..不要再做人.........</p>\n<p>  不知道这章看哭的有没有嘞？</p>\n<p>　</p>\n";

