module.exports = "<hr>\n<p>title: 第31章\ndate: 2017-07-18 03:59:59\nwords: 3601</p>\n<h2 id=\"index-31\">index: 31</h2>\n<!-- deleteAbove -->\n<p>慕容临天绝情的说道..</p>\n<p>  王爷奴家怎么会没有资格呢？奴家的肚子里可还有你的孩子...如雪说着摸摸自己已经有些凸出的小腹...</p>\n<p>  听着她的话慕容临天冷笑了两声简直让如雪毛骨悚然..想用孩子保命？也不看看他是不是本王的种！</p>\n<p>  王爷你说什么呢？这怎会不是话还没说完慕容临天已经伸出手扣住她纤细的脖子..还想狡辩紫梅凝已经把一切都告诉本王了！你肚子的孩子根本就是你跟别的男人的野种！</p>\n<p>  王..爷...饶....命........如雪像求情无奈脖子被掐的死死的半天才吐出五个字...由于呼吸不畅如雪的脸开始变红已而渐渐变紫.再由紫变青...眼见的就要断气了....</p>\n<p>  突然有人向慕容临天空袭一掌..慕容临天意识到危险放开手一躲...咳咳..得到空气的如雪大口呼吸着..脸色慢慢恢复正常..</p>\n<p>  易天齐？慕容临天半眯着眼睛看着偷袭他的人..怎么有胆闯王府？不怕本王在这里布下天罗地网抓你吗？</p>\n<p>  易天齐狞笑着..眼角的诈尽显无遗如果怕今天我就不会来了..我正是专门来让王爷抓的..不知王爷信不信呢？</p>\n<p>  哼！慕容临天懒得跟他费话来人把他们俩给我绑起来关到大牢去！</p>\n<p>  是！侍卫们异口同声的答道...</p>\n<p>  很快易天齐和柳如雪便被五花大绑..看准时机..</p>\n<p>  嗖易天齐向慕容临天吐出嘴里的银针..像是看见易天齐的暗器一闪一闪发着光慕容临天轻易躲过习惯的给了易天齐一掌..噗..易天齐的嘴里吐出一口鲜血...</p>\n<p>  干爹一个清脆的声音响起...爱儿从旁边经过正好看见慕容临天出掌的那一幕不惊叫出声...</p>\n<p>  易天齐的眼神里闪过一丝不易被察觉的笑..干爹...你有没有怎么样？爱儿看着口吐鲜血的易天齐问道...易天齐故意装出伤得很重的样子..爱..爱儿...干爹没事....咳咳..又是一口血...</p>\n<p>  慕容临天你太过分了！爱儿怒视着慕容临天啪一巴掌打在他脸上...</p>\n<p>  慕容临天捏紧了拳头..她居然为了他打我！来人！把他们押下去！</p>\n<p>  干爹干爹看着被带下去的易天齐爱儿哭着叫喊道...却被慕容临天一把抓住..爱儿你听我说！</p>\n<p>  我不要听！我要见我干爹！爱儿挣扎着..</p>\n<p>  刚才是他先偷袭我我才慕容临天解释着...</p>\n<p>  不要再说了！你放开我我要见我干爹！爱儿疯狂的想要挣脱他..奈何他的力气太大..爱儿愤恨的在他手臂上狠狠咬了一口...</p>\n<p>  ..慕容临天吃痛的放开手..趁机爱儿赶紧跑走..</p>\n<p>  看着爱儿渐行渐远的背影慕容临天的眉头紧锁...</p>\n<p>  牢房</p>\n<p>  易天齐你怎么会到这里来？如雪疑惑的问道她绝不相信易天齐是为了救她来的..</p>\n<p>  我要实行我的最后一步计划..易天齐冷冷的说..</p>\n<p>  所以最后一步就是你以身犯险彻底挑拨他们俩的关系？如雪接着问..</p>\n<p>  你很聪明..易天齐不屑的瞥了她一眼..来人！来人！他大声喊道...</p>\n<p>  吵什么吵！狱卒不耐烦的走过来..再吵我就不客气了！</p>\n<p>  小哥过来..易天齐喊着..我有好东西要给小哥你..</p>\n<p>  一听到好东西狱卒立刻两眼放光..什么好东西？</p>\n<p>  在我的袖子里有一颗夜明珠想送给小哥...易天齐笑着说..</p>\n<p>  闻言狱卒赶紧把手伸到易天齐的袖子里一掏..果然有！看着发光的夜明珠狱卒简直笑得合不拢嘴...立刻换了一副模样..不知这位大爷有什么吩咐呢？</p>\n<p>  很简单用鞭子抽我！易天齐诈的眼神闪烁着..</p>\n<p>  什么？打你？狱卒不敢相信自己的耳朵哪有人出钱让人打自己的？大爷你别逗我了..</p>\n<p>  我说的是真的而且抽的越狠越好...不过...易天齐在狱卒耳边耳语一番...</p>\n<p>爱儿一来到大牢便听到易天齐凄惨的叫喊声还有刺耳的鞭笞声...不好！爱儿赶紧朝着发出声音的方向跑过去...眼前血淋淋的一幕刺痛了爱儿的心...易天齐正躺在血泊中身上满是鞭伤...这样重的伤对于易天齐这种年过四旬的人怎么受得了？</p>\n<p>  住手！她大喊着推开施刑的狱卒抱起地上奄奄一息的易天齐...看着他痛苦的模样爱儿的心里揪痛着眼泪不停的滴落...</p>\n<p>第六十七章 我欲与君相知，长命无绝衰（一）</p>\n<p>干爹干爹..你怎么样了...对不起爱儿来晚了...让您老受苦了...爱儿抱着伤痕累累的易天齐已经泣不成声...</p>\n<p>  吃力的抹去爱儿脸上的泪珠.....爱..爱儿别哭...干..干爹..很好...呕...一大口触目惊心的血从易天齐嘴里喷出来溅得到处都是...接着晕了过去...</p>\n<p>  干爹！干爹！爱儿惊吓的看着鲜血一般的事实...抬头怒目着施刑的狱卒..谁叫你用刑的！</p>\n<p>  是..是王爷...狱卒明显被这样的爱儿吓到..当然这句话就是易天齐耳语他说的..</p>\n<p>  慕容临天爱儿失神了..他怎么可以这么残忍！</p>\n<p>  看着狱卒们把易天齐抱回监牢..爱儿扶着墙却无力的滑下...慕容临天..你不可以这样...</p>\n<p>  呵！那个慕容临天还说爱你..如雪冷笑道..爱你就是把你所有的亲人都赶尽杀绝然后还让别的女人怀上他的孩子...这样的爱真是可笑...看着痛苦的爱儿如雪讽刺着..低下头轻轻摸着自己隆起的小腹笑得令人胆寒....她要看到他们痛苦！</p>\n<p>  你不要说了！爱儿斥责道..</p>\n<p>  你叫我别说我偏要说！如雪就是要说给她听...慕容临天根本没把你放在心里嘴上说爱你可实际呢？他连你的家人都不放过..相信过不了几天你干爹就会惨死在他手上了！哈哈哈哈如雪仰天长笑着...</p>\n<p>  别说了别再说了...如雪的话像梦魇般缠着爱儿就快把她逼疯了...爱儿使劲捂住耳朵不住的摇头...别说了！我去找慕容临天！</p>\n<p>  说完向书房走去她知道这个时候慕容临天在那里...</p>\n<p>  书房</p>\n<p>  卷起袖子两排血牙印呈现在慕容临天眼前...看着爱儿咬的地方他不苦笑...</p>\n<p>  慕容临天！爱儿门也不敲就冲进来..脸上挂着残留的泪珠..慕容临天赶紧放下袖子..爱怜的用手抚去爱儿脸上的泪却被爱儿倔犟的躲过...</p>\n<p>  慕容临天我求你一件事...爱儿尽量让自己的语气软下来...</p>\n<p>  慕容临天尴尬的放下手..说吧..</p>\n<p>  求你放了我干爹..爱儿很坚定..</p>\n<p>  不可以..慕容临天亦是不可商量..</p>\n<p>  爱儿噗通一声跪下..爱儿你这是干什么？慕容临天心里一紧要扶她起来..</p>\n<p>  天..我求求你好不好？求你放了我干爹...今后你爱上别的女人我也不会有怨言的求求你..爱儿哭得暴雨梨花..</p>\n<p>  什么叫我爱上别的女人你不会有怨言？难道你希望我爱上别的女人..慕容临天的拳头握紧..</p>\n<p>  求求你...爱儿仍然求着..</p>\n<p>  就算不是易天齐杀了我爹娘我一定要报仇！绝不可以心软..</p>\n<p>  你要怎样报仇？</p>\n<p>  慕容临天的瞳孔一收紧..杀！就这一个字....</p>\n<p>  什么？爱儿就像受了晴天霹雳...她看着眼前的慕容临天..清澈的眸子里却是恨...</p>\n<p>  慕容临天你一定要这么自私吗？你要报仇杀了我干爹那我呢？我是不是应该报仇杀了你！</p>\n<p>  爱你就是把你所有的亲人都赶尽杀绝然后还让别的女人怀上他的孩子...这样的爱真是可笑...</p>\n<p>  慕容临天根本没把你放在心里嘴上说爱你可实际呢？他连你的家人都不放过..相信过不了几天你干爹就会惨死在他手上了！</p>\n<p>  如雪的话一遍又一遍的在爱儿耳畔回响...</p>\n<p>  啪嗒..一滴滚烫的热泪溅开无数梅花...</p>\n<p>  春华竞芳五色凌素琴尚在御而新声代故！</p>\n<p>  锦水有鸳汉宫有水彼物而新嗟世之人兮瞀于银而不悟！</p>\n<p>  朱弦断明镜缺朝露晞芳时歇白头吟伤离别努力加餐勿念妾锦水汤汤与君长诀！</p>\n<p>  从这一刻起便是诀别...</p>\n<p>  慕容临天的心狠狠的收紧..悲恸的看着爱儿离开的身影...爱儿你真的要跟我诀别？</p>\n<p>  殊不知从这日起...一切都不一样了...</p>\n<p>  自古多情空余恨此恨绵绵无绝期....</p>\n<hr>\n<p>接下来就要发生翻天覆地的转变了到底是什么呢？</p>\n<p>　</p>\n";

