module.exports = "<hr>\n<p>title: 第26章\ndate: 2017-07-18 03:59:59\nwords: 3762</p>\n<h2 id=\"index-26\">index: 26</h2>\n<!-- deleteAbove -->\n<p>慕容临天的拳头握紧关节处咔咔作响...</p>\n<p>  冯枫！！！他怒吼道..下床来伫立在枫的面前..你小子是不是故意的！！每次都是这个时候..</p>\n<p>  王爷..这次真的有急事..枫的表情很严肃..眼神有些闪烁..</p>\n<p>  看来是真的有事..慕容临天寻思着穿上衣服跟着枫走出了麒麟殿...</p>\n<p>  第五十五章 昨夜星辰昨夜风（七）</p>\n<p>到底有什么事呢？爱儿不解..小乐！她叫道..</p>\n<p>  爱儿姑娘你醒了..小乐走了进来端着洗漱要用的东西...</p>\n<p>  很快爱儿就换好一身粉红色的衣服洗漱好..向大厅走去..</p>\n<p>  刚到大厅门口就看到了一张熟悉的面孔...</p>\n<p>  如雪？她惊讶的叫了出来..</p>\n<p>  如雪给爱儿姑娘请安..说着向爱儿福了福身...</p>\n<p>  这是怎么回事？爱儿不敢相信的看着眼前一袭白衣的如雪..</p>\n<p>  来人把柳如雪安排在胜雪居..慕容临天命令道..</p>\n<p>  如雪像风一样的走了..只留下爱儿慕容临天和枫三个人表情都很阴郁..</p>\n<p>  枫哥哥这是怎么回事？爱儿问道...</p>\n<p>  枫！慕容临天喝止了他..起身走到爱儿身边..我来告诉你..</p>\n<p>  带着爱儿来到麒麟殿的小院里..</p>\n<p>  爱儿在告诉你这件事之前我希望你知道..这辈子我只爱你..慕容临天一把把爱儿搂在怀里..可是越是这样爱儿越感到不安..她能感觉到这件事情很严重...</p>\n<p>  到底什么事天你告诉我..爱儿看着慕容临天..</p>\n<p>  柳如雪她..怀了我的孩子...慕容临天悲伤的说道..</p>\n<p>  什么！明显感觉到怀里的人一震..慕容临天更加抱紧了她..爱儿你要相信我我只爱你一个..</p>\n<p>  不知道为什么..爱儿的心里好乱..慕容临天明明这么说了她为什么还会介怀？</p>\n<p>  天..爱儿缓缓的开口了..我想一个人静一静..</p>\n<p>  爱儿...慕容临天心疼的看着爱儿眼里流露出的悲伤...</p>\n<p>  你放心吧我不会想不开的..我只是需要一点时间好吗？爱儿对着慕容临天轻轻一笑可此时这笑看起来却是那么的苍白...</p>\n<p>  嗯..他同意了..</p>\n<p>  爱儿转过身慢慢走进麒麟殿...</p>\n<p>  看着爱儿黯然离开的背影..慕容临天的心里涌上一丝酸楚...</p>\n<p>为什么我的心里这么难受呢？爱儿叹着气...天也是无可奈何的..是不是一旦爱上一个人就会变得这么敏感连他身边的一粒沙都容不下呢？</p>\n<p>  介意、不介意、介意、不介意爱儿数着花瓣...呀！怎么又是介意！呼...都数了三朵了全是介意...难道我真的介意吗.....不嘟起嘴...</p>\n<p>  是什么惹得我妹妹这么生气？枫笑得一脸无害...</p>\n<p>  枫哥哥..此时的爱儿像泄了气的皮球...</p>\n<p>  你在介意吗？枫坐在她对面看着她问道...</p>\n<p>  对...爱儿耷拉着脑袋我很想自己能不介意的..可是我却管不住我的心...枫哥哥我是不是个爱吃醋的坏女人？</p>\n<p>  看着爱儿一脸天真枫扑哧一声笑了出来..傻丫头怎么会呢？你要是不介意慕容临天可是会生气的..</p>\n<p>  为什么？</p>\n<p>  你想想如果你都不吃醋的话他肯定会觉得你不爱他..枫假装很严肃..</p>\n<p>  怎么会！我明明就很爱他...所以现在才搞的自己这么郁闷....爱儿说着说着又叹了一口气...</p>\n<p>  好了傻丫头别想太多了..枫宠溺的摸摸她的头...你们好不容易才在一起你要相信他..</p>\n<p>  嗯..爱儿点了点头..</p>\n<p>  好啦那我就先走了省得那个醋坛子又看我不顺眼了..枫笑道...</p>\n<p>  什么醋坛子？爱儿不解的问..</p>\n<p>  你说还有什么醋坛子除了你不就剩你的慕容临天了！枫装作不满道..走出了麒麟殿...</p>\n<p>  枫哥哥说得对我不该杞人忧天的...</p>\n<hr>\n<p>  如雪的出现到底是为了什么呢？</p>\n<p>  第五十六章 昨夜星辰昨夜风（八）</p>\n<p>已经立冬了今年冷的特别早刚立冬就已经是寒风彻骨...</p>\n<p>  麒麟殿里各个角落都放置着温暖的火炉爱儿穿着粉红色的貂皮衣裙领口和袖口分别有着一圈白色的貂毛头上还戴着一个毛绒绒的白色帽子衬着爱儿姣好的容颜...好像这冬日的寒风一点也不能将她侵袭..</p>\n<p>  又过去了一个月..如雪的肚子有些微微的隆起不过不仔细看还是看不出来..这一个月如雪到还是恪守本分没弄出什么风浪来只是爱儿和慕容临天的心里始终有个疙瘩..</p>\n<p>  爱儿姑娘你说今年什么时候会下雪呢？小乐一手提着伴手火盆一边问道..</p>\n<p>  应该不会久了吧..爱儿看看天空若有所思的说道..</p>\n<p>  爱儿姑娘...不用说一听就知道这个声音是谁了..如雪笑吟吟的从后面走上前来..稍微向爱儿欠了欠身..如雪见过爱儿姑娘..</p>\n<p>  不用了..爱儿扶起如雪..你有孕在身要小心..这么冷的天如雪姐姐为什么不在胜雪居休息呢？</p>\n<p>  如雪轻轻地摸着自己的肚子脸上洋溢起幸福的笑容..如雪正是因为每天都在胜雪居但大夫说孕妇多多运动对孩子有好处所以才出来的...说着顿了顿..王爷看在以往的情份上对如雪还是呵护有加..每天都是这样那样的补品如雪还真是怕吃不消..说罢掩嘴一笑...</p>\n<p>  爱儿的心莫名的一抽紧..可能是天很重视这个孩子吧..她也不想多说什么..</p>\n<p>  那是自然！看王爷是怎么对如雪姑娘的就知道了..如雪身旁的梅凝添油加醋着..若生下了个男孩那王爷一定会笑得合不拢嘴的！</p>\n<p>  是吗..他真的会很开心吗..不..爱儿不想再想...那你们继续散散步吧天寒要注意些..我先回去了..说完爱儿正欲走..</p>\n<p>  爱儿妹妹..如雪叫住了她..不知道什么时候才能改口叫你王妃呢？怎么王爷还没说要娶你吗？</p>\n<p>  爱儿一怔..没错回王府这么久了他的确没有提过..不过这并不重要名分只不过是个空壳只要知道他是爱自己的就行了...爱儿没说什么径直离开了...只留下如雪和梅凝两张狰狞的面孔...</p>\n<p>小乐..今天王爷又不回来吗？爱儿用手托着下巴..眼光呆呆的看着前面..</p>\n<p>  爱儿姑娘都这么晚了想必是不回来了..小乐回答道..</p>\n<p>  唉...他已经三天没回麒麟殿了..爱儿叹了一口气...不过慕容临天的忙他是可以理解的..虽然不知道他在忙些什么..这几天也没有看到枫哥哥的身影..</p>\n<p>  想到这里爱儿一个激灵..上次他们一起出去的时候是抓干爹难道这次也是？爱儿不紧张起来顾不了那么多他要去找慕容临天..</p>\n<p>  爱儿姑娘你要去哪里？不顾小乐的叫喊..爱儿向殿外跑去..可是突然撞上了一堵硬墙..</p>\n<p>  好痛..揉揉撞痛的小鼻子..看着来人..</p>\n<p>  这么晚了要去哪儿？慕容临天一把把爱儿搂在怀里..语气里满是温柔..</p>\n<p>  要去找你！爱儿气愤的说..</p>\n<p>  找我？慕容临天笑了笑是几天没见你夫君我心里直痒痒吧...他戏谑道..</p>\n<p>  小乐见他们正在打情骂俏中悄悄退了出去..</p>\n<p>  才不是呢！爱儿朝着他吹胡子瞪眼..我问你你这几天干嘛去了？</p>\n<p>  这个不是你管的..慕容临天好像在遮掩什么..</p>\n<p>  这次还有上次你是不是去抓我干爹了？看出了慕容临天的遮掩爱儿更加确定..</p>\n<p>  你都知道了..慕容临天也不再隐瞒...</p>\n<p>  为什么你一定要抓他们呢！爱儿责问道..看来干爹说的是真的..</p>\n<p>  爱儿我知道你会生气但是我必须这么做我要为父王和母妃报仇...慕容临天的手落在爱儿肩上...</p>\n<p>  你要为父王和母后报仇那我是不是应该找你报仇？爱儿心里想..可终究没有说出来..</p>\n<p>  爱儿你理解我吗？慕容临天的大手轻轻抚上爱儿光滑的脸...却被爱儿躲开..不理解..如果我理解你那谁来理解我？</p>\n<p>  空气中的手尴尬的放下...我走了你休息吧...放下这句话慕容临天心痛的走出了麒麟殿...</p>\n<p>  慕容临天你不能这么自私....爱儿看着慕容临天离开的背影眼泪不争气的流了下来..</p>\n<p>  第五十七章 昨夜星辰昨夜风（九）</p>\n<p>又是几天未见到慕容临天..爱儿心里想可是却有一种莫名的情愫排斥着...</p>\n<p>  走在王府里..爱儿一言不发只是静静的看着周围的一切感觉都是那么萧条..她不喜欢冬天冬天一切生命都被埋没或睡去有些死气沉沉...但是只要熬过了这寒冬春天就来了大地便会复苏桃花也能遍开山间...</p>\n<p>  想到桃花爱儿往往能抿嘴一笑那是她最爱的花了...</p>\n<p>  爱儿姑娘好兴致在这儿散步呢？</p>\n<p>　</p>\n";

