module.exports = "<hr>\n<p>title: 第39章\ndate: 2017-07-18 04:00:00\nwords: 3515</p>\n<h2 id=\"index-39\">index: 39</h2>\n<!-- deleteAbove -->\n<p>爱儿扑哧一声笑出来...</p>\n<p>  哇！娘你越来越阴险了！小爱嘟着小嘴..</p>\n<p>  怎么你们调皮娘就不能骗骗你们！爱儿调皮的一笑..好了给你们介绍一下..这是我的孩子小天和小爱..</p>\n<p>  爱儿还用你说一看就知道了简直和你还有慕容临天是一个模子刻出来的嘛！枫笑道..</p>\n<p>  爱儿接着说这位姑娘叫云梦娘..梦娘站起来向大家致意..眼神还不停的瞟着枫..恰巧这一幕落进了小天小爱眼里...</p>\n<p>  慕容临天站起来指着枫..这是你们的枫舅舅..</p>\n<p>  小天小爱使了个眼色..枫舅舅好祝枫舅舅和干娘早日恋爱早日成亲！</p>\n<p>  哈哈除了枫个梦娘满脸通红外其他人都笑得合不拢嘴..</p>\n<p>  慕容临天好不容易忍住笑指着如水和寒..这是你们的如水舅妈和寒舅舅..</p>\n<p>  祝如水舅妈和寒舅舅生十个八个孩子！小天小爱一起说道还做了个恭喜发财的姿势..</p>\n<p>  接着又惹来一阵哄笑..</p>\n<p>  爱儿你都是怎么教他们的太可爱了..如水笑道..</p>\n<p>  这...爱儿尴尬的笑了笑她也不知道这对活宝从哪里学的这些话...</p>\n<p>  我们是无师自通！两人异口同声道...</p>\n<p>  哈哈大厅中笑声不断</p>\n<p>  远处</p>\n<p>  一抹身影恶狠狠的盯着大厅上的人..哼易爱儿慕容临天我要你们死！</p>\n<hr>\n<p>哇哇哇！！今天更了一万字哈哈！太佩服我自己了！！明天准备大结局哦！到底爱儿的眼睛会不会好呢？那个想杀他们的又是谁呢？一切谜团明日为你揭晓！亲们要多多留言多收藏！票票多多的砸来吧！！</p>\n<p>  第九章 执子之手，与子偕老（一）</p>\n<p>典医阁</p>\n<p>  寒怎么样？爱儿的眼睛可以治好吗？慕容临天着急的问...</p>\n<p>  寒认真检查着爱儿的双眼可是...唉..他叹了口气摇摇头...毒全被逼到眼睛上了靠外力是无法逼出来的不过这样爱儿也不会有生命危险若我强行用内力将眼睛上的毒打散的话恐怕</p>\n<p>  恐怕什么？慕容临天的心中有不好的预感...</p>\n<p>  恐怕会毒发身亡...寒严肃的说道...</p>\n<p>  什么！慕容临天的身子微微颤抖了一下...</p>\n<p>  爱儿好像能感觉到他心里的害怕紧紧握住慕容临天的手莞尔一笑道其实治不好眼睛没关系的这么多年我都已经习惯了看不见的生活而且寒哥哥说了我不会有生命危险！天..放心吧...</p>\n<p>  慕容临天很感动亦很难过..他觉得自己真的很没用连他最爱的女人的眼睛都治不好.反而还要爱儿来安慰他...慕容临天把爱儿拥在怀里看着她那双美丽的眼睛心里隐隐作痛...</p>\n<p>  紧贴着他温热的胸膛爱儿觉得真的够了她很知足...得夫如此妻复何求？</p>\n<p>  天我们回麒麟殿吧！寒哥哥还要回去照顾如水姐姐呢...我们还是别打扰他太久...爱儿说道..</p>\n<p>  好都听你的...慕容临天扶起爱儿寒我们回去了...说着准备离开...天...寒抓住慕容临天的手臂微微一用力..慢走..</p>\n<p>  嗯..慕容临天会意的点点头扶着爱儿离开了典医阁...</p>\n<p>  走在路上...</p>\n<p>  爱儿不要灰心我一定会想办法治好你的眼睛的！慕容临天坚定的说道...</p>\n<p>  闻言爱儿轻轻靠在慕容临天的肩膀上幸福地笑着..其实治不好也没关系这些都不重要重要的是我们所有人都在一起快快乐乐的生活...</p>\n<p>  爱儿慕容临天郑重的问..你跟我说实话你想眼睛复明吗？</p>\n<p>  爱儿犹豫了一下不过她不想说谎..想...我想看到你的模样看到你看我的眼神看到小天和小爱玩耍时的样子我还想和你在一起去桃花林看日出日落..但是...她还是无奈的顿了顿..凡事都不能强求只能顺其自然......话还没说完..</p>\n<p>  唔嘴就被慕容临天堵得死死的...爱儿放心没有但是....慕容临天在心里发誓道...</p>\n<p>  爱儿脸颊绯红不停的用粉拳向慕容临天的胸口砸去...这个时候王府里应该有很多人的让他们看着也太</p>\n<p>  慕容临天仿佛看出了爱儿的小心思松开她的唇一把横抱起爱儿向麒麟殿走去...</p>\n<p>  麒麟殿</p>\n<p>  见王爷抱着爱儿回来小乐偷笑着退了下去只留下热情如火的两个人....</p>\n<p>  爱儿现在我们是不是该完成上次没完成的事？慕容临天的嘴角勾起一抹邪邪的笑..爱儿的小脸羞得通红只是那一对溺死人的眸子还在不知死活的他...</p>\n<p>  既然你不说话那就是默认了...慕容临天在爱儿耳边说道声音极其xing感惹得身下的人儿一阵轻颤....</p>\n<p>  白色的纱帘放下却掩盖不住里面裸的纠缠..偌大的麒麟殿内一片浮靡</p>\n<p>  待爱儿醒来已经是第二天了身边的人早已起身离开但是还残留着淡淡的只属于他的男气息...回想起昨晚爱儿的脸情不自的红了起来他居然要了她整整一个晚上...</p>\n<p>  爱儿姑娘...小乐笑着端水走进来...爱儿姑娘王爷这么疼你想必奴婢很快就要改口叫您王妃了！呵呵！</p>\n<p>  小乐说了不要自称奴婢不然我生气了！爱而故意装作生气的样子...</p>\n<p>  好小乐知错了！爱儿姑娘我来帮您梳洗..小乐说...</p>\n<p>  嗯爱儿点点头..等会我们去月华殿....</p>\n<p>  第十章 执子之手，与子偕老（二）</p>\n<p>月华殿</p>\n<p>  爱儿小心...如水看到爱儿来了赶忙上前扶住她...</p>\n<p>  如水姐姐你才该小心肚子里还有孩子呢有小乐扶着我就行了..爱儿说道摸索着坐下..</p>\n<p>  爱儿今天怎么来看我了？如水笑着问..</p>\n<p>  这不是惦记你肚子里的小宝宝嘛呵呵爱儿笑道..不如我们就指腹为婚怎么样？如果是女孩就嫁给小天如果是男孩那就让小爱嫁给他如水姐姐你说好吗？</p>\n<p>  好！如水欣然同意了...</p>\n<p>  唉...如水轻叹一口气.....</p>\n<p>  如水姐姐你是不是有什么心事？爱儿问...</p>\n<p>  没有怎么这么问？如水不想说出来..</p>\n<p>  如水姐姐你知道吗眼睛看不见的人听觉会特别敏感我甚至可以听到你呼吸的声音..你刚才为什么叹气？是因为如雪？爱儿已经猜了出来...</p>\n<p>  爱儿..如水握住她的手..你真的成熟了很多..她顿了顿..的确是为了她...上次你跳崖后她就悄悄离开了..我真的很想她不知道她过得好不好..不知不觉中眼泪已经盈满了眼眶...</p>\n<p>  如水姐姐别担心她一定会过得很好的...爱儿真的不知道怎样安慰她只能更加握紧她的手...</p>\n<p>  她一定还很恨我这个姐姐...如水有些啜泣...我真的很后悔..如果当年我不先逃走的话如雪和我就不会闹成这样...我对不起她..呜呜...</p>\n<p>  如水姐姐...爱儿搂着她..这不是你的错...你当年已经去找过她了没找到不能怪你..如水姐姐..</p>\n<p>  我真的很愧疚...这么多年来我没能给她一点亲人的温暖..她恨我是应该的..我不配当她的姐姐...爱儿..如水简直是泣不成声...</p>\n<p>  如水姐姐..爱儿轻轻拍着她的背...哭吧哭过就好了...</p>\n<p>  太多无奈只能等待另一个人的改变...</p>\n<p>  门外一个白影闪过</p>\n<p>  次日</p>\n<p>  小乐带我去枫哥哥那儿也不知道小天和小爱这两个小捣蛋给他们添了多少麻烦总得给枫哥哥和梦娘一点独处时间吧..爱儿偷笑着..</p>\n<p>  爱儿姑娘小心阶梯..小乐提醒道..</p>\n<p>  走到王府中央的水池边</p>\n<p>  娘娘小天和小爱呼喊着..</p>\n<p>  小天小爱快到娘身边来..爱儿叫道...</p>\n<p>  哼！你叫他们来就来吗？他们现在在我手上！一个熟悉得不能再熟悉的声音..</p>\n<p>  如雪？爱儿感觉不妙....</p>\n<p>  没错！就是我！没想到你眼睛看不见耳朵还挺好使的嘛！如雪满脸得意的笑着..</p>\n<p>  如雪你把我两个孩子怎么样了？快放了他们..爱儿心急如焚..</p>\n<p>  放了他们？你开玩笑吧！放了他们我拿什么来威胁慕容临天？如雪笑得一脸狰狞..</p>\n<p>  娘！娘！小天小爱多想跑到娘身边可是这个女人正掐着他们的脖子...</p>\n<p>  小天小爱你们别害怕...爱儿简直不知道该怎么办..</p>\n<p>  慕容临天呢！</p>\n<p>　</p>\n";

