module.exports = "<hr>\n<p>title: 第7章\ndate: 2017-07-18 03:59:58\nwords: 3607</p>\n<h2 id=\"index-7\">index: 7</h2>\n<!-- deleteAbove -->\n<p>   嗯干爹..爱儿边哭边说...</p>\n<p>  哎我的好女儿..来叫干娘指着萧淑（易的夫人）</p>\n<p>  干娘...</p>\n<p>  萧淑点点头摸摸爱儿前额的秀发：乖.....</p>\n<p>  爱儿从今天开始你要跟着干爹干娘学武功这样才能保护你自己知道吗？</p>\n<p>  爱儿擦干眼泪爱儿知道我会好好学的...</p>\n<p>  乖女儿...大哥大嫂在天之灵会感到安慰的..易天齐转过头看着窗外的天空....</p>\n<p>  干爹我有个请求..</p>\n<p>  哦？</p>\n<p>  说吧干爹一定答应你...</p>\n<p>  你也收如水姐姐为干女儿好不好？</p>\n<p>  爱儿真诚地问..</p>\n<p>  这个好吧易天齐面露难色但还是答应了...</p>\n<p>  干爹干娘请受如水一拜..如水跪下来向易天齐夫妇磕头...</p>\n<p>  如水只要你今后跟爱儿好好相处我们易家是不会亏待你的..易天齐一本正经道..</p>\n<p>  如水知道了谢干爹干娘...</p>\n<p>  第十四章众里寻她千百度，蓦然回首（六）</p>\n<p>  爱儿今天干爹教你武功...易天齐笑着拉着爱儿的小手来到后院的练武场...</p>\n<p>  干爹..爱儿抬起那可爱的脸庞大眼睛扑闪扑闪的看着他那我今天应该学什么呢？</p>\n<p>  不会很难很难吧？</p>\n<p>  呵呵爱儿你知道吗？任何事都不可能一步登天的要把基础打好一步一步来稳打稳扎才行..明白了吗？</p>\n<p>  易天齐笑着回答言语中充满了慈爱...</p>\n<p>  嗯爱儿明白了..扬起一个甜甜的微笑..</p>\n<p>  今天就从基础学起先学扎马步..易天齐忽然收起笑容干爹会对你很严格你能忍住吗？</p>\n<p>  嗯爱儿无论如何一定会忍住！爱儿坚定地说..</p>\n<p>  好开始吧！</p>\n<p>  易天齐说着左脚向左叉开直到两脚与肩同宽双手握拳放在腰间下蹲...爱儿你来推我看你能不能推的动..</p>\n<p>  好！</p>\n<p>  爱儿冲过去双手从背后推他可不管怎么推他都一动不动...半晌爱儿累得香汗淋漓....干爹你太重了爱儿推不动..说罢伸手擦擦汗...</p>\n<p>  呵呵爱儿不是干爹重而是干爹马步扎得稳爱儿也要练成这样哦。</p>\n<p>  易天齐笑着说..</p>\n<p>  好！爱儿也试试！说完也学着易天齐刚才的动作扎起马步来...干爹是不是这样？</p>\n<p>  易天齐上前双手握住爱儿的腰往下压蹲得要再低一点腿要再开一点这样才扎的稳！</p>\n<p>  今天先扎半个时辰爱儿一定要坚持哦..</p>\n<p>  嗯！爱儿回应道..</p>\n<p>  远处的墙后</p>\n<p>  爱儿好像练得很幸苦为什么干爹要爱儿练武呢？</p>\n<p>  而我...如水自言自语道..</p>\n<p>  你在这里干什么！</p>\n<p>  干娘！</p>\n<p>  我..我没干什么在看爱儿练武....如水有些害怕的看着满脸气愤的萧淑...她手里拿着竹条干什么？</p>\n<p>  哦？怎么？你羡慕？我们答应爱儿的要求你还真以为你是我们的干女儿啦？</p>\n<p>  告诉你在爱儿面前你是我们的干女儿但爱儿不在的时候你只是我们府里的一个奴婢听清楚没！</p>\n<p>  萧淑指着如水教训道..</p>\n<p>  如水被眼前凶狠的萧淑吓着了...知..知道了干娘..</p>\n<p>  干娘？</p>\n<p>  萧淑明显不悦一巴掌扇过去如水的左脸上立刻出现了五个绯红的手指印..</p>\n<p>  说了爱儿不在时你只是奴婢还敢叫我干娘？</p>\n<p>  那娇美的容颜顿时拧成一团...</p>\n<p>  如水再也控制不住那泪水..从眼睛夺眶而出...主人..如水不愿叫她夫人在她心中只有爱儿的娘才是夫人最疼她的夫人...</p>\n<p>  这还差不多..萧淑得意着...跟我去厨房干活！</p>\n<p>  是主人..如水用手捂着火辣辣的左脸跟着萧淑走了...</p>\n<p>  厨房中</p>\n<p>  今天你要把这些盘子全洗好！</p>\n<p>  萧淑叫嚣着...</p>\n<p>  看着满盆大大小小的盘子如水愣住了..这么多...</p>\n<p>  怎么？</p>\n<p>  嫌多？嫌多也得洗！要是敢打破一个盘子看我怎么收拾你！</p>\n<p>  不多不多..如水赶忙说道..我洗...于是蹲来拿起盘子和抹布一个一个洗起来....以前夫人从来没让她做过这些事.....夫人....</p>\n<p>  终于把盘子洗完了..十个一摞叠放着如水搬起一摞准备放进柜子里..结果刚走一步脚不小心绊到另一摞连人带盘子摔到地上噼里啪啦..盘子打得粉碎如水的手被碎片割出了一条又一条的血痕...眼泪落下来....本以为萧淑会同情她把她扶起来帮她上药可是...</p>\n<p>  竹条重重的落在如水的背上....</p>\n<p>  你个jian俾叫你不要打碎盘子你居然打碎这么多！</p>\n<p>  看我今天不打死你！说罢竹条像雨点般落下里落在如水的背上手臂上腿上....</p>\n<p>  ....主人..不要打....奴婢知错了....可怜的小人儿痛得在地上翻来覆去...</p>\n<p>  半晌萧淑打累了这才停下来..哼今天就当给你个教训！</p>\n<p>  起来把碎片捡好！还有这些事不准让爱儿知道！当初留你一条命你要知道感恩！</p>\n<p>  奴..奴婢知道..</p>\n<p>  萧淑走出了厨房如水忍着痛慢慢爬起来一片一片捡着地上的碎片血染红了一片...不争气的眼泪忍不住一颗颗掉下来...夫人....如水好想你....她喃喃道...</p>\n<p>  第十五章众里寻她千百度，蓦然回首（七）</p>\n<p>  爱儿如水快出来吃晚饭了....萧淑笑脸盈盈的喊着..</p>\n<p>  一会儿爱儿揉着腰和腿从里面出来了如水也走了出来...</p>\n<p>  餐桌上鸡鸭鱼样样都有让人垂涎....</p>\n<p>  爱儿怎么样今天跟你干爹练功累不累？</p>\n<p>  萧淑往爱儿的碗里夹了点菜...</p>\n<p>  干娘你不知道干爹今天让我练的那个扎马步好厉害我现在腰和腿还没力气呢..</p>\n<p>  爱儿撅起小嘴又揉揉腰...</p>\n<p>  呵呵爱儿你今天肯定累着了吧不过练功贵在坚持知道吗？</p>\n<p>  易天齐笑着..</p>\n<p>  嗯爱儿谨遵干爹教诲..露出一个大大的微笑....</p>\n<p>  爱儿真乖来干爹奖你吃个鸡腿！</p>\n<p>  说着加了一块最大的鸡腿放到爱儿碗里..</p>\n<p>  谢谢干爹..爱儿笑着狼吞虎咽起来...</p>\n<p>  如水坐在旁边双手包裹着厚厚的纱布身上到处都疼但她得忍着...轻轻地刨着白饭...</p>\n<p>  既然爱儿腰腿酸如水你作为姐姐的给妹妹按摩一下吧..萧淑看着不动声色的如水提议道...</p>\n<p>  如水抬起头来看着萧淑的眼睛它们仿佛在说着：我故意的我就是故意的怎么样！</p>\n<p>  哎呀如水你的手怎么了？萧淑抬起如水的双手假装呵护的样子让人看了就觉得恶心..</p>\n<p>  对如水姐姐你的手怎么回事？</p>\n<p>  痛不痛？是不是有人欺负你？告诉我我帮你教训他！爱儿关切的问小粉拳攥起来做出个打的样子...</p>\n<p>  看着眼前天真可爱的爱儿如水的眼里满是眼泪她多想站起来指着萧淑大声说就是她！</p>\n<p>  但是......如水轻轻的叫了一声该死的萧淑她居然捏她受伤的手...转过头看见萧淑阴狠的脸恶狠狠的眼神告诉她：不要乱说话！</p>\n<p>  她又如何和他们斗只有忍气吞声....</p>\n<p>  如水姐姐你怎么了是不是手痛了？</p>\n<p>  爱儿轻轻接过来如水的双手用嘴吹吹..我给你呼呼就不痛了..</p>\n<p>  爱儿我没事我今天不小心摔了一跤手就被划破了没事的..如水爱怜的看着爱儿..</p>\n<p>  那你今后要小心一点哦....爱儿看着如水说..</p>\n<p>  吃完饭....</p>\n<p>  爱儿你今天也累了先回房休息吧..易天齐道..</p>\n<p>  那如水姐姐呢？</p>\n<p>  爱儿天真的问道..</p>\n<p>  她不是受伤了吗干爹干娘给她上点药..</p>\n<p>  那好吧干爹干娘如水姐姐爱儿先回房了..说罢福了福身转身离开..</p>\n<p>  待爱儿离开后...易天齐开口了..</p>\n<p>  如儿...你的手怎么样了？</p>\n<p>  如儿？真够恶心的...</p>\n<p>  主人叫我如水就好了奴婢受不起这么亲切的称呼...如水完全不领情...</p>\n<p>  怎么叫我主人呢？</p>\n<p>  还有什么奴婢？我是你干爹你是我最亲最爱的干女儿...</p>\n<p>  如水犹豫着该不该相信易天齐的话....</p>\n<p>  好了我知道今天是你干娘太过分了记住了你今后是我最爱的如儿不是什么奴婢..</p>\n<p>  原来他知道哼现在又来假猩猩吗真是蛇鼠一窝...</p>\n<p>  是如儿谨遵干爹教诲...如水站起来做了个万福..</p>\n<p>  嗯我的乖女儿..易天齐捋着胡须笑道...今后有谁欺负你跟我说干爹绝不会让我的乖女儿受委屈的！</p>\n<p>  易天齐信誓旦旦的说但眼角的jian诈始终藏不住...</p>\n<p>  谢干爹疼爱..既然他们要演戏她就跟他们演到底...</p>\n<p>  如儿既然你是我的干女儿我照样也给你些忠告...他凑到如水耳朵旁不该说的话不该做的事就不要说不要做你懂我的意思吗？</p>\n<p>　</p>\n";

