module.exports = "<hr>\n<p>title: 第27章\ndate: 2017-07-18 03:59:59\nwords: 3511</p>\n<h2 id=\"index-27\">index: 27</h2>\n<!-- deleteAbove -->\n<p>如雪依旧是像雪一样的素衣..只可惜不食人间烟火的外表下有一颗丑陋不堪的心...真是浪费了这副好皮囊..</p>\n<p>  如雪姐姐..爱儿心里特别不想见到她见到她就会想到她肚子里的孩子..</p>\n<p>  不知爱儿妹妹这几日是否呢？如雪若有所指也难怪王爷这几日每天都来胜雪居对我嘘寒问暖恐怕是无暇顾及妹妹了..</p>\n<p>  什么？慕容临天这几天都在她那儿？她不相信..如果如雪姐姐没事的话我就先走了..</p>\n<p>  怎么妹妹每次看到我就想走呢？如雪是故意的她相信..</p>\n<p>  爱儿只是还有事要忙..甩下这句话爱儿便离开了往典医阁的方向走去..</p>\n<p>  梅凝我们跟上去..如雪小声吩咐...</p>\n<p>  典医阁</p>\n<p>  寒一个人独坐在典医阁内温着酒..</p>\n<p>  寒哥哥...爱儿走了进来..</p>\n<p>  爱儿看清来人寒有些激动..连忙扶着她的手坐下..不料一碰到她的脉搏..什么！她怀孕了...</p>\n<p>  爱儿你这几日可有什么不适？寒关怀的问..</p>\n<p>  没什么大问题就是胃口有些欠佳..爱儿平淡的说..</p>\n<p>  你知道吗？你怀孕了...寒说着替她高兴可心里却有些抽痛...毕竟她怀的是别人的孩子..</p>\n<p>  怀孕？爱儿着实吃了一惊..为什么我不知道？</p>\n<p>  是的不过孩子只有一个月大所以你还感觉不到..寒解释道..</p>\n<p>  爱儿低下头用手轻轻摸了摸自己平坦的小腹..心中有一丝欣喜..她要当妈妈了！是天的孩子...可是想到慕容临天心里不觉有些隐隐作痛...</p>\n<p>  怎么了？爱儿..寒看出了她眼中的异样..</p>\n<p>  爱儿叹了一口气...没什么只是心情不太好..她没有说出真相...</p>\n<p>  见她不愿意说寒也并不勉强..</p>\n<p>  爱儿你知道吗？你是一个与众不同的女子..寒看着她道..</p>\n<p>  我？爱儿满脸疑惑..</p>\n<p>  是的你善良、纯真、一心只为别人着想...我从未见过像你这样的女子...寒平静的说但心里却是很不平静..</p>\n<p>  被他这样一夸爱儿不有些脸红...寒哥哥过奖了..如水姐姐比爱儿好千万倍为何你没发现她的好处呢？</p>\n<p>  寒似要说什么又咽了回去..几经周折最终还是下定决心...爱儿..我喜欢你..</p>\n<p>  什么！爱儿吓了一跳...可是...</p>\n<p>  爱儿不用说出来我知道你一心只在慕容临天身上..寒的眼神暗淡了些..只不过有些话我想说出来不然我会后悔...寒鼓起勇气...</p>\n<p>第五十八章 昨夜星辰昨夜风（十）</p>\n<p>其实在很久以前有一位女子叫明月..她的眼睛就像月亮一样美..见到她后我便深深爱上了她以致无法自拔...后来我便娶了她做我的夫人我们一直相处得很融洽...直到那天我潜回麟府既然看到...他和我的大哥在....说到这里寒顿了顿..眼里的悲痛是藏不住的...他接着说...我问她为什么要这样对我她却告诉我..她从来没有爱过我她跟我在一起完全是因为我的钱和权...一气之下我便一剑杀死了她...那一夜发生的事每天都会像放电影般在他脑海回放...他不知道他杀了多少人..只知道那一晚麟府上方的天空也被映成了血红色...还有他最爱的女人背叛了他..寒苦笑着那晚之后我的头发就全变成了现在这个样子..说着捏起一小撮额前的银发摆弄着...从那以后我便不再相信任何人..我拼命学医却很少医治别人我认为薄情寡义的人根本不配活在世界上..渐渐的..我就变成了一副冷漠异常的模样..正如你第一次见到我时..</p>\n<p>  回忆起第一次见面..</p>\n<p>  好一对姐妹情深..</p>\n<p>  那好要我救你姐姐你就把你最珍贵的东西给我..</p>\n<p>  命？我要你的命做什么？对于你们女子最珍贵的应该是....我说得对吗？</p>\n<p>  那时的寒确实让人感到可怕..</p>\n<p>  我行医的原则是只治重情义者..当我看到你和如水姐妹情深的样子我很反感..觉得这是装出来的..可是你居然同意用还你姐姐的命..第一次我被你震撼了...</p>\n<p>  所以你说的那些是考验我的咯？爱儿问道..</p>\n<p>  的确..寒毫不隐瞒..我知道你是有情有义的人所以情不自的把我的故事告诉了你..而你后来所说的话再次拨动了我的心弦..接着暗夜铁骑军来追杀我没跟你们道别我就离去了..本以为这辈子不会再见到你可谁知道在王府又见到了你..</p>\n<p>  那你是怎么到王府当太医的呢？爱儿插道..</p>\n<p>  因为我被他们抓了回来结果王爷根本不追究我杀麟家人的事就让我在王府当个太医..</p>\n<p>  哦原来如此..爱儿点点头..</p>\n<p>  上天让我再遇到你我相信这是缘分..后来我第一次笑是因为你..看见你受伤我会心痛..我原本不明白这是为什么以为只是对你有好感罢了...可现在我明白了..这是因为...我喜欢你爱儿...寒的脸居然也会红..</p>\n<p>  爱儿亦没有说话...</p>\n<p>  屋外如雪笑着离开了..易爱儿这次看你还能怎么办！</p>\n<p>  另一边..一个魁梧的黑色身影猛地震了一下..心狠狠的揪痛着...拳头握的咔咔直响...头也不回的离开了...</p>\n<p>  寒继续说着..只是在我明白一切的时候已经晚了..你爱上了慕容临天...所以我决定..默默祝福你..说出这些话的时候..寒的心碎了..很彻底..一场本不该有的爱恋最终的结果就是伤了自己..爱儿你一定要幸福..</p>\n<p>  此时爱儿早已是泪流满面寒哥哥那么好的人..为什么上天要对他这么不公平让他如此命运多桀...而且爱上了一个注定不会爱他的人...是自己不好..我居然伤了他这么深..爱儿一把握住寒的手..寒哥哥..我们永远都是好朋友..你永远是爱儿的好哥哥..</p>\n<p>  寒苦笑了一下..不过能一直看着她幸福自己也会是幸福的吧..嗯..他点了点头..</p>\n<p>  夜月亮淹没在乌云里...星星也钻进云里不露出那一闪一闪的大眼睛..</p>\n<p>  而偌大的王府里碎了一地的..是心</p>\n<hr>\n<p>  唉....真情告白固然是美好的可是却造成了很不美好的事情.....到底什么事呢？如雪又会有怎样的阴谋...</p>\n<p>  第五十九章 昨夜星辰昨夜风（十一）</p>\n<p>月华殿</p>\n<p>  如水一个人站在窗前眺望这窗外的蓝天...</p>\n<p>  到底干爹有什么阴谋？我该不该把那些事告诉爱儿..如水为这些烦心着无奈的低头叹着气....</p>\n<p>  姐姐好兴致！如雪被梅凝搀扶着慢慢走进来...语气里尽是讽刺..</p>\n<p>  如雪..她赶快走过来扶如雪坐下..你怀着孕呢小心点...</p>\n<p>  今天如雪是专门来看姐姐你的..如雪不屑的看着她漫不经心的玩弄着手指甲..顺便为姐姐你感到悲哀...</p>\n<p>  妹妹何出此言？如水不解...</p>\n<p>  妹妹早就看出来姐姐你对那个麟寒麟太医情有独钟...对吗？如雪戏谑的看着她..</p>\n<p>  我...如水的脸上布满了红霞心里有些甜滋滋的...</p>\n<p>  可是....如雪话锋一转..姐姐可知我昨日在典医阁外听见了什么？</p>\n<p>  如水摇了摇头...</p>\n<p>  我听见..你的梦中在跟你的爱儿妹妹告白呢...如雪的嘴角露出一丝狞笑..</p>\n<p>  什么！如水的身子一震跌坐在椅子上...</p>\n<p>  那个麻的程度妹妹还真是找不到词来形容..两个人又是拉手又是拥抱的毫不避嫌...看他们这个样子说不定明天就了！如雪继续添油加醋道...</p>\n<p>  如水一言不发..其实她心里清楚寒是喜欢爱儿的..但是为什么从如雪的嘴里说出来她还是会这么这么的心痛...一见钟情真的有这么刻骨铭心吗？只不过是一面就挖掘了自己心里如此强烈的爱？</p>\n<p>  如雪还真是替姐姐不值..既然姐姐没什么说的妹妹就先走了...说罢如雪收起那得意的表情一步一步的走了...留下如水一个人孤寂的身影...</p>\n<p>  如雪你今天干嘛专门来跟她说这些？梅凝扶着如雪不解的问..</p>\n<p>  这是为了以防万一..如雪有些得意..</p>\n<p>  哦？</p>\n<p>  你看她那个样子飘忽不定的要是她那天把事情告诉了那个易爱儿我们的计划不就全被破坏了嘛？</p>\n<p>　</p>\n";

