module.exports = "<hr>\n<p>title: 第38章\ndate: 2017-07-18 04:00:00\nwords: 3597</p>\n<h2 id=\"index-38\">index: 38</h2>\n<!-- deleteAbove -->\n<p>   不好意思我没管住他们..梦娘尴尬的走过来一把把他们俩抱走..你们继续我们什么都没看到..顺便偷笑了两声..</p>\n<p>  算了现在还哪有心情..慕容临天不高兴的撇撇嘴..爱儿我们去吃饭吧..</p>\n<p>  好..爱儿脸红的说了一声..伸手去摸自己的衣服可是半天都没摸到..看着爱儿的动作慕容临天一阵心痛...都是为了我为了我们的孩子她才会这样的我一定要想办法治好她！</p>\n<p>  伸手拿来爱儿的衣服..爱儿我帮你....说完拿起衣服给爱儿穿上..</p>\n<p>  天..我是不是很没用？</p>\n<p>  爱儿的眸光黯淡下来..毕竟对于自己看不见的事实她也很难过只是平时掩饰得好罢了..</p>\n<p>  怎么会呢？</p>\n<p>  慕容临天爱怜的看着她的眼睛..在额头上印下一个吻...</p>\n<p>  饭桌上</p>\n<p>  小天小爱你们过来..爱儿笑着张开双臂..</p>\n<p>  娘~~~两人一左一右的扑进爱儿的怀里..</p>\n<p>  小天小爱你们想不想见你们的爹？</p>\n<p>  爱儿问道...</p>\n<p>  不想！两人一齐回答...</p>\n<p>  不想？慕容临天有些难过..想必是这么多年自己不在他们身边孩子心里肯定有些怪我...唉...他叹了一口气....</p>\n<p>  爱儿看出了他的难过心里也很愧疚...都是因为自己没有看清楚易天齐的真面目才造成今天这样...</p>\n<p>  为什么呢？</p>\n<p>  爱儿问道..她一定要让孩子们爱上他们的爹..</p>\n<p>  因为...两个小家伙互相使了个眼色..双双扑进慕容临天的怀里..我们已经知道爹爹是谁了！</p>\n<p>  ！呵呵！！爹！</p>\n<p>  诶慕容临天被这突如其来的举动感动了..</p>\n<p>  扑哧爱儿不笑了出来..</p>\n<p>  小爱肥嘟嘟的手捏上了慕容临天的脸..爹爹..你长得好帅！</p>\n<p>  比哥哥还帅！！</p>\n<p>  望着自己怀里的小天小爱慕容临天真心的笑了出来..哈哈...小爱长得也很美像一样..</p>\n<p>  哪有！</p>\n<p>  她那么肥！小天插嘴道...</p>\n<p>  哼！你是嫉妒爹长得比你帅！小爱朝小天吐了吐舌头..</p>\n<p>  原以为孩子们不喜欢自己原来只是想给自己一个惊喜..</p>\n<p>  好啦好啦别赖在你们爹身上不下来了快吃饭吧！</p>\n<p>  梦娘笑道..看着这一家人终于幸福的团聚了她也由衷的高兴...</p>\n<p>  哦对了云姑娘多谢你这么多年对爱儿他们的照顾我慕容临天感激不尽..慕容临天放下小天小爱抱拳道..</p>\n<p>  不用谢他们也带给我很多欢乐..说着悄悄对爱儿说..你相公还真是个极品男人！</p>\n<p>  那当然啦不然我怎么会看得上！爱儿有点小得意..我跟你说的那件事你考虑好没有？</p>\n<p>  爱儿！梦娘的脸唰一下红了...</p>\n<p>  什么事爱儿？慕容临天不明白她们这是唱的那一出..</p>\n<p>  是这样我想把枫哥哥介绍给她...说完戏谑梦娘道..你不觉得他们两个很配吗？</p>\n<p>  爱儿你再说我就不理你了！梦娘这下可是脸全红了...</p>\n<p>  好好好！</p>\n<p>  我不说行了吧..吃饭吧..猜也猜得到梦娘肯定是害羞了说明她对枫还是有些好感的...</p>\n<p>  爱儿你打算什么时候回去？</p>\n<p>  慕容临天问道..</p>\n<p>  梦娘你觉得呢？爱儿继而问梦娘..</p>\n<p>  我？</p>\n<p>  跟你们一起回去？梦娘有些惊讶..</p>\n<p>  当然你难道不想见识一下我口中的枫哥哥吗？</p>\n<p>  趁梦娘没开口爱儿话锋一转..况且小天小爱也舍不得你..</p>\n<p>  对对干娘小天舍不得离开你..</p>\n<p>  我也是..小爱插嘴道..两人眼泪汪汪的看着梦娘满是期待...</p>\n<p>  这...梦娘还真是难住了...</p>\n<p>  云姑娘你就跟我们一起回去吧..慕容临天也劝道..</p>\n<p>  唉纵使梦娘再顽强也敌不过他们的全家总动员..勉为其难之下...那好吧..</p>\n<p>  耶！</p>\n<p>  ！小天小爱大声欢呼着..</p>\n<p>  那我们明天就出发吧别让他们等太久...慕容临天说道..</p>\n<p>  这么快？</p>\n<p>  爱儿有些惊讶..</p>\n<p>  你知道吗他们都很想见到你他们肯定还不相信你活着呢！</p>\n<p>  你不想你的如水姐姐吗？慕容临天看着爱儿说..</p>\n<p>  如水姐姐...浓密的思愁齐涌而来...她过得好吗？</p>\n<p>  你说呢？她和寒成亲了现在肚子里正怀着一个小孩呢..慕容临天知道爱儿一定会开心的..</p>\n<p>  真的！</p>\n<p>  果然不出慕容临天所料..太好了那我要早点回去见她！</p>\n<p>  慕容临天悄悄凑到她耳边轻轻耳语...什么时候我再让你怀一个？</p>\n<p>  讨厌！爱儿娇嗔道...</p>\n<p>  饭桌前的三个人就这么呆呆的看着他们夫妻俩..真是的！</p>\n<p>  也不注意一下场合...梦娘心里想..</p>\n<p>  不过小天小爱两个人倒是一副羡慕的样子：哇塞！</p>\n<p>  爹娘好恩爱哦！</p>\n<p>  第八章从别后，忆相逢，几回梦魂与君同（八）</p>\n<p>  第二天清早慕容临天一行人就踏上了回府的征程...</p>\n<p>  今天的天气格外的好小草慢慢长小鸟啾啾叫..</p>\n<p>  小天小爱跑在最前面嬉戏着还真是一对活宝..慕容临天扶着爱儿在后面慢慢走着眉宇动作间充满了深情...梦娘一个人孤独的走在中间不过他到底是什么样的呢？</p>\n<p>  是不是真有爱儿说的那么好呢？呸呸呸！想什么呢！</p>\n<p>  王府</p>\n<p>  你说天到底追那个万忠彪追到哪里去了？</p>\n<p>  这么几天还没回来..枫焦急的来回踱着步..</p>\n<p>  枫别担心他一定会回来的..如水安慰道..</p>\n<p>  寒走上来扶如水坐下..你肚子里还有孩子呢别乱动..正在大家都在担心的时候...</p>\n<p>  冯统领！</p>\n<p>  王爷回来了！周管家气喘吁吁的来报..</p>\n<p>  枫赶快冲到了王府大门口随后寒也扶着如水跟着他..</p>\n<p>  天..枫笑着拍了拍慕容临天的肩膀你终于回来了！</p>\n<p>  当然了我是那么容易回不来的吗？慕容临天依旧勾起一抹笑..而且这次我还带回来了一个人..</p>\n<p>  万忠彪？</p>\n<p>  枫问道...</p>\n<p>  慕容临天摇摇头..睁大你的眼睛看好了！说着走到马车边牵着爱儿下来..</p>\n<p>  枫哥哥...爱儿甜甜一笑...</p>\n<p>  爱..爱儿..你没死？</p>\n<p>  太好了！枫激动的热泪盈眶上前一把抱住爱儿...枫哥哥很想你..</p>\n<p>  枫哥哥我也是...爱儿高兴得眼眶都红了终于又见到了他们这五年也值了...</p>\n<p>  嗯哼！</p>\n<p>  慕容临天不高兴的哼了一声..这个枫真不识时务居然抱着自己的爱人这么久！</p>\n<p>  兄妹情深也不用这么上演吧毕竟不是亲兄妹！还是赶快把梦娘嫁给枫自己也少个竞争对手..</p>\n<p>  好啦天你也太小气了吧！</p>\n<p>  我五年没见爱儿了才抱一下你就这么吹胡子瞪眼的...枫不服气道...</p>\n<p>  枫哥哥别跟他计较我给你介绍一个人..梦娘..爱儿轻唤一声梦娘就从马车上下来...一身紫衣映衬着姣好的面容让人赏心悦目...而枫依旧是一袭白衣翩翩英俊的面容配以迷人的微笑..</p>\n<p>  只一眼...她（他）就迷上了他（她）...就这样眉目传情着...</p>\n<p>  爱儿是不是爱儿回来了？</p>\n<p>  如水听到了爱儿的声音激动不已...</p>\n<p>  看着眼前这张熟悉的脸...爱儿！</p>\n<p>  如水高兴极了...寒亦是没想到还能再见到她要不是她自己又怎么会发现如水的好？</p>\n<p>  如水姐姐..爱儿笑靥如花用木棍摸索着前进...</p>\n<p>  我扶你..慕容临天赶紧拉住她的手..</p>\n<p>  爱儿你的眼睛如水震住了...寒没有说话只是眉头深锁..</p>\n<p>  对呀爱儿你的眼睛怎么了？</p>\n<p>  枫刚才都是主动冲过来的还没发现她的眼睛有问题...</p>\n<p>  没什么只是看不见了..爱儿笑着说她不想让他们伤心所以语气尽量放平淡..殊不知这样的她更让人心疼...</p>\n<p>  怎么会看不见呢？</p>\n<p>  如水心疼的看着他...赶紧走过来扶她..</p>\n<p>  先进去吧爱儿再慢慢跟你们说...慕容临天说道...</p>\n<p>  回到大厅爱儿又将前因后果交代了一遍....</p>\n<p>  众人无不悲恸也无不敬佩这个爱儿...爱儿我一定会努力治好你的眼睛的..寒道..</p>\n<p>  嗯..爱儿点头致谢..</p>\n<p>  爱儿那你的两个孩子呢？</p>\n<p>  枫问道..</p>\n<p>  我们在这里！！小天和小爱居然从慕容临天的座位后面溜出来...咯咯的笑着..</p>\n<p>  小天小爱又在调皮对不对？</p>\n<p>  爱儿能感觉到这两个小家伙又在搞鬼...</p>\n<p>  娘~~~他们跑到爱儿面前..我们只是想给你们一个惊喜嘛....</p>\n<p>  见爱儿还是一脸严肃小天小爱又扑进慕容临天的怀里撒娇道..爹~~~你叫娘不要生气了嘛...</p>\n<p>  好~~~慕容临天笑开了花...爱儿你就别怪他们了..</p>\n<p>  我有说怪他们吗？</p>\n<p>　</p>\n";

